const initState = {
  projects: [],
  project: null,
  search: null,
  filter: null,
  customers: [],
  employees: [],
  project_parents: [],
  loading: false,
  loadingSubmit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function projectsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_PROJECTS_REQUEST": {
      return {
        ...state,
        //projects: [],
        roles:[],
        loading: true,
        loadingFilterRoles: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_PROJECTS_SUCCESS": {
      return {
        ...state,
        projects: action.projects,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_PROJECTS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      }
    }
    case "CREATE_PROJECT_REQUEST": {
      return {
        ...state,
        customers: [],
        employees: [],
        project_parents: [],
        loading: true,
        loadingSubmit: true,
      }
    }
    case "CREATE_PROJECT_SUCCESS": {
      return {
        ...state,
        customers: action.customers,
        employees: action.employees,
        project_parents: action.project_parents,
        loading: false,
        loadingSubmit: false,
      }
    }
    case "STORE_PROJECT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_PROJECT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "STORE_PROJECT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_PROJECT_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "EDIT_PROJECT_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        project: action.project
      };
    }
    case "EDIT_PROJECT_ERROR": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_PROJECT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_PROJECT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_PROJECT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "UPDATE_PROJECT_HISTORY_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_PROJECT_HISTORY_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_PROJECT_HISTORY_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_PROJECT_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_PROJECT_SUCCESS": {
      return {
        ...state,
        loading: false,
        projects: action.projects
      };
    }
    case "DEACTIVATE_PROJECT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_PROJECT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_PROJECT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_PROJECT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_PROJECT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_PROJECT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
