const initState = {
  departments: [],
  department: null,
  employees: [],
  dropdown_update: [],
  departments_list: [],
  loadingSubmit: false,
  loadingDepartment: false,
  loadingDepartments: false,
  loadingDepartmentsList: false,
  condition: 0,
  success: null,
  error: null,
  errorField: null
};

const departmentsReducer = (state = initState, action) => {
  switch (action.type) {
    case "GET_DEPARTMENTS_REQUEST":
      return {
        ...state,
        condition: 0,
        department: null,
        loadingDepartments: true,
        loadingDepartmentsList: true,
      };
    case "GET_DEPARTMENTS_SUCCESS":
      return {
        ...state,
        loadingDepartmentsList: false,
        departments_list: action.departments_list,
        employees: action.employees,
        loadingDepartments: false,
        departments: action.departments
      };
    case "GET_DEPARTMENTS_ERROR":
      return {
        ...state,
        loadingDepartments: false,
        error: action.error.message
      };
    case "STORE_DEPARTMENT_REQUEST":
      return {
        ...state,
        loadingDepartment: true,
        loadingSubmit: true
      };
    case "STORE_DEPARTMENT_SUCCESS":
      return {
        ...state,
        loadingDepartment: false,
        loadingSubmit: false,
        department: action.department,
        success: action.success.message,
      };
    case "STORE_DEPARTMENT_ERROR":
      return {
        ...state,
        loadingDepartment: false,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    case "SHOW_DEPARTMENT_REQUEST":
      return {
        ...state,
        department: null,
        dropdown_update: [],
        loadingDepartment: true
      };
    case "SHOW_DEPARTMENT_SUCCESS":
      return {
        ...state,
        loadingDepartment: false,
        department: action.department,
        dropdown_update: action.dropdown_update,
        condition: 2,
      };
    case "SHOW_DEPARTMENT_ERROR":
      return {
        ...state,
        loadingDepartment: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    case "UPDATE_DEPARTMENT_REQUEST":
      return {
        ...state,
        loadingDepartment: true,
        loadingSubmit: true
      };
    case "UPDATE_DEPARTMENT_SUCCESS":
      return {
        ...state,
        loadingDepartment: false,
        loadingSubmit: false,
        department: action.department,
        success: action.success.message,
      };
    case "UPDATE_DEPARTMENT_ERROR":
      return {
        ...state,
        loadingDepartment: false,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    case "ACTIVATE_DEPARTMENT_REQUEST":
      return {
        ...state,
        loadingDepartment: true,
        loadingSubmit: true
      };
    case "ACTIVATE_DEPARTMENT_SUCCESS":
      return {
        ...state,
        loadingDepartment: false,
        loadingSubmit: false,
        department: action.department,
        success: action.success.message,
        condition: 2,
      };
    case "ACTIVATE_DEPARTMENT_ERROR":
      return {
        ...state,
        loadingDepartment: false,
        loadingSubmit: false,
        error: action.error.message
      };
    case "DEACTIVATE_DEPARTMENT_REQUEST":
      return {
        ...state,
        loadingDepartment: true,
        loadingSubmit: true
      };
    case "DEACTIVATE_DEPARTMENT_SUCCESS":
      return {
        ...state,
        loadingDepartment: false,
        loadingSubmit: false,
        department: action.department,
        success: action.success.message,
        condition: 2,
      };
    case "DEACTIVATE_DEPARTMENT_ERROR":
      return {
        ...state,
        loadingDepartment: false,
        loadingSubmit: false,
        error: action.error.message
      };
    case "CLEAR_DEPARTMENT":
      return {
        ...state,
        department: null,
        loadingDepartment: false,
        condition: 1,
      };
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return {
        ...state
      };
  }
};

export default departmentsReducer;
