import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPurchaseOrders({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [purchaseOrders, foreign] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(
              get,
              "purchases/purchase-orders?search=" + search + "&page=" + page
            ),
            call(get, "purchases/purchase-orders/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "purchases/purchase-orders?" + filter + "&page=" + page),
            call(get, "purchases/purchase-orders/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "purchases/purchase-orders?search=" + search),
          call(get, "purchases/purchase-orders/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "purchases/purchase-orders?" + filter),
          call(get, "purchases/purchase-orders/foreign-keys")
        ]);

    // console.log(purchaseOrders);

    // Jika sukses get data purchaseOrders
    if (purchaseOrders.status === "success") {
      // Jika di API menggunakan pagination
      if (purchaseOrders.result.current_page && search) {
        yield put({
          type: "GET_PURCHASE_ORDERS_SUCCESS",
          purchaseOrders: purchaseOrders.result.data,
          current_page: purchaseOrders.result.current_page,
          page_size: purchaseOrders.result.per_page,
          total: purchaseOrders.result.total,
          search: search
        });
      } else if (purchaseOrders.result.current_page && filter) {
        yield put({
          type: "GET_PURCHASE_ORDERS_SUCCESS",
          purchaseOrders: purchaseOrders.result.data,
          current_page: purchaseOrders.result.current_page,
          page_size: purchaseOrders.result.per_page,
          total: purchaseOrders.result.total,
          filter: filter
        });
      } else if (purchaseOrders.result.current_page) {
        yield put({
          type: "GET_PURCHASE_ORDERS_SUCCESS",
          purchaseOrders: purchaseOrders.result.data,
          current_page: purchaseOrders.result.current_page,
          page_size: purchaseOrders.result.per_page,
          total: purchaseOrders.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_PURCHASE_ORDERS_SUCCESS",
          purchaseOrders: purchaseOrders.result,
          page_size: null,
          total: null
        });
      }
    } else if (purchaseOrders.status === "fail") {
      // Set message ke session storage
      yield put({
        type: "GET_PURCHASE_ORDERS_ERROR",
        error: { message: purchaseOrders.message }
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_PURCHASE_ORDERS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      yield put({
        type: "GET_PURCHASE_ORDER_FOREIGN_KEYS_SUCCESS",
        locations: foreign.result.locations,
        suppliers: foreign.result.suppliers,
        currencies: foreign.result.currencies,
        taxes: foreign.result.taxes,
        accounts: foreign.result.accounts,
        dpAccount: foreign.result.dp_account,
        cashBanks: foreign.result.cash_banks,
        cashFlowTypes: foreign.result.cash_flow_types,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        couriers: foreign.result.couriers,
        prevTransConfirm: foreign.result.item_request_info,
        processItemConfirm: foreign.result.item_exist_info
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_PURCHASE_ORDER_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_PURCHASE_ORDER_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_PURCHASE_ORDER_FOREIGN_KEYS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Get Items data
export function* getItemsBySupplier({ payload }) {
  const { id_supplier, id_location, date, search } = payload;
  let filter = "items?is_active=1&limit=1";
  filter = search
    ? `${filter}&search=${search}&id_supplier=${id_supplier}&id_location=${id_location}&transaction_date=${date}&item_type=1,2,4`
    : `${filter}&id_supplier=${id_supplier}&id_location=${id_location}&transaction_date=${date}&item_type=1,2,4`;

  try {
    const items = yield call(get, filter);

    if (items.status === "success") {
      yield put({
        type: "GET_FILTER_ITEMS_BY_SUPPLIER_PO_SUCCESS",
        items: items.result
      });
    } else if (items.status === "fail") {
      yield put({
        type: "GET_FILTER_ITEMS_BY_SUPPLIER_PO_ERROR",
        error: { message: items.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_ITEMS_BY_SUPPLIER_PO_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_ITEMS_BY_SUPPLIER_PO_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Get Items with discount
export function* getItemDiscount({ payload }) {
  const { body } = payload;

  try {
    const itemDiscount = yield call(
      post,
      "purchases/purchase-orders/discount-price",
      body
    );
  
    if (itemDiscount.status === "success") {
      yield put({
        type: "GET_DISCOUNT_PO_SUCCESS",
        itemDiscount: itemDiscount.result
      });
    } else if (itemDiscount.status === "fail") {
      yield put({
        type: "GET_DISCOUNT_PO_ERROR",
        itemDiscount: body.purchase_order_details,
        error: {
          message: itemDiscount.message
        }
      });
    } else {
      yield put({
        type: "GET_DISCOUNT_PO_ERROR",
        itemDiscount: body.purchase_order_details,
        error: {
          message: itemDiscount.message
            ? itemDiscount.message
            : "Check your internet connection"
        }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_DISCOUNT_PO_ERROR",
      itemDiscount: body.purchase_order_details,
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createPurchaseOrder() {
  try {
    const [transactions, itemRequests, foreign] = yield all([
      call(get, "purchases/purchase-orders?get_old_trans=1"),
      call(
        get,
        "inventories/item-requests?get_old_trans=1&transaction_status=2,3"
      ),
      call(get, "purchases/purchase-orders/foreign-keys")
    ]);
  
    if (transactions.status === "success") {
      yield put({
        type: "GET_FILTER_PURCHASE_ORDERS_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_FILTER_PURCHASE_ORDERS_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_PURCHASE_ORDERS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (itemRequests.status === "success") {
      yield put({
        type: "GET_FILTER_ITEM_REQUESTS_PO_SUCCESS",
        itemRequests: itemRequests.result.data,
        current_page: itemRequests.result.current_page,
        page_size: itemRequests.result.per_page,
        total: itemRequests.result.total
      });
    } else if (itemRequests.status === "fail") {
      yield put({
        type: "GET_FILTER_ITEM_REQUESTS_PO_ERROR",
        error: { message: itemRequests.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_ITEM_REQUESTS_PO_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (foreign.status === "success") {
      yield put({
        type: "GET_PURCHASE_ORDER_FOREIGN_KEYS_SUCCESS",
        locations: foreign.result.locations,
        suppliers: foreign.result.suppliers,
        currencies: foreign.result.currencies,
        taxes: foreign.result.taxes,
        accounts: foreign.result.accounts,
        dpAccount: foreign.result.dp_account,
        cashBanks: foreign.result.cash_banks,
        cashFlowTypes: foreign.result.cash_flow_types,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        couriers: foreign.result.couriers,
        prevTransConfirm: foreign.result.item_request_info,
        processItemConfirm: foreign.result.item_exist_info
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_PURCHASE_ORDER_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_PURCHASE_ORDER_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_PURCHASE_ORDERS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export function* getTransactions({ payload }) {
  const { page, search, filter } = payload;
  // console.log(filter);

  try {
    const transactions = filter
      ? yield call(
          get,
          "purchases/purchase-orders?get_old_trans=1&" + filter + "&page=" + page
        )
      : search
      ? yield call(
          get,
          "purchases/purchase-orders?get_old_trans=1&search=" +
            search +
            "&page=" +
            page
        )
      : yield call(
          get,
          "purchases/purchase-orders?get_old_trans=1" + "&page=" + page
        );

    if (transactions.status === "success") {
      yield put({
        type: "GET_FILTER_PURCHASE_ORDERS_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_FILTER_PURCHASE_ORDERS_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_PURCHASE_ORDERS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_PURCHASE_ORDERS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

