// Store data
export function* storePurchaseOrder({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "purchases/purchase-orders", body);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_PURCHASE_ORDER_SUCCESS",
        success: { message: resultStore.message },
        id_purchase_order: resultStore.result.id_purchase_order
      });
    } else if (resultStore.errors) {
      yield put({
        type: "STORE_PURCHASE_ORDER_ERROR",
        error: resultStore
      });
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_PURCHASE_ORDER_ERROR",
        error: { message: resultStore.message }
      });
    } else {
      yield put({
        type: "STORE_PURCHASE_ORDER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_PURCHASE_ORDER_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

