// Update data
export function* updatePurchaseOrder({ payload }) {
  const { id_purchase_order, body } = payload;
  // console.log(body);

  try {
    const resultUpdate = yield call(
      putApi,
      "purchases/purchase-orders/" + id_purchase_order,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        put({
          type: "UPDATE_PURCHASE_ORDER_SUCCESS",
          success: { message: resultUpdate.message },
          id_purchase_order: id_purchase_order
        }),
        put({
          type: "EDIT_PURCHASE_ORDER_REQUEST",
          payload: { id_purchase_order: id_purchase_order }
        })
      ]);
    } else if (resultUpdate.errors) {
      yield all([
        yield put({
          type: "UPDATE_PURCHASE_ORDER_ERROR",
          error: resultUpdate
        }),
        yield put({
          type: "EDIT_PURCHASE_ORDER_REQUEST",
          payload: { id_purchase_order: id_purchase_order }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_PURCHASE_ORDER_ERROR",
        error: { message: resultUpdate.message }
      });
    } else {
      yield put({
        type: "UPDATE_PURCHASE_ORDER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_PURCHASE_ORDER_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

