export function* confirmPurchaseOrder({ payload }) {
  const { id_purchase_order } = payload;

  try {
    // POST data ke API
    const confirmResult = yield call(
      putApi,
      "purchases/purchase-orders/confirm-transaction/" + id_purchase_order
    );

    // Jika sukses
    if (confirmResult.status === "success") {
      // Set session message
      yield all([
        put({
          type: "CONFIRM_PURCHASE_ORDER_SUCCESS",
          success: { message: confirmResult.message },
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_PURCHASE_ORDER_REQUEST",
          payload: {
            id_purchase_order: id_purchase_order
          }
        })
      ]);
    } else if (confirmResult.status === "fail") {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_PURCHASE_ORDER_ERROR",
          error: { message: confirmResult.message }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_PURCHASE_ORDER_REQUEST",
          payload: { id_purchase_order: id_purchase_order }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_PURCHASE_ORDER_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_PURCHASE_ORDER_REQUEST",
          payload: { id_purchase_order: id_purchase_order }
        })
      ]);
    }
  } catch (error) {
    yield all([
      put({
        type: "CONFIRM_PURCHASE_ORDER_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      put({
        type: "EDIT_PURCHASE_ORDER_REQUEST",
        payload: { id_purchase_order: id_purchase_order }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

