// Decancel data (mengubah is_active => 0)
export function* processJobCostRollover({ payload }) {
  const { id_job_cost_rollover } = payload;

  try {
    // POST data ke API
    const processResult = yield call(
      putApi,
      "inventories/job-cost-rollovers/process-transaction/" + id_job_cost_rollover,
      // body
    );

    // Jika sukses
    if (processResult.status === "success") {
      yield all([
        yield put({
          type: 'PROCESS_JOB_COST_ROLLOVER_SUCCESS',
          success: processResult
        }),
        yield put({
          type: 'EDIT_JOB_COST_ROLLOVER_REQUEST',
          payload: { id_job_cost_rollover }
        })
      ])
    }
    else if(processResult.status === "fail"){
      yield put({
        type: 'PROCESS_JOB_COST_ROLLOVER_ERROR',
        error: processResult
      })
    }
    else{
      yield put({
        type: 'PROCESS_JOB_COST_ROLLOVER_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'PROCESS_JOB_COST_ROLLOVER_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error);
  }
}

