
const initState = {
  asset_service_types: [],
  asset_service_type: null,
  search: null,
  filter: null,
  loading: true,
  loadingEdit: true,
  asset_categories: [],
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function assetServiceTypesReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_ASSET_SERVICE_TYPES_REQUEST': {
      return {
        ...state,
        //asset_service_types: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_ASSET_SERVICE_TYPES_SUCCESS': {
      return {
        ...state,
        asset_service_types: action.asset_service_types,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_ASSET_SERVICE_TYPES_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
      };
    }
    case 'GET_ASSET_SERVICE_TYPE_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        asset_categories: action.asset_categories,
      };
    }
    case 'GET_ASSET_SERVICE_TYPE_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        error: action.error.message
      }
    }

    case 'CREATE_ASSET_SERVICE_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'CREATE_ASSET_SERVICE_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
      };
    }
    
    case 'STORE_ASSET_SERVICE_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_ASSET_SERVICE_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_ASSET_SERVICE_TYPE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_ASSET_SERVICE_TYPE_REQUEST': {
      return {
        ...state,
        // asset_fiscal: null,
        loading: true,
        loadingEdit: true,
        errorField: null,
      };
    }
    case 'EDIT_ASSET_SERVICE_TYPE_SUCCESS': {
      return {
        ...state,
        // asset_fiscal: action.asset_fiscal,
        loading: false,
        loadingEdit: false,
        asset_service_type: action.asset_service_type,
      };
    }
    case 'EDIT_ASSET_SERVICE_TYPE_ERROR': {
      return {
        ...state,
        // loading: false,
        error: action.error.message,
      };
    }
    case 'UPDATE_ASSET_SERVICE_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_ASSET_SERVICE_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_ASSET_SERVICE_TYPE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_ASSET_SERVICE_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_ASSET_SERVICE_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
        asset_service_types: action.asset_service_types
      };
    }
    case "DEACTIVATE_ASSET_SERVICE_TYPE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_ASSET_SERVICE_TYPE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_ASSET_SERVICE_TYPE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_ASSET_SERVICE_TYPE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_ASSET_SERVICE_TYPE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_ASSET_SERVICE_TYPE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
