import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPurchaseReceiptDetailReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/purchase-receipts/details?";
  if (search) {
    url += search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if (filter) {
      const [purchase_receipt_detail_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/purchase-receipts/foreign-keys")
      ]);
  
      // Jika sukses get data purchase_receipt_detail_reports
      if (purchase_receipt_detail_reports.status === "success") {
        yield put({
          type: "GET_PURCHASE_RECEIPT_DETAIL_REPORTS_SUCCESS",
          purchase_receipt_detail_reports: purchase_receipt_detail_reports.result,
          filter: filter,
          search: search
        });
      } else if (purchase_receipt_detail_reports.status === "fail") {
        yield put({
          type: "GET_PURCHASE_RECEIPT_DETAIL_REPORTS_ERROR",
          error: purchase_receipt_detail_reports
        });
      } else {
        yield put({
          type: "GET_PURCHASE_RECEIPT_DETAIL_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data purchase_receipt_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_PURCHASE_RECEIPT_DETAIL_REPORT_FOREIGN_KEYS_SUCCESS",
          base_currency: foreign_keys.result.base_currency,
          departments: foreign_keys.result.departments,
          locations: foreign_keys.result.locations,
          messages: foreign_keys.result.messages,
          projects: foreign_keys.result.projects,
          pr_detail_search_filter: foreign_keys.result.pr_detail_search_filter,
          suppliers: foreign_keys.result.suppliers
        });
      } else {
        yield put({
          type: "GET_PURCHASE_RECEIPT_DETAIL_REPORT_FOREIGN_KEYS_ERROR"
          //error: {message: "Check your internet connection"}
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/purchase-receipts/foreign-keys")
      ]);
  
      yield put({
        type: "GET_PURCHASE_RECEIPT_DETAIL_REPORTS_SUCCESS",
        purchase_receipt_detail_reports: []
      });
  
      // Jika sukses get data purchase_receipt_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_PURCHASE_RECEIPT_DETAIL_REPORT_FOREIGN_KEYS_SUCCESS",
          base_currency: foreign_keys.result.base_currency,
          departments: foreign_keys.result.departments,
          locations: foreign_keys.result.locations,
          messages: foreign_keys.result.messages,
          projects: foreign_keys.result.projects,
          pr_detail_search_filter: foreign_keys.result.pr_detail_search_filter,
          suppliers: foreign_keys.result.suppliers
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_PURCHASE_RECEIPT_DETAIL_REPORT_FOREIGN_KEYS_ERROR",
          error: foreign_keys
        });
      } else {
        yield put({
          type: "GET_PURCHASE_RECEIPT_DETAIL_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_PURCHASE_RECEIPT_DETAIL_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_PURCHASE_RECEIPT_DETAIL_REPORTS_REQUEST",
      getPurchaseReceiptDetailReports
    )
  ]);
}
