const actions = {
  serverSetup: () => ({
    type: "SERVER_SETUP_REQUEST"
  }),
  typingServer: () => ({
    type: "TYPING_SERVER"
  }),
  serverValidate: serverAddress => ({
    type: "SERVER_VALIDATE",
    payload: { serverAddress }
  }),
  serverSuccessValidate: result => ({
    type: "SERVER_SUCCESS_VALIDATE",
    result
  }),
  serverErrorValidate: () => ({
    type: "SERVER_ERROR_VALIDATE",
  }),
  serverFailValidate: message => ({
    type: "SERVER_FAIL_VALIDATE",
    message
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS",
  }),
  clearError: () => ({
    type: "CLEAR_ERROR",
  })
};
export default actions;
