import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getFixedAssetTransfers({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [fixed_asset_transfers, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "fixed-assets/transfers?search=" + search + "&page=" + page),
            call(get, "fixed-assets/transfers/foreign-keys"),
          ])
        : // Jika filter
          yield all([
            call(get, "fixed-assets/transfers?" + filter + "&page=" + page),
            call(get, "fixed-assets/transfers/foreign-keys"),
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "fixed-assets/transfers?search=" + search),
          call(get, "fixed-assets/transfers/foreign-keys"),
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "fixed-assets/transfers?" + filter),
          call(get, "fixed-assets/transfers/foreign-keys"),
        ]);

    // console.log(fixed_asset_transfers);
    // console.log(foreign_keys);

    // Jika sukses get data fixed_asset_transfers
    if (fixed_asset_transfers.status === "success") {
      // Jika di API menggunakan pagination
      if (fixed_asset_transfers.result.current_page && search) {
        yield put({
          type: 'GET_FIXED_ASSET_TRANSFERS_SUCCESS',
          fixed_asset_transfers: fixed_asset_transfers.result.data,
          current_page: fixed_asset_transfers.result.current_page,
          page_size: fixed_asset_transfers.result.per_page,
          total: fixed_asset_transfers.result.total,
          search: search
        });
      }
      else if(fixed_asset_transfers.result.current_page && filter)
      {
        yield put({
          type: 'GET_FIXED_ASSET_TRANSFERS_SUCCESS',
          fixed_asset_transfers: fixed_asset_transfers.result.data,
          current_page: fixed_asset_transfers.result.current_page,
          page_size: fixed_asset_transfers.result.per_page,
          total: fixed_asset_transfers.result.total,
          filter: filter
        });
      }
      else if (fixed_asset_transfers.result.current_page) {
        yield put({
          type: 'GET_FIXED_ASSET_TRANSFERS_SUCCESS',
          fixed_asset_transfers: fixed_asset_transfers.result.data,
          current_page: fixed_asset_transfers.result.current_page,
          page_size: fixed_asset_transfers.result.per_page,
          total: fixed_asset_transfers.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_FIXED_ASSET_TRANSFERS_SUCCESS',
          fixed_asset_transfers: fixed_asset_transfers.result,
          page_size: null,
          total: null
        });
      }
    } else if (fixed_asset_transfers.status === "fail") {
      yield put({
        type: 'GET_FIXED_ASSET_TRANSFERS_ERROR', 
        error: fixed_asset_transfers
      })
    } else {
      yield put({
        type: 'GET_FIXED_ASSET_TRANSFERS_ERROR', 
        error: {message: "Check your internet connection"}
      })
    }

    // Jika sukses get data fixed_asset_transfer types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_FIXED_ASSET_TRANSFER_FOREIGN_KEYS_SUCCESS',
        accounts: foreign_keys.result.accounts,
        from_locations: foreign_keys.result.from_locations,
        to_locations: foreign_keys.result.to_locations,
        prevTransConfirm: foreign_keys.result.confirmed_info,
        prevTransReceive: foreign_keys.result.received_info,
        departments: foreign_keys.result.departments,
        base_currency: foreign_keys.result.base_currency,
      });
    }
    else{
      yield put({
        type: 'GET_FIXED_ASSET_TRANSFER_FOREIGN_KEYS_ERROR',
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_FIXED_ASSET_TRANSFERS_ERROR', 
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Get all data
export function* getImportFixedAssets({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [fixed_assets] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "fixed-assets?get_old_trans=1&search=" + search + "&page=" + page),
          ])
        : // Jika filter
          yield all([
            call(get, "fixed-assets?get_old_trans=1&" + filter + "&page=" + page),
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "fixed-assets?get_old_trans=1&search=" + search),
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "fixed-assets?get_old_trans=1&" + (filter ? "&" + filter : "")),
        ]);

    // console.log(fixed_assets);

    // Jika sukses get data fixed_assets
    if (fixed_assets.status === "success") {
      // Jika di API menggunakan pagination
      if (fixed_assets.result.current_page && search) {
        yield put({
          type: 'GET_IMPORT_FIXED_ASSETS_FAT_SUCCESS',
          fixed_assets: fixed_assets.result.data,
          current_page: fixed_assets.result.current_page,
          page_size: fixed_assets.result.per_page,
          total: fixed_assets.result.total,
          search: search
        });
      }
      else if(fixed_assets.result.current_page && filter)
      {
        yield put({
          type: 'GET_IMPORT_FIXED_ASSETS_FAT_SUCCESS',
          fixed_assets: fixed_assets.result.data,
          current_page: fixed_assets.result.current_page,
          page_size: fixed_assets.result.per_page,
          total: fixed_assets.result.total,
          filter: filter
        });
      }
      else if (fixed_assets.result.current_page) {
        yield put({
          type: 'GET_IMPORT_FIXED_ASSETS_FAT_SUCCESS',
          fixed_assets: fixed_assets.result.data,
          current_page: fixed_assets.result.current_page,
          page_size: fixed_assets.result.per_page,
          total: fixed_assets.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_IMPORT_FIXED_ASSETS_FAT_SUCCESS',
          fixed_assets: fixed_assets.result,
          page_size: null,
          total: null
        });
      }
    } else if (fixed_assets.status === "fail") {
      yield put({
        type: 'GET_IMPORT_FIXED_ASSETS_FAT_ERROR', 
        error: fixed_assets
      })
    } else {
      yield put({
        type: 'GET_IMPORT_FIXED_ASSETS_FAT_ERROR', 
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'GET_IMPORT_FIXED_ASSETS_FAT_ERROR', 
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createFixedAssetTransfer() {
  try {
    const [foreign_keys] = yield all([
      call(get, "fixed-assets/transfers/foreign-keys"),
    ]);
  
    // Jika sukses get data account
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_FIXED_ASSET_TRANSFER_FOREIGN_KEYS_SUCCESS',
        accounts: foreign_keys.result.accounts,
        from_locations: foreign_keys.result.from_locations,
        to_locations: foreign_keys.result.to_locations,
        prevTransConfirm: foreign_keys.result.confirmed_info,
        prevTransReceive: foreign_keys.result.received_info,
        departments: foreign_keys.result.departments,
        base_currency: foreign_keys.result.base_currency,
      });
    }
    else{
      yield put({
        type: 'GET_FIXED_ASSET_TRANSFER_FOREIGN_KEYS_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_FIXED_ASSET_TRANSFER_FOREIGN_KEYS_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeFixedAssetTransfer({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "fixed-assets/transfers", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_FIXED_ASSET_TRANSFER_SUCCESS',
        id_fixed_asset_transfer: resultStore.result.id_fixed_asset_transfer,
        success: resultStore
      });
    }
    else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_FIXED_ASSET_TRANSFER_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_FIXED_ASSET_TRANSFER_ERROR",
        error: {message: "Check your internet connection"}
      });
    } 
  } catch (error) {
    yield put({
      type: "STORE_FIXED_ASSET_TRANSFER_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editFixedAssetTransfer({ payload }) {
  const { id_fixed_asset_transfer } = payload;

  try {
    const [fixed_asset_transfer, foreign_keys] = yield all([
      call(get, "fixed-assets/transfers/" + id_fixed_asset_transfer),
      call(get, "fixed-assets/transfers/foreign-keys"),
    ]);
  
    // Jika sukses
    if (fixed_asset_transfer.status === "success") {
      yield put({
        type: 'EDIT_FIXED_ASSET_TRANSFER_SUCCESS',
        fixed_asset_transfer: fixed_asset_transfer.result
      });
    }
    else{
      yield put({
        type: "EDIT_FIXED_ASSET_TRANSFER_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
    
    // Jika sukses get data fixed_asset_transfer types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_FIXED_ASSET_TRANSFER_FOREIGN_KEYS_SUCCESS',
        accounts: foreign_keys.result.accounts,
        from_locations: foreign_keys.result.from_locations,
        to_locations: foreign_keys.result.to_locations,
        prevTransConfirm: foreign_keys.result.confirmed_info,
        prevTransReceive: foreign_keys.result.received_info,
        departments: foreign_keys.result.departments,
        base_currency: foreign_keys.result.base_currency,
      });
    }
    else{
      yield put({
        type: 'GET_FIXED_ASSET_TRANSFER_FOREIGN_KEYS_ERROR',
        // error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_FIXED_ASSET_TRANSFER_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateFixedAssetTransfer({ payload }) {
  const { id_fixed_asset_transfer, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "fixed-assets/transfers/" + id_fixed_asset_transfer,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: 'UPDATE_FIXED_ASSET_TRANSFER_SUCCESS',
          success: resultUpdate
        }),
        yield put({
          type: 'EDIT_FIXED_ASSET_TRANSFER_REQUEST',
          payload: { id_fixed_asset_transfer }
        })
      ])
      
    }
    else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_FIXED_ASSET_TRANSFER_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_FIXED_ASSET_TRANSFER_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_FIXED_ASSET_TRANSFER_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showFixedAssetTransfer({ payload }) {
  const { id_fixed_asset_transfer } = payload;
  try {
    const resultShow = yield call(get, "fixed-assets/transfers/" + id_fixed_asset_transfer);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_FIXED_ASSET_TRANSFER_SUCCESS',
        fixed_asset_transfer: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Decancel data (mengubah is_active => 0)
export function* processFixedAssetTransfer({ payload }) {
  const { id_fixed_asset_transfer } = payload;

  try {
    // POST data ke API
    const processResult = yield call(
      putApi,
      "fixed-assets/transfers/process-transaction/" + id_fixed_asset_transfer,
      // body
    );

    // Jika sukses
    if (processResult.status === "success") {
      yield all([
        yield put({
          type: 'PROCESS_FIXED_ASSET_TRANSFER_SUCCESS',
          success: processResult
        }),
        yield put({
          type: 'EDIT_FIXED_ASSET_TRANSFER_REQUEST',
          payload: { id_fixed_asset_transfer }
        })
      ])
    }
    else if(processResult.status === "fail"){
      yield put({
        type: 'PROCESS_FIXED_ASSET_TRANSFER_ERROR',
        error: processResult
      })
    }
    else{
      yield put({
        type: 'PROCESS_FIXED_ASSET_TRANSFER_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'PROCESS_FIXED_ASSET_TRANSFER_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Activate data (mengubah is_active => 1)
