import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getInterbankTransfers({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [interbank_transfers, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "financials/interbank-transfers?search=" + search + "&page=" + page),
            call(get, "financials/interbank-transfers/foreign-keys"),
          ])
        : // Jika filter
          yield all([
            call(get, "financials/interbank-transfers?" + filter + "&page=" + page),
            call(get, "financials/interbank-transfers/foreign-keys"),
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "financials/interbank-transfers?search=" + search),
          call(get, "financials/interbank-transfers/foreign-keys"),
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "financials/interbank-transfers?" + filter),
          call(get, "financials/interbank-transfers/foreign-keys"),
        ]);

    // console.log(interbank_transfers);
    // console.log(foreign_keys);

    // Jika sukses get data interbank_transfers
    if (interbank_transfers.status === "success") {
      // Jika di API menggunakan pagination
      if (interbank_transfers.result.current_page && search) {
        yield put({
          type: 'GET_INTERBANK_TRANSFERS_SUCCESS',
          interbank_transfers: interbank_transfers.result.data,
          current_page: interbank_transfers.result.current_page,
          page_size: interbank_transfers.result.per_page,
          total: interbank_transfers.result.total,
          search: search
        });
      }
      else if(interbank_transfers.result.current_page && filter)
      {
        yield put({
          type: 'GET_INTERBANK_TRANSFERS_SUCCESS',
          interbank_transfers: interbank_transfers.result.data,
          current_page: interbank_transfers.result.current_page,
          page_size: interbank_transfers.result.per_page,
          total: interbank_transfers.result.total,
          filter: filter
        });
      }
      else if (interbank_transfers.result.current_page) {
        yield put({
          type: 'GET_INTERBANK_TRANSFERS_SUCCESS',
          interbank_transfers: interbank_transfers.result.data,
          current_page: interbank_transfers.result.current_page,
          page_size: interbank_transfers.result.per_page,
          total: interbank_transfers.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_INTERBANK_TRANSFERS_SUCCESS',
          interbank_transfers: interbank_transfers.result,
          page_size: null,
          total: null
        });
      }
    } else if (interbank_transfers.status === "fail") {
      yield put({
        type: 'GET_INTERBANK_TRANSFERS_ERROR', 
        error: interbank_transfers
      })
    } else {
      yield put({
        type: 'GET_INTERBANK_TRANSFERS_ERROR', 
        error: {message: "Check your internet connection"}
      })
    }

    // Jika sukses get data interbank_transfer types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_INTERBANK_TRANSFER_FOREIGN_KEYS_SUCCESS',
        withdraw_locations: foreign_keys.result.withdraw_locations,
        deposit_locations: foreign_keys.result.deposit_locations,
        cash_banks: foreign_keys.result.cash_banks,
        accounts: foreign_keys.result.accounts,
        cash_flow_types: foreign_keys.result.cash_flow_types,
        currencies: foreign_keys.result.currencies,
        id_base_currency: foreign_keys.result.id_base_currency,
      });
    }
    else{
      yield put({
        type: 'GET_INTERBANK_TRANSFER_FOREIGN_KEYS_ERROR',
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_INTERBANK_TRANSFERS_ERROR', 
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

