// Create data
export function* createInterbankTransfer() {
  try {
    const [foreign_keys] = yield all([
      call(get, "financials/interbank-transfers/foreign-keys"),
    ]);
  
    // Jika sukses get data account
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_INTERBANK_TRANSFER_FOREIGN_KEYS_SUCCESS',
        withdraw_locations: foreign_keys.result.withdraw_locations,
        deposit_locations: foreign_keys.result.deposit_locations,
        cash_banks: foreign_keys.result.cash_banks,
        accounts: foreign_keys.result.accounts,
        cash_flow_types: foreign_keys.result.cash_flow_types,
        currencies: foreign_keys.result.currencies,
        id_base_currency: foreign_keys.result.id_base_currency,
      });
    }
    else{
      yield put({
        type: 'GET_INTERBANK_TRANSFER_FOREIGN_KEYS_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_INTERBANK_TRANSFER_FOREIGN_KEYS_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeInterbankTransfer({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "financials/interbank-transfers", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_INTERBANK_TRANSFER_SUCCESS',
        id_interbank_transfer: resultStore.result.id_interbank_transfer,
        success: resultStore
      });
    }
    else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_INTERBANK_TRANSFER_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_INTERBANK_TRANSFER_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_INTERBANK_TRANSFER_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editInterbankTransfer({ payload }) {
  const { id_interbank_transfer } = payload;

  try {
    const [interbank_transfer, foreign_keys] = yield all([
      call(get, "financials/interbank-transfers/" + id_interbank_transfer),
      call(get, "financials/interbank-transfers/foreign-keys"),
    ]);
  
    // Jika sukses
    if (interbank_transfer.status === "success") {
      yield put({
        type: 'EDIT_INTERBANK_TRANSFER_SUCCESS',
        interbank_transfer: interbank_transfer.result
      });
    }
    else{
      yield put({
        type: "EDIT_INTERBANK_TRANSFER_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
    
    // Jika sukses get data interbank_transfer types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_INTERBANK_TRANSFER_FOREIGN_KEYS_SUCCESS',
        withdraw_locations: foreign_keys.result.withdraw_locations,
        deposit_locations: foreign_keys.result.deposit_locations,      
        cash_banks: foreign_keys.result.cash_banks,
        accounts: foreign_keys.result.accounts,
        cash_flow_types: foreign_keys.result.cash_flow_types,
        currencies: foreign_keys.result.currencies,
        id_base_currency: foreign_keys.result.id_base_currency,
      });
    }
    else{
      yield put({
        type: 'GET_INTERBANK_TRANSFER_FOREIGN_KEYS_ERROR',
        // error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_INTERBANK_TRANSFER_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateInterbankTransfer({ payload }) {
  const { id_interbank_transfer, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "financials/interbank-transfers/" + id_interbank_transfer,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: 'UPDATE_INTERBANK_TRANSFER_SUCCESS',
          success: resultUpdate
        }),
        yield put({
          type: 'EDIT_INTERBANK_TRANSFER_REQUEST',
          payload: { id_interbank_transfer }
        })
      ])
      
    }
    else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_INTERBANK_TRANSFER_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_INTERBANK_TRANSFER_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_INTERBANK_TRANSFER_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showInterbankTransfer({ payload }) {
  const { id_interbank_transfer } = payload;

  try {
    const resultShow = yield call(get, "financials/interbank-transfers/" + id_interbank_transfer);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_INTERBANK_TRANSFER_SUCCESS',
        interbank_transfer: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Decancel data (mengubah is_active => 0)
export function* processInterbankTransfer({ payload }) {
  const { id_interbank_transfer } = payload;

  try {
    // POST data ke API
    const processResult = yield call(
      putApi,
      "financials/interbank-transfers/process-transaction/" + id_interbank_transfer,
      // body
    );

    // Jika sukses
    if (processResult.status === "success") {
      yield all([
        yield put({
          type: 'PROCESS_INTERBANK_TRANSFER_SUCCESS',
          success: processResult
        }),
        yield put({
          type: 'EDIT_INTERBANK_TRANSFER_REQUEST',
          payload: { id_interbank_transfer }
        })
      ])
    }
    else if(processResult.status === "fail"){
      yield put({
        type: 'PROCESS_INTERBANK_TRANSFER_ERROR',
        error: processResult
      })
    }
    else{
      yield put({
        type: 'PROCESS_INTERBANK_TRANSFER_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'PROCESS_INTERBANK_TRANSFER_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error);
  }
}

