// Upload Document
export function* uploadInterbankTransferDocument({ payload }) {
  const { id_interbank_transfer, body } = payload;
  let data = {
    ...body,
    id_interbank_transfer 
  }

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "financials/interbank-transfers/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: 'UPLOAD_INTERBANK_TRANSFER_DOCUMENT_SUCCESS',
          success: uploadResult
        }),
        yield put({
          type: 'EDIT_INTERBANK_TRANSFER_REQUEST',
          payload: { id_interbank_transfer }
        })
      ])
    }
    else if(uploadResult.errors){
      yield put({
        type: 'UPLOAD_INTERBANK_TRANSFER_DOCUMENT_ERROR',
        error: {message: uploadResult.errors.document[0]}
      })
    }
    else if(uploadResult.status === "fail"){
      yield put({
        type: 'UPLOAD_INTERBANK_TRANSFER_DOCUMENT_ERROR',
        error: uploadResult
      })
    }
    else{
      yield put({
        type: 'UPLOAD_INTERBANK_TRANSFER_DOCUMENT_ERROR',
        error: {message: "Check your internet connection"}
      })
    }

    // }
  } catch (error) {
    yield put({
      type: 'UPLOAD_INTERBANK_TRANSFER_DOCUMENT_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error);
  }
}

// Download Document
export function* downloadInterbankTransferDocument({ payload }) {
  const { id_interbank_transfer_document } = payload;

  try {
    // POST ke API
    // console.log("masukk")
    yield call(
      get,
      "financials/interbank-transfers/documents/download/" + id_interbank_transfer_document,
    );
    // }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deleteInterbankTransferDocument({ payload }) {
  const { id_interbank_transfer_document } = payload;
  // const body = { is_active: 0 };

  try {
    // POST data ke API
    const deleteResult = yield call(
      putApi,
      "financials/interbank-transfers/documents/" + id_interbank_transfer_document,
    //  body
    );

    // Jika sukses
    if (deleteResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_INTERBANK_TRANSFER_DOCUMENT_SUCCESS",
          success: deleteResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'EDIT_INTERBANK_TRANSFER_REQUEST',
          payload: { id_interbank_transfer: deleteResult.result.id_interbank_transfer }
        })
      ]);
    }
    else if (deleteResult.status === "fail") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_INTERBANK_TRANSFER_DOCUMENT_ERROR",
          error: deleteResult
        }),
      ]);
    }
    else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_INTERBANK_TRANSFER_DOCUMENT_ERROR",
          error: {message: "Check your internet connection"}
        }),
      ]);
    }
  } catch (error) {
    yield put({
      type: "DELETE_INTERBANK_TRANSFER_DOCUMENT_ERROR",
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error);
  }
}


export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_INTERBANK_TRANSFERS_REQUEST', getInterbankTransfers),
    yield takeEvery('GET_IMPORT_INTERBANK_TRANSFERS_REQUEST', getImportInterbankTransfers),
    yield takeEvery('CREATE_INTERBANK_TRANSFER_REQUEST', createInterbankTransfer),
    yield takeEvery('STORE_INTERBANK_TRANSFER_REQUEST', storeInterbankTransfer),
    yield takeEvery('EDIT_INTERBANK_TRANSFER_REQUEST', editInterbankTransfer),
    yield takeEvery('UPDATE_INTERBANK_TRANSFER_REQUEST', updateInterbankTransfer),
    yield takeEvery('SHOW_INTERBANK_TRANSFER_REQUEST', showInterbankTransfer),
    yield takeEvery('PROCESS_INTERBANK_TRANSFER_REQUEST', processInterbankTransfer),
    yield takeEvery('CANCEL_INTERBANK_TRANSFER_REQUEST', cancelInterbankTransfer),
    yield takeEvery('UPLOAD_INTERBANK_TRANSFER_DOCUMENT_REQUEST', uploadInterbankTransferDocument),
    yield takeEvery('DOWNLOAD_INTERBANK_TRANSFER_DOCUMENT_REQUEST', downloadInterbankTransferDocument),
    yield takeEvery('DELETE_INTERBANK_TRANSFER_DOCUMENT_REQUEST', deleteInterbankTransferDocument),

  ]);
}
