const initState = {
  id_account_type: null,
  accounts: [],
  account: null,
  accountList: [],
  accountTypes: [],
  currencies: [],
  locations: [],
  cashFlowTypes: [],
  loadingSubmit: false,
  loadingAccount: false,
  loadingAccounts: false,
  loadingAccountList: false,
  loadingForeign: false,
  success: null,
  error: null,
  errorField: null
};

const accountsReducer = (state = initState, action) => {
  switch (action.type) {
    case "GET_ACCOUNTS_REQUEST":
      return {
        ...state,
        account: null,
        id_account_type: action.payload.id_account_type,
        loadingAccounts: true,
        loadingAccountList: true,
        loadingForeign: true
      };
    case "GET_ACCOUNTS_SUCCESS":
      return {
        ...state,
        loadingAccounts: false,
        accounts: action.accounts
      };
    case "GET_ACCOUNTS_ERROR":
      return {
        ...state,
        loadingAccount: false,
        error: action.error.message
      };
    case "GET_ACCOUNT_LIST_SUCCESS":
      return {
        ...state,
        loadingAccountList: false,
        accountList: action.accountList
      };
    case "GET_ACCOUNT_LIST_ERROR":
      return {
        ...state,
        loadingAccountList: false,
        error: action.error.message
      };
    case "GET_ACCOUNT_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_ACCOUNT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loadingForeign: false,
        currencies: action.currencies,
        locations: action.locations,
        cashFlowTypes: action.cashFlowTypes,
        accountTypes: action.accountTypes
      };
    }
    case "GET_ACCOUNT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "STORE_ACCOUNT_REQUEST":
      return {
        ...state,
        loadingAccount: true,
        loadingSubmit: true
      };
    case "STORE_ACCOUNT_SUCCESS":
      return {
        ...state,
        loadingAccount: false,
        loadingSubmit: false,
        account: action.account,
        success: action.success.message
      };
    case "STORE_ACCOUNT_ERROR":
      return {
        ...state,
        loadingAccount: false,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    case "SHOW_ACCOUNT_REQUEST":
      return {
        ...state,
        account: null,
        loadingAccount: true
      };
    case "SHOW_ACCOUNT_SUCCESS":
      return {
        ...state,
        loadingAccount: false,
        account: action.account
      };
    case "SHOW_ACCOUNT_ERROR":
      return {
        ...state,
        loadingAccount: false,
        error: action.error.message
      };
    case "UPDATE_ACCOUNT_REQUEST":
      return {
        ...state,
        loadingAccount: true,
        loadingSubmit: true
      };
    case "UPDATE_ACCOUNT_SUCCESS":
      return {
        ...state,
        loadingAccount: false,
        loadingSubmit: false,
        account: action.account,
        success: action.success.message
      };
    case "UPDATE_ACCOUNT_ERROR":
      return {
        ...state,
        loadingAccount: false,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    case "ACTIVATE_ACCOUNT_REQUEST":
      return {
        ...state,
        loadingAccount: true,
        loadingSubmit: true
      };
    case "ACTIVATE_ACCOUNT_SUCCESS":
      return {
        ...state,
        loadingAccount: false,
        loadingSubmit: false,
        account: action.account,
        success: action.success.message
      };
    case "ACTIVATE_ACCOUNT_ERROR":
      return {
        ...state,
        loadingAccount: false,
        loadingSubmit: false,
        error: action.error.message
      };
    case "DEACTIVATE_ACCOUNT_REQUEST":
      return {
        ...state,
        loadingAccount: true,
        loadingSubmit: true
      };
    case "DEACTIVATE_ACCOUNT_SUCCESS":
      return {
        ...state,
        loadingAccount: false,
        loadingSubmit: false,
        account: action.account,
        success: action.success.message
      };
    case "DEACTIVATE_ACCOUNT_ERROR":
      return {
        ...state,
        loadingAccount: false,
        loadingSubmit: false,
        error: action.error.message
      };
    case "CLEAR_ACCOUNT":
      return {
        ...state,
        account: null,
        loadingAccount: false
      };
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return {
        ...state
      };
  }
};

export default accountsReducer;
