
const initState = {
  credit_memos: [],
  credit_memo: null,
  locations: [],
  customers: [],
  cash_banks: [],
  accounts: [],
  cash_flow_types: [],  
  currencies: [],
  base_currency: null,
  search: null,
  filter: null,
  loading: false,
  loadingEdit:false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
  id_credit_memo: null,
};

export default function creditMemosReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_CREDIT_MEMOS_REQUEST': {
      return {
        ...state,
        //credit_memos: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_CREDIT_MEMOS_SUCCESS': {
      return {
        ...state,
        credit_memos: action.credit_memos,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_CREDIT_MEMOS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      }
    }
    case 'CREATE_CREDIT_MEMO_REQUEST': {
      return {
        ...state,
        credit_memo: null,
        errorField: null,
      };
    }

    case 'GET_CREDIT_MEMO_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        locations: action.locations,
        customers: action.customers,
        cash_banks: action.cash_banks,
        accounts: action.accounts,
        cash_flow_types: action.cash_flow_types,  
        currencies: action.currencies,
        base_currency: action.base_currency,
      };
    }
    case 'GET_CREDIT_MEMO_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        error: action.error.message
      };
    }
    
    case 'STORE_CREDIT_MEMO_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_CREDIT_MEMO_SUCCESS': {
      return {
        ...state,
        loading: false,
        id_credit_memo: action.id_credit_memo,
        success: action.success.message
      };
    }
    case 'STORE_CREDIT_MEMO_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_CREDIT_MEMO_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit:true,
        errorField: null,
      };
    }
    case 'EDIT_CREDIT_MEMO_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit:false,
        credit_memo: action.credit_memo
      };
    }
    case 'EDIT_CREDIT_MEMO_ERROR': {
      return {
        ...state,
        // loading: false,
        // loadingEdit:false,
        error: action.error.message
      };
    }
    case 'UPDATE_CREDIT_MEMO_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_CREDIT_MEMO_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_CREDIT_MEMO_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_CREDIT_MEMO_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_CREDIT_MEMO_SUCCESS': {
      return {
        ...state,
        loading: false,
        credit_memos: action.credit_memos
      };
    }
    case "PROCESS_CREDIT_MEMO_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "PROCESS_CREDIT_MEMO_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "PROCESS_CREDIT_MEMO_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CANCEL_CREDIT_MEMO_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "CANCEL_CREDIT_MEMO_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "CANCEL_CREDIT_MEMO_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "UPLOAD_CREDIT_MEMO_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_CREDIT_MEMO_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_CREDIT_MEMO_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_CREDIT_MEMO_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_CREDIT_MEMO_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_CREDIT_MEMO_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_CREDIT_MEMO_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
