
const initState = {
  units: [],
  unit: null,
  search: null,
  //filter: null,
  loading: false,
  loadingEdit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function unitsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_UNITS_REQUEST': {
      return {
        ...state,
        //units: [],
        loading: true,
        search: action.payload.search,
        //filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_UNITS_SUCCESS': {
      return {
        ...state,
        units: action.units,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        //filter: action.filter,
        loading: false,
      };
    }
    case 'GET_UNITS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        //errorField: action.error.errors
      };
    }
    case 'CREATE_UNIT_REQUEST': {
      return {
        ...state,
        // unit: null,
        errorField: null,
      };
    }
    
    case 'STORE_UNIT_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_UNIT_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_UNIT_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_UNIT_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null,
      };
    }
    case 'EDIT_UNIT_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        unit: action.unit,
      };
    }
    case 'EDIT_UNIT_ERROR': {
      return {
        ...state,
        //loading: false,
        error: action.error.message,
        //errorField: action.error.errors
      };
    }
    case 'UPDATE_UNIT_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_UNIT_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_UNIT_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_UNIT_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_UNIT_SUCCESS': {
      return {
        ...state,
        loading: false,
        units: action.units
      };
    }
    case "DEACTIVATE_UNIT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_UNIT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_UNIT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_UNIT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_UNIT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_UNIT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
