const initState = {
  monthly_sales_reports: [],
  locations: [],
  customers: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function monthlySalesReportsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_MONTHLY_SALES_REPORTS_REQUEST": {
      return {
        ...state,
        //monthly_sales_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_MONTHLY_SALES_REPORTS_SUCCESS": {
      return {
        ...state,
        monthly_sales_reports: action.monthly_sales_reports,
        filter: action.filter,
        search: action.search,
        loading: false
      };
    }
    case "GET_MONTHLY_SALES_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_MONTHLY_SALES_REPORT_REQUEST": {
      return {
        ...state,
        monthly_sales_report: null,
        errorField: null
      };
    }

    case "GET_MONTHLY_SALES_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        customers: action.customers
      };
    }
    case "GET_MONTHLY_SALES_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
