import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getUnits({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [units] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "items/units?search=" + search + "&page=" + page), 
          ])
        : // Jika filter
          yield all([
            call(get, "items/units?" + filter + "&page=" + page), 
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "items/units?search=" + search),  
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "items/units?" + filter),
        ]);

    // console.log(units);
    // console.log(accounts);

    // Jika sukses get data units
    if (units.status === "success") {
      // Jika di API menggunakan pagination
      if (units.result.current_page && search) {
        yield put({
          type: 'GET_UNITS_SUCCESS',
          units: units.result.data,
          current_page: units.result.current_page,
          page_size: units.result.per_page,
          total: units.result.total,
          search: search
        });
      }
      // else if(units.result.current_page && filter)
      // {
      //   yield put({
      //     type: 'GET_UNITS_SUCCESS',
      //     units: units.result.data,
      //     current_page: units.result.current_page,
      //     page_size: units.result.per_page,
      //     total: units.result.total,
      //     filter: filter
      //   });
      // }
      else if (units.result.current_page) {
        yield put({
          type: 'GET_UNITS_SUCCESS',
          units: units.result.data,
          current_page: units.result.current_page,
          page_size: units.result.per_page,
          total: units.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_UNITS_SUCCESS',
          units: units.result,
          page_size: null,
          total: null
        });
      }
    }
    else if (units.status === "fail") {
      yield put({
        type: 'GET_UNITS_ERROR',
        error: units
      })
    }
    else {
      yield put({
        type: 'GET_UNITS_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'GET_UNITS_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createUnit() {
  
}

// Store data
export function* storeUnit({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "items/units", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_UNIT_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_UNIT_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_UNIT_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_UNIT_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editUnit({ payload }) {
  //console.log("masuk")
  const { id_unit } = payload;

  try {
    const [unit] = yield all([
      call(get, "items/units/" + id_unit),
      
    ]);
  
    // Jika sukses
    if (unit.status === "success") {
      yield put({
        type: 'EDIT_UNIT_SUCCESS',
        unit: unit.result
      });
    }
    else{
      yield put({
        type: 'EDIT_UNIT_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'EDIT_UNIT_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateUnit({ payload }) {
  const { id_unit, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "items/units/" + id_unit,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_UNIT_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_UNIT_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_UNIT_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_UNIT_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showUnit({ payload }) {
  const { id_unit } = payload;

  try {
    const resultShow = yield call(get, "items/units/" + id_unit);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_UNIT_SUCCESS',
        unit: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateUnit({ payload }) {
  const { id_unit } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "items/units/" + id_unit,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: 'DEACTIVATE_UNIT_SUCCESS',
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_UNITS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: 'DEACTIVATE_UNIT_ERROR',
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_UNITS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'DEACTIVATE_UNIT_ERROR',
          error: {message: "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_UNITS_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'DEACTIVATE_UNIT_ERROR',
        error: {message: "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_UNITS_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activateUnit({ payload }) {
  const { id_unit } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "items/units/" + id_unit,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_UNIT_SUCCESS',
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_UNITS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: 'ACTIVATE_UNIT_ERROR',
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_UNITS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'ACTIVATE_UNIT_ERROR',
          error: {message: "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_UNITS_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'ACTIVATE_UNIT_ERROR',
        error: {message: "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_UNITS_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_UNITS_REQUEST', getUnits),
    yield takeEvery('CREATE_UNIT_REQUEST', createUnit),
    yield takeEvery('STORE_UNIT_REQUEST', storeUnit),
    yield takeEvery('EDIT_UNIT_REQUEST', editUnit),
    yield takeEvery('UPDATE_UNIT_REQUEST', updateUnit),
    yield takeEvery('SHOW_UNIT_REQUEST', showUnit),
    yield takeEvery('DEACTIVATE_UNIT_REQUEST', deactivateUnit),
    yield takeEvery('ACTIVATE_UNIT_REQUEST', activateUnit)
  ]);
}
