const initState = {
  inventory_history_reports: [],
  locations: [],
  item_categories: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function inventoryHistoryReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_INVENTORY_HISTORY_REPORTS_REQUEST": {
      return {
        ...state,
        //inventory_history_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_INVENTORY_HISTORY_REPORTS_SUCCESS": {
      return {
        ...state,
        inventory_history_reports: action.inventory_history_reports,
        loading: false
      };
    }
    case "GET_INVENTORY_HISTORY_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "GET_INVENTORY_HISTORY_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        item_categories: action.item_categories
      };
    }
    case "GET_INVENTORY_HISTORY_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
