import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getAccountHistoryReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/account-histories?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    // console.log(filter)
    if (filter) {
      const [account_history_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/account-histories/foreign-keys")
      ]);

      // Jika sukses get data account_history_reports
      if (
        account_history_reports.status === "success" &&
        account_history_reports.result !== null
      ) {

        yield put({
          type: "GET_ACCOUNT_HISTORY_REPORTS_SUCCESS",
          account_history_reports: account_history_reports.result,
          filter
        });

      } else if (
        account_history_reports.status === "success" &&
        account_history_reports.result === null
      ) {
        yield put({
          type: "GET_ACCOUNT_HISTORY_REPORTS_SUCCESS",
          account_history_reports: [],
          filter
        });
      } else if (account_history_reports.status === "fail") {
        yield put({
          type: "GET_ACCOUNT_HISTORY_REPORTS_ERROR",
          error: { message: account_history_reports.message }
        });
      } else {
        yield put({
          type: "GET_ACCOUNT_HISTORY_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }

      // Jika sukses get data account_history_department_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ACCOUNT_HISTORY_REPORT_FOREIGN_KEYS_SUCCESS",
          accounts: foreign_keys.result.accounts,
          account_types: foreign_keys.result.account_types,
          locations: foreign_keys.result.locations,
          projects: foreign_keys.result.projects
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_ACCOUNT_HISTORY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_ACCOUNT_HISTORY_REPORT_FOREIGN_KEYS_ERROR"
          //error: {message: "Check your internet connection"}
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/account-histories/foreign-keys")
      ]);

      yield put({
        type: "GET_ACCOUNT_HISTORY_REPORTS_SUCCESS",
        account_history_reports: [],
      });

      // Jika sukses get data account_history_department_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ACCOUNT_HISTORY_REPORT_FOREIGN_KEYS_SUCCESS",
          accounts: foreign_keys.result.accounts,
          account_types: foreign_keys.result.account_types,
          locations: foreign_keys.result.locations,
          projects: foreign_keys.result.projects
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_ACCOUNT_HISTORY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_ACCOUNT_HISTORY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_ACCOUNT_HISTORY_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_ACCOUNT_HISTORY_REPORTS_REQUEST",
      getAccountHistoryReports
    )
  ]);
}
