import { all, takeEvery, put, call } from "redux-saga/effects";
import actions from "./actions";
import { bugsnagClient } from "../..";
import { APP_VERSION } from "../../helpers/helper";
export const per_page = 10;

const onValidateRequest = async serverAddress => {
  return await fetch(serverAddress + "api/check-server?APP_VERSION=" + APP_VERSION)
    .then(res => res.json())
    .then(res => res)
    .catch(error => error);
};

function* validateRequest({ payload }) {
  const { serverAddress } = payload;
  try {
    const validateResult = yield call(onValidateRequest, serverAddress);
    if (
      validateResult.status === "success"
      // &&
      // validateResult.result === "equal"
    ) {
      yield put(actions.serverSuccessValidate(validateResult.result));
    } else if (validateResult.status === "fail") {
      yield put(actions.serverFailValidate(validateResult.message))
    } else {
      yield put(actions.serverErrorValidate());
    }
  } catch (error) {
    yield put(actions.serverErrorValidate());
    bugsnagClient.notify(error);
  }
}
export default function* rootSaga() {
  yield all([takeEvery("SERVER_VALIDATE", validateRequest)]);
}
