import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import moment from "moment";
import { bugsnagClient } from "../../..";

// Get all data
export function* getItemTransfers({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [itemTransfers, foreign] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(
              get,
              "inventories/item-transfers?search=" + search + "&page=" + page
            ),
            call(get, "inventories/item-transfers/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "inventories/item-transfers?" + filter + "&page=" + page),
            call(get, "inventories/item-transfers/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "inventories/item-transfers?search=" + search),
          call(get, "inventories/item-transfers/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "inventories/item-transfers?" + filter),
          call(get, "inventories/item-transfers/foreign-keys")
        ]);

    // console.log(itemTransfers);

    // Jika sukses get data itemTransfers
    if (itemTransfers.status === "success") {
      // Jika di API menggunakan pagination
      if (itemTransfers.result.current_page && search) {
        yield put({
          type: "GET_ITEM_TRANSFERS_SUCCESS",
          itemTransfers: itemTransfers.result.data,
          current_page: itemTransfers.result.current_page,
          page_size: itemTransfers.result.per_page,
          total: itemTransfers.result.total,
          search: search
        });
      } else if (itemTransfers.result.current_page && filter) {
        yield put({
          type: "GET_ITEM_TRANSFERS_SUCCESS",
          itemTransfers: itemTransfers.result.data,
          current_page: itemTransfers.result.current_page,
          page_size: itemTransfers.result.per_page,
          total: itemTransfers.result.total,
          filter: filter
        });
      } else if (itemTransfers.result.current_page) {
        yield put({
          type: "GET_ITEM_TRANSFERS_SUCCESS",
          itemTransfers: itemTransfers.result.data,
          current_page: itemTransfers.result.current_page,
          page_size: itemTransfers.result.per_page,
          total: itemTransfers.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_ITEM_TRANSFERS_SUCCESS",
          itemTransfers: itemTransfers.result,
          page_size: null,
          total: null,
          filter: filter
        });
      }
    } else if (itemTransfers.status === "fail") {
      yield put({
        type: "GET_ITEM_TRANSFERS_ERROR",
        error: { message: itemTransfers.message }
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_ITEM_TRANSFERS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      yield put({
        type: "GET_ITEM_TRANSFER_FOREIGN_KEYS_SUCCESS",
        fromLocations: foreign.result.from_locations,
        toLocations: foreign.result.to_locations,
        couriers: foreign.result.couriers,
        adjustmentTypes: foreign.result.adjustment_types,
        prevTransConfirm: foreign.result.item_request_info,
        prevTransReceive: foreign.result.received_info
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_ITEM_TRANSFER_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_ITEM_TRANSFER_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_ITEM_TRANSFERS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Get Items data
export function* getItems({ payload }) {
  const { id_location, search } = payload;
  let filter = "items?is_active=1&limit=1";
  filter = search
    ? `${filter}&search=${search}&id_location=${id_location}&item_type=1,2`
    : `${filter}&id_location=${id_location}&item_type=1,2`;

  try {
    const items = yield call(get, filter);

    if (items.status === "success") {
      yield put({
        type: "GET_FILTER_ITEMS_IT_SUCCESS",
        items: items.result
      });
    } else if (items.status === "fail") {
      yield put({
        type: "GET_FILTER_ITEMS_IT_ERROR",
        error: { message: items.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_ITEMS_IT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_ITEMS_IT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createItemTransfer() {
  const now = moment().format("YYYY-MM-DD HH:mm:ss");
  
  try {
    const [transactions, foreign] = yield all([
      call(
        get,
        "inventories/item-requests?all_locations=1&get_old_trans=1&transaction_status=2,3&transcation_date=" + now
      ),
      call(get, "inventories/item-transfers/foreign-keys")
    ]);
  
    if (transactions.status === "success") {
      yield put({
        type: "GET_TRANSACTIONS_IT_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_TRANSACTIONS_IT_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_TRANSACTIONS_IT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (foreign.status === "success") {
      yield put({
        type: "GET_ITEM_TRANSFER_FOREIGN_KEYS_SUCCESS",
        fromLocations: foreign.result.from_locations,
        toLocations: foreign.result.to_locations,
        couriers: foreign.result.couriers,
        adjustmentTypes: foreign.result.adjustment_types,
        prevTransConfirm: foreign.result.item_request_info,
        prevTransReceive: foreign.result.received_info
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_ITEM_TRANSFER_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_ITEM_TRANSFER_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_TRANSACTIONS_IT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export function* getTransactions({ payload }) {
  const { page, search, filter } = payload;
  // console.log(filter);

  try {
    const transactions = filter
      ? yield call(
          get,
          "inventories/item-requests?all_locations=1&get_old_trans=1&transaction_status=2,3&" +
            filter +
            "&page=" +
            page
        )
      : search
      ? yield call(
          get,
          "inventories/item-requests?all_locations=1&get_old_trans=1&transaction_status=2,3&search=" +
            search +
            "&page=" +
            page
        )
      : yield call(
          get,
          "inventories/item-requests?all_locations=1&get_old_trans=1" +
            "transaction_status=2,3&page=" +
            page
        );

    if (transactions.status === "success") {
      yield put({
        type: "GET_TRANSACTIONS_IT_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_TRANSACTIONS_IT_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_TRANSACTIONS_IT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_TRANSACTIONS_IT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeItemTransfer({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "inventories/item-transfers", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_ITEM_TRANSFER_SUCCESS",
        success: { message: resultStore.message },
        id_item_transfer: resultStore.result.id_item_transfer
      });
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_ITEM_TRANSFER_ERROR",
        error: { message: resultStore.message }
      });
    } else if (resultStore.errors) {
      yield put({
        type: "STORE_ITEM_TRANSFER_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_ITEM_TRANSFER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_ITEM_TRANSFER_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

