// Update data
export function* updateItemTransfer({ payload }) {
  const { id_item_transfer, body } = payload;
  // console.log(body);

  try {
    const resultUpdate = yield call(
      putApi,
      "inventories/item-transfers/" + id_item_transfer,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: "UPDATE_ITEM_TRANSFER_SUCCESS",
          success: { message: resultUpdate.message },
          id_item_transfer: id_item_transfer
        }),
        yield put({
          type: "EDIT_ITEM_TRANSFER_REQUEST",
          payload: { id_item_transfer: id_item_transfer }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield all([
        yield put({
          type: "UPDATE_ITEM_TRANSFER_ERROR",
          error: { message: resultUpdate.message }
        }),
        yield put({
          type: "EDIT_ITEM_TRANSFER_REQUEST",
          payload: { id_item_transfer: id_item_transfer }
        })
      ]);
    } else if (resultUpdate.errors) {
      yield all([
        yield put({
          type: "UPDATE_ITEM_TRANSFER_ERROR",
          error: resultUpdate
        }),
        yield put({
          type: "EDIT_ITEM_TRANSFER_REQUEST",
          payload: { id_item_transfer: id_item_transfer }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "UPDATE_ITEM_TRANSFER_ERROR",
          error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "EDIT_ITEM_TRANSFER_REQUEST",
          payload: { id_item_transfer: id_item_transfer }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "UPDATE_ITEM_TRANSFER_ERROR",
        error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "EDIT_ITEM_TRANSFER_REQUEST",
        payload: { id_item_transfer: id_item_transfer }
      })
    ]);
    bugsnagClient.notify(error)
  }
}

