// Upload Document
export function* uploadItemTransferDocument({ payload }) {
  const { id_item_transfer, body } = payload;
  let data = {
    ...body,
    id_item_transfer
  };

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "inventories/item-transfers/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: "UPLOAD_ITEM_TRANSFER_DOCUMENT_SUCCESS",
          success: uploadResult
        }),
        yield put({
          type: "EDIT_ITEM_TRANSFER_REQUEST",
          payload: { id_item_transfer }
        })
      ]);
    } else if (uploadResult.errors) {
      yield all([
        yield put({
          type: "UPLOAD_ITEM_TRANSFER_DOCUMENT_ERROR",
          error: { message: uploadResult.errors.document[0] }
        }),
        yield put({
          type: "EDIT_ITEM_TRANSFER_REQUEST",
          payload: { id_item_transfer }
        })
      ]);
    } else if (uploadResult.status === "fail") {
      yield all([
        yield put({
          type: "UPLOAD_ITEM_TRANSFER_DOCUMENT_ERROR",
          error: { message: uploadResult.message }
        }),
        yield put({
          type: "EDIT_ITEM_TRANSFER_REQUEST",
          payload: { id_item_transfer }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "UPLOAD_ITEM_TRANSFER_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "EDIT_ITEM_TRANSFER_REQUEST",
          payload: { id_item_transfer }
        })
      ]);
    }

    // }
  } catch (error) {
    yield all([
      yield put({
        type: "UPLOAD_ITEM_TRANSFER_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "EDIT_ITEM_TRANSFER_REQUEST",
        payload: { id_item_transfer }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

// Download Document
export function* downloadItemTransferDocument({ payload }) {
  const { id_item_transfer_document } = payload;

  try {
    // POST ke API
    // console.log("masukk")
    yield call(
      get,
      "inventories/item-transfers/documents/download/" +
        id_item_transfer_document
    );
    // }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// delete document
export function* deleteItemTransferDocument({ payload }) {
  const { id_item_transfer_document, id_item_transfer } = payload;
  // const body = { is_active: 0 };

  try {
    // POST data ke API
    const deleteResult = yield call(
      putApi,
      "inventories/item-transfers/documents/" + id_item_transfer_document
      //  body
    );

    // Jika sukses
    if (deleteResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_ITEM_TRANSFER_DOCUMENT_SUCCESS",
          success: deleteResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_ITEM_TRANSFER_REQUEST",
          payload: { id_item_transfer: id_item_transfer }
        })
      ]);
    } else if (deleteResult.status === "fail") {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_ITEM_TRANSFER_DOCUMENT_ERROR",
          error: { message: deleteResult.message }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_ITEM_TRANSFER_REQUEST",
          payload: { id_item_transfer: id_item_transfer }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_ITEM_TRANSFER_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_ITEM_TRANSFER_REQUEST",
          payload: { id_item_transfer: id_item_transfer }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DELETE_ITEM_TRANSFER_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "EDIT_ITEM_TRANSFER_REQUEST",
        payload: { id_item_transfer: id_item_transfer }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_ITEM_TRANSFERS_REQUEST", getItemTransfers),
    yield takeEvery("CREATE_ITEM_TRANSFER_REQUEST", createItemTransfer),
    yield takeEvery("GET_TRANSACTIONS_IT_REQUEST", getTransactions),
    yield takeEvery("GET_FILTER_ITEMS_IT_REQUEST", getItems),
    yield takeEvery("STORE_ITEM_TRANSFER_REQUEST", storeItemTransfer),
    yield takeEvery("EDIT_ITEM_TRANSFER_REQUEST", editItemTransfer),
    yield takeEvery("UPDATE_ITEM_TRANSFER_REQUEST", updateItemTransfer),
    yield takeEvery("SHOW_ITEM_TRANSFER_REQUEST", showItemTransfer),
    yield takeEvery("CONFIRM_ITEM_TRANSFER_REQUEST", confirmItemTransfer),
    yield takeEvery("RECEIVE_ITEM_TRANSFER_REQUEST", receiveItemTransfer),
    yield takeEvery("UNRECEIVE_ITEM_TRANSFER_REQUEST", unreceiveItemTransfer),
    yield takeEvery("CANCEL_ITEM_TRANSFER_REQUEST", cancelItemTransfer),
    yield takeEvery(
      "UPLOAD_ITEM_TRANSFER_DOCUMENT_REQUEST",
      uploadItemTransferDocument
    ),
    yield takeEvery(
      "DOWNLOAD_ITEM_TRANSFER_DOCUMENT_REQUEST",
      downloadItemTransferDocument
    ),
    yield takeEvery(
      "DELETE_ITEM_TRANSFER_DOCUMENT_REQUEST",
      deleteItemTransferDocument
    )
  ]);
}
