
const initState = {
  fixed_asset_reports: [],
  locations: [],
  asset_types: [],
  asset_categories: [],
  departments: [],
  search: null,
  filter: null,
  loading: false,
  total: null,
  success: null,
  error: null,
  errorField: null,
};

export default function fixedAssetReportsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_FIXED_ASSET_REPORTS_REQUEST': {
      return {
        ...state,
        //fixed_asset_reports: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null,
      };
    }
    case 'GET_FIXED_ASSET_REPORTS_SUCCESS': {
      return {
        ...state,
        fixed_asset_reports: action.fixed_asset_reports,
        // current_page: action.current_page,
        // page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_FIXED_ASSET_REPORTS_ERROR': {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      }
    }

    case 'GET_FIXED_ASSET_REPORT_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        asset_types: action.asset_types,
        asset_categories: action.asset_categories,
        departments: action.departments,
      };
    }
    case 'GET_FIXED_ASSET_REPORT_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
