const initState = {
  currencies: [],
  current_page: 1,
  errorField: null,
  filter: null,
  id_supplier_pricelist: null,
  id_location: null,
  items: [],
  loading: false,
  loadingItems: false,
  loadingSubmit: false,
  loadingSuppliers: false,
  page_size: null,
  pricelist_info: null,
  search: null,
  success: null,
  suppliers: [],
  supplierPriceList: null,
  supplierPriceLists: [],
  total: null,
};

export default function supplierPriceListsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_SUPPLIER_PRICE_LISTS_REQUEST": {
      return {
        ...state,
        current_page: action.payload.page,
        errorField: null,
        filter: action.payload.filter,
        loadingGetCP: true,
        search: action.payload.search
      };
    }
    case "GET_SUPPLIER_PRICE_LISTS_SUCCESS": {
      return {
        ...state,
        supplierPriceLists: action.supplierPriceLists,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loadingGetCP: false
      };
    }
    case "GET_SUPPLIER_PRICE_LISTS_ERROR": {
      return {
        ...state,
        error: action.error.message,
        filter: action.filter,
        loadingGetCP: false,
        search: action.search
      };
    }
    case "CREATE_SUPPLIER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        errorField: null
      };
    }
    case "GET_SUPPLIER_PRICE_LIST_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        currencies: action.currencies,
        locations: action.locations,
        pricelist_info: action.pricelist_info,
        supplier_types: action.supplier_types
      };
    }
    case "GET_SUPPLIER_PRICE_LIST_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "GET_FILTER_ITEMS_FOR_SUPPLIER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        loadingItems: true,
        search: action.payload.search,
        id_location: action.payload.id_location
      };
    }
    case "GET_SUPPLIERS_FOR_SUPPLIER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        loadingSuppliers: true,
        id_location: action.payload.id_location
      };
    }
    case "GET_SUPPLIERS_FOR_SUPPLIER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        loadingSuppliers: false,
        suppliers: action.suppliers
      };
    }
    case "GET_FILTER_ITEMS_FOR_SUPPLIER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        loadingItems: false,
        items: action.items
      };
    }
    case "GET_FILTER_ITEMS_FOR_SUPPLIER_PRICE_LIST_ERROR": {
      return {
        ...state,
        error: action.error.message,
        loadingItems: false
      };
    }
    case "STORE_SUPPLIER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_SUPPLIER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_supplier_pricelist: action.id_supplier_pricelist
      };
    }
    case "STORE_SUPPLIER_PRICE_LIST_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_SUPPLIER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null,
        supplierPriceList: null
      };
    }
    case "EDIT_SUPPLIER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        loading: false,
        supplierPriceList: action.supplierPriceList
      };
    }
    case "EDIT_SUPPLIER_PRICE_LIST_ERROR": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_SUPPLIER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_SUPPLIER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_supplier_pricelist: action.id_supplier_pricelist
      };
    }
    case "UPDATE_SUPPLIER_PRICE_LIST_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "GET_FILTER_SUPPLIER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        supplierPriceList: action.supplierPriceList
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null,
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    case "DEACTIVATE_SUPPLIER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_SUPPLIER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_SUPPLIER_PRICE_LIST_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "REJECT_SUPPLIER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "REJECT_SUPPLIER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "REJECT_SUPPLIER_PRICE_LIST_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CONFIRM_SUPPLIER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "CONFIRM_SUPPLIER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "CONFIRM_SUPPLIER_PRICE_LIST_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_SUPPLIER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_SUPPLIER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_SUPPLIER_PRICE_LIST_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    default:
      return state;
  }
}
