const initState = {
  first_balance_sheet_reports: [],
  second_balance_sheet_reports: [],
  locations: [],
  departments: [],
  projects: [],
  filter: null,
  filter_select: [],
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function balanceSheetComparativeReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_BALANCE_SHEET_COMPARATIVE_REPORTS_REQUEST": {
      return {
        ...state,
        //balance_sheet_percentage_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_BALANCE_SHEET_COMPARATIVE_REPORTS_SUCCESS": {
      return {
        ...state,
        first_balance_sheet_reports: action.first_balance_sheet_reports,
        second_balance_sheet_reports: action.second_balance_sheet_reports,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_BALANCE_SHEET_COMPARATIVE_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_BALANCE_SHEET_COMPARATIVE_REPORT_REQUEST": {
      return {
        ...state,
        balance_sheet_report: null,
        errorField: null
      };
    }

    case "GET_BALANCE_SHEET_COMPARATIVE_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading:false,
        locations: action.locations,
        departments: action.departments,
        projects: action.projects,
      };
    }
    case "GET_BALANCE_SHEET_COMPARATIVE_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading:false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
