import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getCashManagements({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [cash_managements, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(
              get,
              "financials/cash-managements?search=" + search + "&page=" + page
            ),
            call(get, "financials/cash-managements/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(
              get,
              "financials/cash-managements?" + filter + "&page=" + page
            ),
            call(get, "financials/cash-managements/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "financials/cash-managements?search=" + search),
          call(get, "financials/cash-managements/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "financials/cash-managements?" + filter),
          call(get, "financials/cash-managements/foreign-keys")
        ]);

    // console.log(cash_managements);
    // console.log(foreign_keys);

    // Jika sukses get data cash_managements
    if (cash_managements.status === "success") {
      // Jika di API menggunakan pagination
      if (cash_managements.result.current_page && search) {
        yield put({
          type: "GET_CASH_MANAGEMENTS_SUCCESS",
          cash_managements: cash_managements.result.data,
          current_page: cash_managements.result.current_page,
          page_size: cash_managements.result.per_page,
          total: cash_managements.result.total,
          search: search
        });
      } else if (cash_managements.result.current_page && filter) {
        yield put({
          type: "GET_CASH_MANAGEMENTS_SUCCESS",
          cash_managements: cash_managements.result.data,
          current_page: cash_managements.result.current_page,
          page_size: cash_managements.result.per_page,
          total: cash_managements.result.total,
          filter: filter
        });
      } else if (cash_managements.result.current_page) {
        yield put({
          type: "GET_CASH_MANAGEMENTS_SUCCESS",
          cash_managements: cash_managements.result.data,
          current_page: cash_managements.result.current_page,
          page_size: cash_managements.result.per_page,
          total: cash_managements.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_CASH_MANAGEMENTS_SUCCESS",
          cash_managements: cash_managements.result,
          page_size: null,
          total: null
        });
      }
    } else if (cash_managements.status === "fail") {
      yield put({
        type: "GET_CASH_MANAGEMENTS_ERROR",
        error: cash_managements
      });
    } else {
      yield put({
        type: "GET_CASH_MANAGEMENTS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    // Jika sukses get data cash_management types
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_CASH_MANAGEMENT_FOREIGN_KEYS_SUCCESS",
        accounts: foreign_keys.result.accounts,
        cash_bank_accounts: foreign_keys.result.cash_bank_accounts,
        cash_banks: foreign_keys.result.cash_banks,
        cash_flow_types: foreign_keys.result.cash_flow_types,
        customers: foreign_keys.result.customers,
        currencies: foreign_keys.result.currencies,
        departments: foreign_keys.result.departments,
        locations: foreign_keys.result.locations,
        messages: foreign_keys.result.messages,
        projects: foreign_keys.result.projects,
        suppliers: foreign_keys.result.suppliers
      });
    } else {
      yield put({
        type: "GET_CASH_MANAGEMENT_FOREIGN_KEYS_ERROR"
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "GET_CASH_MANAGEMENTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

