// Create data
export function* createCashManagement() {
  try {
    const [foreign_keys] = yield all([
      call(get, "financials/cash-managements/foreign-keys")
    ]);

    // Jika sukses get data account
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_CASH_MANAGEMENT_FOREIGN_KEYS_SUCCESS",
        accounts: foreign_keys.result.accounts,
        cash_bank_accounts: foreign_keys.result.cash_bank_accounts,
        cash_banks: foreign_keys.result.cash_banks,
        cash_flow_types: foreign_keys.result.cash_flow_types,
        customers: foreign_keys.result.customers,
        currencies: foreign_keys.result.currencies,
        departments: foreign_keys.result.departments,
        locations: foreign_keys.result.locations,
        messages: foreign_keys.result.messages,
        projects: foreign_keys.result.projects,
        suppliers: foreign_keys.result.suppliers
      });
    } else {
      yield put({
        type: "GET_CASH_MANAGEMENT_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_CASH_MANAGEMENT_FOREIGN_KEYS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Store data
export function* storeCashManagement({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "financials/cash-managements", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_CASH_MANAGEMENT_SUCCESS",
        id_cash_management: resultStore.result.id_cash_management,
        success: resultStore
      });
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_CASH_MANAGEMENT_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_CASH_MANAGEMENT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_CASH_MANAGEMENT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Edit data
export function* editCashManagement({ payload }) {
  const { id_cash_management } = payload;

  try {
    const [cash_management, foreign_keys] = yield all([
      call(get, "financials/cash-managements/" + id_cash_management),
      call(get, "financials/cash-managements/foreign-keys")
    ]);

    // Jika sukses
    if (cash_management.status === "success") {
      yield put({
        type: "EDIT_CASH_MANAGEMENT_SUCCESS",
        cash_management: cash_management.result
      });
    } else {
      yield put({
        type: "EDIT_CASH_MANAGEMENT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    // Jika sukses get data cash_management types
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_CASH_MANAGEMENT_FOREIGN_KEYS_SUCCESS",
        accounts: foreign_keys.result.accounts,
        cash_bank_accounts: foreign_keys.result.cash_bank_accounts,
        cash_banks: foreign_keys.result.cash_banks,
        cash_flow_types: foreign_keys.result.cash_flow_types,
        customers: foreign_keys.result.customers,
        currencies: foreign_keys.result.currencies,
        departments: foreign_keys.result.departments,
        locations: foreign_keys.result.locations,
        messages: foreign_keys.result.messages,
        projects: foreign_keys.result.projects,
        suppliers: foreign_keys.result.suppliers
      });
    } else {
      yield put({
        type: "GET_CASH_MANAGEMENT_FOREIGN_KEYS_ERROR"
        // error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_CASH_MANAGEMENT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Update data
export function* updateCashManagement({ payload }) {
  const { id_cash_management, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "financials/cash-managements/" + id_cash_management,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: "UPDATE_CASH_MANAGEMENT_SUCCESS",
          success: resultUpdate
        }),
        yield put({
          type: "EDIT_CASH_MANAGEMENT_REQUEST",
          payload: { id_cash_management }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_CASH_MANAGEMENT_ERROR",
        error: resultUpdate
      });
    } else {
      yield put({
        type: "UPDATE_CASH_MANAGEMENT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_CASH_MANAGEMENT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Show data
