// Decancel data (mengubah is_active => 0)
export function* processCashManagement({ payload }) {
  const { id_cash_management } = payload;

  try {
    // POST data ke API
    const processResult = yield call(
      putApi,
      "financials/cash-managements/process-transaction/" + id_cash_management
      // body
    );

    // Jika sukses
    if (processResult.status === "success") {
      yield all([
        yield put({
          type: "PROCESS_CASH_MANAGEMENT_SUCCESS",
          success: processResult
        }),
        yield put({
          type: "EDIT_CASH_MANAGEMENT_REQUEST",
          payload: { id_cash_management }
        })
      ]);
    } else if (processResult.status === "fail") {
      yield put({
        type: "PROCESS_CASH_MANAGEMENT_ERROR",
        error: processResult
      });
    } else {
      yield put({
        type: "PROCESS_CASH_MANAGEMENT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "PROCESS_CASH_MANAGEMENT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

