// Upload Document
export function* uploadCashManagementDocument({ payload }) {
  const { id_cash_management, body } = payload;
  let data = {
    ...body,
    id_cash_management
  };

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "financials/cash-managements/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: "UPLOAD_CASH_MANAGEMENT_DOCUMENT_SUCCESS",
          success: uploadResult
        }),
        yield put({
          type: "EDIT_CASH_MANAGEMENT_REQUEST",
          payload: { id_cash_management }
        })
      ]);
    } else if (uploadResult.errors) {
      yield put({
        type: "UPLOAD_CASH_MANAGEMENT_DOCUMENT_ERROR",
        error: { message: uploadResult.errors.document[0] }
      });
    } else if (uploadResult.status === "fail") {
      yield put({
        type: "UPLOAD_CASH_MANAGEMENT_DOCUMENT_ERROR",
        error: uploadResult
      });
    } else {
      yield put({
        type: "UPLOAD_CASH_MANAGEMENT_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    // }
  } catch (error) {
    yield put({
      type: "UPLOAD_CASH_MANAGEMENT_DOCUMENT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Download Document
export function* downloadCashManagementDocument({ payload }) {
  const { id_cash_management_document } = payload;

  try {
    // POST ke API
    // console.log("masukk")
    yield call(
      get,
      "financials/cash-managements/documents/download/" +
        id_cash_management_document
    );
    // }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deleteCashManagementDocument({ payload }) {
  const { id_cash_management_document } = payload;
  // const body = { is_active: 0 };

  try {
    // POST data ke API
    const deleteResult = yield call(
      putApi,
      "financials/cash-managements/documents/" + id_cash_management_document
      //  body
    );

    // Jika sukses
    if (deleteResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_CASH_MANAGEMENT_DOCUMENT_SUCCESS",
          success: deleteResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_CASH_MANAGEMENT_REQUEST",
          payload: {
            id_cash_management: deleteResult.result.id_cash_management
          }
        })
      ]);
    } else if (deleteResult.status === "fail") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_CASH_MANAGEMENT_DOCUMENT_ERROR",
          error: deleteResult
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_CASH_MANAGEMENT_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        })
      ]);
    }
  } catch (error) {
    yield put({
      type: "DELETE_CASH_MANAGEMENT_DOCUMENT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_CASH_MANAGEMENTS_REQUEST", getCashManagements),
    yield takeEvery(
      "GET_IMPORT_CASH_MANAGEMENTS_REQUEST",
      getImportCashManagements
    ),
    yield takeEvery("CREATE_CASH_MANAGEMENT_REQUEST", createCashManagement),
    yield takeEvery("STORE_CASH_MANAGEMENT_REQUEST", storeCashManagement),
    yield takeEvery("EDIT_CASH_MANAGEMENT_REQUEST", editCashManagement),
    yield takeEvery("UPDATE_CASH_MANAGEMENT_REQUEST", updateCashManagement),
    yield takeEvery("SHOW_CASH_MANAGEMENT_REQUEST", showCashManagement),
    yield takeEvery("PROCESS_CASH_MANAGEMENT_REQUEST", processCashManagement),
    yield takeEvery("CANCEL_CASH_MANAGEMENT_REQUEST", cancelCashManagement),
    yield takeEvery(
      "UPLOAD_CASH_MANAGEMENT_DOCUMENT_REQUEST",
      uploadCashManagementDocument
    ),
    yield takeEvery(
      "DOWNLOAD_CASH_MANAGEMENT_DOCUMENT_REQUEST",
      downloadCashManagementDocument
    ),
    yield takeEvery(
      "DELETE_CASH_MANAGEMENT_DOCUMENT_REQUEST",
      deleteCashManagementDocument
    )
  ]);
}
