const initState = {
  key: null
};

export default function setupMenuReducer(state = initState, action) {
  switch (action.type) {
    case "SET_BACK_TO_PREV_ROUTE": {
      return {
        ...state,
        key: action.payload.key
      };
    }
    case "CLEAR_KEY": {
      return {
        ...state,
        key: ""
      };
    }
    default:
      return state;
  }
}
