import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getAccountHistorySubLedgerReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/account-histories/by-sub-ledgers?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if (filter) {
      const [account_history_sub_ledger_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/gl-transactions/foreign-keys")
      ]);
  
      // Jika sukses get data account_history_sub_ledger_reports
      if (account_history_sub_ledger_reports.status === "success" && account_history_sub_ledger_reports.result !== null) {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORTS_SUCCESS",
          account_history_sub_ledger_reports: account_history_sub_ledger_reports.result,
          filter
        });
      }
      else if (account_history_sub_ledger_reports.status === "success" && account_history_sub_ledger_reports.result === null) {
        yield put({
          type: "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORTS_SUCCESS",
          account_history_sub_ledger_reports: [],
          filter
        });
      }
      else if (account_history_sub_ledger_reports.status === "fail") {
        yield put({
          type: "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORTS_ERROR",
          error: { message: account_history_sub_ledger_reports.message }
        });
      }
      else {
        yield put({
          type: "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data account_history_sub_ledger_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          accounts: foreign_keys.result.accounts,
          account_types: foreign_keys.result.account_types,
          departments: foreign_keys.result.departments,
          projects: foreign_keys.result.projects,
          customers: foreign_keys.result.customers,
          // suppliers: foreign_keys.result.suppliers,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORT_FOREIGN_KEYS_ERROR",
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else {
      const [foreign_keys] = yield all([
        call(get, "reports/gl-transactions/foreign-keys")
      ]);
  
      yield put({
        type: "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORTS_SUCCESS",
        account_history_sub_ledger_reports: [],
      });
  
      // Jika sukses get data account_history_sub_ledger_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          accounts: foreign_keys.result.accounts,
          account_types: foreign_keys.result.account_types,
          departments: foreign_keys.result.departments,
          projects: foreign_keys.result.projects,
          customers: foreign_keys.result.customers,
          // suppliers: foreign_keys.result.suppliers,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Get Suppliers List Data based on Location
export function* getSuppliersBasedOnLocation({ payload }) {
  const { id_location } = payload;
  let filter = "suppliers?is_active=1" + "&id_location=" + id_location;

  try {
    const suppliers = yield call(get, filter);

    if (suppliers.status === "success") {
      yield put({
        type: "GET_SUPPLIERS_BASED_ON_LOCATION_SUCCESS",
        suppliers: suppliers.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORTS_REQUEST",
      getAccountHistorySubLedgerReports
    ),
    yield takeEvery(
      "GET_SUPPLIERS_BASED_ON_LOCATION_REQUEST",
      getSuppliersBasedOnLocation
    )
  ]);
}
