
const initState = {
  app_master_codes: [],
  app_master_code: null,
  search: null,
  //filter: null,
  loading: false,
  loadingEdit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function appMasterCodesReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_APP_MASTER_CODES_REQUEST': {
      return {
        ...state,
        //app_master_codes: [],
        loading: true,
        search: action.payload.search,
        //filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_APP_MASTER_CODES_SUCCESS': {
      return {
        ...state,
        app_master_codes: action.app_master_codes,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        //filter: action.filter,
        loading: false,
      };
    }
    case 'GET_APP_MASTER_CODES_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        //errorField: action.error.errors
      };
    }
    
    case 'STORE_APP_MASTER_CODE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_APP_MASTER_CODE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_APP_MASTER_CODE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_APP_MASTER_CODE_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null,
      };
    }
    case 'EDIT_APP_MASTER_CODE_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        app_master_code: action.app_master_code,
      };
    }
    case 'EDIT_APP_MASTER_CODE_ERROR': {
      return {
        ...state,
        //loading: false,
        error: action.error.message,
        //errorField: action.error.errors
      };
    }
    case 'UPDATE_APP_MASTER_CODE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_APP_MASTER_CODE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_APP_MASTER_CODE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }

    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
