const initState = {
  asset_categories: [],
  asset_category: null,
  dropdown_update: [],
  asset_categories_list: [],
  loadingSubmit: false,
  loadingAssetCategory: false,
  loadingAssetCategories: false,
  loadingAssetCategoriesList: false,
  condition: 0,
  success: null,
  error: null,
  errorField: null
};

const assetCategoriesReducer = (state = initState, action) => {
  switch (action.type) {
    case "GET_ASSET_CATEGORIES_REQUEST":
      return {
        ...state,
        condition: 0,
        asset_category: null,
        loadingAssetCategories: true,
        loadingAssetCategoriesList: true,
      };
    case "GET_ASSET_CATEGORIES_SUCCESS":
      return {
        ...state,
        loadingAssetCategoriesList: false,
        asset_categories_list: action.asset_categories_list,
        loadingAssetCategories: false,
        asset_categories: action.asset_categories
      };
    case "GET_ASSET_CATEGORIES_ERROR":
      return {
        ...state,
        loadingAssetCategories: false,
        error: action.error.message
      };
    case "STORE_ASSET_CATEGORY_REQUEST":
      return {
        ...state,
        loadingAssetCategory: true,
        loadingSubmit: true
      };
    case "STORE_ASSET_CATEGORY_SUCCESS":
      return {
        ...state,
        loadingAssetCategory: false,
        loadingSubmit: false,
        asset_category: action.asset_category,
        success: action.success.message,
      };
    case "STORE_ASSET_CATEGORY_ERROR":
      return {
        ...state,
        loadingAssetCategory: false,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    case "SHOW_ASSET_CATEGORY_REQUEST":
      return {
        ...state,
        asset_category: null,
        dropdown_update: [],
        loadingAssetCategory: true
      };
    case "SHOW_ASSET_CATEGORY_SUCCESS":
      return {
        ...state,
        loadingAssetCategory: false,
        asset_category: action.asset_category,
        dropdown_update: action.dropdown_update,
        condition: 2,
      };
    case "SHOW_ASSET_CATEGORY_ERROR":
      return {
        ...state,
        loadingAssetCategory: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    case "UPDATE_ASSET_CATEGORY_REQUEST":
      return {
        ...state,
        loadingAssetCategory: true,
        loadingSubmit: true
      };
    case "UPDATE_ASSET_CATEGORY_SUCCESS":
      return {
        ...state,
        loadingAssetCategory: false,
        loadingSubmit: false,
        asset_category: action.asset_category,
        success: action.success.message,
      };
    case "UPDATE_ASSET_CATEGORY_ERROR":
      return {
        ...state,
        loadingAssetCategory: false,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    case "ACTIVATE_ASSET_CATEGORY_REQUEST":
      return {
        ...state,
        loadingAssetCategory: true,
        loadingSubmit: true
      };
    case "ACTIVATE_ASSET_CATEGORY_SUCCESS":
      return {
        ...state,
        loadingAssetCategory: false,
        loadingSubmit: false,
        asset_category: action.asset_category,
        success: action.success.message,
        condition: 2,
      };
    case "ACTIVATE_ASSET_CATEGORY_ERROR":
      return {
        ...state,
        loadingAssetCategory: false,
        loadingSubmit: false,
        error: action.error.message
      };
    case "DEACTIVATE_ASSET_CATEGORY_REQUEST":
      return {
        ...state,
        loadingAssetCategory: true,
        loadingSubmit: true
      };
    case "DEACTIVATE_ASSET_CATEGORY_SUCCESS":
      return {
        ...state,
        loadingAssetCategory: false,
        loadingSubmit: false,
        asset_category: action.asset_category,
        success: action.success.message,
        condition: 2,
      };
    case "DEACTIVATE_ASSET_CATEGORY_ERROR":
      return {
        ...state,
        loadingAssetCategory: false,
        loadingSubmit: false,
        error: action.error.message
      };
    case "CLEAR_ASSET_CATEGORY":
      return {
        ...state,
        asset_category: null,
        loadingAssetCategory: false,
        condition: 1,
      };
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return {
        ...state
      };
  }
};

export default assetCategoriesReducer;
