
const initState = {
  colors: [],
  color: null,
  units: [],
  search: null,
  //filter: null,
  loading: false,
  loadingEdit: false,
  current_page: 1,
  page_color: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function colorsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_COLORS_REQUEST': {
      return {
        ...state,
        //colors: [],
        loading: true,
        search: action.payload.search,
        //filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_COLORS_SUCCESS': {
      return {
        ...state,
        colors: action.colors,
        current_page: action.current_page,
        page_color: action.page_color,
        total: action.total,
        search: action.search,
        //filter: action.filter,
        loading: false,
      };
    }
    case 'GET_COLORS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        //errorField: action.error.errors
      };
    }
    case 'CREATE_COLOR_REQUEST': {
      return {
        ...state,
        // color: null,
        errorField: null,
      };
    }
    case 'STORE_COLOR_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_COLOR_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_COLOR_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_COLOR_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null,
      };
    }
    case 'EDIT_COLOR_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        color: action.color,
      };
    }
    case 'EDIT_COLOR_ERROR': {
      return {
        ...state,
        //loading: false,
        error: action.error.message,
        //errorField: action.error.errors
      };
    }
    case 'UPDATE_COLOR_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_COLOR_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_COLOR_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_COLOR_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_COLOR_SUCCESS': {
      return {
        ...state,
        loading: false,
        colors: action.colors
      };
    }
    case "DEACTIVATE_COLOR_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_COLOR_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_COLOR_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_COLOR_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_COLOR_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_COLOR_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
