const initState = {
  acc_pay_trans_history_reports: [],
  locations: [],
  suppliers: [],
  supplier_types: [],
  currencies: [],
  filter: null,
  filter_select: [],
  loading: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function accPayTransHistoryReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_ACC_PAY_TRANS_HISTORY_REPORTS_REQUEST": {
      return {
        ...state,
        //acc_pay_trans_history_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_ACC_PAY_TRANS_HISTORY_REPORTS_SUCCESS": {
      return {
        ...state,
        acc_pay_trans_history_reports: action.acc_pay_trans_history_reports,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        // filter: action.filter,
        loading: false
      };
    }
    case "GET_ACC_PAY_TRANS_HISTORY_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "GET_ACC_PAY_TRANS_HISTORY_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        suppliers: action.suppliers,
        supplier_types: action.supplier_types,
        currencies: action.currencies,
      };
    }
    case "GET_ACC_PAY_TRANS_HISTORY_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
