const initState = {
  accounts: [],
  baseCurrency: null,
  deliveryOrders: [],
  discountBills: [],
  discountItems: [],
  cashBanks: [],
  cashFlowTypes: [],
  currencies: [],
  currencyRates: [],
  current_page: 1,
  current_page_do: 1,
  customers: [],
  departments: [],
  couriers: [],
  error: null,
  errorField: null,
  filter: null,
  id_customer: null,
  id_location: null,
  id_payment_category: null,
  id_sales_invoice: null,
  info: [], //display ordered item which exceeds available qty
  infoMessages: [],
  itemDiscount: [],
  itemRequests: [],
  items: [],
  loading: false,
  loadingCancel: false,
  loadingConfirm: false,
  loadingDeliveryOrders: false,
  loadingForeign: false,
  loadingItems: false,
  loadingSubmit: false,
  loadingTable: false,
  loadingUpdateSalesInvoiceDetails: false,
  locations: [],
  page_size: null,
  page_size_do: null,
  paymentCategories: [],
  paymentTerms: [],
  prevTrans: [],
  prevTransConfirm: {},
  projects: [],
  SITransactionDate: null,
  SIDiscountBill: null,
  salesInvoices: [],
  salesInvoice: null,
  salesOrders: [],
  salesOrders: [],
  salesman: [],
  success: null,
  search: null,
  taxes: [],
  tax_bases: [],
  total: null,
  total_do: null,
  transactions: [],
  updateItems: true,
  update_sales_invoice_details: [],
};

export default function salesInvoicesReducer(state = initState, action) {
  switch (action.type) {
    case "GET_SALES_INVOICES_REQUEST": {
      return {
        ...state,
        //salesInvoices: [],
        itemDiscount: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_SALES_INVOICES_SUCCESS": {
      return {
        ...state,
        salesInvoices: action.salesInvoices,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_SALES_INVOICES_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_SALES_INVOICE_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_SALES_INVOICE_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        accounts: action.accounts,
        customers: action.customers,
        deliveryOrders: action.deliveryOrders,
        departments: action.departments,
        infoMessages: action.infoMessages,
        loadingDeliveryOrders: false,
        loadingForeign: false,
        locations: action.locations,
        paymentCategories: action.paymentCategories,
        paymentTerms: action.paymentTerms,
        prevTransConfirm: action.prevTransConfirm,
        projects: action.projects,
        couriers: action.couriers,
        salesman: action.salesman,
        salesOrders: action.salesOrders,
        taxes: action.taxes
      };
    }
    case "GET_SALES_INVOICE_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "CREATE_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        id_sales_invoice: null,
        id_location: null,
        salesInvoice: null,
        itemDiscount: [],
        deliveryOrders: [],
        // loadingTransactions: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null,
        current_page_do: 1,
        page_size_do: null,
        total_do: null,
        tax_bases: []
      };
    }
    case "GET_FILTER_ITEMS_BY_CUSTOMER_SI_REQUEST": {
      return {
        ...state,
        loadingItems: true,
        search: action.payload.search,
        id_location: action.payload.id_location,
        id_customer: action.payload.id_customer,
        id_payment_category: action.payload.id_payment_category
      };
    }
    case "GET_FILTER_ITEMS_BY_CUSTOMER_SI_SUCCESS": {
      return {
        ...state,
        loadingItems: false,
        discountBills: action.discountBills,
        discountItems: action.discountItems,
        items: action.items
      };
    }
    case "GET_FILTER_ITEMS_BY_CUSTOMER_SI_ERROR": {
      return {
        ...state,
        loadingItems: false,
        error: action.error.message
      };
    }
    case "GET_DISCOUNT_SI_REQUEST": {
      return {
        ...state,
        loadingTable: true
      };
    }
    case "GET_DISCOUNT_SI_SUCCESS": {
      return {
        ...state,
        loadingTable: false,
        itemDiscount: action.itemDiscount
      };
    }
    case "GET_DISCOUNT_SI_ERROR": {
      return {
        ...state,
        loadingTable: false,
        itemDiscount: action.itemDiscount,
        error: action.error.message
      };
    }
    case "GET_FILTER_SALES_INVOICES_REQUEST": {
      return {
        ...state,
        loadingTransactions: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page
      };
    }
    case "GET_FILTER_SALES_INVOICES_SUCCESS": {
      return {
        ...state,
        loadingTransactions: false,
        transactions: action.transactions,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total
      };
    }
    case "GET_FILTER_SALES_INVOICES_ERROR": {
      return {
        ...state,
        loadingTransactions: false,
        error: action.error.message
      };
    }
    case "GET_FILTER_DELIVERY_ORDERS_SI_REQUEST": {
      return {
        ...state,
        loadingDeliveryOrders: true,
        current_page_do: action.payload.page,
        deliveryOrders: [],
        filter: action.payload.filter,
        search: action.payload.search
      };
    }
    case "GET_FILTER_DELIVERY_ORDERS_SUCCESS": {
      return {
        ...state,
        loadingDeliveryOrders: false,
        deliveryOrders: action.deliveryOrders,
        current_page_do: action.current_page,
        page_size_do: action.page_size,
        total_do: action.total
      };
    }
    case "GET_FILTER_DELIVERY_ORDERS_ERROR": {
      return {
        ...state,
        loadingDeliveryOrders: false,
        error: action.error.message
      };
    }
    case "STORE_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_SALES_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_sales_invoice: action.id_sales_invoice,
        info: action.info
      };
    }
    case "STORE_SALES_INVOICE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        loading: true,
        id_sales_invoice: null,
        id_location: null,
        // loadingTransactions: true,
        loadingForeign: true,
        errorField: null,
        // current_page: 1,
        // page_size: null,
        // total: null,
        current_page_do: 1,
        page_size_do: null,
        total_do: null,
        tax_bases: []
      };
    }
    case "EDIT_SALES_INVOICE_SUCCESS": {
      return {
        ...state,
        loading: false,
        // loadingTransactions: false, //bug
        loadingSubmit: false,
        salesInvoice: action.salesInvoice
      };
    }
    case "EDIT_SALES_INVOICE_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_SALES_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_sales_invoice: action.id_sales_invoice,
        info: action.info
      };
    }
    case "UPDATE_SALES_INVOICE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_SALES_INVOICE_SUCCESS": {
      return {
        ...state,
        loading: false,
        salesInvoices: action.salesInvoices
      };
    }
    case "CONFIRM_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        loadingConfirm: true
      };
    }
    case "CONFIRM_SALES_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingConfirm: false,
        success: action.success.message,
      };
    }
    case "CONFIRM_SALES_INVOICE_ERROR": {
      return {
        ...state,
        loadingConfirm: false,
        error: action.error.message
      };
    }
    case "UNCONFIRM_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "UNCONFIRM_SALES_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
      };
    }
    case "UNCONFIRM_SALES_INVOICE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CLOSE_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CLOSE_SALES_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
      };
    }
    case "CLOSE_SALES_INVOICE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CANCEL_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        loadingCancel: true
      };
    }
    case "CANCEL_SALES_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingCancel: false,
        success: action.success.message,
      };
    }
    case "CANCEL_SALES_INVOICE_ERROR": {
      return {
        ...state,
        loadingCancel: false,
        error: action.error.message
      };
    }
    case "UPLOAD_SALES_INVOICE_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_SALES_INVOICE_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_SALES_INVOICE_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_SALES_INVOICE_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_SALES_INVOICE_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_SALES_INVOICE_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_SALES_INVOICE_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null,
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    case "SET_TAX_BASES_SALES_INVOICE": {
      return {
        ...state,
        tax_bases: action.payload.tax_bases
      };
    }
    case "GET_UPDATE_SALES_INVOICE_DETAILS_REQUEST": {
      return {
        ...state,
        loadingUpdateSalesInvoiceDetails: true
      };
    }
    case "GET_UPDATE_SALES_INVOICE_DETAILS_SUCCESS": {
      return {
        ...state,
        loadingUpdateSalesInvoiceDetails: false,
        updateItems: action.payload.updateItems,
        update_sales_invoice_details:
          action.payload.update_sales_invoice_details,
        SITransactionDate: action.payload.transaction_date,
        SIDiscountBill: action.payload.discount_bill
      };
    }
    default:
      return state;
  }
}
