const initState = {
  cash_managements: [],
  cash_management: null,
  locations: [],
  cash_banks: [],
  customers: [],
  accounts: [],
  cash_bank_accounts: [],
  cash_flow_types: [],
  currencies: [],
  projects: [],
  suppliers: [],
  departments: [],
  messages: [],
  search: null,
  filter: null,
  loading: false,
  loadingImport: false,
  loadingEdit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
  id_cash_management: null,
  import_cash_managements: [],
  import_current_page: 1,
  import_page_size: null,
  import_total: null,
  import_search: null,
  import_filter: null
};

export default function cashManagementsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_CASH_MANAGEMENTS_REQUEST": {
      return {
        ...state,
        //cash_managements: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_CASH_MANAGEMENTS_SUCCESS": {
      return {
        ...state,
        cash_managements: action.cash_managements,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_CASH_MANAGEMENTS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_IMPORT_CASH_MANAGEMENTS_REQUEST": {
      return {
        ...state,
        //cash_managements: [],
        loadingImport: true,
        import_search: action.payload.search,
        import_filter: action.payload.filter,
        import_current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_IMPORT_CASH_MANAGEMENTS_SUCCESS": {
      return {
        ...state,
        import_cash_managements: action.cash_managements,
        import_current_page: action.current_page,
        import_page_size: action.page_size,
        import_total: action.total,
        import_search: action.search,
        import_filter: action.filter,
        loadingImport: false
      };
    }
    case "GET_IMPORT_CASH_MANAGEMENTS_ERROR": {
      return {
        ...state,
        loadingImport: false,
        error: action.error.message
      };
    }
    case "CREATE_CASH_MANAGEMENT_REQUEST": {
      return {
        ...state,
        cash_management: null,
        errorField: null
      };
    }

    case "GET_CASH_MANAGEMENT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        accounts: action.accounts,
        cash_bank_accounts: action.cash_bank_accounts,
        cash_banks: action.cash_banks,
        cash_flow_types: action.cash_flow_types,
        currencies: action.currencies,
        customers: action.customers,
        departments: action.departments,
        locations: action.locations,
        messages: action.messages,
        projects: action.projects,
        suppliers: action.suppliers
      };
    }
    case "GET_CASH_MANAGEMENT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }

    case "STORE_CASH_MANAGEMENT_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "STORE_CASH_MANAGEMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        id_cash_management: action.id_cash_management,
        success: action.success.message
      };
    }
    case "STORE_CASH_MANAGEMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "EDIT_CASH_MANAGEMENT_REQUEST": {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null
      };
    }
    case "EDIT_CASH_MANAGEMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        cash_management: action.cash_management
      };
    }
    case "EDIT_CASH_MANAGEMENT_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingEdit:false,
        error: action.error.message
      };
    }
    case "UPDATE_CASH_MANAGEMENT_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "UPDATE_CASH_MANAGEMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPDATE_CASH_MANAGEMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_CASH_MANAGEMENT_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_CASH_MANAGEMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        cash_managements: action.cash_managements
      };
    }
    case "PROCESS_CASH_MANAGEMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "PROCESS_CASH_MANAGEMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "PROCESS_CASH_MANAGEMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CANCEL_CASH_MANAGEMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "CANCEL_CASH_MANAGEMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "CANCEL_CASH_MANAGEMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "UPLOAD_CASH_MANAGEMENT_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_CASH_MANAGEMENT_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_CASH_MANAGEMENT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_CASH_MANAGEMENT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_CASH_MANAGEMENT_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_CASH_MANAGEMENT_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_CASH_MANAGEMENT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
