const initState = {
  inventory_aging_reports: [],
  locations: [],
  filter: null,
  filter_select: [],
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function inventoryAgingReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_INVENTORY_AGING_REPORTS_REQUEST": {
      return {
        ...state,
        //inventory_aging_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_INVENTORY_AGING_REPORTS_SUCCESS": {
      return {
        ...state,
        inventory_aging_reports: action.inventory_aging_reports,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_INVENTORY_AGING_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_INVENTORY_AGING_REPORT_REQUEST": {
      return {
        ...state,
        inventory_aging_report: null,
        errorField: null
      };
    }

    case "GET_INVENTORY_AGING_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
      };
    }
    case "GET_INVENTORY_AGING_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
