import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getInventoryAgingReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/inventory-transactions/inventory-agings?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if (filter) {
      const [inventory_aging_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/inventory-transactions/foreign-keys")
      ]);
    
      // Jika sukses get data inventory_aging_reports
      if (inventory_aging_reports.status === "success") {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_INVENTORY_AGING_REPORTS_SUCCESS",
          inventory_aging_reports: inventory_aging_reports.result,
          filter
        });
      } else if (inventory_aging_reports.status === "fail") {
        yield put({
          type: "GET_INVENTORY_AGING_REPORTS_ERROR",
          error: { message: inventory_aging_reports.message }
        });
      } else {
        yield put({
          type: "GET_INVENTORY_AGING_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data inventory_aging_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_INVENTORY_AGING_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_INVENTORY_AGING_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_INVENTORY_AGING_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/inventory-transactions/foreign-keys")
      ]);
    
      yield put({
        type: "GET_INVENTORY_AGING_REPORTS_SUCCESS",
        inventory_aging_reports: []
      });
  
      // Jika sukses get data inventory_aging_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_INVENTORY_AGING_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_INVENTORY_AGING_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_INVENTORY_AGING_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_INVENTORY_AGING_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_INVENTORY_AGING_REPORTS_REQUEST",
      getInventoryAgingReports
    )
  ]);
}
