import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getSupplierPriceLists({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [supplierPriceLists, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(
              get,
              "promotions/supplier-pricelists?search=" +
                search +
                "&page=" +
                page
            ),
            call(get, "promotions/supplier-pricelists/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(
              get,
              "promotions/supplier-pricelists?" + filter + "&page=" + page
            ),
            call(get, "promotions/supplier-pricelists/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "promotions/supplier-pricelists?search=" + search),
          call(get, "promotions/supplier-pricelists/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "promotions/supplier-pricelists?" + filter),
          call(get, "promotions/supplier-pricelists/foreign-keys")
        ]);

    // Jika sukses get data supplierPriceLists
    if (supplierPriceLists.status === "success") {
      // Jika di API menggunakan pagination
      if (supplierPriceLists.result.current_page && search) {
        yield put({
          type: "GET_SUPPLIER_PRICE_LISTS_SUCCESS",
          supplierPriceLists: supplierPriceLists.result.data,
          current_page: supplierPriceLists.result.current_page,
          page_size: supplierPriceLists.result.per_page,
          total: supplierPriceLists.result.total,
          search: search
        });
      } else if (supplierPriceLists.result.current_page) {
        yield put({
          type: "GET_SUPPLIER_PRICE_LISTS_SUCCESS",
          supplierPriceLists: supplierPriceLists.result.data,
          current_page: supplierPriceLists.result.current_page,
          page_size: supplierPriceLists.result.per_page,
          total: supplierPriceLists.result.total,
          filter: filter
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_SUPPLIER_PRICE_LISTS_SUCCESS",
          supplierPriceLists: supplierPriceLists.result,
          page_size: null,
          total: null
        });
      }
    } else if (supplierPriceLists.status === "fail") {
      yield put({
        type: "GET_SUPPLIER_PRICE_LISTS_ERROR",
        error: { message: supplierPriceLists.message }
      });
    } else {
      yield put({
        type: "GET_SUPPLIER_PRICE_LISTS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
    // Jika sukses get data foreign keys
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_SUPPLIER_PRICE_LIST_FOREIGN_KEYS_SUCCESS",
        currencies: foreign_keys.result.currencies,
        locations: foreign_keys.result.locations,
        pricelist_info: foreign_keys.result.pricelist_info,
        supplier_types: foreign_keys.result.supplier_types
      });
    } else if (foreign_keys.status === "fail") {
      yield put({
        type: "GET_SUPPLIER_PRICE_LIST_FOREIGN_KEYS_ERROR",
        error: foreign_keys
      });
    } else {
      yield put({
        type: "GET_SUPPLIER_PRICE_LIST_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_SUPPLIER_PRICE_LIST_FOREIGN_KEYS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Preparing item for create data
export function* createSupplierPriceList() {
  try {
    const foreign_keys = yield call(
      get,
      "promotions/supplier-pricelists/foreign-keys"
    );

    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_SUPPLIER_PRICE_LIST_FOREIGN_KEYS_SUCCESS",
        currencies: foreign_keys.result.currencies,
        locations: foreign_keys.result.locations,
        pricelist_info: foreign_keys.result.pricelist_info,
        supplier_types: foreign_keys.result.supplier_types
      });
    } else if (foreign_keys.status === "fail") {
      yield put({
        type: "GET_SUPPLIER_PRICE_LIST_FOREIGN_KEYS_ERROR",
        error: foreign_keys
      });
    } else {
      yield put({
        type: "GET_SUPPLIER_PRICE_LIST_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_SUPPLIER_PRICE_LIST_FOREIGN_KEYS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Get Item Data by Location
export function* getItemsForSupplierPriceList({ payload }) {
  const { id_location, search } = payload;
  let filter =
    "items?is_active=1&limit=1" +
    "&id_location=" +
    id_location +
    "&last_purchase_price=1";
  filter = search ? filter + "&search=" + search : filter;

  try {
    const items = yield call(get, filter);

    if (items.status === "success") {
      yield put({
        type: "GET_FILTER_ITEMS_FOR_SUPPLIER_PRICE_LIST_SUCCESS",
        items: items.result
      });
    } else if (items.status === "fail") {
      yield put({
        type: "GET_FILTER_ITEMS_FOR_SUPPLIER_PRICE_LIST_ERROR",
        error: { message: items.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_ITEMS_FOR_SUPPLIER_PRICE_LIST_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// Store data
export function* storeSupplierPriceList({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(
      post,
      "promotions/supplier-pricelists",
      body
    );
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_SUPPLIER_PRICE_LIST_SUCCESS",
        success: { message: resultStore.message },
        id_supplier_pricelist: resultStore.result.id_supplier_pricelist
      });
    } else if (resultStore.errors) {
      yield put({
        type: "STORE_SUPPLIER_PRICE_LIST_ERROR",
        error: resultStore
      });
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_SUPPLIER_PRICE_LIST_ERROR",
        error: { message: resultStore.message }
      });
    } else {
      yield put({
        type: "STORE_SUPPLIER_PRICE_LIST_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_SUPPLIER_PRICE_LIST_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Edit data
export function* editSupplierPriceList({ payload }) {
  const { id_supplier_pricelist } = payload;

  try {
    const [supplierPriceList, foreign_keys] = yield all([
      call(get, "promotions/supplier-pricelists/" + id_supplier_pricelist),
      call(get, "promotions/supplier-pricelists/foreign-keys")
    ]);

    // manage foreign keys
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_SUPPLIER_PRICE_LIST_FOREIGN_KEYS_SUCCESS",
        currencies: foreign_keys.result.currencies,
        locations: foreign_keys.result.locations,
        pricelist_info: foreign_keys.result.pricelist_info,
        supplier_types: foreign_keys.result.supplier_types
      });
    } else if (foreign_keys.status === "fail") {
      yield put({
        type: "GET_SUPPLIER_PRICE_LIST_FOREIGN_KEYS_ERROR",
        error: foreign_keys
      });
    } else {
      yield put({
        type: "GET_SUPPLIER_PRICE_LIST_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    // manage supplierPriceList
    if (supplierPriceList.status === "success") {
      yield put({
        type: "EDIT_SUPPLIER_PRICE_LIST_SUCCESS",
        supplierPriceList: supplierPriceList.result
      });
    } else {
      yield put({
        type: "EDIT_SUPPLIER_PRICE_LIST_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_SUPPLIER_PRICE_LIST_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// update data
export function* updateSupplierPriceList({ payload }) {
  const { id_supplier_pricelist, body } = payload;
  // console.log("body, ", body);

  try {
    const resultUpdate = yield call(
      putApi,
      "promotions/supplier-pricelists/" + id_supplier_pricelist,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        put({
          type: "UPDATE_SUPPLIER_PRICE_LIST_SUCCESS",
          success: { message: resultUpdate.message },
          id_supplier_pricelist: id_supplier_pricelist
        }),
        put({
          type: "EDIT_SUPPLIER_PRICE_LIST_REQUEST",
          payload: {
            id_supplier_pricelist: resultUpdate.result.id_supplier_pricelist
          }
        })
      ]);
    } else if (resultUpdate.errors) {
      yield all([
        yield put({
          type: "UPDATE_SUPPLIER_PRICE_LIST_ERROR",
          error: resultUpdate
        }),
        yield put({
          type: "EDIT_SUPPLIER_PRICE_LIST_REQUEST",
          payload: {
            id_supplier_pricelist: resultUpdate.result.id_supplier_pricelist
          }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_SUPPLIER_PRICE_LIST_ERROR",
        error: { message: resultUpdate.message }
      });
    } else {
      yield put({
        type: "UPDATE_SUPPLIER_PRICE_LIST_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_SUPPLIER_PRICE_LIST_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 0)
