// Get Supplier Data by Location
export function* getSuppliersBasedOnLocation({ payload }) {
  const { id_location, search } = payload;
  let filter = "";
  if (search) {
    filter =
      "suppliers?is_active=1&limit=10&id_location=" +
      id_location +
      "&search=" +
      search;
  } else {
    filter = "suppliers?is_active=1&limit=10&id_location=" + id_location;
  }

  try {
    const suppliers = yield call(get, filter);

    if (suppliers.status === "success") {
      yield put({
        type: "GET_SUPPLIERS_FOR_SUPPLIER_PRICE_LIST_SUCCESS",
        suppliers: suppliers.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_SUPPLIER_PRICE_LISTS_REQUEST", getSupplierPriceLists),
    yield takeEvery(
      "CREATE_SUPPLIER_PRICE_LIST_REQUEST",
      createSupplierPriceList
    ),
    yield takeEvery(
      "GET_FILTER_ITEMS_FOR_SUPPLIER_PRICE_LIST_REQUEST",
      getItemsForSupplierPriceList
    ),
    yield takeEvery(
      "STORE_SUPPLIER_PRICE_LIST_REQUEST",
      storeSupplierPriceList
    ),
    yield takeEvery("EDIT_SUPPLIER_PRICE_LIST_REQUEST", editSupplierPriceList),
    yield takeEvery(
      "UPDATE_SUPPLIER_PRICE_LIST_REQUEST",
      updateSupplierPriceList
    ),
    yield takeEvery(
      "DEACTIVATE_SUPPLIER_PRICE_LIST_REQUEST",
      deactivateSupplierPriceList
    ),
    yield takeEvery(
      "ACTIVATE_SUPPLIER_PRICE_LIST_REQUEST",
      activateSupplierPriceList
    ),
    yield takeEvery(
      "REJECT_SUPPLIER_PRICE_LIST_REQUEST",
      rejectSupplierPriceList
    ),
    yield takeEvery(
      "CONFIRM_SUPPLIER_PRICE_LIST_REQUEST",
      confirmSupplierPriceList
    ),
    yield takeEvery(
      "GET_SUPPLIERS_FOR_SUPPLIER_PRICE_LIST_REQUEST",
      getSuppliersBasedOnLocation
    )
  ]);
}
