import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getIssueReceiptReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/issue-receipts?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if(filter){
      const [issue_receipt_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/issue-receipts/foreign-keys")
      ]);
    
      // Jika sukses get data issue_receipt_reports
      if (issue_receipt_reports.status === "success") {
        yield put({
          type: "GET_ISSUE_RECEIPT_REPORTS_SUCCESS",
          issue_receipt_reports: issue_receipt_reports.result,
          filter
        });
      } else if (issue_receipt_reports.status === "fail") {
        yield put({
          type: "GET_ISSUE_RECEIPT_REPORTS_ERROR",
          error: { message: issue_receipt_reports.message }
        });
      } else {
        yield put({
          type: "GET_ISSUE_RECEIPT_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data issue_receipt_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ISSUE_RECEIPT_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          adjustment_types: foreign_keys.result.adjustment_types
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_ISSUE_RECEIPT_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_ISSUE_RECEIPT_REPORT_FOREIGN_KEYS_ERROR"
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/issue-receipts/foreign-keys")
      ]);
    
      yield put({
        type: "GET_ISSUE_RECEIPT_REPORTS_SUCCESS",
        issue_receipt_reports: [],
      });
  
      // Jika sukses get data issue_receipt_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ISSUE_RECEIPT_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          adjustment_types: foreign_keys.result.adjustment_types
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_ISSUE_RECEIPT_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_ISSUE_RECEIPT_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_ISSUE_RECEIPT_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_ISSUE_RECEIPT_REPORTS_REQUEST", getIssueReceiptReports)
  ]);
}
