import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPurchaseReturns({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [purchaseReturns, foreign] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(
              get,
              "purchases/purchase-returns?search=" + search + "&page=" + page
            ),
            call(get, "purchases/purchase-returns/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "purchases/purchase-returns?" + filter + "&page=" + page),
            call(get, "purchases/purchase-returns/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "purchases/purchase-returns?search=" + search),
          call(get, "purchases/purchase-returns/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "purchases/purchase-returns?" + filter),
          call(get, "purchases/purchase-returns/foreign-keys")
        ]);

    // console.log(purchaseReturns);

    // Jika sukses get data purchaseReturns
    if (purchaseReturns.status === "success") {
      // Jika di API menggunakan pagination
      if (purchaseReturns.result.current_page && search) {
        yield put({
          type: "GET_PURCHASE_RETURNS_SUCCESS",
          purchaseReturns: purchaseReturns.result.data,
          current_page: purchaseReturns.result.current_page,
          page_size: purchaseReturns.result.per_page,
          total: purchaseReturns.result.total,
          search: search
        });
      } else if (purchaseReturns.result.current_page && filter) {
        yield put({
          type: "GET_PURCHASE_RETURNS_SUCCESS",
          purchaseReturns: purchaseReturns.result.data,
          current_page: purchaseReturns.result.current_page,
          page_size: purchaseReturns.result.per_page,
          total: purchaseReturns.result.total,
          filter: filter
        });
      } else if (purchaseReturns.result.current_page) {
        yield put({
          type: "GET_PURCHASE_RETURNS_SUCCESS",
          purchaseReturns: purchaseReturns.result.data,
          current_page: purchaseReturns.result.current_page,
          page_size: purchaseReturns.result.per_page,
          total: purchaseReturns.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_PURCHASE_RETURNS_SUCCESS",
          purchaseReturns: purchaseReturns.result,
          page_size: null,
          total: null
        });
      }
    } else if (purchaseReturns.status === "fail") {
      // Set message ke session storage
      yield put({
        type: "GET_PURCHASE_RETURNS_ERROR",
        error: { message: purchaseReturns.message }
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_PURCHASE_RETURNS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      yield put({
        type: "GET_PURCHASE_RETURN_FOREIGN_KEYS_SUCCESS",
        locations: foreign.result.locations,
        suppliers: foreign.result.suppliers,
        currencies: foreign.result.currencies,
        taxes: foreign.result.taxes,
        cashBanks: foreign.result.cash_banks,
        returnReasons: foreign.result.return_reasons,
        accounts: foreign.result.accounts,
        cashFlowTypes: foreign.result.cash_flow_types,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        prevTrans: foreign.result.purchase_receipts,
        purchaseOrders: foreign.result.purchase_orders
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_PURCHASE_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_PURCHASE_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_PURCHASE_RETURNS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createPurchaseReturn() {
  try {
    const [purchaseReceipts, foreign] = yield all([
      call(get, "purchases/purchase-receipts?get_old_trans=1&returned=1&status=2"),
      call(get, "purchases/purchase-returns/foreign-keys")
    ]);
  
    if (purchaseReceipts.status === "success") {
      yield put({
        type: "GET_FILTER_PURCHASE_RECEIPTS_SUCCESS",
        purchaseReceipts: purchaseReceipts.result.data,
        current_page: purchaseReceipts.result.current_page,
        page_size: purchaseReceipts.result.per_page,
        total: purchaseReceipts.result.total
      });
    } else if (purchaseReceipts.status === "fail") {
      yield put({
        type: "GET_FILTER_PURCHASE_RECEIPTS_ERROR",
        error: { message: purchaseReceipts.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_PURCHASE_RECEIPTS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (foreign.status === "success") {
      yield put({
        type: "GET_PURCHASE_RETURN_FOREIGN_KEYS_SUCCESS",
        locations: foreign.result.locations,
        suppliers: foreign.result.suppliers,
        currencies: foreign.result.currencies,
        taxes: foreign.result.taxes,
        cashBanks: foreign.result.cash_banks,
        returnReasons: foreign.result.return_reasons,
        accounts: foreign.result.accounts,
        cashFlowTypes: foreign.result.cash_flow_types,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        prevTrans: foreign.result.purchase_receipts,
        purchaseOrders: foreign.result.purchase_orders
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_PURCHASE_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_PURCHASE_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_PURCHASE_RETURN_FOREIGN_KEYS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

