export function* getPurchaseInvoices({ payload }) {
  let { page, search, filter } = payload;
  // console.log(filter);

  if (!filter.includes("payment_status=")) {
    filter += "&payment_status=0,1";
  }

  try {
    const purchaseInvoices = filter
      ? yield call(
          get,
          "purchases/purchase-invoices?get_old_trans=1&returned=1&status=2&transaction_type=1&" +
            filter +
            "&page=" +
            page
        )
      : search
      ? yield call(
          get,
          "purchases/purchase-invoices?get_old_trans=1&returned=1&status=2&payment_status=0,1&transaction_type=1&search=" +
            search +
            "&page=" +
            page
        )
      : yield call(
          get,
          "purchases/purchase-invoices?get_old_trans=1&returned=1&status=2&payment_status=0,1&transaction_type=1&page=" + page
        );

    if (purchaseInvoices.status === "success") {
      yield put({
        type: "GET_FILTER_PURCHASE_INVOICES_PT_SUCCESS",
        purchaseInvoices: purchaseInvoices.result.data,
        current_page: purchaseInvoices.result.current_page,
        page_size: purchaseInvoices.result.per_page,
        total: purchaseInvoices.result.total
      });
    } else if (purchaseInvoices.status === "fail") {
      yield put({
        type: "GET_FILTER_PURCHASE_INVOICES_PT_ERROR",
        error: { message: purchaseInvoices.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_PURCHASE_INVOICES_PT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_PURCHASE_INVOICES_PT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storePurchaseReturn({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "purchases/purchase-returns", body);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_PURCHASE_RETURN_SUCCESS",
        success: { message: resultStore.message },
        id_purchase_return: resultStore.result.id_purchase_return
      });
    } else if (resultStore.errors) {
      yield put({
        type: "STORE_PURCHASE_RETURN_ERROR",
        error: resultStore
      });
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_PURCHASE_RETURN_ERROR",
        error: { message: resultStore.message }
      });
    } else {
      yield put({
        type: "STORE_PURCHASE_RETURN_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_PURCHASE_RETURN_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editPurchaseReturn({ payload }) {
  // console.log("masuk");
  const { id_purchase_return } = payload;
  let purchaseReceipts = [];

  try {
    const [purchaseReturn, foreign] = yield all([
      call(get, "purchases/purchase-returns/" + id_purchase_return),
      call(get, "purchases/purchase-returns/foreign-keys")
    ]);
  
    // console.log(purchaseReturn);
  
    // Jika sukses
    if (purchaseReturn.status === "success") {
      // Get RQ by id_location
      const success = yield all([
        put({
          type: "EDIT_PURCHASE_RETURN_SUCCESS",
          purchaseReturn: purchaseReturn.result
        }),
        call(
          get,
          "purchases/purchase-receipts?get_old_trans=1&id_location=" +
            purchaseReturn.result.id_location
        )
      ]);
      purchaseReceipts = success[1];
    } else if (purchaseReturn.status === "fail") {
      yield put({
        type: "EDIT_PURCHASE_RETURN_ERROR",
        error: { message: purchaseReturn.message }
      });
    } else {
      yield put({
        type: "EDIT_PURCHASE_RETURN_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (purchaseReceipts.status === "success") {
      yield put({
        type: "GET_FILTER_PURCHASE_RECEIPTS_SUCCESS",
        purchaseReceipts: purchaseReceipts.result.data,
        current_page: purchaseReceipts.result.current_page,
        page_size: purchaseReceipts.result.per_page,
        total: purchaseReceipts.result.total
      });
    } else if (purchaseReceipts.status === "fail") {
      yield put({
        type: "GET_FILTER_PURCHASE_RECEIPTS_ERROR",
        error: { message: purchaseReceipts.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_PURCHASE_RECEIPTS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (foreign.status === "success") {
      yield put({
        type: "GET_PURCHASE_RETURN_FOREIGN_KEYS_SUCCESS",
        locations: foreign.result.locations,
        suppliers: foreign.result.suppliers,
        currencies: foreign.result.currencies,
        taxes: foreign.result.taxes,
        cashBanks: foreign.result.cash_banks,
        returnReasons: foreign.result.return_reasons,
        accounts: foreign.result.accounts,
        cashFlowTypes: foreign.result.cash_flow_types,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        prevTrans: foreign.result.purchase_receipts,
        purchaseOrders: foreign.result.purchase_orders
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_PURCHASE_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_PURCHASE_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_PURCHASE_RETURN_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updatePurchaseReturn({ payload }) {
  const { id_purchase_return, body } = payload;
  // console.log(body);

  try {
    const resultUpdate = yield call(
      putApi,
      "purchases/purchase-returns/" + id_purchase_return,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        put({
          type: "UPDATE_PURCHASE_RETURN_SUCCESS",
          success: { message: resultUpdate.message },
          id_purchase_return: id_purchase_return
        }),
        put({
          type: "EDIT_PURCHASE_RETURN_REQUEST",
          payload: { id_purchase_return: id_purchase_return }
        })
      ]);
    } else if (resultUpdate.errors) {
      yield all([
        yield put({
          type: "UPDATE_PURCHASE_RETURN_ERROR",
          error: resultUpdate
        }),
        yield put({
          type: "EDIT_PURCHASE_RETURN_REQUEST",
          payload: {
            id_purchase_return: id_purchase_return
          }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_PURCHASE_RETURN_ERROR",
        error: { message: resultUpdate.message }
      });
    } else {
      yield put({
        type: "UPDATE_PURCHASE_RETURN_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_PURCHASE_RETURN_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

