export function* confirmPurchaseReturn({ payload }) {
  const { id_purchase_return } = payload;

  try {
    // POST data ke API
    const confirmResult = yield call(
      putApi,
      "purchases/purchase-returns/process-transaction/" + id_purchase_return
    );

    // Jika sukses
    if (confirmResult.status === "success") {
      // Set session message
      yield all([
        put({
          type: "CONFIRM_PURCHASE_RETURN_SUCCESS",
          success: { message: confirmResult.message },
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_PURCHASE_RETURN_REQUEST",
          payload: {
            id_purchase_return: id_purchase_return
          }
        })
      ]);
    } else if (confirmResult.status === "fail") {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_PURCHASE_RETURN_ERROR",
          error: { message: confirmResult.message }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_PURCHASE_RETURN_REQUEST",
          payload: { id_purchase_return: id_purchase_return }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_PURCHASE_RETURN_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_PURCHASE_RETURN_REQUEST",
          payload: { id_purchase_return: id_purchase_return }
        })
      ]);
    }
  } catch (error) {
    yield all([
      put({
        type: "CONFIRM_PURCHASE_RETURN_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      put({
        type: "EDIT_PURCHASE_RETURN_REQUEST",
        payload: { id_purchase_return: id_purchase_return }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

