// Upload Document
export function* uploadPurchaseReceiptDocument({ payload }) {
  const { id_purchase_return, body } = payload;
  let data = {
    ...body,
    id_purchase_return
  };

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "purchases/purchase-returns/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: "UPLOAD_PURCHASE_RETURN_DOCUMENT_SUCCESS",
          success: uploadResult
        }),
        yield put({
          type: "EDIT_PURCHASE_RETURN_REQUEST",
          payload: { id_purchase_return }
        })
      ]);
    } else if (uploadResult.errors) {
      yield all([
        yield put({
          type: "UPLOAD_PURCHASE_RETURN_DOCUMENT_ERROR",
          error: { message: uploadResult.errors.document[0] }
        }),
        yield put({
          type: "EDIT_PURCHASE_RETURN_REQUEST",
          payload: { id_purchase_return }
        })
      ]);
    } else if (uploadResult.status === "fail") {
      yield all([
        yield put({
          type: "UPLOAD_PURCHASE_RETURN_DOCUMENT_ERROR",
          error: { message: uploadResult.message }
        }),
        yield put({
          type: "EDIT_PURCHASE_RETURN_REQUEST",
          payload: { id_purchase_return }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "UPLOAD_PURCHASE_RETURN_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "EDIT_PURCHASE_RETURN_REQUEST",
          payload: { id_purchase_return }
        })
      ]);
    }

    // }
  } catch (error) {
    yield all([
      yield put({
        type: "UPLOAD_PURCHASE_RETURN_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "EDIT_PURCHASE_RETURN_REQUEST",
        payload: { id_purchase_return }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

