const initState = {
  cost_sales_sort_by_list: [],
  cost_to_sales_ratio_reports: [],
  filter: null,
  filter_select: null,
  error: null,
  errorField: null,
  loading: false,
  locations: [],
  success: null,
}

export default function getCostToSalesRatioReportsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_COST_TO_SALES_RATIO_REPORTS_REQUEST':
      return {
        ...state,
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select
      }
    case 'GET_COST_TO_SALES_RATIO_REPORTS_SUCCESS':
      return {
        ...state,
        cost_to_sales_ratio_reports: action.cost_to_sales_ratio_reports,
        loading: false,
      }
    case "GET_COST_TO_SALES_RATIO_REPORTS_ERROR":
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    case "GET_COST_TO_SALES_RATIO_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        cost_sales_sort_by_list: action.cost_sales_sort_by_list,
        loading: false,
        locations: action.locations
      };
    }
    case "GET_COST_TO_SALES_RATIO_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}