import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getStockCardReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/stock-cards?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    if(filter){
      const [stock_card_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/stock-cards/foreign-keys")
      ]);
    
      // Jika sukses get data stock_card_reports
      if (stock_card_reports.status === "success" && stock_card_reports.result !== null) {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_STOCK_CARD_REPORTS_SUCCESS",
          stock_card_reports: stock_card_reports.result,
          filter
        });
      }
      else if (stock_card_reports.status === "success" && stock_card_reports.result === null) {
        yield put({
          type: "GET_STOCK_CARD_REPORTS_SUCCESS",
          stock_card_reports: [],
          filter
        });
      }
      else if (stock_card_reports.status === "fail") {
        yield put({
          type: "GET_STOCK_CARD_REPORTS_ERROR",
          error: { message: stock_card_reports.message }
        });
      }
      else {
        yield put({
          type: "GET_STOCK_CARD_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data stock_card_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_STOCK_CARD_REPORT_FOREIGN_KEYS_SUCCESS",
          item_categories: foreign_keys.result.item_categories,
          locations: foreign_keys.result.locations,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_STOCK_CARD_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_STOCK_CARD_REPORT_FOREIGN_KEYS_ERROR",
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [ foreign_keys] = yield all([
        call(get, "reports/stock-cards/foreign-keys")
      ]);
    
      yield put({
        type: "GET_STOCK_CARD_REPORTS_SUCCESS",
        stock_card_reports: []
      });
    
      // Jika sukses get data stock_card_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_STOCK_CARD_REPORT_FOREIGN_KEYS_SUCCESS",
          item_categories: foreign_keys.result.item_categories,
          locations: foreign_keys.result.locations,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_STOCK_CARD_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_STOCK_CARD_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_STOCK_CARD_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_STOCK_CARD_REPORTS_REQUEST",
      getStockCardReports
    )
  ]);
}
