
const initState = {
  adjustment_types: [],
  adjustment_type: null,
  accounts: [],
  search: null,
  filter: null,
  loading: false,
  loadingEdit:false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
};

export default function adjustmentTypesReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_ADJUSTMENT_TYPES_REQUEST': {
      return {
        ...state,
        //adjustment_types: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_ADJUSTMENT_TYPES_SUCCESS': {
      return {
        ...state,
        adjustment_types: action.adjustment_types,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_ADJUSTMENT_TYPES_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      }
    }
    case 'CREATE_ADJUSTMENT_TYPE_REQUEST': {
      return {
        ...state,
        adjustment_type: null,
        errorField: null,
      };
    }

    case 'GET_FILTER_ACCOUNTS_SUCCESS': {
      return {
        ...state,
        accounts: action.accounts
      };
    }
    case 'GET_FILTER_ACCOUNTS_ERROR': {
      return {
        ...state,
        error: action.error.message
      };
    }
    
    case 'STORE_ADJUSTMENT_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_ADJUSTMENT_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_ADJUSTMENT_TYPE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_ADJUSTMENT_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit:true,
        errorField: null,
      };
    }
    case 'EDIT_ADJUSTMENT_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit:false,
        adjustment_type: action.adjustment_type
      };
    }
    case 'EDIT_ADJUSTMENT_TYPE_ERROR': {
      return {
        ...state,
        // loading: false,
        // loadingEdit:false,
        error: action.error.message
      };
    }
    case 'UPDATE_ADJUSTMENT_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_ADJUSTMENT_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_ADJUSTMENT_TYPE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_ADJUSTMENT_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_ADJUSTMENT_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
        adjustment_types: action.adjustment_types
      };
    }
    case "DEACTIVATE_ADJUSTMENT_TYPE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_ADJUSTMENT_TYPE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_ADJUSTMENT_TYPE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_ADJUSTMENT_TYPE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_ADJUSTMENT_TYPE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_ADJUSTMENT_TYPE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
