import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getCouriers({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [couriers, employees] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "suppliers/couriers?search=" + search + "&page=" + page),
            call(get, "hrd/employees?is_active=1"),
          ])
        : // Jika filter
          yield all([
            call(get, "suppliers/couriers?" + filter + "&page=" + page),
            call(get, "hrd/employees?is_active=1"),
          ])
      : search
        ? // Jika search
          yield all([
            call(get, "suppliers/couriers?search=" + search),
            call(get, "hrd/employees?is_active=1"),
          ])
        : // Jika tidak ada
          yield all([
            call(get, "suppliers/couriers?" + filter),
            call(get, "hrd/employees?is_active=1"),
          ]);

    // console.log(couriers);
    // console.log(employees);

    // Jika sukses get data couriers
    if (couriers.status === "success") {
      // Jika di API menggunakan pagination
      if (couriers.result.current_page && search) {
        yield put({
          type: "GET_COURIERS_SUCCESS",
          couriers: couriers.result.data,
          current_page: couriers.result.current_page,
          page_size: couriers.result.per_page,
          total: couriers.result.total,
          search: search
        });
      }
      else if(couriers.result.current_page && filter)
      {
        yield put({
          type: 'GET_COURIERS_SUCCESS',
          couriers: couriers.result.data,
          current_page: couriers.result.current_page,
          page_size: couriers.result.per_page,
          total: couriers.result.total,
          filter: filter
        });
      }
      else if (couriers.result.current_page) {
        yield put({
          type: "GET_COURIERS_SUCCESS",
          couriers: couriers.result.data,
          current_page: couriers.result.current_page,
          page_size: couriers.result.per_page,
          total: couriers.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_COURIERS_SUCCESS",
          couriers: couriers.result,
          page_size: null,
          total: null
        });
      }
    } else if (couriers.status === "fail") {
      yield put({
        type: "GET_COURIERS_ERROR",
        error: couriers
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_COURIERS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if(employees.status === "success"){
      yield put({
        type: "GET_FILTER_EMPLOYEES_SUCCESS",
        employees: employees.result,
      })
    }
  } catch (error) {
    yield put({
      type: "GET_COURIERS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createCourier() {
  try {
    const [employees] = yield all([
      call(get, "hrd/employees?is_active=1"),
    ]);
  
    // console.log(employees);
  
    // Jika sukses get data courier type
    if(employees.status === "success"){
      yield put({
        type: "GET_FILTER_EMPLOYEES_SUCCESS",
        employees: employees.result,
      })
    }
  
    yield put({
      type: 'CREATE_COURIER_SUCCESS',
    });
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeCourier({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "suppliers/couriers", body);
    // console.log(body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_COURIER_SUCCESS",
        id_courier: resultStore.result.id_courier,
        success: resultStore
      });
    } else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_COURIER_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_COURIER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_COURIER_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}



// Edit data Courier
export function* editCourier({ payload }) {
  // console.log("masuk");
  const { id_courier } = payload;

  try {
    const [courier, employees] = yield all([
      call(get, "suppliers/couriers/" + id_courier),
      call(get, "hrd/employees?is_active=1"),
    ]);
  
    // console.log(courier);
    // console.log(employees);
  
    // Jika sukses get data employees
    if(employees.status === "success"){
      yield put({
        type: "GET_FILTER_EMPLOYEES_SUCCESS",
        employees: employees.result,
      })
    }
  
    // Jika sukses
    if (courier.status === "success") {
      yield put({
        type: "EDIT_COURIER_SUCCESS",
        courier: courier.result
      });
    } else if (courier.status === "fail") {
      yield put({
        type: "EDIT_COURIER_ERROR",
        error: courier
      });
    } else {
      yield put({
        type: "EDIT_COURIER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_COURIER_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}


// Update data
export function* updateCourier({ payload }) {
  const { id_courier, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "suppliers/couriers/" + id_courier,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        put({
          type: "UPDATE_COURIER_SUCCESS",
          success: resultUpdate
        }),
        put({
          type: "EDIT_COURIER_REQUEST",
          payload: { id_courier: id_courier }
        })
      ]);
    } 
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_COURIER_ERROR",
        error: resultUpdate
      });
    } 
    else {
      yield put({
        type: "UPDATE_COURIER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_COURIER_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showCourier({ payload }) {
  const { id_courier } = payload;

  try {
    const resultShow = yield call(get, "suppliers/couriers/" + id_courier);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: "SHOW_COURIER_SUCCESS",
        unit: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateCourier({ payload }) {
  const { id_courier } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "suppliers/couriers/" + id_courier,
      body
    );
    // console.log(deactivateResult);
    // Jika sukses
    if (deactivateResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DEACTIVATE_COURIER_SUCCESS",
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_COURIERS_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (deactivateResult.status === "fail" || deactivateResult.errors) {
      yield all([
        yield put({
          type: "DEACTIVATE_COURIER_ERROR",
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_COURIERS_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DEACTIVATE_COURIER_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_COURIERS_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DEACTIVATE_COURIER_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_COURIERS_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}


// Activate data (mengubah is_active => 1)
export function* activateCourier({ payload }) {
  const { id_courier } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "suppliers/couriers/" + id_courier,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: "ACTIVATE_COURIER_SUCCESS",
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_COURIERS_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (activateResult.status === "fail" || activateResult.errors) {
      yield all([
        yield put({
          type: "ACTIVATE_COURIER_ERROR",
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_COURIERS_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "ACTIVATE_COURIER_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_COURIERS_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "ACTIVATE_COURIER_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_COURIERS_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_COURIERS_REQUEST", getCouriers),
    yield takeEvery("CREATE_COURIER_REQUEST", createCourier),
    yield takeEvery("STORE_COURIER_REQUEST", storeCourier),
    yield takeEvery("EDIT_COURIER_REQUEST", editCourier),
    yield takeEvery("UPDATE_COURIER_REQUEST", updateCourier),
    yield takeEvery("SHOW_COURIER_REQUEST", showCourier),
    yield takeEvery("DEACTIVATE_COURIER_REQUEST", deactivateCourier),
    yield takeEvery("ACTIVATE_COURIER_REQUEST", activateCourier)

  ]);
}
