import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi } from "../../../helpers/helper";
import bugsnag from "@bugsnag/js";
import { bugsnagClient } from "../../..";

// Get all data
export function* getGLTaxes() {
  try {
    const [taxes, accounts] = 
      yield all([
        call(get, "configuration-managements/taxes?is_active=gl_config"),
        call(get, "accounts?is_active=child"),
      ]);

    // console.log(taxes);
    // console.log(accounts);

    // Jika sukses get data taxes
    if (taxes.status === "success" && accounts.status === "success"){   
      yield put({
        type: 'GET_GL_TAXES_SUCCESS',
        taxes: taxes.result,
        accounts: accounts.result
      })  
    }
    else {
      yield put({
        type: "GET_GL_TAXES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_GL_TAXES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateGLTax({ payload }) {
  const { id_tax, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "configuration-managements/taxes/" + id_tax,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      
      yield all([
        yield put({
          type: 'UPDATE_GL_TAX_SUCCESS',
          success: resultUpdate
        }),
        yield put({
          type: 'GET_GL_TAXES_REQUEST'
        })
      ])
      
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_GL_TAX_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_GL_TAXES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_GL_TAXES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_GL_TAXES_REQUEST', getGLTaxes),
    yield takeEvery('UPDATE_GL_TAX_REQUEST', updateGLTax),
  ]);
}
