import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getAdjustmentTypes({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [adjustment_types, accounts] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "configuration-managements/adjustment-types?search=" + search + "&page=" + page),
            call(get, "accounts?is_active=child"),
          ])
        : // Jika filter
          yield all([
            call(get, "configuration-managements/adjustment-types?" + filter + "&page=" + page),
            call(get, "accounts?is_active=child"),
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "configuration-managements/adjustment-types?search=" + search),
          call(get, "accounts?is_active=child"),
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "configuration-managements/adjustment-types?" + filter),
          call(get, "accounts?is_active=child"),
        ]);

    // console.log(adjustment_types);
    // console.log(accounts);

    // Jika sukses get data adjustment_types
    if (adjustment_types.status === "success") {
      // Jika di API menggunakan pagination
      if (adjustment_types.result.current_page && search) {
        yield put({
          type: 'GET_ADJUSTMENT_TYPES_SUCCESS',
          adjustment_types: adjustment_types.result.data,
          current_page: adjustment_types.result.current_page,
          page_size: adjustment_types.result.per_page,
          total: adjustment_types.result.total,
          search: search
        });
      }
      else if(adjustment_types.result.current_page && filter)
      {
        yield put({
          type: 'GET_ADJUSTMENT_TYPES_SUCCESS',
          adjustment_types: adjustment_types.result.data,
          current_page: adjustment_types.result.current_page,
          page_size: adjustment_types.result.per_page,
          total: adjustment_types.result.total,
          filter: filter
        });
      }
      else if (adjustment_types.result.current_page) {
        yield put({
          type: 'GET_ADJUSTMENT_TYPES_SUCCESS',
          adjustment_types: adjustment_types.result.data,
          current_page: adjustment_types.result.current_page,
          page_size: adjustment_types.result.per_page,
          total: adjustment_types.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_ADJUSTMENT_TYPES_SUCCESS',
          adjustment_types: adjustment_types.result,
          page_size: null,
          total: null
        });
      }
    } else if (adjustment_types.status === "fail") {
      yield put({
        type: 'GET_ADJUSTMENT_TYPES_ERROR', 
        error: adjustment_types
      })
    } else {
      yield put({
        type: 'GET_ADJUSTMENT_TYPES_ERROR', 
        error: {message: "Check your internet connection"}
      })
    }

    // Jika sukses get data adjustment_type types
    if (accounts.status === "success") {
      yield put({
        type: 'GET_FILTER_ACCOUNTS_SUCCESS',
        accounts: accounts.result
      });
    }
    else{
      yield put({
        type: 'GET_FILTER_ACCOUNTS_ERROR',
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_ADJUSTMENT_TYPES_ERROR', 
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createAdjustmentType() {
  try {
    const [accounts] = yield all([
      call(get, "accounts?is_active=child"),
    ]);
  
    // Jika sukses get data account
    if (accounts.status === "success") {
      yield put({
        type: 'GET_FILTER_ACCOUNTS_SUCCESS',
        accounts: accounts.result
      });
    }
    else{
      yield put({
        type: 'GET_FILTER_ACCOUNTS_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_FILTER_ACCOUNTS_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeAdjustmentType({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "configuration-managements/adjustment-types", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_ADJUSTMENT_TYPE_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_ADJUSTMENT_TYPE_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_ADJUSTMENT_TYPE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_ADJUSTMENT_TYPE_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editAdjustmentType({ payload }) {
  const { id_adjustment_type } = payload;

  try {
    const [adjustment_type, accounts] = yield all([
      call(get, "configuration-managements/adjustment-types/" + id_adjustment_type),
      call(get, "accounts?is_active=child"),
    ]);
  
    // Jika sukses
    if (adjustment_type.status === "success") {
      yield put({
        type: 'EDIT_ADJUSTMENT_TYPE_SUCCESS',
        adjustment_type: adjustment_type.result
      });
    }
    else{
      yield put({
        type: "EDIT_ADJUSTMENT_TYPE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
    
    // Jika sukses get data adjustment_type types
    if (accounts.status === "success") {
      yield put({
        type: 'GET_FILTER_ACCOUNTS_SUCCESS',
        accounts: accounts.result
      });
    }
    else{
      yield put({
        type: 'GET_FILTER_ACCOUNTS_ERROR',
        // error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_ADJUSTMENT_TYPE_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateAdjustmentType({ payload }) {
  const { id_adjustment_type, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "configuration-managements/adjustment-types/" + id_adjustment_type,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_ADJUSTMENT_TYPE_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_ADJUSTMENT_TYPE_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_ADJUSTMENT_TYPE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_ADJUSTMENT_TYPE_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showAdjustmentType({ payload }) {
  const { id_adjustment_type } = payload;

  try {
    const resultShow = yield call(get, "configuration-managements/adjustment-types/" + id_adjustment_type);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_ADJUSTMENT_TYPE_SUCCESS',
        adjustment_type: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateAdjustmentType({ payload }) {
  const { id_adjustment_type } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "configuration-managements/adjustment-types/" + id_adjustment_type,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: 'DEACTIVATE_ADJUSTMENT_TYPE_SUCCESS',
          success: deactivateResult
        }),
        yield put({
          type: 'GET_ADJUSTMENT_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: 'DEACTIVATE_ADJUSTMENT_TYPE_ERROR',
          error: deactivateResult
        }),
        yield put({
          type: 'GET_ADJUSTMENT_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'DEACTIVATE_ADJUSTMENT_TYPE_ERROR',
          error: {message: "Check your internet connection"}
        }),
        yield put({
          type: 'GET_ADJUSTMENT_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'DEACTIVATE_ADJUSTMENT_TYPE_ERROR',
        error: {message: "Check your internet connection"}
      }),
      yield put({
        type: 'GET_ADJUSTMENT_TYPES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activateAdjustmentType({ payload }) {
  const { id_adjustment_type } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "configuration-managements/adjustment-types/" + id_adjustment_type,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_ADJUSTMENT_TYPE_SUCCESS',
          success: activateResult
        }),
        yield put({
          type: 'GET_ADJUSTMENT_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: 'ACTIVATE_ADJUSTMENT_TYPE_ERROR',
          error: activateResult
        }),
        yield put({
          type: 'GET_ADJUSTMENT_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'ACTIVATE_ADJUSTMENT_TYPE_ERROR',
          error: {message: "Check your internet connection"}
        }),
        yield put({
          type: 'GET_ADJUSTMENT_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'ACTIVATE_ADJUSTMENT_TYPE_ERROR',
        error: {message: "Check your internet connection"}
      }),
      yield put({
        type: 'GET_ADJUSTMENT_TYPES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_ADJUSTMENT_TYPES_REQUEST', getAdjustmentTypes),
    yield takeEvery('CREATE_ADJUSTMENT_TYPE_REQUEST', createAdjustmentType),
    yield takeEvery('STORE_ADJUSTMENT_TYPE_REQUEST', storeAdjustmentType),
    yield takeEvery('EDIT_ADJUSTMENT_TYPE_REQUEST', editAdjustmentType),
    yield takeEvery('UPDATE_ADJUSTMENT_TYPE_REQUEST', updateAdjustmentType),
    yield takeEvery('SHOW_ADJUSTMENT_TYPE_REQUEST', showAdjustmentType),
    yield takeEvery('DEACTIVATE_ADJUSTMENT_TYPE_REQUEST', deactivateAdjustmentType),
    yield takeEvery('ACTIVATE_ADJUSTMENT_TYPE_REQUEST', activateAdjustmentType)
  ]);
}
