import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPurchaseReceiptReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/purchase-receipts?";
  if(search){
    url += "search=" + search  
  }
  if(filter){
    search
      ? url += "&" + filter
      : url += filter  
  }
  if(page){
    search || filter
      ? url += "&page=" + page
      : url += "page=" + page 
  }

  try {
    if(filter){
      const [purchase_receipt_reports, foreign_keys] = 
      yield all([
        call(get, url),
        call(get, "reports/purchase-receipts/foreign-keys"),
      ])
  
      // Jika sukses get data purchase_receipt_reports
      if (purchase_receipt_reports.status === "success") {
        yield put({
          type: 'GET_PURCHASE_RECEIPT_REPORTS_SUCCESS',
          purchase_receipt_reports: purchase_receipt_reports.result.details,
          total_qty_order: purchase_receipt_reports.result.summaries.total_qty_order,
          total_qty_receipt: purchase_receipt_reports.result.summaries.total_qty_receipt,
          total_item: purchase_receipt_reports.result.summaries.total_item,
          total: purchase_receipt_reports.result.summaries.total,
          filter: filter,
          search: search
        });
      } else if (purchase_receipt_reports.status === "fail") {
        yield put({
          type: 'GET_PURCHASE_RECEIPT_REPORTS_ERROR', 
          error: purchase_receipt_reports
        })
      } else {
        yield put({
          type: 'GET_PURCHASE_RECEIPT_REPORTS_ERROR', 
          error: {message: "Check your internet connection"}
        })
      }
  
      // Jika sukses get data purchase_receipt_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: 'GET_PURCHASE_RECEIPT_REPORT_FOREIGN_KEYS_SUCCESS',
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers,
          departments: foreign_keys.result.departments,
          projects: foreign_keys.result.projects,
          base_currency: foreign_keys.result.base_currency,
          messages: foreign_keys.result.messages,
        });
      }
      else{
        yield put({
          type: 'GET_PURCHASE_RECEIPT_REPORT_FOREIGN_KEYS_ERROR',
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = 
      yield all([
        call(get, "reports/purchase-receipts/foreign-keys"),
      ])
  
      yield put({
        type: 'GET_PURCHASE_RECEIPT_REPORTS_SUCCESS',
        purchase_receipt_reports: [],
        total_qty_order: null,
        total_qty_receipt: null,
        total_item: null,
        total: null,
      });
  
      // Jika sukses get data purchase_receipt_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: 'GET_PURCHASE_RECEIPT_REPORT_FOREIGN_KEYS_SUCCESS',
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers,
          departments: foreign_keys.result.departments,
          projects: foreign_keys.result.projects,
          base_currency: foreign_keys.result.base_currency,
          messages: foreign_keys.result.messages,
        });
      }
      else if (foreign_keys.status === "fail") {
        yield put({
          type: 'GET_PURCHASE_RECEIPT_REPORT_FOREIGN_KEYS_ERROR',
          error: foreign_keys
        });
      }
      else{
        yield put({
          type: 'GET_PURCHASE_RECEIPT_REPORT_FOREIGN_KEYS_ERROR',
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: 'GET_PURCHASE_RECEIPT_REPORTS_ERROR', 
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}



export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_PURCHASE_RECEIPT_REPORTS_REQUEST', getPurchaseReceiptReports),
  ]);
}
