
const initState = {
  cash_banks: [],
  cash_bank: null,
  accounts: [],
  locations: [],
  currencies: [],
  search: null,
  filter: null,
  loading: false,
  loadingEdit: false,
  current_page: 1,
  page_size: null,
  total: null,
  errorField: null,
  error:null,
  success:null,
};

export default function cashBanksReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_CASH_BANKS_REQUEST': {
      return {
        ...state,
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_CASH_BANKS_SUCCESS': {
      return {
        ...state,
        cash_banks: action.cash_banks,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_CASH_BANKS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message     
      };
    }
    case 'CREATE_CASH_BANK_REQUEST': {
      return {
        ...state,
        cash_bank: null,
        errorField: null,
      };
    }
    case 'CREATE_CASH_BANK_SUCCESS': {
      return {
        ...state,
        loading: false,
      };
    }

    case 'GET_CASH_BANK_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        accounts: action.accounts,
        locations: action.locations,
        currencies: action.currencies
      };
    }
    case 'GET_CASH_BANK_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        error: action.error.message      
      };
    }
    case 'STORE_CASH_BANK_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_CASH_BANK_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_CASH_BANK_ERROR': {
      return {
        ...state,
        loading: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case 'EDIT_CASH_BANK_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null,
      };
    }
    case 'EDIT_CASH_BANK_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        cash_bank: action.cash_bank,
        //success : action.success
      };
    }
    case 'EDIT_CASH_BANK_ERROR': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        error: action.error.message      
      };
    }
    case 'UPDATE_CASH_BANK_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_CASH_BANK_SUCCESS': {
      return {
        ...state,
        loading: false,
        success : action.success.message
      };
    }
    case 'UPDATE_CASH_BANK_ERROR': {
      return {
        ...state,
        loading: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case 'SHOW_CASH_BANK_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_CASH_BANK_SUCCESS': {
      return {
        ...state,
        loading: false,
        cash_banks: action.cash_banks,
        success : action.success
      };
    }
    case "DEACTIVATE_CASH_BANK_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_CASH_BANK_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_CASH_BANK_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_CASH_BANK_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_CASH_BANK_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };

    default:
      return state;
  }
}
