const initState = {
  id_purchase_receipt: null,
  id_location: null,
  purchaseReceipts: [],
  purchaseReceipt: null,
  printData: {},
  purchaseOrders: [],
  transactions: [],
  suppliers: [],
  couriers: [],
  // selectedTransaction: null,
  locations: [],
  search: null,
  filter: null,
  loading: false,
  loadingForeign: false,
  loadingSubmit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function purchaseReceiptsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_PURCHASE_RECEIPTS_REQUEST": {
      return {
        ...state,
        //purchaseReceipts: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_PURCHASE_RECEIPTS_SUCCESS": {
      return {
        ...state,
        purchaseReceipts: action.purchaseReceipts,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_PURCHASE_RECEIPTS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_PURCHASE_RECEIPT_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_PURCHASE_RECEIPT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loadingForeign: false,
        purchaseOrders: action.purchaseOrders,
        locations: action.locations,
        suppliers: action.suppliers,
        currencies: action.currencies,
        taxes: action.taxes,
        accounts: action.accounts,
        cashBanks: action.cashBanks,
        cashFlowTypes: action.cashFlowTypes,
        paymentCategories: action.paymentCategories,
        paymentTerms: action.paymentTerms,
        departments: action.departments,
        projects: action.projects,
        couriers: action.couriers,
      };
    }
    case "GET_PURCHASE_RECEIPT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "CREATE_PURCHASE_RECEIPT_REQUEST": {
      return {
        ...state,
        id_purchase_receipt: null,
        purchaseReceipt: null,
        loadingTransactions: true,
        loadingItemRequests: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null,
        item_requests_current_page: 1,
        item_requests_page_size: null,
        item_requests_total: null
      };
    }

    case "GET_FILTER_PO_PURCHASE_RECEIPT_REQUEST": {
      return {
        ...state,
        loadingTransactions: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page
      };
    }
    case "GET_FILTER_PO_PURCHASE_RECEIPT_SUCCESS": {
      return {
        ...state,
        loadingTransactions: false,
        transactions: action.transactions,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total
      };
    }
    case "GET_FILTER_PO_PURCHASE_RECEIPT_ERROR": {
      return {
        ...state,
        loadingTransactions: false,
        error: action.error.message
      };
    }
    case "STORE_PURCHASE_RECEIPT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_PURCHASE_RECEIPT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_purchase_receipt: action.id_purchase_receipt
      };
    }
    case "STORE_PURCHASE_RECEIPT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_PURCHASE_RECEIPT_REQUEST": {
      return {
        ...state,
        loading: true,
        id_purchase_receipt: null,
        loadingTransactions: true,
        loadingItemRequests: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null,
        item_requests_current_page: 1,
        item_requests_page_size: null,
        item_requests_total: null
      };
    }
    case "EDIT_PURCHASE_RECEIPT_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        purchaseReceipt: action.purchaseReceipt
      };
    }
    case "EDIT_PURCHASE_RECEIPT_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_PURCHASE_RECEIPT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_PURCHASE_RECEIPT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_purchase_receipt: action.id_purchase_receipt
      };
    }
    case "UPDATE_PURCHASE_RECEIPT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_PURCHASE_RECEIPT_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_PURCHASE_RECEIPT_SUCCESS": {
      return {
        ...state,
        loading: false,
        purchaseReceipts: action.purchaseReceipts
      };
    }
    case "CONFIRM_PURCHASE_RECEIPT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CONFIRM_PURCHASE_RECEIPT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CONFIRM_PURCHASE_RECEIPT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "REJECT_PURCHASE_RECEIPT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "REJECT_PURCHASE_RECEIPT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "REJECT_PURCHASE_RECEIPT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CANCEL_PURCHASE_RECEIPT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CANCEL_PURCHASE_RECEIPT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CANCEL_PURCHASE_RECEIPT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPLOAD_PURCHASE_RECEIPT_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_PURCHASE_RECEIPT_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_PURCHASE_RECEIPT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_PURCHASE_RECEIPT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_PURCHASE_RECEIPT_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_PURCHASE_RECEIPT_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_PURCHASE_RECEIPT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "SET_PRINT_DATA_PURCHASE_RECEIPT": {
      return {
        ...state,
        printData: action.payload.body
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null,
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    default:
      return state;
  }
}
