import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getAccPayAgingReports({ payload }) {
  const { search, filter } = payload;

  let url = "reports/account-payables/due-agings?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    if (search || filter) {
      const [acc_pay_aging_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/account-payables/due-agings/foreign-keys")
      ]);
  
      // Jika sukses get data acc_pay_aging_reports
      if (acc_pay_aging_reports.status === "success") {
        
        yield put({
          type: "GET_ACC_PAY_AGING_REPORTS_SUCCESS",
          acc_pay_aging_reports: acc_pay_aging_reports.result,
          filter
        });
        
      } else if (acc_pay_aging_reports.status === "fail") {
        yield put({
          type: "GET_ACC_PAY_AGING_REPORTS_ERROR",
          error: { message: acc_pay_aging_reports.message }
        });
      } else {
        yield put({
          type: "GET_ACC_PAY_AGING_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data acc_pay_summary_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ACC_PAY_AGING_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_ACC_PAY_AGING_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_ACC_PAY_AGING_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/account-payables/due-agings/foreign-keys")
      ]);
  
      yield put({
        type: "GET_ACC_PAY_AGING_REPORTS_SUCCESS",
        acc_pay_aging_reports: []
      });
  
      // Jika sukses get data acc_pay_summary_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ACC_PAY_AGING_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_ACC_PAY_AGING_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_ACC_PAY_AGING_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_ACC_PAY_AGING_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_ACC_PAY_AGING_REPORTS_REQUEST", getAccPayAgingReports)
  ]);
}
