import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getDebitMemoReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/debit-memos?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if (search || filter) {
      const [debit_memo_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/debit-memos/foreign-keys")
      ]);
  
      // Jika sukses get data debit_memo_reports
      if (debit_memo_reports.status === "success") {
        
        yield put({
          type: "GET_DEBIT_MEMO_REPORTS_SUCCESS",
          debit_memo_reports: debit_memo_reports.result,
          filter: filter,
          search: search
        });
        
      } else if (debit_memo_reports.status === "fail") {
        yield put({
          type: "GET_DEBIT_MEMO_REPORTS_ERROR",
          error: { message: debit_memo_reports.message }
        });
      } else {
        yield put({
          type: "GET_DEBIT_MEMO_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data debit_memo_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_DEBIT_MEMO_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers,
          cash_banks: foreign_keys.result.cash_banks
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_DEBIT_MEMO_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_DEBIT_MEMO_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/debit-memos/foreign-keys")
      ]);
  
      yield put({
        type: "GET_DEBIT_MEMO_REPORTS_SUCCESS",
        debit_memo_reports: []
      });
  
      // Jika sukses get data debit_memo_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_DEBIT_MEMO_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers,
          cash_banks: foreign_keys.result.cash_banks
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_DEBIT_MEMO_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_DEBIT_MEMO_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_DEBIT_MEMO_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_DEBIT_MEMO_REPORTS_REQUEST", getDebitMemoReports)
  ]);
}
