import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getJobTitles({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [job_titles] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "hrd/job-titles?search=" + search + "&page=" + page)
          ])
        : // Jika filter
          yield all([call(get, "hrd/job-titles?" + filter + "&page=" + page)])
      : search
      ? // Jika search
        yield all([call(get, "hrd/job-titles?search=" + search)])
      : // Jika tidak ada
        yield all([call(get, "hrd/job-titles?" + filter)]);

    // console.log(job_titles);

    // Jika sukses get data job_titles
    if (job_titles.status === "success") {
      // Jika di API menggunakan pagination
      if (job_titles.result.current_page && search) {
        yield put({
          type: "GET_JOB_TITLES_SUCCESS",
          job_titles: job_titles.result.data,
          current_page: job_titles.result.current_page,
          page_size: job_titles.result.per_page,
          total: job_titles.result.total,
          search: search
        });
      }
      // else if(job_titles.result.current_page && filter)
      // {
      //   yield put({
      //     type: 'GET_JOB_TITLES_SUCCESS',
      //     job_titles: job_titles.result.data,
      //     current_page: job_titles.result.current_page,
      //     page_size: job_titles.result.per_page,
      //     total: job_titles.result.total,
      //     filter: filter
      //   });
      // }
      else if (job_titles.result.current_page) {
        yield put({
          type: "GET_JOB_TITLES_SUCCESS",
          job_titles: job_titles.result.data,
          current_page: job_titles.result.current_page,
          page_size: job_titles.result.per_page,
          total: job_titles.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_JOB_TITLES_SUCCESS",
          job_titles: job_titles.result,
          page_size: null,
          total: null
        });
      }
    } else if (job_titles.status === "fail") {
      yield put({
        type: "GET_JOB_TITLES_ERROR",
        error: job_titles
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_JOB_TITLES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_JOB_TITLES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeJobTitle({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "hrd/job-titles", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_JOB_TITLE_SUCCESS",
        success: resultStore
      });
    } else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_JOB_TITLE_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_JOB_TITLE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_JOB_TITLE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editJobTitle({ payload }) {
  // console.log("masuk");
  const { id_job_title } = payload;

  try {
    const job_title = yield call(get, "hrd/job-titles/" + id_job_title);
    // console.log(job_title);

    // Jika sukses
    if (job_title.status === "success") {
      yield put({
        type: "EDIT_JOB_TITLE_SUCCESS",
        job_title: job_title.result
      });
    } else if (job_title.status === "fail") {
      yield put({
        type: "EDIT_JOB_TITLE_ERROR",
        error: job_title
      });
    } else {
      yield put({
        type: "EDIT_JOB_TITLE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_JOB_TITLE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateJobTitle({ payload }) {
  const { id_job_title, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "hrd/job-titles/" + id_job_title,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: "UPDATE_JOB_TITLE_SUCCESS",
        success: resultUpdate
      });
    } else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_JOB_TITLE_ERROR",
        error: resultUpdate
      });
    } else {
      yield put({
        type: "UPDATE_JOB_TITLE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_JOB_TITLE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showJobTitle({ payload }) {
  const { id_job_title } = payload;

  try {
    const resultShow = yield call(get, "hrd/job-titles/" + id_job_title);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: "SHOW_JOB_TITLE_SUCCESS",
        unit: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateJobTitle({ payload }) {
  const { id_job_title } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "hrd/job-titles/" + id_job_title,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DEACTIVATE_JOB_TITLE_SUCCESS",
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_JOB_TITLES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: "DEACTIVATE_JOB_TITLE_ERROR",
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_JOB_TITLES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DEACTIVATE_JOB_TITLE_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_JOB_TITLES_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DEACTIVATE_JOB_TITLE_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_JOB_TITLES_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activateJobTitle({ payload }) {
  const { id_job_title } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "hrd/job-titles/" + id_job_title,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: "ACTIVATE_JOB_TITLE_SUCCESS",
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_JOB_TITLES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: "ACTIVATE_JOB_TITLE_ERROR",
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_JOB_TITLES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "ACTIVATE_JOB_TITLE_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_JOB_TITLES_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "ACTIVATE_JOB_TITLE_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_JOB_TITLES_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_JOB_TITLES_REQUEST", getJobTitles),
    yield takeEvery("STORE_JOB_TITLE_REQUEST", storeJobTitle),
    yield takeEvery("EDIT_JOB_TITLE_REQUEST", editJobTitle),
    yield takeEvery("UPDATE_JOB_TITLE_REQUEST", updateJobTitle),
    yield takeEvery("SHOW_JOB_TITLE_REQUEST", showJobTitle),
    yield takeEvery("DEACTIVATE_JOB_TITLE_REQUEST", deactivateJobTitle),
    yield takeEvery("ACTIVATE_JOB_TITLE_REQUEST", activateJobTitle)
  ]);
}
