const initState = {
  id_purchase_order: null,
  id_location: null,
  id_supplier: null,
  purchaseOrders: [],
  purchaseOrder: null,
  items: [],
  itemDiscount: [],
  transactions: [],
  itemRequests: [],
  suppliers: [],
  taxes: [],
  accounts: [],
  dpAccount: {},
  cashBanks: [],
  cashFlowTypes: [],
  currencies: [],
  currencyRates: [],
  paymentCategories: [],
  paymentTerms: [],
  projects: [],
  departments: [],
  couriers: [],
  // selectedTransaction: null,
  locations: [],
  search: null,
  filter: null,
  loading: false,
  loadingForeign: false,
  loadingSubmit: false,
  loadingItems: false,
  loadingTable: false,
  loadingItemRequests: false,
  current_page: 1,
  page_size: null,
  total: null,
  item_requests_current_page: 1,
  item_requests_page_size: null,
  item_requests_total: null,
  success: null,
  error: null,
  errorField: null,
  prevTransConfirm: null,
  processItemConfirm: null
};

export default function purchaseOrdersReducer(state = initState, action) {
  switch (action.type) {
    case "GET_PURCHASE_ORDERS_REQUEST": {
      return {
        ...state,
        //purchaseOrders: [],
        itemDiscount: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_PURCHASE_ORDERS_SUCCESS": {
      return {
        ...state,
        purchaseOrders: action.purchaseOrders,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_PURCHASE_ORDERS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_PURCHASE_ORDER_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_PURCHASE_ORDER_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loadingForeign: false,
        locations: action.locations,
        suppliers: action.suppliers,
        currencies: action.currencies,
        taxes: action.taxes,
        accounts: action.accounts,
        dpAccount: action.dpAccount,
        cashBanks: action.cashBanks,
        cashFlowTypes: action.cashFlowTypes,
        paymentCategories: action.paymentCategories,
        paymentTerms: action.paymentTerms,
        departments: action.departments,
        projects: action.projects,
        couriers: action.couriers,
        prevTransConfirm: action.prevTransConfirm,
        processItemConfirm: action.processItemConfirm
      };
    }
    case "GET_PURCHASE_ORDER_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "CREATE_PURCHASE_ORDER_REQUEST": {
      return {
        ...state,
        id_purchase_order: null,
        id_location: null,
        purchaseOrder: null,
        itemDiscount: [],
        loadingTransactions: true,
        loadingItemRequests: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null,
        item_requests_current_page: 1,
        item_requests_page_size: null,
        item_requests_total: null
      };
    }
    case "GET_FILTER_ITEMS_BY_SUPPLIER_PO_REQUEST": {
      return {
        ...state,
        loadingItems: true,
        search: action.payload.search,
        id_location: action.payload.id_location,
        id_supplier: action.payload.id_supplier
      };
    }
    case "GET_FILTER_ITEMS_BY_SUPPLIER_PO_SUCCESS": {
      return {
        ...state,
        loadingItems: false,
        items: action.items
      };
    }
    case "GET_DISCOUNT_PO_REQUEST": {
      return {
        ...state,
        loadingTable: true
      };
    }
    case "GET_DISCOUNT_PO_SUCCESS": {
      return {
        ...state,
        loadingTable: false,
        itemDiscount: action.itemDiscount
      };
    }
    case "GET_DISCOUNT_PO_ERROR": {
      return {
        ...state,
        loadingTable: false,
        itemDiscount: action.itemDiscount,
        error: action.error.message
      };
    }
    case "GET_FILTER_PURCHASE_ORDERS_REQUEST": {
      return {
        ...state,
        loadingTransactions: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page
      };
    }
    case "GET_FILTER_PURCHASE_ORDERS_SUCCESS": {
      return {
        ...state,
        loadingTransactions: false,
        transactions: action.transactions,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total
      };
    }
    case "GET_FILTER_PURCHASE_ORDERS_ERROR": {
      return {
        ...state,
        loadingTransactions: false,
        error: action.error.message
      };
    }
    case "GET_FILTER_ITEM_REQUESTS_PO_REQUEST": {
      return {
        ...state,
        loadingItemRequests: true,
        search: action.payload.search,
        filter: action.payload.filter,
        item_requests_current_page: action.payload.page
      };
    }
    case "GET_FILTER_ITEM_REQUESTS_PO_SUCCESS": {
      return {
        ...state,
        loadingItemRequests: false,
        itemRequests: action.itemRequests,
        item_requests_current_page: action.current_page,
        item_requests_page_size: action.page_size,
        item_requests_total: action.total
      };
    }
    case "GET_FILTER_ITEM_REQUESTS_PO_ERROR": {
      return {
        ...state,
        loadingItemRequests: false,
        error: action.error.message
      };
    }
    case "STORE_PURCHASE_ORDER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_PURCHASE_ORDER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_purchase_order: action.id_purchase_order
      };
    }
    case "STORE_PURCHASE_ORDER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_PURCHASE_ORDER_REQUEST": {
      return {
        ...state,
        loading: true,
        id_purchase_order: null,
        id_location: null,
        loadingTransactions: true,
        loadingItemRequests: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null,
        item_requests_current_page: 1,
        item_requests_page_size: null,
        item_requests_total: null
      };
    }
    case "EDIT_PURCHASE_ORDER_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        purchaseOrder: action.purchaseOrder
      };
    }
    case "EDIT_PURCHASE_ORDER_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_PURCHASE_ORDER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_PURCHASE_ORDER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_purchase_order: action.id_purchase_order
      };
    }
    case "UPDATE_PURCHASE_ORDER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_PURCHASE_ORDER_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_PURCHASE_ORDER_SUCCESS": {
      return {
        ...state,
        loading: false,
        purchaseOrders: action.purchaseOrders
      };
    }
    case "CONFIRM_PURCHASE_ORDER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CONFIRM_PURCHASE_ORDER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CONFIRM_PURCHASE_ORDER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UNCONFIRM_PURCHASE_ORDER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "UNCONFIRM_PURCHASE_ORDER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UNCONFIRM_PURCHASE_ORDER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CLOSE_PURCHASE_ORDER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CLOSE_PURCHASE_ORDER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CLOSE_PURCHASE_ORDER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CANCEL_PURCHASE_ORDER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CANCEL_PURCHASE_ORDER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CANCEL_PURCHASE_ORDER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPLOAD_PURCHASE_ORDER_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_PURCHASE_ORDER_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_PURCHASE_ORDER_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_PURCHASE_ORDER_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_PURCHASE_ORDER_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_PURCHASE_ORDER_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_PURCHASE_ORDER_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null,
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    default:
      return state;
  }
}
