
const initState = {
  sales_invoice_reports: [],
  total_amount: null,
  total_discount: null,
  total_qty: null,
  total_tax: null,
  total_item: null,
  locations: [],
  customers: [],
  currencies: [],
  taxes: [],
  cash_banks: [],
  accounts: [],
  cash_flow_types: [],  
  payment_categories: [],
  payment_terms: [],
  departments: [],
  projects: [],
  base_currency: null,
  messages: [],
  search: null,
  filter: null,
  filter_select: [],
  loading: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
};

export default function salesInvoiceReportsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_SALES_INVOICE_REPORTS_REQUEST': {
      return {
        ...state,
        //sales_invoice_reports: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_SALES_INVOICE_REPORTS_SUCCESS': {
      return {
        ...state,
        sales_invoice_reports: action.sales_invoice_reports,
        // current_page: action.current_page,
        // page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        total_amount: action.total_amount,
        total_discount: action.total_discount,
        total_qty: action.total_qty,
        total_tax: action.total_tax,
        total_item: action.total_item,
        loading: false,
      };
    }
    case 'GET_SALES_INVOICE_REPORTS_ERROR': {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      }
    }
    case 'CREATE_SALES_INVOICE_REPORT_REQUEST': {
      return {
        ...state,
        sales_invoice_report: null,
        errorField: null,
      };
    }

    case 'GET_SALES_INVOICE_REPORT_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        customers: action.customers,
        currencies: action.currencies,
        taxes: action.taxes,
        cash_banks: action.cash_banks,
        accounts: action.accounts,
        cash_flow_types: action.cash_flow_types,
        payment_categories: action.payment_categories,
        payment_terms: action.payment_terms,
        departments: action.departments,  
        projects: action.projects, 
        base_currency : action.base_currency,
        messages: action.messages,
      };
    }
    case 'GET_SALES_INVOICE_REPORT_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
