import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, axiosGet } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getItems({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [items, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "items?search=" + search + "&page=" + page), 
            call(get, "items/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "items?" + filter + "&page=" + page), 
            call(get, "items/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "items?search=" + search), 
          call(get, "items/foreign-keys") 
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "items?" + filter),
          call(get, "items/foreign-keys")
        ]);

    // console.log(items);
    // console.log(accounts);

    // Jika sukses get data items
    if (items.status === "success") {
      // Jika di API menggunakan pagination
      if (items.result.current_page && search) {
        yield put({
          type: 'GET_ITEMS_SUCCESS',
          items: items.result.data,
          current_page: items.result.current_page,
          page_size: items.result.per_page,
          total: items.result.total,
          search: search
        });
      }
      else if(items.result.current_page && filter)
      {
        yield put({
          type: 'GET_ITEMS_SUCCESS',
          items: items.result.data,
          current_page: items.result.current_page,
          page_size: items.result.per_page,
          total: items.result.total,
          filter: filter
        });
      }
      else if (items.result.current_page) {
        yield put({
          type: 'GET_ITEMS_SUCCESS',
          items: items.result.data,
          current_page: items.result.current_page,
          page_size: items.result.per_page,
          total: items.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_ITEMS_SUCCESS',
          items: items.result,
          page_size: null,
          total: null
        });
      }
    }
    else if (items.status === "fail") {
      yield put({
        type: 'GET_ITEMS_ERROR',
        error: items
      })
    }
    else {
      yield put({
        type: 'GET_ITEMS_ERROR',
        error: {message: "Check your internet connection"}
      })
    }

    if(foreign_keys.status === "success"){
      yield put({
        type : 'GET_ITEM_FOREIGN_KEYS_SUCCESS',
        item_categories : foreign_keys.result.item_categories, 
        locations: foreign_keys.result.locations,
        suppliers: foreign_keys.result.suppliers,
        taxes: foreign_keys.result.taxes,
        accounts: foreign_keys.result.accounts,
        array_id_location: foreign_keys.result.array_id_location,
        default_id_item_tax: foreign_keys.result.default_id_item_tax,
        units: foreign_keys.result.units,
        inventory_items : foreign_keys.result.inventory_items,
        racks : foreign_keys.result.racks,
        colors: foreign_keys.result.colors,
        sizes: foreign_keys.result.sizes,
        raptor_module: foreign_keys.result.raptor_module,
        info_message: foreign_keys.result.info_message,
      })
    }
    else{
      yield put({
        type : 'GET_ITEM_FOREIGN_KEYS_ERROR',
        //error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'GET_ITEMS_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createItem() {
  try {
    const foreign_keys = yield  call(get, "items/foreign-keys")

    if(foreign_keys.status === "success"){
      yield put({
        type : 'GET_ITEM_FOREIGN_KEYS_SUCCESS',
        item_categories : foreign_keys.result.item_categories, 
        locations: foreign_keys.result.locations,
        suppliers: foreign_keys.result.suppliers,
        taxes: foreign_keys.result.taxes,
        accounts: foreign_keys.result.accounts,
        array_id_location: foreign_keys.result.array_id_location,
        default_id_item_tax: foreign_keys.result.default_id_item_tax,
        units: foreign_keys.result.units,
        inventory_items : foreign_keys.result.inventory_items,
        racks : foreign_keys.result.racks, 
        colors: foreign_keys.result.colors,
        sizes: foreign_keys.result.sizes, 
        raptor_module: foreign_keys.result.raptor_module,
        info_message: foreign_keys.result.info_message, 
      })
    }
    else{
      yield put({
        type : 'GET_ITEM_FOREIGN_KEYS_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type : 'GET_ITEM_FOREIGN_KEYS_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

//import From Raptor
export function* importRaptorItems() {
  try {
    const resultImport = yield call(
      axiosGet,
      "raptor/import-items"
    );
    // console.log(resultUpdate);
    if (resultImport.status === "success") {
      yield all([
        yield put({
          type: "IMPORT_RAPTOR_ITEMS_SUCCESS",
          success: resultImport
        }),
        yield put({
          type: 'GET_ITEMS_REQUEST',
          payload: {
            page: 1,
            search: null,
            filter: null
          }
        })
      ])
    }  
    else if (resultImport.status === "fail") {
      yield put({
        type: "IMPORT_RAPTOR_ITEMS_ERROR",
        error: resultImport
      });
    }
    else if (resultImport.errors) {
      yield put({
        type: "IMPORT_RAPTOR_ITEMS_ERROR",
        error: resultImport
      });
    }
    else {
      yield put({
        type: "IMPORT_RAPTOR_ITEMS_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "IMPORT_RAPTOR_ITEMS_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeItem({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "items", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_ITEM_SUCCESS',
        id_item: resultStore.result.id_item,
        success: resultStore
      });
    }
    else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_ITEM_ERROR",
        error: resultStore
      });
    }
    else if (resultStore.errors) {
      yield put({
        type: "STORE_ITEM_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_ITEM_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_ITEM_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

