export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_ITEMS_REQUEST', getItems),
    yield takeEvery('CREATE_ITEM_REQUEST', createItem),
    yield takeEvery('IMPORT_RAPTOR_ITEMS_REQUEST', importRaptorItems),
    yield takeEvery('STORE_ITEM_REQUEST', storeItem),
    yield takeEvery('STORE_ITEM_LOCATION_REQUEST', storeItemLocation),
    yield takeEvery('EDIT_ITEM_REQUEST', editItem),
    yield takeEvery('EDIT_ITEM_LOCATION_REQUEST', editItemLocation),
    yield takeEvery('UPDATE_ITEM_REQUEST', updateItem),
    yield takeEvery('UPDATE_ITEM_LOCATION_REQUEST', updateItemLocation),
    yield takeEvery('UPDATE_ITEM_SALES_PURCHASE_REQUEST', updateItemSalesPurchase),
    yield takeEvery('UPDATE_ITEM_GROUP_REQUEST', updateItemGroup),
    yield takeEvery('UPDATE_ITEM_ACCOUNT_REQUEST', updateItemAccount),
    yield takeEvery('SHOW_ITEM_REQUEST', showItem),
    yield takeEvery('DEACTIVATE_ITEM_REQUEST', deactivateItem),
    yield takeEvery('ACTIVATE_ITEM_REQUEST', activateItem)
  ]);
}
