import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getStockValuationSummaryByItemReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/stock-valuations/summary-by-item?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    if(filter){
      const [stock_valuation_summary_by_item_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/stock-valuations/foreign-keys")
      ]);
    
      // Jika sukses get data stock_valuation_summary_by_item_reports
      if (stock_valuation_summary_by_item_reports.status === "success" && stock_valuation_summary_by_item_reports.result !== null) {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_STOCK_VALUATION_SUMMARY_BY_ITEM_REPORTS_SUCCESS",
          stock_valuation_summary_by_item_reports: stock_valuation_summary_by_item_reports.result,
          filter
        });
      }
      else if (stock_valuation_summary_by_item_reports.status === "success" && stock_valuation_summary_by_item_reports.result === null) {
        yield put({
          type: "GET_STOCK_VALUATION_SUMMARY_BY_ITEM_REPORTS_SUCCESS",
          stock_valuation_summary_by_item_reports: [],
          filter
        });
      }
      else if (stock_valuation_summary_by_item_reports.status === "fail") {
        yield put({
          type: "GET_STOCK_VALUATION_SUMMARY_BY_ITEM_REPORTS_ERROR",
          error: { message: stock_valuation_summary_by_item_reports.message }
        });
      }
      else {
        yield put({
          type: "GET_STOCK_VALUATION_SUMMARY_BY_ITEM_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data stock_valuation_summary_by_item_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_STOCK_VALUATION_SUMMARY_BY_ITEM_REPORT_FOREIGN_KEYS_SUCCESS",
          item_categories: foreign_keys.result.item_categories,
          locations: foreign_keys.result.locations,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_STOCK_VALUATION_SUMMARY_BY_ITEM_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_STOCK_VALUATION_SUMMARY_BY_ITEM_REPORT_FOREIGN_KEYS_ERROR",
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/stock-valuations/foreign-keys")
      ]);
  
      yield put({
        type: "GET_STOCK_VALUATION_SUMMARY_BY_ITEM_REPORTS_SUCCESS",
        stock_valuation_summary_by_item_reports: [],
      });
    
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_STOCK_VALUATION_SUMMARY_BY_ITEM_REPORT_FOREIGN_KEYS_SUCCESS",
          item_categories: foreign_keys.result.item_categories,
          locations: foreign_keys.result.locations,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_STOCK_VALUATION_SUMMARY_BY_ITEM_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_STOCK_VALUATION_SUMMARY_BY_ITEM_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_STOCK_VALUATION_SUMMARY_BY_ITEM_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_STOCK_VALUATION_SUMMARY_BY_ITEM_REPORTS_REQUEST",
      getStockValuationSummaryByItemReports
    )
  ]);
}
