import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getAccPaySummaryReports({ payload }) {
  const { search, filter } = payload;

  let url = "reports/account-payables/summaries?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    if (search || filter) {
      const [acc_pay_summary_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/account-payables/summaries/foreign-keys")
      ]);
  
      // Jika sukses get data acc_pay_summary_reports
      if (acc_pay_summary_reports.status === "success") {
        
        yield put({
          type: "GET_ACC_PAY_SUMMARY_REPORTS_SUCCESS",
          acc_pay_summary_reports: acc_pay_summary_reports.result,
          filter
        });
        
      } else if (acc_pay_summary_reports.status === "fail") {
        yield put({
          type: "GET_ACC_PAY_SUMMARY_REPORTS_ERROR",
          error: { message: acc_pay_summary_reports.message }
        });
      } else {
        yield put({
          type: "GET_ACC_PAY_SUMMARY_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data acc_pay_summary_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ACC_PAY_SUMMARY_REPORT_FOREIGN_KEYS_SUCCESS",
          currencies: foreign_keys.result.currencies
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_ACC_PAY_SUMMARY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_ACC_PAY_SUMMARY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/account-payables/summaries/foreign-keys")
      ]);
  
      yield put({
        type: "GET_ACC_PAY_SUMMARY_REPORTS_SUCCESS",
        acc_pay_summary_reports: []
      });
  
      // Jika sukses get data acc_pay_summary_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ACC_PAY_SUMMARY_REPORT_FOREIGN_KEYS_SUCCESS",
          currencies: foreign_keys.result.currencies
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_ACC_PAY_SUMMARY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_ACC_PAY_SUMMARY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_ACC_PAY_SUMMARY_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_ACC_PAY_SUMMARY_REPORTS_REQUEST",
      getAccPaySummaryReports
    )
  ]);
}
