import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getFinancialRatioReports({ payload }) {
  const { search, filter } = payload;

  let url = "reports/financial-ratios?";
  // if (search) {
  //   url += "search=" + search;
  // }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    if (filter) {
      const [financial_ratio_reports] = yield all([
        call(get, url)
      ]);
  
      // Jika sukses get data financial_ratio_reports
      if (financial_ratio_reports.status === "success") {  
        yield put({
          type: "GET_FINANCIAL_RATIO_REPORTS_SUCCESS",
          financial_ratio_reports: financial_ratio_reports.result,
          filter: filter,
          search: search
        }); 
      } else if (financial_ratio_reports.status === "fail") {
        yield put({
          type: "GET_FINANCIAL_RATIO_REPORTS_ERROR",
          error: { message: financial_ratio_reports.message }
        });
      } else {
        yield put({
          type: "GET_FINANCIAL_RATIO_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } else {
      yield put({
        type: "GET_FINANCIAL_RATIO_REPORTS_SUCCESS",
        financial_ratio_reports: []
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FINANCIAL_RATIO_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_FINANCIAL_RATIO_REPORTS_REQUEST",
      getFinancialRatioReports
    )
  ]);
}
