const actions = {
  getLocations: (page, search, filter) => ({
    type: "GET_LOCATIONS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createLocation: () => ({
    type: "CREATE_LOCATION_REQUEST"
  }),
  storeLocation: data => ({
    type: "STORE_LOCATION_REQUEST",
    payload: { body: data }
  }),
  editLocation: id_location => ({
    type: "EDIT_LOCATION_REQUEST",
    payload: { id_location }
  }),
  updateLocation: (id_location, data) => ({
    type: "UPDATE_LOCATION_REQUEST",
    payload: {
      id_location,
      body: data
    }
  }),
  deactivateLocation: id_location => ({
    type: "DEACTIVATE_LOCATION_REQUEST",
    payload: { id_location }
  }),
  activateLocation: id_location => ({
    type: "ACTIVATE_LOCATION_REQUEST",
    payload: { id_location }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
