
const initState = {
  return_reasons: [],
  return_reason: null,
  search: null,
  filter: null,
  loading: false,
  loadingEdit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function returnReasonsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_RETURN_REASONS_REQUEST': {
      return {
        ...state,
        //return_reasons: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_RETURN_REASONS_SUCCESS': {
      return {
        ...state,
        return_reasons: action.return_reasons,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_RETURN_REASONS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        // errorField: action.error.errors
      };
    }
    case 'CREATE_RETURN_REASON_REQUEST': {
      return {
        ...state,
        //return_reason: null,
        errorField: null,
      };
    }
    
    case 'STORE_RETURN_REASON_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_RETURN_REASON_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_RETURN_REASON_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_RETURN_REASON_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null,
      };
    }
    case 'EDIT_RETURN_REASON_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        return_reason: action.return_reason,
      };
    }
    case 'EDIT_RETURN_REASON_ERROR': {
      return {
        ...state,
        // loading: false,
        error: action.error.message,
        // errorField: action.error.errors
      };
    }
    case 'UPDATE_RETURN_REASON_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_RETURN_REASON_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_RETURN_REASON_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_RETURN_REASON_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_RETURN_REASON_SUCCESS': {
      return {
        ...state,
        loading: false,
        return_reasons: action.return_reasons
      };
    }
    case "DEACTIVATE_RETURN_REASON_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_RETURN_REASON_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_RETURN_REASON_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_RETURN_REASON_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_RETURN_REASON_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_RETURN_REASON_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
