import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getCashBankSummaryReports({ payload }) {
  const { search, filter } = payload;

  let url = "reports/petty-cashes/summaries?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    if(filter){
      const [petty_cash_summary_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/petty-cashes/summaries/foreign-keys")
      ]);
    
      // Jika sukses get data petty_cash_summary_reports
      if (petty_cash_summary_reports.status === "success") {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_PETTY_CASH_SUMMARY_REPORTS_SUCCESS",
          petty_cash_summary_reports: petty_cash_summary_reports.result,
          filter
        });
      } else if (petty_cash_summary_reports.status === "fail") {
        yield put({
          type: "GET_PETTY_CASH_SUMMARY_REPORTS_ERROR",
          error: { message: petty_cash_summary_reports.message }
        });
      } else {
        yield put({
          type: "GET_PETTY_CASH_SUMMARY_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data petty_cash_summary_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_PETTY_CASH_SUMMARY_REPORT_FOREIGN_KEYS_SUCCESS",
          petty_cash_types: foreign_keys.result.petty_cash_types,
          locations: foreign_keys.result.locations
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_PETTY_CASH_SUMMARY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_PETTY_CASH_SUMMARY_REPORT_FOREIGN_KEYS_ERROR"
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/petty-cashes/summaries/foreign-keys")
      ]);
  
      yield put({
        type: "GET_PETTY_CASH_SUMMARY_REPORTS_SUCCESS",
        petty_cash_summary_reports: []
      });
    
      // Jika sukses get data petty_cash_summary_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_PETTY_CASH_SUMMARY_REPORT_FOREIGN_KEYS_SUCCESS",
          petty_cash_types: foreign_keys.result.petty_cash_types,
          locations: foreign_keys.result.locations
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_PETTY_CASH_SUMMARY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_PETTY_CASH_SUMMARY_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_PETTY_CASH_SUMMARY_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_PETTY_CASH_SUMMARY_REPORTS_REQUEST",
      getCashBankSummaryReports
    )
  ]);
}
