import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getAppTransCodes({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [app_trans_codes] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "settings/app-trans-codes?search=" + search + "&page=" + page), 
          ])
        : // Jika filter
          yield all([
            call(get, "settings/app-trans-codes?" + filter + "&page=" + page), 
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "settings/app-trans-codes?search=" + search),  
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "settings/app-trans-codes?" + filter),
        ]);

    // console.log(app_trans_codes);
    // console.log(accounts);

    // Jika sukses get data app_trans_codes
    if (app_trans_codes.status === "success") {
      // Jika di API menggunakan pagination
      if (app_trans_codes.result.current_page && search) {
        yield put({
          type: 'GET_APP_TRANS_CODES_SUCCESS',
          app_trans_codes: app_trans_codes.result.data,
          current_page: app_trans_codes.result.current_page,
          page_size: app_trans_codes.result.per_page,
          total: app_trans_codes.result.total,
          search: search
        });
      }
      // else if(app_trans_codes.result.current_page && filter)
      // {
      //   yield put({
      //     type: 'GET_APP_TRANS_CODES_SUCCESS',
      //     app_trans_codes: app_trans_codes.result.data,
      //     current_page: app_trans_codes.result.current_page,
      //     page_size: app_trans_codes.result.per_page,
      //     total: app_trans_codes.result.total,
      //     filter: filter
      //   });
      // }
      else if (app_trans_codes.result.current_page) {
        yield put({
          type: 'GET_APP_TRANS_CODES_SUCCESS',
          app_trans_codes: app_trans_codes.result.data,
          current_page: app_trans_codes.result.current_page,
          page_size: app_trans_codes.result.per_page,
          total: app_trans_codes.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_APP_TRANS_CODES_SUCCESS',
          app_trans_codes: app_trans_codes.result,
          page_size: null,
          total: null
        });
      }
    }
    else if (app_trans_codes.status === "fail") {
      yield put({
        type: 'GET_APP_TRANS_CODES_ERROR',
        error: app_trans_codes
      })
    }
    else {
      yield put({
        type: 'GET_APP_TRANS_CODES_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'GET_APP_TRANS_CODES_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeAppTransCode({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "settings/app-trans-codes", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_APP_TRANS_CODE_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_APP_TRANS_CODE_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_APP_TRANS_CODE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_APP_TRANS_CODE_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editAppTransCode({ payload }) {
  //console.log("masuk")
  const { id_app_trans_code } = payload;

  try {
    const [app_trans_code] = yield all([
      call(get, "settings/app-trans-codes/" + id_app_trans_code),
      
    ]);
  
    // Jika sukses
    if (app_trans_code.status === "success") {
      yield put({
        type: 'EDIT_APP_TRANS_CODE_SUCCESS',
        app_trans_code: app_trans_code.result
      });
    }
    else{
      yield put({
        type: 'EDIT_APP_TRANS_CODE_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'EDIT_APP_TRANS_CODE_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateAppTransCode({ payload }) {
  const { id_app_trans_code, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "settings/app-trans-codes/" + id_app_trans_code,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_APP_TRANS_CODE_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_APP_TRANS_CODE_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_APP_TRANS_CODE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_APP_TRANS_CODE_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_APP_TRANS_CODES_REQUEST', getAppTransCodes),
    // yield takeEvery('CREATE_APP_TRANS_CODE_REQUEST', createAppTransCode),
    yield takeEvery('STORE_APP_TRANS_CODE_REQUEST', storeAppTransCode),
    yield takeEvery('EDIT_APP_TRANS_CODE_REQUEST', editAppTransCode),
    yield takeEvery('UPDATE_APP_TRANS_CODE_REQUEST', updateAppTransCode),
  ]);
}
