const initState = {
  debit_memo_reports: [],
  locations: [],
  suppliers: [],
  cash_banks: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function debitMemoReportsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_DEBIT_MEMO_REPORTS_REQUEST": {
      return {
        ...state,
        //debit_memo_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_DEBIT_MEMO_REPORTS_SUCCESS": {
      return {
        ...state,
        debit_memo_reports: action.debit_memo_reports,
        // filter: action.filter,
        // search: action.search,
        loading: false
      };
    }
    case "GET_DEBIT_MEMO_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "GET_DEBIT_MEMO_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        suppliers: action.suppliers,
        cash_banks: action.cash_banks
      };
    }
    case "GET_DEBIT_MEMO_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
