import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getItemGroupingReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/item-groupings?";
  if (search) {
    url += search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if (filter) {
      const [item_grouping_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/item-groupings/foreign-keys")
      ]);
  
      // Jika sukses get data item_grouping_reports
      if (item_grouping_reports.status === "success") {
        yield put({
          type: "GET_ITEM_GROUPING_REPORTS_SUCCESS",
          item_grouping_reports: item_grouping_reports.result,
          filter: filter,
          search: search
        });
      } else if (item_grouping_reports.status === "fail") {
        yield put({
          type: "GET_ITEM_GROUPING_REPORTS_ERROR",
          error: item_grouping_reports
        });
      } else {
        yield put({
          type: "GET_ITEM_GROUPING_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data item_grouping_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ITEM_GROUPING_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          //pt_detail_search_filter: foreign_keys.result.pt_detail_search_filter
        });
      } else {
        yield put({
          type: "GET_ITEM_GROUPING_REPORT_FOREIGN_KEYS_ERROR"
          //error: {message: "Check your internet connection"}
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/item-groupings/foreign-keys")
      ]);
  
      yield put({
        type: "GET_ITEM_GROUPING_REPORTS_SUCCESS",
        item_grouping_reports: []
      });
  
      // Jika sukses get data item_grouping_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ITEM_GROUPING_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          //pt_detail_search_filter: foreign_keys.result.pt_detail_search_filter
        });
      } else if(foreign_keys.status === "fail") {
        yield put({
          type: "GET_ITEM_GROUPING_REPORT_FOREIGN_KEYS_ERROR",
          error: foreign_keys
        });
      } else {
        yield put({
          type: "GET_ITEM_GROUPING_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_ITEM_GROUPING_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export function* getItemsForItemGroupingReport({ payload }) {
  const { id_location, search } = payload;

  let filter = "items?is_active=1&limit=1&item_type=3";
  filter = search
    ? `${filter}&search=${search}&id_location=${id_location}`
    : `${filter}&id_location=${id_location}`;

  try {
    const items = yield call(get, filter);

    if (items.status === "success") {
      yield put({
        type: "GET_ITEMS_FOR_ITEM_GROUPING_REPORT_SUCCESS",
        items: items.result
      });
    }
    else if(items.status === "fail"){
      yield put({
        type: "GET_ITEMS_FOR_ITEM_GROUPING_REPORT_ERROR",
        error: items
      });
    }
    else{
      yield put({
        type: "GET_ITEMS_FOR_ITEM_GROUPING_REPORT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_ITEMS_FOR_ITEM_GROUPING_REPORT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_ITEM_GROUPING_REPORTS_REQUEST",
      getItemGroupingReports
    ),
    yield takeEvery(
      "GET_ITEMS_FOR_ITEM_GROUPING_REPORT_REQUEST",
      getItemsForItemGroupingReport
    )
  ]);
}
