import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getYearlyPurchaseReports({ payload }) {
  const { search, filter } = payload;

  let url = "reports/purchase-summaries/yearly-purchases?";
  // if (search) {
  //   url += "search=" + search;
  // }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    if (filter) {
      const [yearly_purchase_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/purchase-histories/foreign-keys")
      ]);
  
      // Jika sukses get data yearly_purchase_reports
      if (yearly_purchase_reports.status === "success") {
        yield put({
          type: "GET_YEARLY_PURCHASE_REPORTS_SUCCESS",
          yearly_purchase_reports: yearly_purchase_reports.result,
          filter: filter,
          search: search
        }); 
      } else if (yearly_purchase_reports.status === "fail") {
        yield put({
          type: "GET_YEARLY_PURCHASE_REPORTS_ERROR",
          error: { message: yearly_purchase_reports.message }
        });
      } else {
        yield put({
          type: "GET_YEARLY_PURCHASE_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data yearly_purchase_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_YEARLY_PURCHASE_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_YEARLY_PURCHASE_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_YEARLY_PURCHASE_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/purchase-histories/foreign-keys")
      ]);
  
      yield put({
        type: "GET_YEARLY_PURCHASE_REPORTS_SUCCESS",
        yearly_purchase_reports: []
      }); 
  
      // Jika sukses get data yearly_purchase_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_YEARLY_PURCHASE_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_YEARLY_PURCHASE_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_YEARLY_PURCHASE_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_YEARLY_PURCHASE_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_YEARLY_PURCHASE_REPORTS_REQUEST",
      getYearlyPurchaseReports
    )
  ]);
}
