import { all, takeEvery, put, call, fork } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPaymentTerms({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [payment_terms] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "payments/terms?search=" + search + "&page=" + page)
          ])
        : // Jika filter
          yield all([call(get, "payments/terms?" + filter + "&page=" + page)])
      : search
      ? // Jika search
        yield all([call(get, "payments/terms?search=" + search)])
      : // Jika tidak ada
        yield all([call(get, "payments/terms?" + filter)]);

    //console.log(payment_terms);
    // console.log(accounts);

    // Jika sukses get data payment_terms
    if (payment_terms.status === "success") {
      // Jika di API menggunakan pagination
      if (payment_terms.result.current_page && search) {
        yield put({
          type: "GET_PAYMENT_TERMS_SUCCESS",
          payment_terms: payment_terms.result.data,
          current_page: payment_terms.result.current_page,
          page_size: payment_terms.result.per_page,
          total: payment_terms.result.total,
          search: search
        });
      }
      // else if(payment_terms.result.current_page && filter)
      // {
      //   yield put({
      //     type: 'GET_PAYMENT_TERMS_SUCCESS',
      //     payment_terms: payment_terms.result.data,
      //     current_page: payment_terms.result.current_page,
      //     page_size: payment_terms.result.per_page,
      //     total: payment_terms.result.total,
      //     filter: filter
      //   });
      // }
      else if (payment_terms.result.current_page) {
        yield put({
          type: "GET_PAYMENT_TERMS_SUCCESS",
          payment_terms: payment_terms.result.data,
          current_page: payment_terms.result.current_page,
          page_size: payment_terms.result.per_page,
          total: payment_terms.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_PAYMENT_TERMS_SUCCESS",
          payment_terms: payment_terms.result,
          page_size: null,
          total: null
        });
      }
    } else if (payment_terms.status === "fail") {
      yield put({
        type: "GET_PAYMENT_TERMS_ERROR",
        error: payment_terms
      });
    } else {
      yield put({
        type: "GET_PAYMENT_TERMS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_PAYMENT_TERMS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Create data
export function* createPaymentTerm() {
  yield put({
    type: "CREATE_PAYMENT_TERM_SUCCESS"
  });
}

// Store data
export function* storePaymentTerm({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "payments/terms", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_PAYMENT_TERM_SUCCESS",
        success: resultStore
      });
    } else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_PAYMENT_TERM_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_PAYMENT_TERM_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_PAYMENT_TERM_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Edit data
export function* editPaymentTerm({ payload }) {
  //console.log("masuk")
  const { id_payment_term } = payload;

  try {
    const [payment_term] = yield all([
      call(get, "payments/terms/" + id_payment_term)
    ]);

    // Jika sukses
    if (payment_term.status === "success") {
      yield put({
        type: "EDIT_PAYMENT_TERM_SUCCESS",
        payment_term: payment_term.result
      });
    } else {
      yield put({
        type: "EDIT_PAYMENT_TERM_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_PAYMENT_TERM_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Update data
export function* updatePaymentTerm({ payload }) {
  const { id_payment_term, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "payments/terms/" + id_payment_term,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: "UPDATE_PAYMENT_TERM_SUCCESS",
        success: resultUpdate
      });
    } else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_PAYMENT_TERM_ERROR",
        error: resultUpdate
      });
    } else {
      yield put({
        type: "UPDATE_PAYMENT_TERM_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_PAYMENT_TERM_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Show data
export function* showPaymentTerm({ payload }) {
  const { id_payment_term } = payload;

  try {
    const resultShow = yield call(get, "payments/terms/" + id_payment_term);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: "SHOW_PAYMENT_TERM_SUCCESS",
        payment_term: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivatePaymentTerm({ payload }) {
  const { id_payment_term } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "payments/terms/" + id_payment_term,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: "DEACTIVATE_PAYMENT_TERM_SUCCESS",
          success: deactivateResult
        }),
        yield put({
          type: "GET_PAYMENT_TERMS_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: "DEACTIVATE_PAYMENT_TERM_ERROR",
          error: deactivateResult
        }),
        yield put({
          type: "GET_PAYMENT_TERMS_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "DEACTIVATE_PAYMENT_TERM_ERROR",
          error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "GET_PAYMENT_TERMS_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DEACTIVATE_PAYMENT_TERM_ERROR",
        error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "GET_PAYMENT_TERMS_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activatePaymentTerm({ payload }) {
  const { id_payment_term } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "payments/terms/" + id_payment_term,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: "ACTIVATE_PAYMENT_TERM_SUCCESS",
          success: activateResult
        }),
        yield put({
          type: "GET_PAYMENT_TERMS_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: "ACTIVATE_PAYMENT_TERM_ERROR",
          error: activateResult
        }),
        yield put({
          type: "GET_PAYMENT_TERMS_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "ACTIVATE_PAYMENT_TERM_ERROR",
          error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "GET_PAYMENT_TERMS_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "ACTIVATE_PAYMENT_TERM_ERROR",
        error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "GET_PAYMENT_TERMS_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_PAYMENT_TERMS_REQUEST", getPaymentTerms),
    yield takeEvery("CREATE_PAYMENT_TERM_REQUEST", createPaymentTerm),
    yield takeEvery("STORE_PAYMENT_TERM_REQUEST", storePaymentTerm),
    yield takeEvery("EDIT_PAYMENT_TERM_REQUEST", editPaymentTerm),
    yield takeEvery("UPDATE_PAYMENT_TERM_REQUEST", updatePaymentTerm),
    yield takeEvery("SHOW_PAYMENT_TERM_REQUEST", showPaymentTerm),
    yield takeEvery("DEACTIVATE_PAYMENT_TERM_REQUEST", deactivatePaymentTerm),
    yield takeEvery("ACTIVATE_PAYMENT_TERM_REQUEST", activatePaymentTerm)
  ]);
}
