import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getLocationTypes({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [location_types] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "companies/location-types?search=" + search + "&page=" + page), 
          ])
        : // Jika filter
          yield all([
            call(get, "companies/location-types?" + filter + "&page=" + page), 
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "companies/location-types?search=" + search),  
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "companies/location-types?" + filter),
        ]);

    // console.log(location_types);
    // console.log(accounts);

    // Jika sukses get data location_types
    if (location_types.status === "success") {
      // Jika di API menggunakan pagination
      if (location_types.result.current_page && search) {
        yield put({
          type: 'GET_LOCATION_TYPES_SUCCESS',
          location_types: location_types.result.data,
          current_page: location_types.result.current_page,
          page_size: location_types.result.per_page,
          total: location_types.result.total,
          search: search
        });
      }
      else if(location_types.result.current_page && filter)
      {
        yield put({
          type: 'GET_LOCATION_TYPES_SUCCESS',
          location_types: location_types.result.data,
          current_page: location_types.result.current_page,
          page_size: location_types.result.per_page,
          total: location_types.result.total,
          filter: filter
        });
      }
      else if (location_types.result.current_page) {
        yield put({
          type: 'GET_LOCATION_TYPES_SUCCESS',
          location_types: location_types.result.data,
          current_page: location_types.result.current_page,
          page_size: location_types.result.per_page,
          total: location_types.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_LOCATION_TYPES_SUCCESS',
          location_types: location_types.result,
          page_size: null,
          total: null
        });
      }
    }
    else if (location_types.status === "fail") {
      yield put({
        type: 'GET_LOCATION_TYPES_ERROR',
        error: location_types
      });
    }
    else {
      yield put({
        type: 'GET_LOCATION_TYPES_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_LOCATION_TYPES_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createLocationType() {
  
}

// Store data
export function* storeLocationType({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "companies/location-types", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_LOCATION_TYPE_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_LOCATION_TYPE_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_LOCATION_TYPE_ERROR",
        error: {message: "Check your"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_LOCATION_TYPE_ERROR",
      error: {message: "Check your"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editLocationType({ payload }) {
  //console.log("masuk")
  const { id_location_type } = payload;

  try {
    const [location_type] = yield all([
      call(get, "companies/location-types/" + id_location_type),
      
    ]);
  
    // Jika sukses
    if (location_type.status === "success") {
      yield put({
        type: 'EDIT_LOCATION_TYPE_SUCCESS',
        location_type: location_type.result
      });
    }
    else{
      yield put({
        type: 'EDIT_LOCATION_TYPE_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'EDIT_LOCATION_TYPE_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateLocationType({ payload }) {
  const { id_location_type, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "companies/location-types/" + id_location_type,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_LOCATION_TYPE_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_LOCATION_TYPE_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_LOCATION_TYPE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_LOCATION_TYPE_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showLocationType({ payload }) {
  const { id_location_type } = payload;

  try {
    const resultShow = yield call(get, "companies/location-types/" + id_location_type);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_LOCATION_TYPE_SUCCESS',
        location_type: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateLocationType({ payload }) {
  const { id_location_type } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "companies/location-types/" + id_location_type,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: 'DEACTIVATE_LOCATION_TYPE_SUCCESS',
          success: deactivateResult
        }),
        yield put({
          type: 'GET_LOCATION_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (deactivateResult.status === "fail" || deactivateResult.errors) {
      yield all([
        yield put({
          type: 'DEACTIVATE_LOCATION_TYPE_ERROR',
          error: deactivateResult
        }),
        yield put({
          type: 'GET_LOCATION_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'DEACTIVATE_LOCATION_TYPE_ERROR',
          error: {message: "Check your internet connection"}
        }),
        yield put({
          type: 'GET_LOCATION_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'DEACTIVATE_LOCATION_TYPE_ERROR',
        error: {message: "Check your internet connection"}
      }),
      yield put({
        type: 'GET_LOCATION_TYPES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error)
  }
}

// Activate data (mengubah is_active => 1)
export function* activateLocationType({ payload }) {
  const { id_location_type } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "companies/location-types/" + id_location_type,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_LOCATION_TYPE_SUCCESS',
          success: activateResult
        }),
        yield put({
          type: 'GET_LOCATION_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (activateResult.status === "fail" || activateResult.errors) {
      yield all([
        yield put({
          type: 'ACTIVATE_LOCATION_TYPE_ERROR',
          error: activateResult
        }),
        yield put({
          type: 'GET_LOCATION_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'ACTIVATE_LOCATION_TYPE_ERROR',
          error: {message: "Check your internet connection"}
        }),
        yield put({
          type: 'GET_LOCATION_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'ACTIVATE_LOCATION_TYPE_ERROR',
        error: {message: "Check your internet connection"}
      }),
      yield put({
        type: 'GET_LOCATION_TYPES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_LOCATION_TYPES_REQUEST', getLocationTypes),
    yield takeEvery('CREATE_LOCATION_TYPE_REQUEST', createLocationType),
    yield takeEvery('STORE_LOCATION_TYPE_REQUEST', storeLocationType),
    yield takeEvery('EDIT_LOCATION_TYPE_REQUEST', editLocationType),
    yield takeEvery('UPDATE_LOCATION_TYPE_REQUEST', updateLocationType),
    yield takeEvery('SHOW_LOCATION_TYPE_REQUEST', showLocationType),
    yield takeEvery('DEACTIVATE_LOCATION_TYPE_REQUEST', deactivateLocationType),
    yield takeEvery('ACTIVATE_LOCATION_TYPE_REQUEST', activateLocationType)
  ]);
}
