import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getReceivablePaymentReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/receivable-payments?";
  if(search){
    url += "search=" + search  
  }
  if(filter){
    search
      ? url += "&" + filter
      : url += filter  
  }
  if(page){
    search || filter
      ? url += "&page=" + page
      : url += "page=" + page 
  }

  try {
    if(filter){
      const [receivable_payment_reports, foreign_keys] = 
      yield all([
        call(get, url),
        call(get, "reports/receivable-payments/foreign-keys"),
      ])
  
      // Jika sukses get data receivable_payment_reports
      if (receivable_payment_reports.status === "success") {
        yield put({
          type: 'GET_RECEIVABLE_PAYMENT_REPORTS_SUCCESS',
          receivable_payment_reports: receivable_payment_reports.result,
          filter
        });
      }
      else if(receivable_payment_reports.status === "fail"){
        yield put({
          type: 'GET_RECEIVABLE_PAYMENT_REPORTS_ERROR', 
          error: {message: receivable_payment_reports.foreign_keys}
        })
      }
      else {
        yield put({
          type: 'GET_RECEIVABLE_PAYMENT_REPORTS_ERROR', 
          error: {message: "Check your internet connection"}
        })
      }
  
      // Jika sukses get data receivable_payment_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: 'GET_RECEIVABLE_PAYMENT_REPORT_FOREIGN_KEYS_SUCCESS',
          locations: foreign_keys.result.locations,
          customers: foreign_keys.result.customers,
          cash_banks: foreign_keys.result.cash_banks,
          sort_lists: foreign_keys.result.sort_lists,
          messages: foreign_keys.result.messages,
        });
      }
      else if(foreign_keys.status === "fail"){
        yield put({
          type: 'GET_RECEIVABLE_PAYMENT_REPORT_FOREIGN_KEYS_ERROR', 
          //error: {message: foreign_keys.foreign_keys}
        })
      }
      else{
        yield put({
          type: 'GET_RECEIVABLE_PAYMENT_REPORT_FOREIGN_KEYS_ERROR',
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = 
      yield all([
        call(get, "reports/receivable-payments/foreign-keys"),
      ])
  
      yield put({
        type: 'GET_RECEIVABLE_PAYMENT_REPORTS_SUCCESS',
        receivable_payment_reports: []
      });
  
      // Jika sukses get data receivable_payment_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: 'GET_RECEIVABLE_PAYMENT_REPORT_FOREIGN_KEYS_SUCCESS',
          locations: foreign_keys.result.locations,
          customers: foreign_keys.result.customers,
          cash_banks: foreign_keys.result.cash_banks,
          sort_lists: foreign_keys.result.sort_lists,
          messages: foreign_keys.result.messages,
        });
      }
      else if(foreign_keys.status === "fail"){
        yield put({
          type: 'GET_RECEIVABLE_PAYMENT_REPORT_FOREIGN_KEYS_ERROR', 
          error: {message: foreign_keys.foreign_keys}
        })
      }
      else{
        yield put({
          type: 'GET_RECEIVABLE_PAYMENT_REPORT_FOREIGN_KEYS_ERROR',
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: 'GET_RECEIVABLE_PAYMENT_REPORTS_ERROR', 
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}



export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_RECEIVABLE_PAYMENT_REPORTS_REQUEST', getReceivablePaymentReports),
  ]);
}
