import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getCustomerReports({ payload }) {
  const { search, filter } = payload;

  let url = "reports/customers?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    if (search || filter) {
      const [customer_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/customers/foreign-keys")
      ]);
  
      // Jika sukses get data customer_reports
      if (customer_reports.status === "success") {
        
        yield put({
          type: "GET_CUSTOMER_REPORTS_SUCCESS",
          customer_reports: customer_reports.result,
          filter: filter,
          search: search
        });
        
      } else if (customer_reports.status === "fail") {
        yield put({
          type: "GET_CUSTOMER_REPORTS_ERROR",
          error: { message: customer_reports.message }
        });
      } else {
        yield put({
          type: "GET_CUSTOMER_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data customer_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_CUSTOMER_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          customer_types: foreign_keys.result.customer_types,
          currencies: foreign_keys.result.currencies,
          filter_current_balance: foreign_keys.result.filter.current_balance_list
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_CUSTOMER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_CUSTOMER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/customers/foreign-keys")
      ]);
  
      yield put({
        type: "GET_CUSTOMER_REPORTS_SUCCESS",
        customer_reports: []
      });
  
      // Jika sukses get data customer_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_CUSTOMER_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          customer_types: foreign_keys.result.customer_types,
          currencies: foreign_keys.result.currencies,
          filter_current_balance: foreign_keys.result.filter.current_balance_list
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_CUSTOMER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_CUSTOMER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_CUSTOMER_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_CUSTOMER_REPORTS_REQUEST", getCustomerReports)
  ]);
}
