const initState = {
  balance_sheet_department_reports: [],
  locations: [],
  departments: [],
  projects: [],
  filter: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function balanceSheetDepartmentReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_BALANCE_SHEET_DEPARTMENT_REPORTS_REQUEST": {
      return {
        ...state,
        //balance_sheet_department_reports: [],
        loading: true,
        filter: action.payload.filter,
        errorField: null
      };
    }
    case "GET_BALANCE_SHEET_DEPARTMENT_REPORTS_SUCCESS": {
      return {
        ...state,
        balance_sheet_department_reports: action.balance_sheet_department_reports,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_BALANCE_SHEET_DEPARTMENT_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CREATE_BALANCE_SHEET_DEPARTMENT_REPORT_REQUEST": {
      return {
        ...state,
        balance_sheet_department_report: null,
        errorField: null
      };
    }

    case "GET_BALANCE_SHEET_DEPARTMENT_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        departments: action.departments,
        projects: action.projects,
      };
    }
    case "GET_BALANCE_SHEET_DEPARTMENT_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
