const initState = {
  customers: [],
  customer: null,
  customer_contact: null,
  customer_address: null,
  customer_types: [],
  locations: [],
  currencies: [],
  base_currency: null,
  accounts: [],
  taxes: [],
  cash_banks: [],
  payment_categories: [],
  payment_terms: [],
  employees: [],
  countries: [],
  provinces: [],
  cities: [],
  districts: [],
  default_id_country: null,
  default_id_customer_type: null,
  default_id_receivable_account: null,
  search: null,
  filter: null,
  loading: false,
  loadingConnection: false,
  loadingEdit: false,
  loadingFranchise: false,
  loadingSubmit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
  id_customer: null
};

export default function unitsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_CUSTOMERS_REQUEST": {
      return {
        ...state,
        //customers: [],
        loading: true,
        loadingFilterCustomerTypes: true,
        loadingFilterLocations: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_CUSTOMERS_SUCCESS": {
      return {
        ...state,
        customers: action.customers,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_CUSTOMERS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_CUSTOMER_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        customer_types: action.customer_types,
        locations: action.locations,
        currencies: action.currencies,
        base_currency: action.base_currency,
        accounts: action.accounts,
        taxes: action.taxes,
        cash_banks: action.cash_banks,
        payment_categories: action.payment_categories,
        payment_terms: action.payment_terms,
        employees: action.employees,
        default_id_customer_type: action.default_id_customer_type,
        countries: action.countries,
        provinces: action.provinces,
        cities: action.cities,
        districts: action.districts,
        default_id_country: action.default_id_country,
        default_id_receivable_account: action.default_id_receivable_account
      };
    }
    case "GET_CUSTOMER_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "CREATE_CUSTOMER_REQUEST": {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "CREATE_CUSTOMER_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        loadingEdit: false
      };
    }

    case "STORE_CUSTOMER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null,
        id_customer: null
      };
    }
    case "STORE_CUSTOMER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_customer: action.id_customer
      };
    }
    case "STORE_CUSTOMER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "STORE_CUSTOMER_CONTACT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null,
        id_customer: null
      };
    }
    case "STORE_CUSTOMER_ADDRESS_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null,
        id_customer: null
      };
    }
    case "STORE_CUSTOMER_OPENING_BALANCE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null,
        id_customer: null
      };
    }
    case "EDIT_CUSTOMER_REQUEST": {
      return {
        ...state,
        customer: null,
        loading: true,
        loadingEdit: true,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "EDIT_CUSTOMER_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        loadingEdit: false,
        customer: action.customer
      };
    }
    case "EDIT_CUSTOMER_ERROR": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        loadingEdit: false,
        error: action.error.message
      };
    }
    case "UPDATE_CUSTOMER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_CUSTOMER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_CUSTOMER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "UPDATE_CUSTOMER_CONTACT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_CUSTOMER_CONTACT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_CUSTOMER_CONTACT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "UPDATE_CUSTOMER_ADDRESS_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_CUSTOMER_ADDRESS_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_CUSTOMER_ADDRESS_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "UPDATE_CUSTOMER_OPENING_BALANCE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_CUSTOMER_OPENING_BALANCE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_CUSTOMER_OPENING_BALANCE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_CUSTOMER_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_CUSTOMER_SUCCESS": {
      return {
        ...state,
        loading: false,
        customers: action.customers
      };
    }
    case "DEACTIVATE_CUSTOMER_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_CUSTOMER_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_CUSTOMER_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DEACTIVATE_CUSTOMER_CONTACT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_CUSTOMER_CONTACT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_CUSTOMER_CONTACT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DEACTIVATE_CUSTOMER_ADDRESS_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_CUSTOMER_ADDRESS_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_CUSTOMER_ADDRESS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_CUSTOMER_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_CUSTOMER_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_CUSTOMER_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    case "STORE_FRANCHISE_REQUEST":
      return {
        ...state,
        loadingFranchise: true
      };
    case "STORE_FRANCHISE_SUCCESS":
      return {
        ...state,
        loadingFranchise: false,
        success: action.success.message
      };
    case "STORE_FRANCHISE_ERROR":
      return {
        ...state,
        loadingFranchise: false,
        error: action.error.message
      };
    case "CHECK_FRANCHISE_CONNECTION_REQUEST":
      return {
        ...state,
        loadingConnection: true,
        errorField: null
      };
    case "CHECK_FRANCHISE_CONNECTION_ERROR":
      return {
        ...state,
        loadingConnection: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    case "CHECK_FRANCHISE_CONNECTION_SUCCESS":
      return {
        ...state,
        loadingConnection: false,
        success: action.success.message
      };
    case "UPDATE_FRANCHISE_REQUEST":
      return {
        ...state,
        loadingConnection: true,
        errorField: null
      };
    case "UPDATE_FRANCHISE_SUCCESS": {
      return {
        ...state,
        loadingConnection: false,
        success: action.success.message
      };
    }
    case "UPDATE_FRANCHISE_ERROR": {
      return {
        ...state,
        loadingConnection: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    default:
      return state;
  }
}
