import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getRacks({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [racks, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "companies/locations/racks?search=" + search + "&page=" + page),
            call(get, "companies/locations/racks/foreign-keys"),
          ])
        : // Jika filter
          yield all([
            call(get, "companies/locations/racks?" + filter + "&page=" + page),
            call(get, "companies/locations/racks/foreign-keys"),
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "companies/locations/racks?search=" + search),
          call(get, "companies/locations/racks/foreign-keys"),
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "companies/locations/racks?" + filter),
          call(get, "companies/locations/racks/foreign-keys"),
        ]);

    // console.log(racks);
    // console.log(foreign_keys);

    // Jika sukses get data racks
    if (racks.status === "success") {
      // Jika di API menggunakan pagination
      if (racks.result.current_page && search) {
        yield put({
          type: 'GET_RACKS_SUCCESS',
          racks: racks.result.data,
          current_page: racks.result.current_page,
          page_size: racks.result.per_page,
          total: racks.result.total,
          search: search
        });
      }
      else if(racks.result.current_page && filter)
      {
        yield put({
          type: 'GET_RACKS_SUCCESS',
          racks: racks.result.data,
          current_page: racks.result.current_page,
          page_size: racks.result.per_page,
          total: racks.result.total,
          filter: filter
        });
      }
      else if (racks.result.current_page) {
        yield put({
          type: 'GET_RACKS_SUCCESS',
          racks: racks.result.data,
          current_page: racks.result.current_page,
          page_size: racks.result.per_page,
          total: racks.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_RACKS_SUCCESS',
          racks: racks.result,
          page_size: null,
          total: null
        });
      }
    } else if (racks.status === "fail") {
      yield put({
        type: 'GET_RACKS_ERROR', 
        error: racks
      })
    } else {
      yield put({
        type: 'GET_RACKS_ERROR', 
        error: {message: "Check your internet connection"}
      })
    }

    // Jika sukses get data rack types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_RACK_FOREIGN_KEYS_SUCCESS',
        locations: foreign_keys.result.locations
      });
    }
    else{
      yield put({
        type: 'GET_RACK_FOREIGN_KEYS_ERROR',
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_RACKS_ERROR', 
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createRack() {
  try {
    const [foreign_keys] = yield all([
      call(get, "companies/locations/racks/foreign-keys"),
    ]);
  
    // Jika sukses get data account
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_RACK_FOREIGN_KEYS_SUCCESS',
        locations: foreign_keys.result.locations
      });
    }
    else{
      yield put({
        type: 'GET_RACK_FOREIGN_KEYS_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_RACK_FOREIGN_KEYS_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeRack({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "companies/locations/racks", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_RACK_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_RACK_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_RACK_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_RACK_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editRack({ payload }) {
  const { id_rack } = payload;

  try {
    const [rack, foreign_keys] = yield all([
      call(get, "companies/locations/racks/" + id_rack),
      call(get, "companies/locations/racks/foreign-keys"),
    ]);
  
    // Jika sukses
    if (rack.status === "success") {
      yield put({
        type: 'EDIT_RACK_SUCCESS',
        rack: rack.result
      });
    }
    else{
      yield put({
        type: "EDIT_RACK_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
    
    // Jika sukses get data rack types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_RACK_FOREIGN_KEYS_SUCCESS',
        locations: foreign_keys.result.locations
      });
    }
    else{
      yield put({
        type: 'GET_RACK_FOREIGN_KEYS_ERROR',
        // error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_RACK_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateRack({ payload }) {
  const { id_rack, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "companies/locations/racks/" + id_rack,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_RACK_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_RACK_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_RACK_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_RACK_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showRack({ payload }) {
  const { id_rack } = payload;

  try {
    const resultShow = yield call(get, "companies/locations/racks/" + id_rack);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_RACK_SUCCESS',
        rack: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateRack({ payload }) {
  const { id_rack } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "companies/locations/racks/" + id_rack,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: 'DEACTIVATE_RACK_SUCCESS',
          success: deactivateResult
        }),
        yield put({
          type: 'GET_RACKS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: 'DEACTIVATE_RACK_ERROR',
          error: deactivateResult
        }),
        yield put({
          type: 'GET_RACKS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'DEACTIVATE_RACK_ERROR',
          error: {message: "Check your internet connection"}
        }),
        yield put({
          type: 'GET_RACKS_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'DEACTIVATE_RACK_ERROR',
        error: {message: "Check your internet connection"}
      }),
      yield put({
        type: 'GET_RACKS_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activateRack({ payload }) {
  const { id_rack } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "companies/locations/racks/" + id_rack,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_RACK_SUCCESS',
          success: activateResult
        }),
        yield put({
          type: 'GET_RACKS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: 'ACTIVATE_RACK_ERROR',
          error: activateResult
        }),
        yield put({
          type: 'GET_RACKS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'ACTIVATE_RACK_ERROR',
          error: {message: "Check your internet connection"}
        }),
        yield put({
          type: 'GET_RACKS_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'ACTIVATE_RACK_ERROR',
        error: {message: "Check your internet connection"}
      }),
      yield put({
        type: 'GET_RACKS_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_RACKS_REQUEST', getRacks),
    yield takeEvery('CREATE_RACK_REQUEST', createRack),
    yield takeEvery('STORE_RACK_REQUEST', storeRack),
    yield takeEvery('EDIT_RACK_REQUEST', editRack),
    yield takeEvery('UPDATE_RACK_REQUEST', updateRack),
    yield takeEvery('SHOW_RACK_REQUEST', showRack),
    yield takeEvery('DEACTIVATE_RACK_REQUEST', deactivateRack),
    yield takeEvery('ACTIVATE_RACK_REQUEST', activateRack)
  ]);
}
