import actions from "./actions";

const userData = JSON.parse(localStorage.getItem("user"));
const lang = localStorage.getItem("language");
const bearer = localStorage.getItem("bearer");
const initState = {
  bearer: bearer ? bearer : null,
  loading: false,
  selectedLanguage: lang ? lang : null,
  message: null,
  warning: null,
  error: null,
  status: null,
  user: userData ? userData : null
};

export default function authReducer(state = initState, action) {
  switch (action.type) {
    case "CHECK_LOGIN_REQUEST":
      return {
        ...state,
        loading: true
      };
    case "CHECK_LOGIN_SUCCESS":
      return {
        ...state,
        loading: false
      };
    case "CHECK_LOGIN_WARNING":
      return {
        ...state,
        loading: false,
        warning: action.warning
      };
    case "LOGIN_REQUEST":
      return {
        ...state,
        loading: true
      };
    case "LOGIN_SUCCESS":
      return {
        ...state,
        bearer: action.token,
        user: action.user,
        selectedLanguage: action.user.language,
        status: "success",
        loading: false,
        message: "Successfully Sign In"
      };
    case "LOGIN_ERROR":
      return {
        ...state,
        loading: false,
        message: action.error.message,
        error: action.error.message,
        status: "error"
      };
    case "LOGOUT":
      return {
        ...state,
        bearer: null,
        user: null,
        status: null,
        message: null
      };
    case "LOGOUT_ERROR":
      return {
        ...state,
        error: action.error.message
      };
    case "CLEAR_WARNING":
      return {
        ...state,
        warning: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "SWITCH_LANGUAGE":
      localStorage.setItem("language", action.payload.lang);
      return {
        ...state,
        selectedLanguage: action.payload.lang
      }
    default:
      return state;
  }
}
