
const initState = {
  purchase_return_detail_reports: [],
  pt_detail_search_filter: [],
  total_amount: null,
  total_discount: null,
  total_qty: null,
  total_tax: null,
  total_item: null,
  locations: [],
  suppliers: [],
  departments: [],
  projects: [],
  base_currency: null,
  messages: [],
  search: null,
  filter: null,
  loading: false,
  // current_page: 1,
  // page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
};

export default function purchaseReturnDetailReportsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_PURCHASE_RETURN_DETAIL_REPORTS_REQUEST': {
      return {
        ...state,
        //purchase_return_detail_reports: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null,
      };
    }
    case 'GET_PURCHASE_RETURN_DETAIL_REPORTS_SUCCESS': {
      return {
        ...state,
        purchase_return_detail_reports: action.purchase_return_detail_reports,
        // current_page: action.current_page,
        // page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_PURCHASE_RETURN_DETAIL_REPORTS_ERROR': {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      }
    }
    case 'CREATE_PURCHASE_RETURN_DETAIL_REPORT_REQUEST': {
      return {
        ...state,
        purchase_return_report: null,
        errorField: null,
      };
    }

    case 'GET_PURCHASE_RETURN_DETAIL_REPORT_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        suppliers: action.suppliers,
        departments: action.departments,  
        projects: action.projects, 
        base_currency : action.base_currency,
        messages: action.messages,
        pt_detail_search_filter: action.pt_detail_search_filter
      };
    }
    case 'GET_PURCHASE_RETURN_DETAIL_REPORT_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
