import { all, takeEvery, put, call } from "redux-saga/effects";
import {
  get,
  put as putApi,
  post,
  getFranchiseCon
} from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getCustomers({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [customers, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "customers?search=" + search + "&page=" + page),
            call(get, "customers/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "customers?" + filter + "&page=" + page),
            call(get, "customers/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "customers?search=" + search),
          call(get, "customers/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "customers?" + filter),
          call(get, "customers/foreign-keys")
        ]);

    // console.log(customers);
    // console.log(customer_types);
    // console.log(locations);

    // Jika sukses get data customers
    if (customers.status === "success") {
      // Jika di API menggunakan pagination
      if (customers.result.current_page && search) {
        yield put({
          type: "GET_CUSTOMERS_SUCCESS",
          customers: customers.result.data,
          current_page: customers.result.current_page,
          page_size: customers.result.per_page,
          total: customers.result.total,
          search: search
        });
      } else if (customers.result.current_page && filter) {
        yield put({
          type: "GET_CUSTOMERS_SUCCESS",
          customers: customers.result.data,
          current_page: customers.result.current_page,
          page_size: customers.result.per_page,
          total: customers.result.total,
          filter: filter
        });
      } else if (customers.result.current_page) {
        yield put({
          type: "GET_CUSTOMERS_SUCCESS",
          customers: customers.result.data,
          current_page: customers.result.current_page,
          page_size: customers.result.per_page,
          total: customers.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_CUSTOMERS_SUCCESS",
          customers: customers.result,
          page_size: null,
          total: null
        });
      }
    } else if (customers.status === "fail") {
      yield put({
        type: "GET_CUSTOMERS_ERROR",
        error: customers
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_CUSTOMERS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_CUSTOMER_FOREIGN_KEYS_SUCCESS",
        customer_types: foreign_keys.result.customer_types,
        locations: foreign_keys.result.locations,
        currencies: foreign_keys.result.currencies,
        base_currency: foreign_keys.result.base_currency,
        accounts: foreign_keys.result.accounts,
        taxes: foreign_keys.result.taxes,
        cash_banks: foreign_keys.result.cash_banks,
        payment_categories: foreign_keys.result.payment_categories,
        payment_terms: foreign_keys.result.payment_terms,
        employees: foreign_keys.result.employees,
        default_id_customer_type: foreign_keys.result.default_id_customer_type,
        countries: foreign_keys.result.countries,
        provinces: foreign_keys.result.provinces,
        cities: foreign_keys.result.cities,
        districts: foreign_keys.result.districts,
        default_id_country: foreign_keys.result.default_id_country,
        default_id_receivable_account:
          foreign_keys.result.default_id_receivable_account
      });
    } else {
      yield put({
        type: "GET_CUSTOMER_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_CUSTOMERS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Create data
export function* createCustomer() {
  try {
    const [foreign_keys] = yield all([call(get, "customers/foreign-keys")]);
    //  console.log(id_customer);

    if (foreign_keys.status === "success") {
      yield all([
        yield put({
          type: "GET_CUSTOMER_FOREIGN_KEYS_SUCCESS",
          customer_types: foreign_keys.result.customer_types,
          locations: foreign_keys.result.locations,
          currencies: foreign_keys.result.currencies,
          base_currency: foreign_keys.result.base_currency,
          accounts: foreign_keys.result.accounts,
          taxes: foreign_keys.result.taxes,
          cash_banks: foreign_keys.result.cash_banks,
          payment_categories: foreign_keys.result.payment_categories,
          payment_terms: foreign_keys.result.payment_terms,
          employees: foreign_keys.result.employees,
          default_id_customer_type:
            foreign_keys.result.default_id_customer_type,
          countries: foreign_keys.result.countries,
          provinces: foreign_keys.result.provinces,
          cities: foreign_keys.result.cities,
          districts: foreign_keys.result.districts,
          default_id_country: foreign_keys.result.default_id_country,
          default_id_receivable_account:
            foreign_keys.result.default_id_receivable_account
        }),
        yield put({
          type: "CREATE_CUSTOMER_SUCCESS"
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "GET_CUSTOMER_FOREIGN_KEYS_ERROR"
          // error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "CREATE_CUSTOMER_ERROR",
          error: { message: "Check your internet connection" }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "GET_CUSTOMER_FOREIGN_KEYS_ERROR"
        // error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "CREATE_CUSTOMER_ERROR",
        error: { message: "Check your internet connection" }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

// Store data
export function* storeCustomer({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "customers", body);
    // console.log(body);
    //  console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_CUSTOMER_SUCCESS",
        id_customer: resultStore.result.id_customer,
        success: resultStore
      });
    } else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_CUSTOMER_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_CUSTOMER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_CUSTOMER_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

