import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getTrialBalanceReports({ payload }) {
  const { search, filter } = payload;

  let url = "reports/trial-balances?";
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }

  try {
    if (filter) {
      const [trial_balance_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/trial-balances/foreign-keys")
      ]);
  
      // console.log(trial_balance_reports);
      // console.log(foreign_keys);
  
      // Jika sukses get data trial_balance_reports
      if (trial_balance_reports.status === "success") {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_TRIAL_BALANCE_REPORTS_SUCCESS",
          trial_balance_reports: trial_balance_reports.result
        });
      } else if (trial_balance_reports.status === "fail") {
        yield put({
          type: "GET_TRIAL_BALANCE_REPORTS_ERROR",
          error: { message: trial_balance_reports.message }
        });
      } else {
        yield put({
          type: "GET_TRIAL_BALANCE_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data trial_balance_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_TRIAL_BALANCE_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          gl_types: foreign_keys.result.gl_types
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_TRIAL_BALANCE_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_TRIAL_BALANCE_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/trial-balances/foreign-keys")
      ]);
  
      yield put({
        type: "GET_TRIAL_BALANCE_REPORTS_SUCCESS",
        trial_balance_reports: []
      });
      // Jika sukses get data trial_balance_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_TRIAL_BALANCE_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          gl_types: foreign_keys.result.gl_types
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_TRIAL_BALANCE_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_TRIAL_BALANCE_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_TRIAL_BALANCE_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_TRIAL_BALANCE_REPORTS_REQUEST", getTrialBalanceReports)
  ]);
}
