const actions = {
  GET_SITES_REQUEST: "GET_SITES_REQUEST",
  GET_SITES_SUCCESS: "GET_SITES_SUCCESS",
  STORE_SITE_REQUEST: "STORE_SITE_REQUEST",
  STORE_SITE_SUCCESS: "STORE_SITE_SUCCESS",
  STORE_SITE_ERROR: "STORE_SITE_ERROR",
  EDIT_SITE_REQUEST: "EDIT_SITE_REQUEST",
  EDIT_SITE_SUCCESS: "EDIT_SITE_SUCCESS",
  EDIT_SITE_ERROR: "EDIT_SITE_ERROR",
  UPDATE_SITE_REQUEST: "UPDATE_SITE_REQUEST",
  UPDATE_SITE_SUCCESS: "UPDATE_SITE_SUCCESS",
  UPDATE_SITE_ERROR: "UPDATE_SITE_ERROR",
  DEACTIVATE_SITE_REQUEST: "DEACTIVATE_SITE_REQUEST",
  DEACTIVATE_SITE_SUCCESS: "DEACTIVATE_SITE_SUCCESS",
  DEACTIVATE_SITE_ERROR: "DEACTIVATE_SITE_ERROR",
  ACTIVATE_SITE_REQUEST: "ACTIVATE_SITE_REQUEST",
  ACTIVATE_SITE_SUCCESS: "ACTIVATE_SITE_SUCCESS",
  ACTIVATE_SITE_ERROR: "ACTIVATE_SITE_ERROR",
  CLEAR_SUCCESS: "CLEAR_SUCCESS",
  CLEAR_ERROR: "CLEAR_ERROR",
  CLEAR_ERROR_FIELD: "CLEAR_ERROR_FIELD",
  getSites: (page, search) => ({
    type: actions.GET_SITES_REQUEST,
    payload: {
      page: page,
      search: search
    }
  }),
  storeSite: param => ({
    type: actions.STORE_SITE_REQUEST,
    payload: { body: param }
  }),
  editSite: id_site => ({
    type: actions.EDIT_SITE_REQUEST,
    payload: { id_site: id_site }
  }),
  updateSite: (id_site, param) => ({
    type: actions.UPDATE_SITE_REQUEST,
    payload: {
      id_site: id_site,
      body: param
    }
  }),
  deactivateSite: id_site => ({
    type: actions.DEACTIVATE_SITE_REQUEST,
    payload: { id_site }
  }),
  activateSite: id_site => ({
    type: actions.ACTIVATE_SITE_REQUEST,
    payload: { id_site }
  }),
  clearSuccess: () => ({
    type: actions.CLEAR_SUCCESS
  }),
  clearError: () => ({
    type: actions.CLEAR_ERROR
  }),
  clearErrorField: () => ({
    type: actions.CLEAR_ERROR_FIELD
  })
};
export default actions;
