
const initState = {
	location_types: [],
	location_type: null,
	search: null,
	//filter: null,
	loading: false,
	loadingEdit: false,
	current_page: 1,
	page_size: null,
	total: null,
	success: null,
	error: null,
	errorField: null
  };
  
export default function locationTypesReducer(state = initState, action) {
	switch (action.type) {
	  case 'GET_LOCATION_TYPES_REQUEST': {
			return {
				...state,
				//location_types: [],
				loading: true,
				search: action.payload.search,
				//filter: action.payload.filter,
				current_page: action.payload.page,
				errorField: null,
			};
	  }
	  case 'GET_LOCATION_TYPES_SUCCESS': {
			return {
				...state,
				location_types: action.location_types,
				current_page: action.current_page,
				page_size: action.page_size,
				total: action.total,
				search: action.search,
				//filter: action.filter,
				loading: false,
			};
		}
		case 'GET_LOCATION_TYPES_ERROR': {
			return {
				...state,
				loading: false,
				error: action.error.message,
				// errorField: action.error.errors
			};
	  }
	  case 'CREATE_LOCATION_TYPE_REQUEST': {
			return {
				...state,
				// location_type: null,
				errorField: null,
			};
	  }
	  
	  case 'STORE_LOCATION_TYPE_REQUEST': {
			return {
				...state,
				loading: true,
				errorField: null,
			};
	  }
	  case 'STORE_LOCATION_TYPE_SUCCESS': {
			return {
				...state,
				loading: false,
				success: action.success.message
			};
	  }
	  case 'STORE_LOCATION_TYPE_ERROR': {
			return {
				...state,
				loading: false,
				error: action.error.message,
				errorField: action.error.errors
			};
	  }
	  case 'EDIT_LOCATION_TYPE_REQUEST': {
			return {
				...state,
				loading: true,
				loadingEdit: true,
				errorField: null,
			};
	  }
	  case 'EDIT_LOCATION_TYPE_SUCCESS': {
			return {
				...state,
				loading: false,
				loadingEdit: false,
				location_type: action.location_type,
			};
		}
		case 'EDIT_LOCATION_TYPE_ERROR': {
			return {
				...state,
				// loading: false,
				error: action.error.message,
				//errorField: action.error.errors
			};
	  }
	  case 'UPDATE_LOCATION_TYPE_REQUEST': {
			return {
				...state,
				loading: true,
				errorField: null,
			};
	  }
	  case 'UPDATE_LOCATION_TYPE_SUCCESS': {
			return {
				...state,
				loading: false,
				success: action.success.message
			};
	  }
	  case 'UPDATE_LOCATION_TYPE_ERROR': {
			return {
				...state,
				loading: false,
				error: action.error.message,
				errorField: action.error.errors
			};
	  }
	  case 'SHOW_LOCATION_TYPE_REQUEST': {
			return {
				...state,
				loading: true,
				errorField: null,
			};
	  }
	  case 'SHOW_LOCATION_TYPE_SUCCESS': {
			return {
				...state,
				loading: false,
				location_types: action.location_types
			};
	  }
	  case "DEACTIVATE_LOCATION_TYPE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_LOCATION_TYPE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_LOCATION_TYPE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_LOCATION_TYPE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_LOCATION_TYPE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_LOCATION_TYPE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
	  default:
			return state;
	}
}
  