const initState = {
  id_purchase_return: null,
  id_location: null,
  purchaseReturns: [],
  purchaseReturn: null,
  printData: {},
  items: [],
  purchaseReceipts: [],
  purchaseInvoices: [],
  suppliers: [],
  taxes: [],
  accounts: [],
  cashBanks: [],
  returnReasons: [],
  cashFlowTypes: [],
  currencies: [],
  currencyRates: [],
  paymentCategories: [],
  paymentTerms: [],
  projects: [],
  departments: [],
  // selectedTransaction: null,
  locations: [],
  search: null,
  filter: null,
  loading: false,
  loadingForeign: false,
  loadingSubmit: false,
  loadingPurchaseReceipts: false,
  loadingPurchaseInvoices: false,
  current_page: 1,
  page_size: null,
  total: null,
  current_page_pr: 1,
  page_size_pr: null,
  total_pr: null,
  current_page_pi: 1,
  page_size_pi: null,
  total_pi: null,
  success: null,
  error: null,
  errorField: null
};

export default function purchaseReturnsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_PURCHASE_RETURNS_REQUEST": {
      return {
        ...state,
        //purchaseReturns: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_PURCHASE_RETURNS_SUCCESS": {
      return {
        ...state,
        purchaseReturns: action.purchaseReturns,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_PURCHASE_RETURNS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_PURCHASE_RETURN_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_PURCHASE_RETURN_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loadingForeign: false,
        locations: action.locations,
        suppliers: action.suppliers,
        currencies: action.currencies,
        taxes: action.taxes,
        accounts: action.accounts,
        cashBanks: action.cashBanks,
        returnReasons: action.returnReasons,
        cashFlowTypes: action.cashFlowTypes,
        paymentCategories: action.paymentCategories,
        paymentTerms: action.paymentTerms,
        departments: action.departments,
        projects: action.projects
      };
    }
    case "GET_PURCHASE_RETURN_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "CREATE_PURCHASE_RETURN_REQUEST": {
      return {
        ...state,
        id_purchase_return: null,
        id_location: null,
        purchaseReturn: null,
        loadingTransactions: true,
        loadingPurchaseReceipts: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null,
        current_page_pr: 1,
        page_size_pr: null,
        total_pr: null
      };
    }
    case "GET_FILTER_ITEMS_BY_LOCATION_REQUEST": {
      return {
        ...state,
        loadingItems: true,
        search: action.payload.search,
        id_location: action.payload.id_location
      };
    }
    case "GET_FILTER_ITEMS_BY_LOCATION_SUCCESS": {
      return {
        ...state,
        loadingItems: false,
        items: action.items
      };
    }
    case "GET_FILTER_PURCHASE_RECEIPTS_PT_REQUEST": {
      return {
        ...state,
        loadingPurchaseReceipts: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page_pr: action.payload.page
      };
    }
    case "GET_FILTER_PURCHASE_RECEIPTS_SUCCESS": {
      return {
        ...state,
        loadingPurchaseReceipts: false,
        purchaseReceipts: action.purchaseReceipts,
        current_page_pr: action.current_page,
        page_size_pr: action.page_size,
        total_pr: action.total
      };
    }
    case "GET_FILTER_PURCHASE_RECEIPTS_ERROR": {
      return {
        ...state,
        loadingPurchaseReceipts: false,
        error: action.error.message
      };
    }

    case "GET_FILTER_PURCHASE_INVOICES_PT_REQUEST": {
      return {
        ...state,
        loadingPurchaseInvoices: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page_pi: action.payload.page
      };
    }
    case "GET_FILTER_PURCHASE_INVOICES_PT_SUCCESS": {
      return {
        ...state,
        loadingPurchaseInvoices: false,
        purchaseInvoices: action.purchaseInvoices,
        current_page_pi: action.current_page,
        page_size_pi: action.page_size,
        total_pi: action.total
      };
    }
    case "GET_FILTER_PURCHASE_INVOICES_PT_ERROR": {
      return {
        ...state,
        loadingPurchaseInvoices: false,
        error: action.error.message
      };
    }

    case "STORE_PURCHASE_RETURN_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_PURCHASE_RETURN_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_purchase_return: action.id_purchase_return
      };
    }
    case "STORE_PURCHASE_RETURN_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_PURCHASE_RETURN_REQUEST": {
      return {
        ...state,
        loading: true,
        id_purchase_return: null,
        id_location: null,
        loadingTransactions: true,
        loadingPurchaseReceipts: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null,
        current_page_pr: 1,
        page_size_pr: null,
        total_pr: null
      };
    }
    case "EDIT_PURCHASE_RETURN_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        purchaseReturn: action.purchaseReturn
      };
    }
    case "EDIT_PURCHASE_RETURN_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_PURCHASE_RETURN_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_PURCHASE_RETURN_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_purchase_return: action.id_purchase_return
      };
    }
    case "UPDATE_PURCHASE_RETURN_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_PURCHASE_RETURN_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_PURCHASE_RETURN_SUCCESS": {
      return {
        ...state,
        loading: false,
        purchaseReturns: action.purchaseReturns
      };
    }
    case "CONFIRM_PURCHASE_RETURN_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CONFIRM_PURCHASE_RETURN_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CONFIRM_PURCHASE_RETURN_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UNCONFIRM_PURCHASE_RETURN_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "UNCONFIRM_PURCHASE_RETURN_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UNCONFIRM_PURCHASE_RETURN_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CLOSE_PURCHASE_RETURN_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CLOSE_PURCHASE_RETURN_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CLOSE_PURCHASE_RETURN_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CANCEL_PURCHASE_RETURN_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CANCEL_PURCHASE_RETURN_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CANCEL_PURCHASE_RETURN_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPLOAD_PURCHASE_RETURN_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_PURCHASE_RETURN_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_PURCHASE_RETURN_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_PURCHASE_RETURN_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_PURCHASE_RETURN_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_PURCHASE_RETURN_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_PURCHASE_RETURN_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "SET_PRINT_DATA_PURCHASE_RETURN": {
      return {
        ...state,
        printData: action.payload.body
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null,
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    default:
      return state;
  }
}
