const initState = {
  acc_rec_summary_reports: [],
  filter_current_balance: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  // current_page: 1,
  // page_size: null,
  // total: null,
  success: null,
  error: null,
  errorField: null
};

export default function accRecSummaryReportsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_ACC_REC_SUMMARY_REPORTS_REQUEST": {
      return {
        ...state,
        //acc_rec_summary_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_ACC_REC_SUMMARY_REPORTS_SUCCESS": {
      return {
        ...state,
        acc_rec_summary_reports: action.acc_rec_summary_reports,
        loading: false
      };
    }
    case "GET_ACC_REC_SUMMARY_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "GET_ACC_REC_SUMMARY_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        filter_current_balance: action.filter_current_balance
      };
    }
    case "GET_ACC_REC_SUMMARY_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
