import React, { Component } from "react";
import { connect } from "react-redux";
import moment from "moment";
import "moment/locale/id";
import { IntlProvider } from "react-intl";
import { Alert, Layout, LocaleProvider } from "antd";
import { Debounce } from "react-throttle";
import { ThemeProvider } from "styled-components";
import WindowResizeListener from "react-window-size-listener";
import authAction from "../../redux/auth/actions";
import appActions from "../../redux/app/actions";

import { AppLocale } from "../../dashApp";
import { getCurrentLanguage } from "../../containers/LanguageSwitcher/config";
import { siteConfig } from "../../settings";
import { themeConfig } from "../../settings";
import Sidebar from "../Sidebar/Sidebar";
import Topbar from "../Topbar/Topbar";
import AppRouter from "./AppRouter";
import themes from "../../settings/themes";
import AppHolder from "./commonStyle";
import getLanguage from "../App/MyLanguange";
import "./global.css";

import IdleTimer from "react-idle-timer";
import { isElectron } from "../../helpers/helper";

const { Content, Footer } = Layout;
const { logout } = authAction;
const { toggleAll } = appActions;

export class App extends Component {
  constructor(props) {
    super(props);
    this.idleTimer = null;
    this.onIdle = this.handleIdle.bind(this);
  }

  componentDidMount = () => {
    document.title = siteConfig.documentTitle;
    if (isElectron()) {
      window.ipcRenderer.on("APP_READY", this.readyListener);
      window.ipcRenderer.on("APP_BEFORE_QUIT", this.beforeQuitListener);
    }
  };

  // logout ketika app ready dan ada di App
  readyListener = () => {
    const { auth } = this.props;

    if (auth.user) {
      this.props.logout(false);
      this.props.history.push("/signin");
      // console.log("send request logout");
    }
  };

  beforeQuitListener = () => {
    const { auth } = this.props;

    if (auth.user) {
      this.props.logout(true);
      this.props.history.push("/signin");
      // console.log("send request logout");
    }
  };

  handleIdle = () => {
    const { auth } = this.props;

    if (auth.user) {
      this.props.logout();
      this.props.history.push("/signin");
    }
  };

  render() {
    const { url } = this.props.match;
    const { height, history, auth } = this.props;
    const appHeight = window.innerHeight;
    const user = auth ? auth.user : null;

    const currentAppLocale =
      AppLocale[
        getCurrentLanguage(this.props.auth.selectedLanguage || "EN").locale
      ];

    const lang = auth.selectedLanguage
      ? auth.selectedLanguage.toLowerCase()
      : "en";
    // Set moment global ganti bahasa
    moment.locale(lang);

    return (
      <LocaleProvider locale={currentAppLocale.antd}>
        <IntlProvider
          locale={currentAppLocale.locale}
          messages={currentAppLocale.messages}
        >
          <IdleTimer
            ref={ref => {
              this.idleTimer = ref;
            }}
            timeout={
              user && user.session_limit ? parseInt(user.session_limit) : 600000
            }
            onIdle={this.onIdle}
          >
            <ThemeProvider theme={themes[themeConfig.theme]}>
              <AppHolder>
                <Layout style={{ height: appHeight }}>
                  <Debounce time="1000" handler="onResize">
                    <WindowResizeListener
                      onResize={windowSize =>
                        this.props.toggleAll(
                          windowSize.windowWidth,
                          windowSize.windowHeight
                        )
                      }
                    />
                  </Debounce>
                  <Topbar url={url} history={history} />
                  <Layout style={{ flexDirection: "row", overflowX: "hidden" }}>
                    <Sidebar url={url} />
                    <Layout
                      className="isoContentMainLayout"
                      style={{
                        height: height
                      }}
                    >
                      <Content
                        className="isomorphicContent"
                        style={{
                          padding: "70px 0 0",
                          flexShrink: "0",
                          background: "#f1f3f6",
                          position: "relative"
                        }}
                      >
                        {auth && user && user.expired_date ? (
                          <Alert
                            // message={}
                            description={
                              <span>
                                <span style={{ fontWeight: "bold" }}>
                                  {getLanguage({ id: "warning!" }) + " "}
                                </span>
                                {getLanguage({
                                  id: "license.warning",
                                  values: {
                                    time: moment(user.expired_date).format(
                                      "HH:mm:ss"
                                    ),
                                    date: moment(user.expired_date).format(
                                      "DD MMMM YYYY"
                                    )
                                  }
                                })}
                              </span>
                            }
                            type="error"
                            style={{ fontSize: "16px" }}
                            banner
                            closable
                            // afterClose={this.handleClose}
                          />
                        ) : null}
                        <AppRouter url={url} />
                      </Content>
                      <Footer
                        style={{
                          background: "#ffffff",
                          textAlign: "center",
                          borderTop: "1px solid #ededed"
                        }}
                      >
                        {siteConfig.footerText}
                      </Footer>
                    </Layout>
                  </Layout>
                </Layout>
              </AppHolder>
            </ThemeProvider>
          </IdleTimer>
        </IntlProvider>
      </LocaleProvider>
    );
  }
}

export default connect(
  state => ({
    auth: state.auth,
    height: state.app.height
  }),
  { logout, toggleAll }
)(App);
