import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getTopReceivedItemReports({ payload }) {
  const { search, filter } = payload;

  let url = "reports/item-summaries/top-receiveds?";
  // if (search) {
  //   url += "search=" + search;
  // }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    if (filter) {
      const [top_received_item_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/item-summaries/purchases/foreign-keys")
      ]);
  
      // Jika sukses get data top_received_item_reports
      if (top_received_item_reports.status === "success") {
        
        yield put({
          type: "GET_TOP_RECEIVED_ITEM_REPORTS_SUCCESS",
          top_received_item_reports: top_received_item_reports.result,
          filter: filter,
          search: search
        });
        
      } else if (top_received_item_reports.status === "fail") {
        yield put({
          type: "GET_TOP_RECEIVED_ITEM_REPORTS_ERROR",
          error: { message: top_received_item_reports.message }
        });
      } else {
        yield put({
          type: "GET_TOP_RECEIVED_ITEM_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data top_received_item_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_TOP_RECEIVED_ITEM_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_TOP_RECEIVED_ITEM_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_TOP_RECEIVED_ITEM_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/item-summaries/purchases/foreign-keys")
      ]);
  
      yield put({
        type: "GET_TOP_RECEIVED_ITEM_REPORTS_SUCCESS",
        top_received_item_reports: []
      });
  
      // Jika sukses get data top_received_item_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_TOP_RECEIVED_ITEM_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_TOP_RECEIVED_ITEM_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_TOP_RECEIVED_ITEM_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_TOP_RECEIVED_ITEM_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_TOP_RECEIVED_ITEM_REPORTS_REQUEST",
      getTopReceivedItemReports
    )
  ]);
}
