const initState = {
  chart_of_accounts_reports: [],
  gl_types: [],
  filter: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function chartOfAccountReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_CHART_OF_ACCOUNT_REPORTS_REQUEST": {
      return {
        ...state,
        //chart_of_accounts_reports: [],
        loading: true,
        filter: action.payload.filter,
        errorField: null
      };
    }
    case "GET_CHART_OF_ACCOUNT_REPORTS_SUCCESS": {
      return {
        ...state,
        chart_of_accounts_reports: action.chart_of_accounts_reports,
        // filter: action.filter,
        loading: false
      };
    }
    case "GET_CHART_OF_ACCOUNT_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CREATE_CHART_OF_ACCOUNT_REPORT_REQUEST": {
      return {
        ...state,
        chart_of_accounts_report: null,
        errorField: null
      };
    }

    case "GET_CHART_OF_ACCOUNT_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        gl_types: action.gl_types
      };
    }
    case "GET_CHART_OF_ACCOUNT_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
