const initState = {
  accounts: [],
  account_history_sub_ledger_reports: [],
  account_types: [],
  loadingSuppliers: false,
  locations: [],
  departments: [],
  projects: [],
  customers: [],
  suppliers: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function accountHistorySubLedgerReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORTS_REQUEST": {
      return {
        ...state,
        //account_history_sub_ledger_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORTS_SUCCESS": {
      return {
        ...state,
        account_history_sub_ledger_reports: action.account_history_sub_ledger_reports,
        filter: action.filter,
        search: action.search,
        loading: false
      };
    }
    case "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_ACCOUNT_HISTORY_SUB_LEDGER_REPORT_REQUEST": {
      return {
        ...state,
        errorField: null
      };
    }

    case "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        accounts: action.accounts,
        account_types: action.account_types,
        locations: action.locations,
        departments: action.departments,
        projects: action.projects,
        customers: action.customers,
        // suppliers: action.suppliers,
      };
    }
    case "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_SUPPLIERS_BASED_ON_LOCATION_REQUEST": {
      return {
        ...state,
        loadingSuppliers: true,
        id_location: action.payload.id_location
      };
    }
    case "GET_SUPPLIERS_BASED_ON_LOCATION_SUCCESS": {
      return {
        ...state,
        loadingSuppliers: false,
        suppliers: action.suppliers
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
