import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPeriods({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [periods] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "configuration-managements/periods?search=" + search + "&page=" + page),
          ])
        : // Jika filter
          yield all([
            call(get, "configuration-managements/periods?" + filter + "&page=" + page),
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "configuration-managements/periods?search=" + search),
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "configuration-managements/periods?" + filter),
        ]);

    // console.log(periods);
    // console.log(foreign_keys);

    // Jika sukses get data periods
    if (periods.status === "success") {
      // Jika di API menggunakan pagination
      if (periods.result.current_page && search) {
        yield put({
          type: 'GET_PERIODS_SUCCESS',
          periods: periods.result.periods,
          period_year: periods.result.period_year,
          list_period_year: periods.result.list_period_year,
          current_page: periods.result.current_page,
          page_size: periods.result.per_page,
          total: periods.result.total,
          search: search
        });
      }
      else if(periods.result.current_page && filter)
      {
        yield put({
          type: 'GET_PERIODS_SUCCESS',
          periods: periods.result.periods,
          period_year: periods.result.period_year,
          list_period_year: periods.result.list_period_year,
          current_page: periods.result.current_page,
          page_size: periods.result.per_page,
          total: periods.result.total,
          filter: filter
        });
      }
      else if (periods.result.current_page) {
        yield put({
          type: 'GET_PERIODS_SUCCESS',
          periods: periods.result.periods,
          period_year: periods.result.period_year,
          list_period_year: periods.result.list_period_year,
          current_page: periods.result.current_page,
          page_size: periods.result.per_page,
          total: periods.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_PERIODS_SUCCESS',
          periods: periods.result.periods,
          period_year: periods.result.period_year,
          list_period_year: periods.result.list_period_year,
          page_size: null,
          total: null
        });
      }
    } else if (periods.status === "fail") {
      yield put({
        type: 'GET_PERIODS_ERROR', 
        error: periods
      })
    } else {
      yield put({
        type: 'GET_PERIODS_ERROR', 
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'GET_PERIODS_ERROR', 
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createPeriod() {

}

// Store data
export function* storeAutoGeneratePeriod({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "configuration-managements/periods/auto-generate", body);
    // console.log(resultStore);
    if (resultStore.status === "success") { 
      yield all([
        yield put({
          type: 'STORE_PERIOD_SUCCESS',
          success: resultStore
        }),
        yield put({
          type: 'GET_PERIODS_REQUEST',
          payload: {
            filter: 'period_year=' + body.period_year
          }
        })
      ]) 
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_PERIOD_ERROR",
        error: resultStore
      });
    }
    else{
      yield put({
        type: "STORE_PERIOD_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_PERIOD_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

