// Edit data
export function* editPeriod({ payload }) {
  const { id_period } = payload;

  try {
    const [period, foreign_keys] = yield all([
      call(get, "configuration-managements/periods/" + id_period),
      call(get, "configuration-managements/periods/foreign-keys"),
    ]);
  
    // Jika sukses
    if (period.status === "success") {
      yield put({
        type: 'EDIT_PERIOD_SUCCESS',
        period: period.result
      });
    }
    else if(period.status === "fail"){
      yield put({
        type: "EDIT_PERIOD_ERROR",
        error: period
      });
    }
    else{
      yield put({
        type: "EDIT_PERIOD_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  
    // Jika sukses
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_PERIOD_FOREIGN_KEYS_SUCCESS',
        currencies: foreign_keys.result.currencies
      });
    }
    else if(foreign_keys.status === "fail"){
      yield put({
        type: "GET_PERIOD_FOREIGN_KEYS_ERROR",
        error: foreign_keys
      });
    }
    else{
      yield put({
        type: "GET_PERIOD_FOREIGN_KEYS_ERROR",
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_PERIOD_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

