// Show data
export function* showPeriod({ payload }) {
  const { id_period } = payload;

  try {
    const resultShow = yield call(get, "configuration-managements/periods/" + id_period);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_PERIOD_SUCCESS',
        period: resultShow.result
      });
    } 
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* closePeriod({ payload }) {
  const { id_period, body } = payload;
  let param = { ...body, is_closed: 1 };

  try {
    // POST data ke API
    const closeResult = yield call(
      putApi,
      "configuration-managements/periods/" + id_period,
      param
    );

    // Jika sukses
    if (closeResult.status === "success") {
      yield all([
        yield put({
          type: 'CLOSE_REOPEN_PERIOD_SUCCESS',
          success: closeResult
        }),
        yield put({
          type: 'EDIT_PERIOD_REQUEST',
          payload: { id_period: id_period }
        })
      ])
    }
    else if (closeResult.status === "fail" || closeResult.errors) {
      yield put({
        type: 'CLOSE_REOPEN_PERIOD_ERROR',
        error: closeResult
      })
    }
    else {
      yield put({
        type: 'CLOSE_REOPEN_PERIOD_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'CLOSE_REOPEN_PERIOD_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error);
  }
}

