export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_PERIODS_REQUEST', getPeriods),
    yield takeEvery('CREATE_PERIOD_REQUEST', createPeriod),
    yield takeEvery('STORE_AUTO_GENERATE_PERIOD_REQUEST', storeAutoGeneratePeriod),
    yield takeEvery('STORE_ADJUSTING_JOURNAL_ENTRY_REQUEST', storeAdjustingJournalEntry),
    yield takeEvery('EDIT_PERIOD_REQUEST', editPeriod),
    yield takeEvery('UPDATE_PERIOD_REQUEST', updatePeriod),
    yield takeEvery('SHOW_PERIOD_REQUEST', showPeriod),
    yield takeEvery('CLOSE_ALL_PERIOD_REQUEST', closeAllPeriod),
    yield takeEvery('CLOSE_PERIOD_REQUEST', closePeriod),
    yield takeEvery('REOPEN_PERIOD_REQUEST', reopenPeriod),
    yield takeEvery('CLOSE_FA_PERIOD_REQUEST', closeFAPeriod),
    yield takeEvery('REOPEN_FA_PERIOD_REQUEST', reopenFAPeriod)
  ]);
}
