const initState = {
  item_transfer_reports: [],
  to_locations: [],
  from_locations: [],
  adjustment_types: [],
  couriers: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function itemTransferReportsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_ITEM_TRANSFER_REPORTS_REQUEST": {
      return {
        ...state,
        //item_transfer_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_ITEM_TRANSFER_REPORTS_SUCCESS": {
      return {
        ...state,
        item_transfer_reports: action.item_transfer_reports,
        filter: action.filter,
        search: action.search,
        loading: false
      };
    }
    case "GET_ITEM_TRANSFER_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_ITEM_TRANSFER_REPORT_REQUEST": {
      return {
        ...state,
        item_transfer_report: null,
        errorField: null
      };
    }

    case "GET_ITEM_TRANSFER_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        to_locations: action.to_locations,
        from_locations: action.from_locations,
        adjustment_types: action.adjustment_types,
        couriers: action.couriers
      };
    }
    case "GET_ITEM_TRANSFER_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
