import React, { Component } from "react";
import { connect } from "react-redux";
import { Layout } from "antd";
import appActions from "../../redux/app/actions";
// import TopbarLanguage from "./topbarLanguage"
import TopbarUser from "./topbarUser";
import TopbarWrapper from "./topbar.style";
import themes from "../../settings/themes";
import { themeConfig } from "../../settings";

const { Header } = Layout;
const { toggleCollapsed } = appActions;
const customizedTheme = themes[themeConfig.theme];

class Topbar extends Component {
  render() {
    // console.log(this.props);
    const { toggleCollapsed, history } = this.props;
    const collapsed = this.props.collapsed && !this.props.openDrawer;
    const styling = {
      background: customizedTheme.backgroundColorA,
      position: "fixed",
      width: "100%",
      height: 70
    };
    return (
      <TopbarWrapper>
        <Header
          style={styling}
          className={
            collapsed ? "isomorphicTopbar collapsed" : "isomorphicTopbar"
          }
        >
          <div className="isoLeft">
            <button
              className={
                collapsed ? "triggerBtn menuCollapsed" : "triggerBtn menuOpen"
              }
              style={{ color: customizedTheme.textColorA }}
              onClick={toggleCollapsed}
            />
          </div>

          <ul className="isoRight">
            {/* <li
              onClick={() => this.setState({ selectedItem: "addToCart" })}
              className="isoCart"
            >
              <TopbarLanguage />
            </li> */}
            <li
              onClick={() => this.setState({ selectedItem: "user" })}
              className="isoUser"
            >
              <TopbarUser history={history} auth={this.props.auth} />
            </li>
          </ul>
        </Header>
      </TopbarWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { ...state.app, auth: state.auth };
};

export default connect(
  mapStateToProps,
  { toggleCollapsed }
)(Topbar);
