import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getUsers({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [users, roles] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "users?search=" + search + "&page=" + page),
            call(get, "users/roles?is_active=1")
          ])
        : // Jika filter
          yield all([
            call(get, "users?" + filter + "&page=" + page),
            call(get, "users/roles?is_active=1")
          ])
      : search
        ? // Jika search
          yield all([
            call(get, "users?search=" + search),
            call(get, "users/roles?is_active=1")
          ])
        : // Jika tidak ada
          yield all([
            call(get, "users?" + filter),
            call(get, "users/roles?is_active=1")
          ]);

    // console.log(users);

    // Jika sukses get data users
    if (users.status === "success") {
      // Jika di API menggunakan pagination
      if (users.result.current_page && search) {
        yield put({
          type: "GET_USERS_SUCCESS",
          users: users.result.data,
          current_page: users.result.current_page,
          page_size: users.result.per_page,
          total: users.result.total,
          search: search
        });
      }
      else if(users.result.current_page && filter)
      {
        yield put({
          type: 'GET_USERS_SUCCESS',
          users: users.result.data,
          current_page: users.result.current_page,
          page_size: users.result.per_page,
          total: users.result.total,
          filter: filter
        });
      }
      else if (users.result.current_page) {
        yield put({
          type: "GET_USERS_SUCCESS",
          users: users.result.data,
          current_page: users.result.current_page,
          page_size: users.result.per_page,
          total: users.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_USERS_SUCCESS",
          users: users.result,
          page_size: null,
          total: null
        });
      }
    } else if (users.status === "fail") {
      yield put({
        type: "GET_USERS_ERROR",
        error: users
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_USERS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (roles.status === "success") {
      yield put({
        type: "CREATE_USER_SUCCESS",
        roles: roles.result
      });
    }
  } catch (error) {
    yield put({
      type: "GET_USERS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createUser() {
  try {
    const [roles] = yield all([call(get, "users/roles?is_active=1")]);
    //console.log(roles);
    if (roles.status === "success") {
      yield put({
        type: "CREATE_USER_SUCCESS",
        roles: roles.result
      });
    }
    else{
      yield put({
        type: "CREATE_USER_ERROR",
        error: { message: "Check your internet connection" },
      });
    }
  } catch (error) {
    yield put({
      type: "CREATE_USER_ERROR",
      error: { message: "Check your internet connection" },
    });
    bugsnagClient.notify(error)
  } 
}

// Store data
export function* storeUser({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "users", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_USER_SUCCESS",
        success: resultStore
      });
    } else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_USER_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_USER_ERROR",
        error: { message: "Check your internet connection" },
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editUser({ payload }) {
  // console.log("masuk");
  const { id_user } = payload;

  try {
    const [user, roles] = yield all([
      call(get, "users/" + id_user),
      call(get, "users/roles?is_active=1")
    ]);
    //  console.log(user);
    //  console.log(roles);
  
    // Jika sukses
    if (user.status === "success") {
      yield put({
        type: "EDIT_USER_SUCCESS",
        user: user.result
      });
    } else if (user.status === "fail") {
      yield put({
        type: "EDIT_USER_ERROR",
        error: user
      });
    } else {
      yield put({
        type: "EDIT_USER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (roles.status === "success") {
      yield put({
        type: "GET_ROLES_SUCCESS",
        roles: roles.result
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_USER_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateUser({ payload }) {
  const { id_user, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "users/" + id_user,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: "UPDATE_USER_SUCCESS",
        success: resultUpdate
      });
    } else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_USER_ERROR",
        error: resultUpdate
      });
    } else {
      yield put({
        type: "UPDATE_USER_ERROR",
        error: { message: "Check your internet connection" },
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_USER_ERROR",
      error: { message: "Check your internet connection" },
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showUser({ payload }) {
  const { id_user } = payload;

  try {
    const resultShow = yield call(get, "users/" + id_user);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: "SHOW_USER_SUCCESS",
        unit: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateUser({ payload }) {
  const { id_user } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "users/" + id_user,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DEACTIVATE_USER_SUCCESS",
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_USERS_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: "DEACTIVATE_USER_ERROR",
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_USERS_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DEACTIVATE_USER_ERROR",
          error: { message: "Check your internet connection" },
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_USERS_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DEACTIVATE_USER_ERROR",
        error: { message: "Check your internet connection" },
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_USERS_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activateUser({ payload }) {
  const { id_user } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "users/" + id_user,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: "ACTIVATE_USER_SUCCESS",
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_USERS_REQUEST",
          payload: { page: false }
        })
      ]);
    }
    else if(activateResult.status === "fail" || activateResult.errors){
      yield all([
        yield put({
          type: "ACTIVATE_USER_ERROR",
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_USERS_REQUEST",
          payload: { page: false }
        })
      ]);
    }
    else {
      yield all([
        yield put({
          type: "ACTIVATE_USER_ERROR",
          error: { message: "Check your internet connection" },
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_USERS_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "ACTIVATE_USER_ERROR",
        error: { message: "Check your internet connection" },
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_USERS_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_USERS_REQUEST", getUsers),
    yield takeEvery("CREATE_USER_REQUEST", createUser),
    yield takeEvery("STORE_USER_REQUEST", storeUser),
    yield takeEvery("EDIT_USER_REQUEST", editUser),
    yield takeEvery("UPDATE_USER_REQUEST", updateUser),
    yield takeEvery("SHOW_USER_REQUEST", showUser),
    yield takeEvery("DEACTIVATE_USER_REQUEST", deactivateUser),
    yield takeEvery("ACTIVATE_USER_REQUEST", activateUser)
  ]);
}
