import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getCreditMemos({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [credit_memos, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "financials/credit-memos?search=" + search + "&page=" + page),
            call(get, "financials/credit-memos/foreign-keys"),
          ])
        : // Jika filter
          yield all([
            call(get, "financials/credit-memos?" + filter + "&page=" + page),
            call(get, "financials/credit-memos/foreign-keys"),
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "financials/credit-memos?search=" + search),
          call(get, "financials/credit-memos/foreign-keys"),
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "financials/credit-memos?" + filter),
          call(get, "financials/credit-memos/foreign-keys"),
        ]);

    // console.log(credit_memos);
    // console.log(foreign_keys);

    // Jika sukses get data credit_memos
    if (credit_memos.status === "success") {
      // Jika di API menggunakan pagination
      if (credit_memos.result.current_page && search) {
        yield put({
          type: 'GET_CREDIT_MEMOS_SUCCESS',
          credit_memos: credit_memos.result.data,
          current_page: credit_memos.result.current_page,
          page_size: credit_memos.result.per_page,
          total: credit_memos.result.total,
          search: search
        });
      }
      else if(credit_memos.result.current_page && filter)
      {
        yield put({
          type: 'GET_CREDIT_MEMOS_SUCCESS',
          credit_memos: credit_memos.result.data,
          current_page: credit_memos.result.current_page,
          page_size: credit_memos.result.per_page,
          total: credit_memos.result.total,
          filter: filter
        });
      }
      else if (credit_memos.result.current_page) {
        yield put({
          type: 'GET_CREDIT_MEMOS_SUCCESS',
          credit_memos: credit_memos.result.data,
          current_page: credit_memos.result.current_page,
          page_size: credit_memos.result.per_page,
          total: credit_memos.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_CREDIT_MEMOS_SUCCESS',
          credit_memos: credit_memos.result,
          page_size: null,
          total: null
        });
      }
    } else if (credit_memos.status === "fail") {
      yield put({
        type: 'GET_CREDIT_MEMOS_ERROR', 
        error: credit_memos
      })
    } else {
      yield put({
        type: 'GET_CREDIT_MEMOS_ERROR', 
        error: {message: "Check your internet connection"}
      })
    }

    // Jika sukses get data credit_memo types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_CREDIT_MEMO_FOREIGN_KEYS_SUCCESS',
        locations: foreign_keys.result.locations,
        customers: foreign_keys.result.customers,
        cash_banks: foreign_keys.result.cash_banks,
        accounts: foreign_keys.result.accounts,
        cash_flow_types: foreign_keys.result.cash_flow_types,
        currencies: foreign_keys.result.currencies,
        base_currency: foreign_keys.result.base_currency
      });
    }
    else{
      yield put({
        type: 'GET_CREDIT_MEMO_FOREIGN_KEYS_ERROR',
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_CREDIT_MEMOS_ERROR', 
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createCreditMemo() {
  try {
    const [foreign_keys] = yield all([
      call(get, "financials/credit-memos/foreign-keys"),
    ]);
  
    // Jika sukses get data account
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_CREDIT_MEMO_FOREIGN_KEYS_SUCCESS',
        locations: foreign_keys.result.locations,
        customers: foreign_keys.result.customers,
        cash_banks: foreign_keys.result.cash_banks,
        accounts: foreign_keys.result.accounts,
        cash_flow_types: foreign_keys.result.cash_flow_types,
        currencies: foreign_keys.result.currencies,
        base_currency: foreign_keys.result.base_currency
      });
    }
    else{
      yield put({
        type: 'GET_CREDIT_MEMO_FOREIGN_KEYS_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_CREDIT_MEMO_FOREIGN_KEYS_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeCreditMemo({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "financials/credit-memos", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_CREDIT_MEMO_SUCCESS',
        id_credit_memo: resultStore.result.id_credit_memo,
        success: resultStore
      });
    }
    else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_CREDIT_MEMO_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_CREDIT_MEMO_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_CREDIT_MEMO_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editCreditMemo({ payload }) {
  const { id_credit_memo } = payload;

  try {
    const [credit_memo, foreign_keys] = yield all([
      call(get, "financials/credit-memos/" + id_credit_memo),
      call(get, "financials/credit-memos/foreign-keys"),
    ]);
  
    // Jika sukses
    if (credit_memo.status === "success") {
      yield put({
        type: 'EDIT_CREDIT_MEMO_SUCCESS',
        credit_memo: credit_memo.result
      });
    }
    else{
      yield put({
        type: "EDIT_CREDIT_MEMO_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
    
    // Jika sukses get data credit_memo types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_CREDIT_MEMO_FOREIGN_KEYS_SUCCESS',
        locations: foreign_keys.result.locations,
        customers: foreign_keys.result.customers,
        cash_banks: foreign_keys.result.cash_banks,
        accounts: foreign_keys.result.accounts,
        cash_flow_types: foreign_keys.result.cash_flow_types,
        currencies: foreign_keys.result.currencies,
        base_currency: foreign_keys.result.base_currency
      });
    }
    else{
      yield put({
        type: 'GET_CREDIT_MEMO_FOREIGN_KEYS_ERROR',
        // error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_CREDIT_MEMO_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateCreditMemo({ payload }) {
  const { id_credit_memo, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "financials/credit-memos/" + id_credit_memo,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: 'UPDATE_CREDIT_MEMO_SUCCESS',
          success: resultUpdate
        }),
        yield put({
          type: 'EDIT_CREDIT_MEMO_REQUEST',
          payload: { id_credit_memo }
        })
      ])
      
    }
    else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_CREDIT_MEMO_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_CREDIT_MEMO_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_CREDIT_MEMO_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showCreditMemo({ payload }) {
  const { id_credit_memo } = payload;

  try {
    const resultShow = yield call(get, "financials/credit-memos/" + id_credit_memo);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_CREDIT_MEMO_SUCCESS',
        credit_memo: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Decancel data (mengubah is_active => 0)
export function* processCreditMemo({ payload }) {
  const { id_credit_memo } = payload;

  try {
    // POST data ke API
    const processResult = yield call(
      putApi,
      "financials/credit-memos/process-transaction/" + id_credit_memo,
      // body
    );

    // Jika sukses
    if (processResult.status === "success") {
      yield all([
        yield put({
          type: 'PROCESS_CREDIT_MEMO_SUCCESS',
          success: processResult
        }),
        yield put({
          type: 'EDIT_CREDIT_MEMO_REQUEST',
          payload: { id_credit_memo }
        })
      ])
    }
    else if(processResult.status === "fail"){
      yield put({
        type: 'PROCESS_CREDIT_MEMO_ERROR',
        error: processResult
      })
    }
    else{
      yield put({
        type: 'PROCESS_CREDIT_MEMO_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'PROCESS_CREDIT_MEMO_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error);
  }
}

