// Upload Document
export function* uploadCreditMemoDocument({ payload }) {
  const { id_credit_memo, body } = payload;
  let data = {
    ...body,
    id_credit_memo 
  }

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "financials/credit-memos/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: 'UPLOAD_CREDIT_MEMO_DOCUMENT_SUCCESS',
          success: uploadResult
        }),
        yield put({
          type: 'EDIT_CREDIT_MEMO_REQUEST',
          payload: { id_credit_memo }
        })
      ])
    }
    else if(uploadResult.errors){
      yield put({
        type: 'UPLOAD_CREDIT_MEMO_DOCUMENT_ERROR',
        error: {message: uploadResult.errors.document[0]}
      })
    }
    else if(uploadResult.status === "fail"){
      yield put({
        type: 'UPLOAD_CREDIT_MEMO_DOCUMENT_ERROR',
        error: uploadResult
      })
    }
    else{
      yield put({
        type: 'UPLOAD_CREDIT_MEMO_DOCUMENT_ERROR',
        error: {message: "Check your internet connection"}
      })
    }

    // }
  } catch (error) {
    yield put({
      type: 'UPLOAD_CREDIT_MEMO_DOCUMENT_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error);
  }
}

// Download Document
export function* downloadCreditMemoDocument({ payload }) {
  const { id_credit_memo_document } = payload;

  try {
    // POST ke API
    // console.log("masukk")
    yield call(
      get,
      "financials/credit-memos/documents/download/" + id_credit_memo_document,
    );
    // }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deleteCreditMemoDocument({ payload }) {
  const { id_credit_memo_document } = payload;
  // const body = { is_active: 0 };

  try {
    // POST data ke API
    const deleteResult = yield call(
      putApi,
      "financials/credit-memos/documents/" + id_credit_memo_document,
    //  body
    );

    // Jika sukses
    if (deleteResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_CREDIT_MEMO_DOCUMENT_SUCCESS",
          success: deleteResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'EDIT_CREDIT_MEMO_REQUEST',
          payload: { id_credit_memo: deleteResult.result.id_credit_memo }
        })
      ]);
    }
    else if (deleteResult.status === "fail") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_CREDIT_MEMO_DOCUMENT_ERROR",
          error: deleteResult
        }),
      ]);
    }
    else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_CREDIT_MEMO_DOCUMENT_ERROR",
          error: {message: "Check your internet connection"}
        }),
      ]);
    }
  } catch (error) {
    yield put({
      type: "DELETE_CREDIT_MEMO_DOCUMENT_ERROR",
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error);
  }
}


export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_CREDIT_MEMOS_REQUEST', getCreditMemos),
    yield takeEvery('CREATE_CREDIT_MEMO_REQUEST', createCreditMemo),
    yield takeEvery('STORE_CREDIT_MEMO_REQUEST', storeCreditMemo),
    yield takeEvery('EDIT_CREDIT_MEMO_REQUEST', editCreditMemo),
    yield takeEvery('UPDATE_CREDIT_MEMO_REQUEST', updateCreditMemo),
    yield takeEvery('SHOW_CREDIT_MEMO_REQUEST', showCreditMemo),
    yield takeEvery('PROCESS_CREDIT_MEMO_REQUEST', processCreditMemo),
    yield takeEvery('CANCEL_CREDIT_MEMO_REQUEST', cancelCreditMemo),
    yield takeEvery('UPLOAD_CREDIT_MEMO_DOCUMENT_REQUEST', uploadCreditMemoDocument),
    yield takeEvery('DOWNLOAD_CREDIT_MEMO_DOCUMENT_REQUEST', downloadCreditMemoDocument),
    yield takeEvery('DELETE_CREDIT_MEMO_DOCUMENT_REQUEST', deleteCreditMemoDocument),

  ]);
}
