import { all, takeEvery, put, call, takeLatest } from "redux-saga/effects";
import { get, put as putApi , post} from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getAppConfigs() {
  try {
    const [app_configs] = 
      yield all([
        call(get, "settings/app-configs"),
      ])
        
    // console.log(app_configs);

    // Jika sukses get data app_configs dan account
    if (app_configs.status === "success") {
      yield put({
        type: "GET_APP_CONFIGS_SUCCESS",
        app_configs: app_configs.result.app_configs,
        countries: app_configs.result.countries,
        taxes: app_configs.result.taxes,
        customer_types: app_configs.result.customer_types,
        customers: app_configs.result.customers,
        locations: app_configs.result.locations,
        raptor_connections: app_configs.result.raptor_connections,
        raptor_connection_location_array: app_configs.result.raptor_connection_location_array
      });
    } 
    else if(app_configs.status === "fail"){
      yield put({
        type: "GET_APP_CONFIGS_ERROR",
        error: app_configs
      });
    }
    else {
      yield put({
        type: "GET_APP_CONFIGS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_APP_CONFIGS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateAppConfig({payload}) {
  const { body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "settings/app-configs/update",
      { app_configs: body }
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: "UPDATE_APP_CONFIG_SUCCESS",
          success: resultUpdate
        }),
        yield put({
          type : "GET_APP_CONFIGS_REQUEST"
        })
      ]);
    } else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_APP_CONFIG_ERROR",
        error: resultUpdate
      });
    } else {
      yield put({
        type: "UPDATE_APP_CONFIG_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_APP_CONFIG_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}


// Store Raptor Connection data
export function* storeRaptorConnection({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "raptor/connections", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield all([
        yield put({
          type: "UPDATE_APP_CONFIG_SUCCESS",
          success: resultStore
        }),
        yield put({
          type : "GET_APP_CONFIGS_REQUEST"
        })
      ]) 
    }
    else if (resultStore.errors || resultStore.status === "fail") {
      yield put({
        type: "UPDATE_APP_CONFIG_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "UPDATE_APP_CONFIG_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_APP_CONFIG_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateRaptorConnection({ payload }) {
  const { id_raptor_connection, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "raptor/connections/" + id_raptor_connection,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield all([
          yield put({
            type: "UPDATE_APP_CONFIG_SUCCESS",
            success: resultUpdate
          }),
          yield put({
            type : "GET_APP_CONFIGS_REQUEST"
          })
        ]) 
      ])
    } 
    else if (resultUpdate.errors || resultUpdate.status ==="fail") {
      yield put({
        type: "UPDATE_RAPTOR_CONNECTION_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_RAPTOR_CONNECTION_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_RAPTOR_CONNECTION_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* checkRaptorConnection({payload}) {
  const { body } = payload;

  try {
    const check = yield call(
      post,
      "raptor/check-connection",
      body
    );
    // console.log(resultUpdate);
    if (check.status === "success") {
      yield put({
        type: "CHECK_RAPTOR_CONNECTION_SUCCESS",
        success: check
      })
    } else if (check.status === "fail") {
      yield put({
        type: "CHECK_RAPTOR_CONNECTION_ERROR",
        error: check
      });
    } else {
      yield put({
        type: "CHECK_RAPTOR_CONNECTION_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "CHECK_RAPTOR_CONNECTION_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_APP_CONFIGS_REQUEST", getAppConfigs),
    yield takeEvery("STORE_RAPTOR_CONNECTION_REQUEST", storeRaptorConnection),
    yield takeEvery("UPDATE_RAPTOR_CONNECTION_REQUEST", updateRaptorConnection),
    yield takeEvery("UPDATE_APP_CONFIG_REQUEST", updateAppConfig),
    yield takeLatest("CHECK_RAPTOR_CONNECTION_REQUEST", checkRaptorConnection)
  ]);
}
