const initState = {
  adjustment_types: [],
  accounts: [],
  loading: true,
  loadingEdit:true,
  loadingFilterAccounts: true,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function gLAdjustmentTypeReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_GL_ADJUSTMENT_TYPES_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField:null,
        loadingFilterAccounts: true,
      };
    }
    case 'GET_GL_ADJUSTMENT_TYPES_SUCCESS': {
      return {
        ...state,
        adjustment_types: action.adjustment_types,
        accounts: action.accounts,
        loading: false,
        loadingFilterAccounts: false,
      };
    }
    case 'GET_GL_ADJUSTMENT_TYPES_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case 'UPDATE_GL_ADJUSTMENT_TYPE_REQUEST': {
      return {
        ...state,
        loadingSubmit: true,
        errorField:null,
      };
    }
    case 'UPDATE_GL_ADJUSTMENT_TYPE_SUCCESS': {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }

    case 'UPDATE_GL_ADJUSTMENT_TYPE_ERROR': {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }

    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };

    default:
      return state;
  }
}
