const initState = {
  top_item_reports: [],
  top_item_amt_reports: [],
  top_item_qty_reports: [],
  locations: [],
  customers: [],
  customer_types: [],
  item_categories: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function topItemReportsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_TOP_ITEM_REPORTS_REQUEST": {
      return {
        ...state,
        //top_item_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_TOP_ITEM_REPORTS_SUCCESS": {
      return {
        ...state,
        top_item_reports: action.top_item_reports,
        // filter: action.filter,
        // search: action.search,
        loading: false
      };
    }
    case "GET_TOP_ITEM_AMT_REPORTS_SUCCESS": {
      return {
        ...state,
        top_item_amt_reports: action.top_item_amt_reports,
        // filter: action.filter,
        // search: action.search,
        loading: false
      };
    }
    case "GET_TOP_ITEM_QTY_REPORTS_SUCCESS": {
      return {
        ...state,
        top_item_qty_reports: action.top_item_qty_reports,
        // filter: action.filter,
        // search: action.search,
        loading: false
      };
    }
    case "GET_TOP_ITEM_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "GET_TOP_ITEM_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        customers: action.customers,
        customer_types: action.customer_types,
        item_categories: action.item_categories
      };
    }
    case "GET_TOP_ITEM_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
