import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getSalesReturnDetailReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/sales-returns/details?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if (filter) {
      const [sales_return_detail_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/sales-returns/foreign-keys")
      ]);
  
      // Jika sukses get data sales_return_detail_reports
      if (sales_return_detail_reports.status === "success") {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_SALES_RETURN_DETAIL_REPORTS_SUCCESS",
          sales_return_detail_reports: sales_return_detail_reports.result,
          filter: filter,
          search: search
        });
      } else if (sales_return_detail_reports.status === "fail") {
        yield put({
          type: "GET_SALES_RETURN_DETAIL_REPORTS_ERROR",
          error: sales_return_detail_reports
        });
      } else {
        yield put({
          type: "GET_SALES_RETURN_DETAIL_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data sales_return_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_SALES_RETURN_DETAIL_REPORT_FOREIGN_KEYS_SUCCESS",
          accounts: foreign_keys.result.accounts,
          base_currency: foreign_keys.result.base_currency,
          cash_banks: foreign_keys.result.cash_banks,
          cash_flow_types: foreign_keys.result.cash_flow_types,
          customers: foreign_keys.result.customers,
          currencies: foreign_keys.result.currencies,
          departments: foreign_keys.result.departments,
          locations: foreign_keys.result.locations,
          messages: foreign_keys.result.messages,
          payment_categories: foreign_keys.result.payment_categories,
          payment_terms: foreign_keys.result.payment_terms,
          projects: foreign_keys.result.projects,
          return_reasons: foreign_keys.result.return_reasons,
          taxes: foreign_keys.result.taxes
        });
      } else {
        yield put({
          type: "GET_SALES_RETURN_DETAIL_REPORT_FOREIGN_KEYS_ERROR"
          //error: {message: "Check your internet connection"}
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/sales-returns/foreign-keys")
      ]);
  
      yield put({
        type: "GET_SALES_RETURN_DETAIL_REPORTS_SUCCESS",
        sales_return_detail_reports: [],
        total_amount: null,
        total_discount: null,
        total_qty: null,
        total_tax: null,
        total_item: null,
        total: null
      });
  
      // Jika sukses get data sales_return_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_SALES_RETURN_DETAIL_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          customers: foreign_keys.result.customers,
          currencies: foreign_keys.result.currencies,
          taxes: foreign_keys.result.taxes,
          cash_banks: foreign_keys.result.cash_banks,
          accounts: foreign_keys.result.accounts,
          cash_flow_types: foreign_keys.result.cash_flow_types,
          payment_categories: foreign_keys.result.payment_categories,
          payment_terms: foreign_keys.result.payment_terms,
          departments: foreign_keys.result.departments,
          projects: foreign_keys.result.projects,
          base_currency: foreign_keys.result.base_currency,
          messages: foreign_keys.result.messages,
          return_reasons: foreign_keys.result.return_reasons
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_SALES_RETURN_DETAIL_REPORT_FOREIGN_KEYS_ERROR",
          error: foreign_keys
        });
      } else {
        yield put({
          type: "GET_SALES_RETURN_DETAIL_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_SALES_RETURN_DETAIL_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_SALES_RETURN_DETAIL_REPORTS_REQUEST",
      getSalesReturnDetailReports
    )
  ]);
}
