const initState = {
  id_issue_receipt: null,
  issueReceipts: [],
  issueReceipt: null,
  items: [],
  transactions: [],
  adjustmentTypes: [],
  accounts: [],
  locations: [],
  departments: [],
  projects: [],
  search: null,
  filter: null,
  loading: false,
  loadingSubmit: false,
  loadingItems: false,
  loadingForeign: false,
  loadingTransactions: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function issueReceiptsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_ISSUE_RECEIPTS_REQUEST": {
      return {
        ...state,
        //issueReceipts: [],
        loading: true,
        loadingForeign: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_ISSUE_RECEIPTS_SUCCESS": {
      return {
        ...state,
        issueReceipts: action.issueReceipts,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
        loadingForeign: false
      };
    }
    case "GET_ISSUE_RECEIPTS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "CREATE_ISSUE_RECEIPT_REQUEST": {
      return {
        ...state,
        id_issue_receipt: null,
        loadingForeign: true,
        search: null,
        errorField: null,
        current_page: null,
        page_size: null,
        total: null
      };
    }
    case "GET_FILTER_ITEMS_IR_REQUEST": {
      return {
        ...state,
        loadingItems: true,
        search: action.search
      };
    }
    case "GET_FILTER_ITEMS_IR_SUCCESS": {
      return {
        ...state,
        loadingItems: false,
        items: action.items
      };
    }
    case "GET_ISSUE_RECEIPT_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_ISSUE_RECEIPT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loadingForeign: false,
        locations: action.locations,
        accounts: action.accounts,
        adjustmentTypes: action.adjustmentTypes,
        departments: action.departments,
        projects: action.projects
      };
    }
    case "GET_ISSUE_RECEIPT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "GET_TRANSACTIONS_REQUEST": {
      return {
        ...state,
        loadingTransactions: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page
      };
    }
    case "GET_TRANSACTIONS_SUCCESS": {
      return {
        ...state,
        loadingTransactions: false,
        transactions: action.transactions,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total
      };
    }
    case "GET_TRANSACTIONS_ERROR": {
      return {
        ...state,
        loadingTransactions: false,
        error: action.error.message
      };
    }
    case "STORE_ISSUE_RECEIPT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_ISSUE_RECEIPT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_issue_receipt: action.id_issue_receipt
      };
    }
    case "STORE_ISSUE_RECEIPT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_ISSUE_RECEIPT_REQUEST": {
      return {
        ...state,
        id_issue_receipt: null,
        loading: true,
        loadingTransactions: true,
        search: null,
        errorField: null,
        current_page: null,
        page_size: null,
        total: null
      };
    }
    case "EDIT_ISSUE_RECEIPT_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        issueReceipt: action.issueReceipt
      };
    }
    case "EDIT_ISSUE_RECEIPT_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_ISSUE_RECEIPT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_ISSUE_RECEIPT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_issue_receipt: action.id_issue_receipt
      };
    }
    case "UPDATE_ISSUE_RECEIPT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_ISSUE_RECEIPT_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_ISSUE_RECEIPT_SUCCESS": {
      return {
        ...state,
        loading: false,
        issueReceipts: action.issueReceipts
      };
    }
    case "CONFIRM_ISSUE_RECEIPT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CONFIRM_ISSUE_RECEIPT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CONFIRM_ISSUE_RECEIPT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CANCEL_ISSUE_RECEIPT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CANCEL_ISSUE_RECEIPT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CANCEL_ISSUE_RECEIPT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPLOAD_ISSUE_RECEIPT_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_ISSUE_RECEIPT_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_ISSUE_RECEIPT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_ISSUE_RECEIPT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_ISSUE_RECEIPT_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_ISSUE_RECEIPT_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_ISSUE_RECEIPT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    default:
      return state;
  }
}
