
const initState = {
  item_grouping_reports: [],
  locations: [],
  items: [],
  search: null,
  filter: null,
  loading: false,
  loadingItem: false,
  // current_page: 1,
  // page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
};

export default function itemGroupingReportsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_ITEM_GROUPING_REPORTS_REQUEST': {
      return {
        ...state,
        //item_grouping_reports: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null,
      };
    }
    case 'GET_ITEM_GROUPING_REPORTS_SUCCESS': {
      return {
        ...state,
        item_grouping_reports: action.item_grouping_reports,
        // current_page: action.current_page,
        // page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_ITEM_GROUPING_REPORTS_ERROR': {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      }
    }
    case 'CREATE_ITEM_GROUPING_REPORT_REQUEST': {
      return {
        ...state,
        item_grouping_report: null,
        errorField: null,
      };
    }

    case 'GET_ITEM_GROUPING_REPORT_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        //pt_detail_search_filter: action.pt_detail_search_filter
      };
    }
    case 'GET_ITEM_GROUPING_REPORT_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_ITEMS_FOR_ITEM_GROUPING_REPORT_REQUEST":
      return {
        ...state,
        loadingItem: true
      };
    case "GET_ITEMS_FOR_ITEM_GROUPING_REPORT_SUCCESS":
      return {
        ...state,
        items: action.items,
        loadingItem: false
      };
    case "GET_ITEMS_FOR_ITEM_GROUPING_REPORT_ERROR": {
      return {
        ...state,
        loadingItem: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
