import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

export function* getAccountTypes() {
  try {
    // Cek apakah ada pagination di request atau tidak
    const account_types = yield call(get, "accounts/types");

    // console.log(account_types);
    // Jika sukses get data
    if (account_types.status === "success") {
      yield put({
        type: "GET_ACCOUNT_TYPES_SUCCESS",
        accountTypes: account_types.result
      });
    } else if (account_types.status === "fail" || account_types.errors) {
      yield put({
        type: "GET_ACCOUNT_TYPES_ERROR",
        error: account_types
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_ACCOUNT_TYPES_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    // Set message ke session storage
    yield put({
      type: "GET_ACCOUNT_TYPES_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error);
  }
  
}

export default function* rootSaga() {
  yield all([yield takeEvery("GET_ACCOUNT_TYPES_REQUEST", getAccountTypes)]);
}
