const initState = {
  stock_card_reports: [],
  item_categories: [],
  locations: [],
  filter: null,
  filter_select: [],
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function stockCardReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_STOCK_CARD_REPORTS_REQUEST": {
      return {
        ...state,
        //stock_card_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_STOCK_CARD_REPORTS_SUCCESS": {
      return {
        ...state,
        stock_card_reports: action.stock_card_reports,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_STOCK_CARD_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_STOCK_CARD_REPORT_REQUEST": {
      return {
        ...state,
        stock_card_report: null,
        errorField: null
      };
    }

    case "GET_STOCK_CARD_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        item_categories: action.item_categories,
        locations: action.locations
      };
    }
    case "GET_STOCK_CARD_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
