// Decancel data (mengubah is_active => 0)
export function* processJobCosting({ payload }) {
  const { id_job_costing } = payload;

  try {
    // POST data ke API
    const processResult = yield call(
      putApi,
      "inventories/job-costings/process-transaction/" + id_job_costing,
      // body
    );

    // Jika sukses
    if (processResult.status === "success") {
      yield all([
        yield put({
          type: 'PROCESS_JOB_COSTING_SUCCESS',
          success: processResult
        }),
        yield put({
          type: 'EDIT_JOB_COSTING_REQUEST',
          payload: { id_job_costing }
        })
      ])
    }
    else if(processResult.status === "fail"){
      yield put({
        type: 'PROCESS_JOB_COSTING_ERROR',
        error: processResult
      })
    }
    else{
      yield put({
        type: 'PROCESS_JOB_COSTING_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'PROCESS_JOB_COSTING_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error);
  }
}

