// Upload Document
export function* uploadJobCostingDocument({ payload }) {
  const { id_job_costing, body } = payload;
  let data = {
    ...body,
    id_job_costing 
  }

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "inventories/job-costings/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: 'UPLOAD_JOB_COSTING_DOCUMENT_SUCCESS',
          success: uploadResult
        }),
        yield put({
          type: 'EDIT_JOB_COSTING_REQUEST',
          payload: { id_job_costing }
        })
      ])
    }
    else if(uploadResult.errors){
      yield put({
        type: 'UPLOAD_JOB_COSTING_DOCUMENT_ERROR',
        error: {message: uploadResult.errors.document[0]}
      })
    }
    else if(uploadResult.status === "fail"){
      yield put({
        type: 'UPLOAD_JOB_COSTING_DOCUMENT_ERROR',
        error: uploadResult
      })
    }
    else{
      yield put({
        type: 'UPLOAD_JOB_COSTING_DOCUMENT_ERROR',
        error: {message: "Check your internet connection"}
      })
    }

    // }
  } catch (error) {
    yield put({
      type: 'UPLOAD_JOB_COSTING_DOCUMENT_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error);
  }
}

// Download Document
export function* downloadJobCostingDocument({ payload }) {
  const { id_job_costing_document } = payload;

  try {
    // POST ke API
    // console.log("masukk")
    yield call(
      get,
      "inventories/job-costings/documents/download/" + id_job_costing_document,
    );
    // }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deleteJobCostingDocument({ payload }) {
  const { id_job_costing_document } = payload;
  // const body = { is_active: 0 };

  try {
    // POST data ke API
    const deleteResult = yield call(
      putApi,
      "inventories/job-costings/documents/" + id_job_costing_document,
    //  body
    );

    // Jika sukses
    if (deleteResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_JOB_COSTING_DOCUMENT_SUCCESS",
          success: deleteResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'EDIT_JOB_COSTING_REQUEST',
          payload: { id_job_costing: deleteResult.result.id_job_costing }
        })
      ]);
    }
    else if (deleteResult.status === "fail") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_JOB_COSTING_DOCUMENT_ERROR",
          error: deleteResult
        }),
      ]);
    }
    else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_JOB_COSTING_DOCUMENT_ERROR",
          error: {message: "Check your internet connection"}
        }),
      ]);
    }
  } catch (error) {
    yield put({
      type: "DELETE_JOB_COSTING_DOCUMENT_ERROR",
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error);
  }
}


export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_JOB_COSTINGS_REQUEST', getJobCostings),
    yield takeEvery('GET_IMPORT_JOB_COSTINGS_REQUEST', getImportJobCostings),
    yield takeEvery("GET_FILTER_ITEMS_JC_REQUEST", getItems),
    yield takeEvery('CREATE_JOB_COSTING_REQUEST', createJobCosting),
    yield takeEvery('STORE_JOB_COSTING_REQUEST', storeJobCosting),
    yield takeEvery('EDIT_JOB_COSTING_REQUEST', editJobCosting),
    yield takeEvery('UPDATE_JOB_COSTING_REQUEST', updateJobCosting),
    yield takeEvery('SHOW_JOB_COSTING_REQUEST', showJobCosting),
    yield takeEvery('PROCESS_JOB_COSTING_REQUEST', processJobCosting),
    yield takeEvery('CANCEL_JOB_COSTING_REQUEST', cancelJobCosting),
    yield takeEvery('UPLOAD_JOB_COSTING_DOCUMENT_REQUEST', uploadJobCostingDocument),
    yield takeEvery('DOWNLOAD_JOB_COSTING_DOCUMENT_REQUEST', downloadJobCostingDocument),
    yield takeEvery('DELETE_JOB_COSTING_DOCUMENT_REQUEST', deleteJobCostingDocument),

  ]);
}
