import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getSalesInvoices({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [salesInvoices, foreign] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "sales/sales-invoices?search=" + search + "&page=" + page),
            call(get, "sales/sales-invoices/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "sales/sales-invoices?" + filter + "&page=" + page),
            call(get, "sales/sales-invoices/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "sales/sales-invoices?search=" + search),
          call(get, "sales/sales-invoices/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "sales/sales-invoices?" + filter),
          call(get, "sales/sales-invoices/foreign-keys")
        ]);

    // console.log(salesInvoices);

    // Jika sukses get data salesInvoices
    if (salesInvoices.status === "success") {
      // Jika di API menggunakan pagination
      if (salesInvoices.result.current_page && search) {
        yield put({
          type: "GET_SALES_INVOICES_SUCCESS",
          salesInvoices: salesInvoices.result.data,
          current_page: salesInvoices.result.current_page,
          page_size: salesInvoices.result.per_page,
          total: salesInvoices.result.total,
          search: search
        });
      } else if (salesInvoices.result.current_page && filter) {
        yield put({
          type: "GET_SALES_INVOICES_SUCCESS",
          salesInvoices: salesInvoices.result.data,
          current_page: salesInvoices.result.current_page,
          page_size: salesInvoices.result.per_page,
          total: salesInvoices.result.total,
          filter: filter
        });
      } else if (salesInvoices.result.current_page) {
        yield put({
          type: "GET_SALES_INVOICES_SUCCESS",
          salesInvoices: salesInvoices.result.data,
          current_page: salesInvoices.result.current_page,
          page_size: salesInvoices.result.per_page,
          total: salesInvoices.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_SALES_INVOICES_SUCCESS",
          salesInvoices: salesInvoices.result,
          page_size: null,
          total: null
        });
      }
    } else if (salesInvoices.status === "fail") {
      // Set message ke session storage
      yield put({
        type: "GET_SALES_INVOICES_ERROR",
        error: { message: salesInvoices.message }
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_SALES_INVOICES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      // console.log(foreign.result.delivery_orders);
      yield put({
        type: "GET_SALES_INVOICE_FOREIGN_KEYS_SUCCESS",
        // baseCurrency: foreign.result.base_currency,
        accounts: foreign.result.accounts,
        customers: foreign.result.customers,
        deliveryOrders: foreign.result.delivery_orders,
        departments: foreign.result.departments,
        infoMessages: foreign.result.info_messages,
        locations: foreign.result.locations,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        prevTransConfirm: foreign.result.delivery_order_info,
        projects: foreign.result.projects,
        couriers: foreign.result.couriers,
        salesman: foreign.result.salesman,
        taxes: foreign.result.taxes
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_SALES_INVOICE_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_SALES_INVOICE_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_SALES_INVOICES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Get Items data
export function* getItemsForSI({ payload }) {
  const {
    id_customer,
    id_location,
    id_payment_category,
    date,
    search
  } = payload;
  let filter = "items?is_active=1&limit=1";
  filter = search
    ? `${filter}&search=${search}&id_customer=${id_customer}&id_location=${id_location}&id_payment_category=${id_payment_category}&transaction_date=${date}`
    : `${filter}&id_customer=${id_customer}&id_location=${id_location}&id_payment_category=${id_payment_category}&transaction_date=${date}`;

  // let filter =
  //   "items?is_active=1&limit=1&id_location=000003571115489901069210955224&id_customer=102003300115501990983747809401&id_payment_category&transaction_date=2019-06-12";

  try {
    const items = yield call(get, filter);

    if (items.status === "success") {
      yield put({
        type: "GET_FILTER_ITEMS_BY_CUSTOMER_SI_SUCCESS",
        discountBills: items.result.discount_bills,
        discountItems: items.result.discount_items,
        items: items.result.items
      });
    } else if (items.status === "fail") {
      yield put({
        type: "GET_FILTER_ITEMS_BY_CUSTOMER_SI_ERROR",
        error: { message: items.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_ITEMS_BY_CUSTOMER_SI_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_ITEMS_BY_CUSTOMER_SI_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Get Items with discount
export function* getItemDiscount({ payload }) {
  const { body } = payload;

  try {
    const itemDiscount = yield call(
      post,
      "sales/sales-invoices/discount-price",
      body
    );
  
    if (itemDiscount.status === "success") {
      yield put({
        type: "GET_DISCOUNT_SI_SUCCESS",
        itemDiscount: itemDiscount.result.sales_invoice_details
      });
    } else if (itemDiscount.status === "fail") {
      yield put({
        type: "GET_DISCOUNT_SI_ERROR",
        itemDiscount: body,
        error: {
          message: itemDiscount.message
        }
      });
    } else {
      yield put({
        type: "GET_DISCOUNT_SI_ERROR",
        itemDiscount: body,
        error: {
          message: itemDiscount.message
            ? itemDiscount.message
            : "Check your internet connection"
        }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_DISCOUNT_SI_ERROR",
      itemDiscount: body,
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

