export function* getTransactions({ payload }) {
  const { page, search, filter } = payload;
  // console.log(filter);

  try {
    const transactions = filter
      ? yield call(
          get,
          "sales/sales-invoices?get_old_trans=1&" + filter + "&page=" + page
        )
      : search
      ? yield call(
          get,
          "sales/sales-invoices?get_old_trans=1&search=" +
            search +
            "&page=" +
            page
        )
      : yield call(get, "sales/sales-invoices?get_old_trans=1" + "&page=" + page);

    if (transactions.status === "success") {
      yield put({
        type: "GET_FILTER_SALES_INVOICES_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_FILTER_SALES_INVOICES_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_SALES_INVOICES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_SALES_INVOICES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export function* getDeliveryOrders({ payload }) {
  const { page, search, filter } = payload;

  try {
    const deliveryOrders = filter
      ? search
        ? yield call(
            get,
            "sales/delivery-orders?get_old_trans=1&status=2&" +
              filter +
              "&search=" +
              search +
              "&page=" +
              page
          )
        : yield call(
            get,
            "sales/delivery-orders?get_old_trans=1&status=2&" +
              filter +
              "&page=" +
              page
          )
      : search
      ? yield call(
          get,
          "sales/delivery-orders?get_old_trans=1&status=2&search=" +
            search +
            "&page=" +
            page
        )
      : yield call(
          get,
          "sales/delivery-orders?get_old_trans=1&status=2" + "&page=" + page
        );

    if (deliveryOrders.status === "success") {
      yield put({
        type: "GET_FILTER_DELIVERY_ORDERS_SUCCESS",
        deliveryOrders: deliveryOrders.result.data,
        current_page_do: deliveryOrders.result.current_page,
        page_size_do: deliveryOrders.result.per_page,
        total_do: deliveryOrders.result.total
      });
    } else if (deliveryOrders.status === "fail") {
      yield put({
        type: "GET_FILTER_DELIVERY_ORDERS_ERROR",
        error: { message: deliveryOrders.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_DELIVERY_ORDERS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_DELIVERY_ORDERS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeSalesInvoice({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "sales/sales-invoices", body);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_SALES_INVOICE_SUCCESS",
        success: { message: resultStore.message },
        info: resultStore.info,
        id_sales_invoice: resultStore.result.id_sales_invoice
      });
    } else if (resultStore.errors) {
      yield put({
        type: "STORE_SALES_INVOICE_ERROR",
        error: resultStore
      });
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_SALES_INVOICE_ERROR",
        error: { message: resultStore.message }
      });
    } else {
      yield put({
        type: "STORE_SALES_INVOICE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_SALES_INVOICE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

