export function* editSalesInvoice({ payload }) {
  const { id_sales_invoice } = payload;

  try {
    const [salesInvoice, foreign] = yield all([
      call(get, "sales/sales-invoices/" + id_sales_invoice),
      call(get, "sales/sales-invoices/foreign-keys")
    ]);
  
    // Jika sukses
    if (salesInvoice.status === "success") {
      yield put({
        type: "EDIT_SALES_INVOICE_SUCCESS",
        salesInvoice: salesInvoice.result
      });
    } else if (salesInvoice.status === "fail") {
      yield put({
        type: "EDIT_SALES_INVOICE_ERROR",
        error: { message: salesInvoice.message }
      });
    } else {
      yield put({
        type: "EDIT_SALES_INVOICE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (foreign.status === "success") {
      yield put({
        type: "GET_SALES_INVOICE_FOREIGN_KEYS_SUCCESS",
        accounts: foreign.result.accounts,
        customers: foreign.result.customers,
        deliveryOrders: foreign.result.delivery_orders,
        departments: foreign.result.departments,
        infoMessages: foreign.result.info_messages,
        locations: foreign.result.locations,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        prevTransConfirm: foreign.result.delivery_order_info,
        projects: foreign.result.projects,
        couriers: foreign.result.couriers,
        salesman: foreign.result.salesman,
        taxes: foreign.result.taxes
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_SALES_INVOICE_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_SALES_INVOICE_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_SALES_INVOICE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateSalesInvoice({ payload }) {
  const { id_sales_invoice, body } = payload;
  // console.log(body);

  try {
    const resultUpdate = yield call(
      putApi,
      "sales/sales-invoices/" + id_sales_invoice,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        put({
          type: "UPDATE_SALES_INVOICE_SUCCESS",
          success: { message: resultUpdate.message },
          info: resultUpdate.info,
          id_sales_invoice: id_sales_invoice
        }),
        put({
          type: "EDIT_SALES_INVOICE_REQUEST",
          payload: {
            id_sales_invoice: id_sales_invoice
          }
        })
      ]);
    } else if (resultUpdate.errors) {
      yield all([
        yield put({
          type: "UPDATE_SALES_INVOICE_ERROR",
          error: resultUpdate
        }),
        yield put({
          type: "EDIT_SALES_INVOICE_REQUEST",
          payload: {
            id_sales_invoice: id_sales_invoice
          }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_SALES_INVOICE_ERROR",
        error: { message: resultUpdate.message }
      });
    } else {
      yield put({
        type: "UPDATE_SALES_INVOICE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_SALES_INVOICE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showSalesInvoice({ payload }) {
  const { id_sales_invoice } = payload;

  try {
    const resultShow = yield call(
      get,
      "sales/sales-invoices/" + id_sales_invoice
    );
  
    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: "SHOW_SALES_INVOICE_SUCCESS",
        unit: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

export function* confirmSalesInvoice({ payload }) {
  const { id_sales_invoice, body } = payload;

  try {
    // POST data ke API
    const confirmResult = yield call(
      putApi,
      "sales/sales-invoices/process-transaction/" + id_sales_invoice,
      body
    );

    // Jika sukses
    if (confirmResult.status === "success") {
      // Set session message
      yield all([
        put({
          type: "CONFIRM_SALES_INVOICE_SUCCESS",
          success: { message: confirmResult.message },
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_SALES_INVOICE_REQUEST",
          payload: {
            id_sales_invoice: id_sales_invoice
          }
        })
      ]);
    } else if (confirmResult.status === "fail") {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_SALES_INVOICE_ERROR",
          error: { message: confirmResult.message }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_SALES_INVOICE_REQUEST",
          payload: { id_sales_invoice: id_sales_invoice }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_SALES_INVOICE_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_SALES_INVOICE_REQUEST",
          payload: { id_sales_invoice: id_sales_invoice }
        })
      ]);
    }
  } catch (error) {
    yield all([
      put({
        type: "CONFIRM_SALES_INVOICE_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      put({
        type: "EDIT_SALES_INVOICE_REQUEST",
        payload: { id_sales_invoice: id_sales_invoice }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

