
const initState = {
  roles: [],
  role: null,
  search: null,
  filter: null,
  loading: true,
  loadingEdit: true,
  raptor_module: 0,
  permissions: [],
  permission_list: [],
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function rolesReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_ROLES_REQUEST': {
      return {
        ...state,
        //roles: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_ROLES_SUCCESS': {
      return {
        ...state,
        roles: action.roles,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_ROLES_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
      };
    }
    case 'GET_ROLE_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        raptor_module: action.raptor_module,
        permissions: action.permissions,
        permission_list: action.permission_list
      };
    }
    case 'GET_ROLE_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        error: action.error.message
      }
    }

    case 'CREATE_ROLE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'CREATE_ROLE_SUCCESS': {
      return {
        ...state,
        loading: false,
      };
    }
    
    case 'STORE_ROLE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_ROLE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_ROLE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_ROLE_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null,
      };
    }
    case 'EDIT_ROLE_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        role: action.role,
      };
    }
    case 'EDIT_ROLE_ERROR': {
      return {
        ...state,
        error: action.error.message,
      };
    }
    case 'UPDATE_ROLE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_ROLE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_ROLE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_ROLE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_ROLE_SUCCESS': {
      return {
        ...state,
        loading: false,
        roles: action.roles
      };
    }
    case "DEACTIVATE_ROLE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_ROLE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_ROLE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_ROLE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_ROLE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_ROLE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
