import React from 'react';
import { Link } from 'react-router-dom';
// import { siteConfig } from '../../settings';
import Logo from "../../image/Equal_White.png";
import LogoWide from "../../image/equal-wide-logo.png";

export default ({ collapsed }) => {
  return (
    <div className="isoLogoWrapper" style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
      {collapsed ? (
        <div>
          <h3 style={{ marginBottom: 0 }}>
            <Link to="/dashboard">
              {/* <i className={siteConfig.siteIcon} /> */}
              <img src={Logo} style={{ width: 43.5, height: "auto" }} />
            </Link>
          </h3>
        </div>
      ) : (
          <h3 style={{ marginBottom: 0 }}>
            <Link to="/dashboard">
              <span style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
                <img src={LogoWide} style={{ width: 165, height: "auto", marginRight: "7px" }} />
                {/* <span>{siteConfig.siteName}</span> */}
              </span>
            </Link>
          </h3>
        )}
    </div>
  );
};
