const initState = {
  purchase_receipt_detail_reports: [],
  pr_detail_search_filter: [],
  total_qty_order: null,
  total_qty_receipt: null,
  total_item: null,
  locations: [],
  suppliers: [],
  departments: [],
  projects: [],
  base_currency: null,
  messages: [],
  search: null,
  filter: null,
  filter_select: [],
  loading: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function purchaseReceiptDetailReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_PURCHASE_RECEIPT_DETAIL_REPORTS_REQUEST": {
      return {
        ...state,
        //purchase_receipt_detail_reports: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_PURCHASE_RECEIPT_DETAIL_REPORTS_SUCCESS": {
      return {
        ...state,
        purchase_receipt_detail_reports: action.purchase_receipt_detail_reports,
        // current_page: action.current_page,
        // page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_PURCHASE_RECEIPT_DETAIL_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_PURCHASE_RECEIPT_DETAIL_REPORT_REQUEST": {
      return {
        ...state,
        loading: false,
        purchase_receipt_report: null,
        errorField: null
      };
    }

    case "GET_PURCHASE_RECEIPT_DETAIL_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        base_currency: action.base_currency,
        departments: action.departments,
        loading: false,
        locations: action.locations,
        messages: action.messages,
        projects: action.projects,
        pr_detail_search_filter: action.pr_detail_search_filter,
        suppliers: action.suppliers
      };
    }
    case "GET_PURCHASE_RECEIPT_DETAIL_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
