import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPettyCashReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/petty-cashes?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if(filter){
      const [petty_cash_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/petty-cashes/foreign-keys")
      ]);
      // Jika sukses get data petty_cash_reports
      if (petty_cash_reports.status === "success" && petty_cash_reports.result !== null) {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_PETTY_CASH_REPORTS_SUCCESS",
          petty_cash_reports: petty_cash_reports.result.petty_cashes,
          petty_cash : petty_cash_reports.result.petty_cash,
          filter_response : petty_cash_reports.result.filter,
          opening_balance : petty_cash_reports.result.opening_balance,
          filter
        });
      }
      else if (petty_cash_reports.status === "success" && petty_cash_reports.result === null) {
        yield put({
          type: "GET_PETTY_CASH_REPORTS_SUCCESS",
          petty_cash_reports: [],
          petty_cash : null,
          filter_response : null,
          opening_balance : null,
          filter
        });
      }
      else if (petty_cash_reports.status === "fail") {
        yield put({
          type: "GET_PETTY_CASH_REPORTS_ERROR",
          error: { message: petty_cash_reports.message }
        });
      }
      else {
        yield put({
          type: "GET_PETTY_CASH_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data petty_cash_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_PETTY_CASH_REPORT_FOREIGN_KEYS_SUCCESS",
          petty_cash_types: foreign_keys.result.petty_cash_types,
          locations: foreign_keys.result.locations,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_PETTY_CASH_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_PETTY_CASH_REPORT_FOREIGN_KEYS_ERROR",
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/petty-cashes/foreign-keys")
      ]);
  
      yield put({
        type: "GET_PETTY_CASH_REPORTS_SUCCESS",
        petty_cash_reports: [],
        petty_cash : null,
        filter_response : null,
        opening_balance : null,
      });
    
      // Jika sukses get data petty_cash_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_PETTY_CASH_REPORT_FOREIGN_KEYS_SUCCESS",
          petty_cash_types: foreign_keys.result.petty_cash_types,
          locations: foreign_keys.result.locations,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_PETTY_CASH_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_PETTY_CASH_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_PETTY_CASH_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_PETTY_CASH_REPORTS_REQUEST",
      getPettyCashReports
    )
  ]);
}
