const initState = {
  top_supplier_reports: [],
  locations: [],
  suppliers: [],
  supplier_types: [],
  item_categories: [],
  employees: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function supplierReportsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_TOP_SUPPLIER_REPORTS_REQUEST": {
      return {
        ...state,
        //top_supplier_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_TOP_SUPPLIER_REPORTS_SUCCESS": {
      return {
        ...state,
        top_supplier_reports: action.top_supplier_reports,
        filter: action.filter,
        search: action.search,
        loading: false
      };
    }
    case "GET_TOP_SUPPLIER_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_TOP_SUPPLIER_REPORT_REQUEST": {
      return {
        ...state,
        top_supplier_report: null,
        errorField: null
      };
    }

    case "GET_TOP_SUPPLIER_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        suppliers: action.suppliers,
        supplier_types: action.supplier_types,
        item_categories: action.item_categories,
        employees: action.employees,
      };
    }
    case "GET_TOP_SUPPLIER_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
