import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getChartOfAccountReports({ payload }) {
  const { search, filter } = payload;

  let url = "reports/chart-of-accounts?";
  // if (search) {
  //   url += "search=" + search;
  // }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    const [chart_of_accounts_reports, foreign_keys] = yield all([
      call(get, url),
      call(get, "reports/chart-of-accounts/foreign-keys")
    ]);
  
    // console.log(chart_of_accounts_reports);
    // console.log(foreign_keys);
  
    // Jika sukses get data chart_of_accounts_reports
    if (chart_of_accounts_reports.status === "success") {
      // Jika API tidak menggunakan pagination
      yield put({
        type: "GET_CHART_OF_ACCOUNT_REPORTS_SUCCESS",
        chart_of_accounts_reports: chart_of_accounts_reports.result
      });
    } else if (chart_of_accounts_reports.status === "fail") {
      yield put({
        type: "GET_CHART_OF_ACCOUNT_REPORTS_ERROR",
        error: { message: chart_of_accounts_reports.message }
      });
    } else {
      yield put({
        type: "GET_CHART_OF_ACCOUNT_REPORTS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    // Jika sukses get data chart_of_accounts_report types
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_CHART_OF_ACCOUNT_REPORT_FOREIGN_KEYS_SUCCESS",
        locations: foreign_keys.result.locations,
        gl_types: foreign_keys.result.gl_types
      });
    } else if (foreign_keys.status === "fail") {
      yield put({
        type: "GET_CHART_OF_ACCOUNT_REPORT_FOREIGN_KEYS_ERROR",
        error: { message: foreign_keys.message }
      });
    } else {
      yield put({
        type: "GET_CHART_OF_ACCOUNT_REPORT_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_CHART_OF_ACCOUNT_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_CHART_OF_ACCOUNT_REPORTS_REQUEST",
      getChartOfAccountReports
    )
  ]);
}
