import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getGLCurrencies() {
  try {
    const [currencies, accounts] = 
      yield all([
        call(get, "configuration-managements/currencies?is_active=gl_config"),
        call(get, "accounts?is_active=child"),
      ]);

    // console.log(currencies);
    // console.log(accounts);

    // Jika sukses get data currencies
    if (currencies.status === "success" && accounts.status === "success"){   
      yield put({
        type: 'GET_GL_CURRENCIES_SUCCESS',
        currencies: currencies.result,
        accounts: accounts.result
      })  
    }
    else {
      yield put({
        type: "GET_GL_CURRENCIES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_GL_CURRENCIES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateGLCurrency({ payload }) {
  const { id_currency, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "configuration-managements/currencies/" + id_currency,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      
      yield all([
        yield put({
          type: 'UPDATE_GL_CURRENCY_SUCCESS',
          success: resultUpdate
        }),
        yield put({
          type: 'GET_GL_CURRENCIES_REQUEST'
        })
      ])
      
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_GL_CURRENCY_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_GL_CURRENCIES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_GL_CURRENCIES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_GL_CURRENCIES_REQUEST', getGLCurrencies),
    yield takeEvery('UPDATE_GL_CURRENCY_REQUEST', updateGLCurrency),
  ]);
}
