import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPayablePayments({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [payablePayments, foreign] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(
              get,
              "financials/payable-payments?search=" + search + "&page=" + page
            ),
            call(get, "financials/payable-payments/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "financials/payable-payments?" + filter + "&page=" + page),
            call(get, "financials/payable-payments/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "financials/payable-payments?search=" + search),
          call(get, "financials/payable-payments/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "financials/payable-payments?" + filter),
          call(get, "financials/payable-payments/foreign-keys")
        ]);

    // console.log(payablePayments);

    // Jika sukses get data payablePayments
    if (payablePayments.status === "success") {
      // Jika di API menggunakan pagination
      if (payablePayments.result.current_page && search) {
        yield put({
          type: "GET_PAYABLE_PAYMENTS_SUCCESS",
          payablePayments: payablePayments.result.data,
          current_page: payablePayments.result.current_page,
          page_size: payablePayments.result.per_page,
          total: payablePayments.result.total,
          search: search
        });
      } else if (payablePayments.result.current_page && filter) {
        yield put({
          type: "GET_PAYABLE_PAYMENTS_SUCCESS",
          payablePayments: payablePayments.result.data,
          current_page: payablePayments.result.current_page,
          page_size: payablePayments.result.per_page,
          total: payablePayments.result.total,
          filter: filter
        });
      } else if (payablePayments.result.current_page) {
        yield put({
          type: "GET_PAYABLE_PAYMENTS_SUCCESS",
          payablePayments: payablePayments.result.data,
          current_page: payablePayments.result.current_page,
          page_size: payablePayments.result.per_page,
          total: payablePayments.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_PAYABLE_PAYMENTS_SUCCESS",
          payablePayments: payablePayments.result,
          page_size: null,
          total: null
        });
      }
    } else if (payablePayments.status === "fail") {
      // Set message ke session storage
      yield put({
        type: "GET_PAYABLE_PAYMENTS_ERROR",
        error: { message: payablePayments.message }
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_PAYABLE_PAYMENTS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      yield put({
        type: "GET_PAYABLE_PAYMENT_FOREIGN_KEYS_SUCCESS",
        locations: foreign.result.locations,
        suppliers: foreign.result.suppliers,
        currencies: foreign.result.currencies,
        cashBanks: foreign.result.cash_banks,
        accounts: foreign.result.accounts,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        cashFlowTypes: foreign.result.cash_flow_types,
        messages: foreign.result.messages
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_PAYABLE_PAYMENT_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_PAYABLE_PAYMENT_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_PAYABLE_PAYMENTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createPayablePayment() {
  try {
    const [transactions, foreign] = yield all([
      call(
        get,
        "purchases/purchase-invoices?get_old_trans=1&status=2&payment_status=0,1"
      ),
      call(get, "financials/payable-payments/foreign-keys")
    ]);
  
    if (transactions.status === "success") {
      yield put({
        type: "GET_FILTER_PURCHASE_INVOICES_PP_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_FILTER_PURCHASE_INVOICES_PP_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_PURCHASE_INVOICES_PP_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (foreign.status === "success") {
      yield put({
        type: "GET_PAYABLE_PAYMENT_FOREIGN_KEYS_SUCCESS",
        locations: foreign.result.locations,
        suppliers: foreign.result.suppliers,
        currencies: foreign.result.currencies,
        cashBanks: foreign.result.cash_banks,
        accounts: foreign.result.accounts,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        cashFlowTypes: foreign.result.cash_flow_types,
        messages: foreign.result.messages
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_PAYABLE_PAYMENT_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_PAYABLE_PAYMENT_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_PURCHASE_INVOICES_PP_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export function* getTransactions({ payload }) {
  const { page, search, filter } = payload;
  // console.log(filter);

  try {
    const transactions = filter
      ? yield call(
          get,
          "purchases/purchase-invoices?get_old_trans=1&status=2&" +
            filter +
            "&page=" +
            page
        )
      : search
      ? yield call(
          get,
          "purchases/purchase-invoices?get_old_trans=1&status=2&payment_status=0,1&search=" +
            search +
            "&page=" +
            page
        )
      : yield call(
          get,
          "purchases/purchase-invoices?get_old_trans=1&status=2&payment_status=0,1" +
            "&page=" +
            page
        );

    if (transactions.status === "success") {
      yield put({
        type: "GET_FILTER_PURCHASE_INVOICES_PP_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_FILTER_PURCHASE_INVOICES_PP_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_PURCHASE_INVOICES_PP_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_PURCHASE_INVOICES_PP_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export function* getDebitMemos({ payload }) {
  const { id_location, id_supplier } = payload;
  // console.log(filter);
  const filter = `financials/debit-memos?is_active=1&status=1&id_location=${id_location}&id_supplier=${id_supplier}`;

  try {
    const debitMemos = yield call(get, filter);

    if (debitMemos.status === "success") {
      yield put({
        type: "GET_FILTER_DEBIT_MEMOS_PP_SUCCESS",
        debitMemos: debitMemos.result
      });
    } else if (debitMemos.status === "fail") {
      yield put({
        type: "GET_FILTER_DEBIT_MEMOS_PP_ERROR",
        error: { message: debitMemos.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_DEBIT_MEMOS_PP_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_DEBIT_MEMOS_PP_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storePayablePayment({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "financials/payable-payments", body);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_PAYABLE_PAYMENT_SUCCESS",
        success: { message: resultStore.message },
        id_payable_payment: resultStore.result.id_payable_payment
      });
    } else if (resultStore.errors) {
      yield put({
        type: "STORE_PAYABLE_PAYMENT_ERROR",
        error: resultStore
      });
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_PAYABLE_PAYMENT_ERROR",
        error: { message: resultStore.message }
      });
    } else {
      yield put({
        type: "STORE_PAYABLE_PAYMENT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_PAYABLE_PAYMENT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
