// Upload Document
export function* uploadPayablePaymentDocument({ payload }) {
  const { id_payable_payment, body } = payload;
  let data = {
    ...body,
    id_payable_payment
  };

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "financials/payable-payments/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: "UPLOAD_PAYABLE_PAYMENT_DOCUMENT_SUCCESS",
          success: uploadResult
        }),
        yield put({
          type: "EDIT_PAYABLE_PAYMENT_REQUEST",
          payload: { id_payable_payment }
        })
      ]);
    } else if (uploadResult.errors) {
      yield all([
        yield put({
          type: "UPLOAD_PAYABLE_PAYMENT_DOCUMENT_ERROR",
          error: { message: uploadResult.errors.document[0] }
        }),
        yield put({
          type: "EDIT_PAYABLE_PAYMENT_REQUEST",
          payload: { id_payable_payment }
        })
      ]);
    } else if (uploadResult.status === "fail") {
      yield all([
        yield put({
          type: "UPLOAD_PAYABLE_PAYMENT_DOCUMENT_ERROR",
          error: { message: uploadResult.message }
        }),
        yield put({
          type: "EDIT_PAYABLE_PAYMENT_REQUEST",
          payload: { id_payable_payment }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "UPLOAD_PAYABLE_PAYMENT_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "EDIT_PAYABLE_PAYMENT_REQUEST",
          payload: { id_payable_payment }
        })
      ]);
    }

    // }
  } catch (error) {
    yield all([
      yield put({
        type: "UPLOAD_PAYABLE_PAYMENT_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "EDIT_PAYABLE_PAYMENT_REQUEST",
        payload: { id_payable_payment }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

