const initState = {
  trial_balance_reports: [],
  locations: [],
  gl_types: [],
  filter: null,
  filter_select: [],
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function trialBalanceReportsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_TRIAL_BALANCE_REPORTS_REQUEST": {
      return {
        ...state,
        //trial_balance_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_TRIAL_BALANCE_REPORTS_SUCCESS": {
      return {
        ...state,
        trial_balance_reports: action.trial_balance_reports,
        // filter: action.filter,
        loading: false
      };
    }
    case "GET_TRIAL_BALANCE_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_TRIAL_BALANCE_REPORT_REQUEST": {
      return {
        ...state,
        trial_balance_report: null,
        errorField: null
      };
    }

    case "GET_TRIAL_BALANCE_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        gl_types: action.gl_types
      };
    }
    case "GET_TRIAL_BALANCE_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
