import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../helpers/helper";
import { bugsnagClient } from "../..";

export function* getDashboardReports({ payload }) {
  const { filter } = payload;

  try {
    const dashboard_reports = filter
      ? yield call(get, "reports/dashboard?" + filter)
      : yield call(get, "reports/dashboard");

    if (dashboard_reports.status === "success") {
      yield put({
        type: "GET_DASHBOARD_REPORTS_SUCCESS",
        account_payables: dashboard_reports.result.account_payables,
        account_receivables: dashboard_reports.result.account_receivables,
        cash_flow: dashboard_reports.result.cash_flow,
        retained_earning: dashboard_reports.result.retained_earning,
        top_items_by_amount: dashboard_reports.result.top_items_by_amount,
        top_items_by_qty: dashboard_reports.result.top_items_by_qty,
        total_sales: dashboard_reports.result.total_sales
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_DASHBOARD_REPORTS_REQUEST", getDashboardReports)
  ]);
}
