import actions from "./actions";

const initState = {
  locations: [],
  location_types: [],
  sites: [],
  accounts: [],
  location: null,
  search: null,
  filter: null,
  loading: false,
  loadingSubmit: false,
  current_page: null,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function locationReducer(state = initState, action) {
  switch (action.type) {
    case "GET_LOCATIONS_REQUEST": {
      return {
        ...state,
        location: null,
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page
      };
    }
    case "GET_LOCATIONS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter
      };
    }
    case "GET_LOCATIONS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_LOCATION_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        location_types: action.location_types,
        sites: action.sites,
        accounts: action.accounts
      };
    }
    case "GET_LOCATION_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "CREATE_LOCATION_REQUEST": {
      return {
        ...state,
        loading: true,
        location: null
      };
    }
    case "STORE_LOCATION_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "STORE_LOCATION_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "STORE_LOCATION_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "EDIT_LOCATION_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "EDIT_LOCATION_SUCCESS": {
      return {
        ...state,
        loading: false,
        location: action.location
      };
    }
    case "EDIT_LOCATION_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "UPDATE_LOCATION_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_LOCATION_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_LOCATION_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_LOCATION_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "SHOW_LOCATION_SUCCESS": {
      return {
        ...state,
        loading: false,
        location: action.location
      };
    }
    case "DEACTIVATE_LOCATION_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_LOCATION_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_LOCATION_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_LOCATION_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_LOCATION_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_LOCATION_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
