const initState = {
  app_configs: [],
  countries: [],
  taxes: [],
  customer_types: [],
  customers: [],
  raptor_connections: [],
  raptor_connection_location_array: [],
  locations: [],
  loading: false,
  loadingSubmit: false,
  loadingConnection: false,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function appConfigsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_APP_CONFIGS_REQUEST": {
      return {
        ...state,
        loading: true,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "GET_APP_CONFIGS_SUCCESS": {
      return {
        ...state,
        app_configs: action.app_configs,
        countries: action.countries,
        taxes: action.taxes,
        customer_types: action.customer_types,
        customers: action.customers,
        raptor_connections: action.raptor_connections,
        raptor_connection_location_array: action.raptor_connection_location_array,
        locations: action.locations,
        loadingSubmit: false,
        loading: false
      };
    }
    case "GET_APP_CONFIGS_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "STORE_RAPTOR_CONNECTION_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null,
      };
    }
    case "UPDATE_APP_CONFIG_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_APP_CONFIG_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_APP_CONFIG_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }

    case "UPDATE_RAPTOR_CONNECTION_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        loading: true,
        errorField: null
      };
    }
    case "UPDATE_RAPTOR_CONNECTION_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        loading: false,
        success: action.success.message
      };
    }
    case "UPDATE_RAPTOR_CONNECTION_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }

    case "CHECK_RAPTOR_CONNECTION_REQUEST": {
      return {
        ...state,
        loadingConnection: true,
        errorField: null
      };
    }
    case "CHECK_RAPTOR_CONNECTION_SUCCESS": {
      return {
        ...state,
        loadingConnection: false,
        success: action.success.message
      };
    }
    case "CHECK_RAPTOR_CONNECTION_ERROR": {
      return {
        ...state,
        loadingConnection: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }

    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
