const initState = {
  fixed_assets: [],
  fixed_asset: null,
  locations: [],
  currencies: [],
  departments: [],
  accounts: [],
  sub_ledger_accounts: [],
  asset_fiscals: [],
  asset_types: [],
  asset_categories: [],
  asset_service_types: [],
  base_currency: null,
  messages: [],
  search: null,
  filter: null,
  loading: false,
  loadingEdit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
  id_fixed_asset: null
};

export default function fixedAssetsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_FIXED_ASSETS_REQUEST": {
      return {
        ...state,
        //fixed_assets: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_FIXED_ASSETS_SUCCESS": {
      return {
        ...state,
        fixed_assets: action.fixed_assets,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_FIXED_ASSETS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CREATE_FIXED_ASSET_REQUEST": {
      return {
        ...state,
        fixed_asset: null,
        errorField: null
      };
    }

    case "GET_FIXED_ASSET_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        locations: action.locations,
        currencies: action.currencies,
        departments: action.departments,
        accounts: action.accounts,
        sub_ledger_accounts: action.sub_ledger_accounts,
        asset_fiscals: action.asset_fiscals,
        asset_types: action.asset_types,
        asset_categories: action.asset_categories,
        asset_service_types: action.asset_service_types,
        base_currency: action.base_currency,
        messages: action.messages
      };
    }
    case "GET_FIXED_ASSET_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }

    case "STORE_FIXED_ASSET_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "STORE_FIXED_ASSET_SUCCESS": {
      return {
        ...state,
        loading: false,
        id_fixed_asset: action.id_fixed_asset,
        success: action.success.message
      };
    }
    case "STORE_FIXED_ASSET_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "STORE_FIXED_ASSET_NOTE_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null,
        id_fixed_asset: null
      };
    }
    case "STORE_FIXED_ASSET_MAINTENANCE_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null,
        id_fixed_asset: null
      };
    }
    case "EDIT_FIXED_ASSET_REQUEST": {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null
      };
    }
    case "EDIT_FIXED_ASSET_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        fixed_asset: action.fixed_asset
      };
    }
    case "EDIT_FIXED_ASSET_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingEdit:false,
        error: action.error.message
      };
    }
    case "UPDATE_FIXED_ASSET_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "UPDATE_FIXED_ASSET_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPDATE_FIXED_ASSET_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "UPDATE_FIXED_ASSET_NOTE_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null,
        id_fixed_asset: null
      };
    }
    case "UPDATE_FIXED_ASSET_MAINTENANCE_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null,
        id_fixed_asset: null
      };
    }
    case "SHOW_FIXED_ASSET_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_FIXED_ASSET_SUCCESS": {
      return {
        ...state,
        loading: false,
        fixed_assets: action.fixed_assets
      };
    }
    case "PROCESS_FIXED_ASSET_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "PROCESS_FIXED_ASSET_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "PROCESS_FIXED_ASSET_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CANCEL_FIXED_ASSET_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "CANCEL_FIXED_ASSET_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "CANCEL_FIXED_ASSET_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "UPLOAD_FIXED_ASSET_PICTURE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_FIXED_ASSET_PICTURE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_FIXED_ASSET_PICTURE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_FIXED_ASSET_PICTURE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_FIXED_ASSET_PICTURE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_FIXED_ASSET_PICTURE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_FIXED_ASSET_PICTURE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
