import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getCurrencyRates({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [currency_rates, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "configuration-managements/currency-rates?search=" + search + "&page=" + page), 
            call(get, "configuration-managements/currency-rates/foreign-keys"), 
          ])
        : // Jika filter
          yield all([
            call(get, "configuration-managements/currency-rates?" + filter + "&page=" + page), 
            call(get, "configuration-managements/currency-rates/foreign-keys"), 
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "configuration-managements/currency-rates?search=" + search),  
          call(get, "configuration-managements/currency-rates/foreign-keys"), 
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "configuration-managements/currency-rates?" + filter),
          call(get, "configuration-managements/currency-rates/foreign-keys"), 
        ]);

    // console.log(currency_rates);
    // console.log(accounts);

    // Jika sukses get data currency_rates
    if (currency_rates.status === "success") {
      // Jika di API menggunakan pagination	    yield put({
        if (currency_rates.result.current_page && search) {	      
        yield put({	      
          type: 'GET_CURRENCY_RATES_SUCCESS',	
          currency_rates: currency_rates.result.data,	
          current_page: currency_rates.result.current_page,	
          page_size: currency_rates.result.per_page,	
          total: currency_rates.result.total,	
          search: search	
        });	
      }	
      else if(currency_rates.result.current_page && filter)	
      {	
        yield put({	
          type: 'GET_CURRENCY_RATES_SUCCESS',	
          currency_rates: currency_rates.result.data,	
          current_page: currency_rates.result.current_page,	
          page_size: currency_rates.result.per_page,	
          total: currency_rates.result.total,	
          filter: filter	
        });	
      }	
      else if (currency_rates.result.current_page) {	
        yield put({	
          type: 'GET_CURRENCY_RATES_SUCCESS',	
          currency_rates: currency_rates.result.data,	
          current_page: currency_rates.result.current_page,	
          page_size: currency_rates.result.per_page,	
          total: currency_rates.result.total	
        });	
      }	
      else {	
        // Jika API tidak menggunakan pagination	
        yield put({	
          type: 'GET_CURRENCY_RATES_SUCCESS',	
          currency_rates: currency_rates.result,	
          page_size: null,	
          total: null	
        });	
      }
    }
    else if(currency_rates.status === "fail"){
      yield put({
        type: 'GET_CURRENCY_RATES_ERROR',
        error: currency_rates
      })
    }
    else {
      yield put({
        type: 'GET_CURRENCY_RATES_ERROR',
        error: {message: "Check your internet connection"}
      })
    }

    // sukses get data foreign key
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_CURRENCY_RATE_FOREIGN_KEYS_SUCCESS',
        currencies: foreign_keys.result.currencies
      });
    }
    else{
      yield put({
        type: 'GET_CURRENCY_RATE_FOREIGN_KEYS_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_CURRENCY_RATES_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createCurrencyRate() {
  try {
    const [foreign_keys] = 
      yield all([
        call(get, "configuration-managements/currency-rates/foreign-keys"), 
      ])

    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_CURRENCY_RATE_FOREIGN_KEYS_SUCCESS',
        currencies: foreign_keys.result.currencies
      });
    }
    else{
      yield put({
        type: 'GET_CURRENCY_RATE_FOREIGN_KEYS_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_CURRENCY_RATE_FOREIGN_KEYS_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeCurrencyRate({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "configuration-managements/currency-rates", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_CURRENCY_RATE_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_CURRENCY_RATE_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_CURRENCY_RATE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_CURRENCY_RATE_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editCurrencyRate({ payload }) {
  //console.log("masuk")
  const { id_currency_rate } = payload;

  try {
    const [currency_rate, foreign_keys] = yield all([
      call(get, "configuration-managements/currency-rates/" + id_currency_rate),
      call(get, "configuration-managements/currency-rates/foreign-keys"),
    ]);
  
    // Jika sukses
    if (currency_rate.status === "success") {
      yield put({
        type: 'EDIT_CURRENCY_RATE_SUCCESS',
        currency_rate: currency_rate.result
      });
    }
    else if(currency_rate.status === "fail"){
      yield put({
        type: 'EDIT_CURRENCY_RATE_ERROR',
        error: currency_rate
      })
    }
    else{
      yield put({
        type: 'EDIT_CURRENCY_RATE_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  
  
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_CURRENCY_RATE_FOREIGN_KEYS_SUCCESS',
        currencies: foreign_keys.result.currencies
      });
    }
    else{
      yield put({
        type: 'GET_CURRENCY_RATE_FOREIGN_KEYS_ERROR',
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'EDIT_CURRENCY_RATE_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateCurrencyRate({ payload }) {
  const { id_currency_rate, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "configuration-managements/currency-rates/" + id_currency_rate,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_CURRENCY_RATE_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_CURRENCY_RATE_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_CURRENCY_RATE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_CURRENCY_RATE_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showCurrencyRate({ payload }) {
  const { id_currency_rate } = payload;

  try {
    const resultShow = yield call(get, "configuration-managements/currency-rates/" + id_currency_rate);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_CURRENCY_RATE_SUCCESS',
        currency_rate: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateCurrencyRate({ payload }) {
  const { id_currency_rate } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "configuration-managements/currency-rates/" + id_currency_rate,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: 'DEACTIVATE_CURRENCY_RATE_SUCCESS',
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_CURRENCY_RATES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: 'DEACTIVATE_CURRENCY_RATE_ERROR',
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_CURRENCY_RATES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'DEACTIVATE_CURRENCY_RATE_ERROR',
          error: {message: "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_CURRENCY_RATES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'DEACTIVATE_CURRENCY_RATE_ERROR',
        error: {message: "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_CURRENCY_RATES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activateCurrencyRate({ payload }) {
  const { id_currency_rate } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "configuration-managements/currency-rates/" + id_currency_rate,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_CURRENCY_RATE_SUCCESS',
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_CURRENCY_RATES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: 'ACTIVATE_CURRENCY_RATE_ERROR',
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_CURRENCY_RATES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'ACTIVATE_CURRENCY_RATE_ERROR',
          error: {message: "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_CURRENCY_RATES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'ACTIVATE_CURRENCY_RATE_ERROR',
        error: {message: "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_CURRENCY_RATES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_CURRENCY_RATES_REQUEST', getCurrencyRates),
    yield takeEvery('CREATE_CURRENCY_RATE_REQUEST', createCurrencyRate),
    yield takeEvery('STORE_CURRENCY_RATE_REQUEST', storeCurrencyRate),
    yield takeEvery('EDIT_CURRENCY_RATE_REQUEST', editCurrencyRate),
    yield takeEvery('UPDATE_CURRENCY_RATE_REQUEST', updateCurrencyRate),
    yield takeEvery('SHOW_CURRENCY_RATE_REQUEST', showCurrencyRate),
    yield takeEvery('DEACTIVATE_CURRENCY_RATE_REQUEST', deactivateCurrencyRate),
    yield takeEvery('ACTIVATE_CURRENCY_RATE_REQUEST', activateCurrencyRate)
  ]);
}
