
const initState = {
  fixed_asset_transfers: [],
  fixed_asset_transfer: null,
  accounts: [],
  from_locations: [],
  to_locations: [],
  departments: [],
  base_currency: null,
  search: null,
  filter: null,
  loading: false,
  loadingImport: false,
  loadingEdit:false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
  id_fixed_asset_transfer: null,
  import_fixed_assets: [],
  import_current_page: 1,
  import_page_size: null,
  import_total: null,
  import_search: null,
  import_filter: null,
  prevTransConfirm: null,
  prevTransReceive: null,
};

export default function fixedAssetTransfersReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_FIXED_ASSET_TRANSFERS_REQUEST': {
      return {
        ...state,
        //fixed_asset_transfers: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_FIXED_ASSET_TRANSFERS_SUCCESS': {
      return {
        ...state,
        fixed_asset_transfers: action.fixed_asset_transfers,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_FIXED_ASSET_TRANSFERS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      }
    }
    case 'GET_IMPORT_FIXED_ASSETS_FAT_REQUEST': {
      return {
        ...state,
        //fixed_asset_transfers: [],
        loadingImport: true,
        import_search: action.payload.search,
        import_filter: action.payload.filter,
        import_current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_IMPORT_FIXED_ASSETS_FAT_SUCCESS': {
      return {
        ...state,
        import_fixed_assets: action.fixed_assets,
        import_current_page: action.current_page,
        import_page_size: action.page_size,
        import_total: action.total,
        import_search: action.search,
        import_filter: action.filter,
        loadingImport: false,
      };
    }
    case 'GET_IMPORT_FIXED_ASSETS_FAT_ERROR': {
      return {
        ...state,
        loadingImport: false,
        error: action.error.message
      }
    }
    case 'CREATE_FIXED_ASSET_TRANSFER_REQUEST': {
      return {
        ...state,
        fixed_asset_transfer: null,
        errorField: null,
      };
    }

    case 'GET_FIXED_ASSET_TRANSFER_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        accounts: action.accounts,
        from_locations: action.from_locations,
        to_locations: action.to_locations,    
        prevTransConfirm: action.prevTransConfirm,
        prevTransReceive: action.prevTransReceive,
        departments: action.departments,
        base_currency: action.base_currency,
      };
    }
    case 'GET_FIXED_ASSET_TRANSFER_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        error: action.error.message
      };
    }
    
    case 'STORE_FIXED_ASSET_TRANSFER_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_FIXED_ASSET_TRANSFER_SUCCESS': {
      return {
        ...state,
        loading: false,
        id_fixed_asset_transfer: action.id_fixed_asset_transfer,
        success: action.success.message
      };
    }
    case 'STORE_FIXED_ASSET_TRANSFER_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_FIXED_ASSET_TRANSFER_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit:true,
        errorField: null,
      };
    }
    case 'EDIT_FIXED_ASSET_TRANSFER_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit:false,
        fixed_asset_transfer: action.fixed_asset_transfer
      };
    }
    case 'EDIT_FIXED_ASSET_TRANSFER_ERROR': {
      return {
        ...state,
        // loading: false,
        // loadingEdit:false,
        error: action.error.message
      };
    }
    case 'UPDATE_FIXED_ASSET_TRANSFER_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_FIXED_ASSET_TRANSFER_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_FIXED_ASSET_TRANSFER_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_FIXED_ASSET_TRANSFER_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_FIXED_ASSET_TRANSFER_SUCCESS': {
      return {
        ...state,
        loading: false,
        fixed_asset_transfers: action.fixed_asset_transfers
      };
    }
    case "PROCESS_FIXED_ASSET_TRANSFER_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "PROCESS_FIXED_ASSET_TRANSFER_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "PROCESS_FIXED_ASSET_TRANSFER_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CANCEL_FIXED_ASSET_TRANSFER_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "CANCEL_FIXED_ASSET_TRANSFER_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "CANCEL_FIXED_ASSET_TRANSFER_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "UPLOAD_FIXED_ASSET_TRANSFER_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_FIXED_ASSET_TRANSFER_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_FIXED_ASSET_TRANSFER_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_FIXED_ASSET_TRANSFER_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_FIXED_ASSET_TRANSFER_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_FIXED_ASSET_TRANSFER_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_FIXED_ASSET_TRANSFER_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
