const initState = {
  item_categories: [],
  item_category: null,
  dropdown_update: [],
  item_categories_list: [],
  loadingSubmit: false,
  loadingItemCategory: false,
  loadingItemCategories: false,
  loadingItemCategoriesList: false,
  condition: 0,
  success: null,
  error: null,
  errorField: null
};

const itemCategoriesReducer = (state = initState, action) => {
  switch (action.type) {
    case "GET_ITEM_CATEGORIES_REQUEST":
      return {
        ...state,
        condition: 0,
        item_category: null,
        loadingItemCategories: true,
        loadingItemCategoriesList: true,
      };
    case "GET_ITEM_CATEGORIES_SUCCESS":
      return {
        ...state,
        loadingItemCategoriesList: false,
        item_categories_list: action.item_categories_list,
        loadingItemCategories: false,
        item_categories: action.item_categories
      };
    case "GET_ITEM_CATEGORIES_ERROR":
      return {
        ...state,
        loadingItemCategories: false,
        error: action.error.message
      };
    case "STORE_ITEM_CATEGORY_REQUEST":
      return {
        ...state,
        loadingItemCategory: true,
        loadingSubmit: true
      };
    case "STORE_ITEM_CATEGORY_SUCCESS":
      return {
        ...state,
        loadingItemCategory: false,
        loadingSubmit: false,
        item_category: action.item_category,
        success: action.success.message,
      };
    case "STORE_ITEM_CATEGORY_ERROR":
      return {
        ...state,
        loadingItemCategory: false,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    case "SHOW_ITEM_CATEGORY_REQUEST":
      return {
        ...state,
        item_category: null,
        dropdown_update: [],
        loadingItemCategory: true
      };
    case "SHOW_ITEM_CATEGORY_SUCCESS":
      return {
        ...state,
        loadingItemCategory: false,
        item_category: action.item_category,
        dropdown_update: action.dropdown_update,
        condition: 2,
      };
    case "SHOW_ITEM_CATEGORY_ERROR":
      return {
        ...state,
        loadingItemCategory: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    case "UPDATE_ITEM_CATEGORY_REQUEST":
      return {
        ...state,
        loadingItemCategory: true,
        loadingSubmit: true
      };
    case "UPDATE_ITEM_CATEGORY_SUCCESS":
      return {
        ...state,
        loadingItemCategory: false,
        loadingSubmit: false,
        item_category: action.item_category,
        success: action.success.message,
      };
    case "UPDATE_ITEM_CATEGORY_ERROR":
      return {
        ...state,
        loadingItemCategory: false,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    case "ACTIVATE_ITEM_CATEGORY_REQUEST":
      return {
        ...state,
        loadingItemCategory: true,
        loadingSubmit: true
      };
    case "ACTIVATE_ITEM_CATEGORY_SUCCESS":
      return {
        ...state,
        loadingItemCategory: false,
        loadingSubmit: false,
        item_category: action.item_category,
        success: action.success.message,
        condition: 2,
      };
    case "ACTIVATE_ITEM_CATEGORY_ERROR":
      return {
        ...state,
        loadingItemCategory: false,
        loadingSubmit: false,
        error: action.error.message
      };
    case "DEACTIVATE_ITEM_CATEGORY_REQUEST":
      return {
        ...state,
        loadingItemCategory: true,
        loadingSubmit: true
      };
    case "DEACTIVATE_ITEM_CATEGORY_SUCCESS":
      return {
        ...state,
        loadingItemCategory: false,
        loadingSubmit: false,
        item_category: action.item_category,
        success: action.success.message,
        condition: 2,
      };
    case "DEACTIVATE_ITEM_CATEGORY_ERROR":
      return {
        ...state,
        loadingItemCategory: false,
        loadingSubmit: false,
        error: action.error.message
      };
    case "CLEAR_ITEM_CATEGORY":
      return {
        ...state,
        item_category: null,
        loadingItemCategory: false,
        condition: 1,
      };
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return {
        ...state
      };
  }
};

export default itemCategoriesReducer;
