const initState = {
  monthly_purchase_reports: [],
  locations: [],
  suppliers: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function monthlyPurchaseReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_MONTHLY_PURCHASE_REPORTS_REQUEST": {
      return {
        ...state,
        //monthly_purchase_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_MONTHLY_PURCHASE_REPORTS_SUCCESS": {
      return {
        ...state,
        monthly_purchase_reports: action.monthly_purchase_reports,
        // filter: action.filter,
        // search: action.search,
        loading: false
      };
    }
    case "GET_MONTHLY_PURCHASE_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "GET_MONTHLY_PURCHASE_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        suppliers: action.suppliers
      };
    }
    case "GET_MONTHLY_PURCHASE_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
