import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getTrialBalanceDepartmentReports({ payload }) {
  const { search, filter } = payload;

  let url = "reports/trial-balances?";
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }

  let trial_balance_department_reports;
  let foreign_keys;
  
  try {
    if (filter && filter.includes("id_department=")) {
      [trial_balance_department_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/trial-balances/foreign-keys")
      ]);
  
      // Jika sukses get data trial_balance_department_reports
      if (trial_balance_department_reports.status === "success") {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_TRIAL_BALANCE_DEPARTMENT_REPORTS_SUCCESS",
          trial_balance_department_reports:
            trial_balance_department_reports.result
        });
      } else if (trial_balance_department_reports.status === "fail") {
        yield put({
          type: "GET_TRIAL_BALANCE_DEPARTMENT_REPORTS_ERROR",
          error: { message: trial_balance_department_reports.message }
        });
      } else {
        yield put({
          type: "GET_TRIAL_BALANCE_DEPARTMENT_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } else {
      foreign_keys = yield call(get, "reports/trial-balances/foreign-keys");
      yield put({
        type: "GET_TRIAL_BALANCE_DEPARTMENT_REPORTS_SUCCESS",
        trial_balance_department_reports: []
      });
    }
  
    // Jika sukses get data trial_balance_department_report types
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_TRIAL_BALANCE_DEPARTMENT_REPORT_FOREIGN_KEYS_SUCCESS",
        locations: foreign_keys.result.locations,
        departments: foreign_keys.result.departments,
        gl_types: foreign_keys.result.gl_types
      });
    } else if (foreign_keys.status === "fail") {
      yield put({
        type: "GET_TRIAL_BALANCE_DEPARTMENT_REPORT_FOREIGN_KEYS_ERROR",
        error: { message: foreign_keys.message }
      });
    } else {
      yield put({
        type: "GET_TRIAL_BALANCE_DEPARTMENT_REPORT_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_TRIAL_BALANCE_DEPARTMENT_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_TRIAL_BALANCE_DEPARTMENT_REPORTS_REQUEST",
      getTrialBalanceDepartmentReports
    )
  ]);
}
