const initState = {
  uncomplete_purchase_order_reports: [],
  locations: [],
  suppliers: [],
  currencies: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function uncompletePurchaseOrderReportsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_UNCOMPLETE_PURCHASE_ORDER_REPORTS_REQUEST": {
      return {
        ...state,
        //uncomplete_purchase_order_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_UNCOMPLETE_PURCHASE_ORDER_REPORTS_SUCCESS": {
      return {
        ...state,
        uncomplete_purchase_order_reports: action.uncomplete_purchase_order_reports,
        // filter: action.filter,
        // search: action.search,
        loading: false
      };
    }
    case "GET_UNCOMPLETE_PURCHASE_ORDER_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "GET_UNCOMPLETE_PURCHASE_ORDER_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        suppliers: action.suppliers,
        currencies: action.currencies
      };
    }
    case "GET_UNCOMPLETE_PURCHASE_ORDER_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
