const initState = {
  accounts: [],
  error: null,
  errorField: null,
  filter: [],
  general_ledgers: [],
  loading: false,
  loadingForeign: false,
  loadingReport: false,
  loadingSubmit: false,
  search: null,
  success: null,
  total: null,
  reports: []
};

export default function jobTitlesReducer(state = initState, action) {
  switch (action.type) {
    case "GET_GENERAL_LEDGERS_REQUEST": {
      return {
        ...state,
        accounts: [],
        loading: true,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "GET_GENERAL_LEDGERS_SUCCESS": {
      return {
        ...state,
        accounts: action.accounts,
        general_ledgers: action.general_ledgers,
        loadingSubmit: false,
        loading: false
      };
    }
    case "GET_GENERAL_LEDGERS_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "UPDATE_GENERAL_LEDGER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_GENERAL_LEDGER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_GENERAL_LEDGER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }

    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    
    default:
      return state;
  }
}
