import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getProfitLossDepartmentReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/profit-losses?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if(filter && filter.includes('id_department')){
      const [profit_loss_department_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/profit-losses/foreign-keys")
      ]);
    
      // Jika sukses get data profit_loss_department_reports
      if (profit_loss_department_reports.status === "success") {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_PROFIT_LOSS_DEPARTMENT_REPORTS_SUCCESS",
          profit_loss_department_reports: profit_loss_department_reports.result,
          filter
        });
      } else if (profit_loss_department_reports.status === "fail") {
        yield put({
          type: "GET_PROFIT_LOSS_DEPARTMENT_REPORTS_ERROR",
          error: { message: profit_loss_department_reports.message }
        });
      } else {
        yield put({
          type: "GET_PROFIT_LOSS_DEPARTMENT_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data profit_loss_department_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_PROFIT_LOSS_DEPARTMENT_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          departments: foreign_keys.result.departments,
          projects: foreign_keys.result.projects,
          company: foreign_keys.result.company,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_PROFIT_LOSS_DEPARTMENT_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_PROFIT_LOSS_DEPARTMENT_REPORT_FOREIGN_KEYS_ERROR",
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/profit-losses/foreign-keys")
      ]);
  
      yield put({
        type: "GET_PROFIT_LOSS_DEPARTMENT_REPORTS_SUCCESS",
        profit_loss_department_reports: [],
      });
    
      // Jika sukses get data profit_loss_department_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_PROFIT_LOSS_DEPARTMENT_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          departments: foreign_keys.result.departments,
          projects: foreign_keys.result.projects,
          company: foreign_keys.result.company,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_PROFIT_LOSS_DEPARTMENT_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_PROFIT_LOSS_DEPARTMENT_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_PROFIT_LOSS_DEPARTMENT_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_PROFIT_LOSS_DEPARTMENT_REPORTS_REQUEST",
      getProfitLossDepartmentReports
    )
  ]);
}
