const initState = {
  credit_memo_reports: [],
  locations: [],
  customers: [],
  cash_banks: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function creditMemoReportsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_CREDIT_MEMO_REPORTS_REQUEST": {
      return {
        ...state,
        //credit_memo_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_CREDIT_MEMO_REPORTS_SUCCESS": {
      return {
        ...state,
        credit_memo_reports: action.credit_memo_reports,
        loading: false
      };
    }
    case "GET_CREDIT_MEMO_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "GET_CREDIT_MEMO_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        customers: action.customers,
        cash_banks: action.cash_banks
      };
    }
    case "GET_CREDIT_MEMO_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
