
const initState = {
  periods: [],
  period_year: null,
  list_period_year: [],
  period: null,
  currencies: [],
  search: null,
  filter: null,
  loading: false,
  loadingEdit:false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
};

export default function periodsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_PERIODS_REQUEST': {
      return {
        ...state,
        //periods: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_PERIODS_SUCCESS': {
      return {
        ...state,
        periods: action.periods,
        period_year: action.period_year,
        list_period_year:action.list_period_year,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_PERIODS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      }
    }
    case 'CREATE_PERIOD_REQUEST': {
      return {
        ...state,
        period: null,
        errorField: null,
      };
    }
    case 'GET_PERIOD_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        currencies: action.currencies,
        messages: action.messages
      };
    }
    case 'GET_PERIOD_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        error: action.error.message
      };
    }
    case 'STORE_AUTO_GENERATE_PERIOD_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_ADJUSTING_JOURNAL_ENTRY_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    
    case 'STORE_PERIOD_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_PERIOD_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_PERIOD_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit:true,
        errorField: null,
      };
    }
    case 'EDIT_PERIOD_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit:false,
        period: action.period
      };
    }
    case 'EDIT_PERIOD_ERROR': {
      return {
        ...state,
        // loading: false,
        // loadingEdit:false,
        error: action.error.message
      };
    }
    case 'UPDATE_PERIOD_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_PERIOD_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_PERIOD_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_PERIOD_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_PERIOD_SUCCESS': {
      return {
        ...state,
        loading: false,
        periods: action.periods
      };
    }
    case 'CLOSE_ALL_PERIOD_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case "CLOSE_PERIOD_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "REOPEN_PERIOD_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "CLOSE_FA_PERIOD_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "REOPEN_FA_PERIOD_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "CLOSE_REOPEN_PERIOD_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "CLOSE_REOPEN_PERIOD_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
