
const initState = {
  job_costings: [],
  job_costing: null,
  locations: [],
  accounts: [],
  projects: [],
  departments: [],
  items: [],
  messages: [],
  base_currency: null,
  search: null,
  filter: null,
  loading: false,
  loadingImport: false,
  loadingEdit:false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
  id_job_costing: null,
  import_job_costings: [],
  import_current_page: 1,
  import_page_size: null,
  import_total: null,
  import_search: null,
  import_filter: null,
};

export default function jobCostingsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_JOB_COSTINGS_REQUEST': {
      return {
        ...state,
        //job_costings: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_JOB_COSTINGS_SUCCESS': {
      return {
        ...state,
        job_costings: action.job_costings,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_JOB_COSTINGS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      }
    }
    case 'GET_IMPORT_JOB_COSTINGS_REQUEST': {
      return {
        ...state,
        //job_costings: [],
        loadingImport: true,
        import_search: action.payload.search,
        import_filter: action.payload.filter,
        import_current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_IMPORT_JOB_COSTINGS_SUCCESS': {
      return {
        ...state,
        import_job_costings: action.job_costings,
        import_current_page: action.current_page,
        import_page_size: action.page_size,
        import_total: action.total,
        import_search: action.search,
        import_filter: action.filter,
        loadingImport: false,
      };
    }
    case 'GET_IMPORT_JOB_COSTINGS_ERROR': {
      return {
        ...state,
        loadingImport: false,
        error: action.error.message
      }
    }
    case 'CREATE_JOB_COSTING_REQUEST': {
      return {
        ...state,
        job_costing: null,
        errorField: null,
      };
    }

    case "GET_FILTER_ITEMS_JC_REQUEST": {
      return {
        ...state,
        loading: true,
        search: action.search
      };
    }
    case "GET_FILTER_ITEMS_JC_SUCCESS": {
      return {
        ...state,
        loading: false,
        items: action.items
      };
    }

    case 'GET_JOB_COSTING_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        locations: action.locations,
        accounts: action.accounts,
        projects: action.projects,
        departments: action.departments,
        messages: action.messages,
        base_currency: action.base_currency,
      };
    }
    case 'GET_JOB_COSTING_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        error: action.error.message
      };
    }
    
    case 'STORE_JOB_COSTING_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_JOB_COSTING_SUCCESS': {
      return {
        ...state,
        loading: false,
        id_job_costing: action.id_job_costing,
        success: action.success.message
      };
    }
    case 'STORE_JOB_COSTING_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_JOB_COSTING_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit:true,
        errorField: null,
      };
    }
    case 'EDIT_JOB_COSTING_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit:false,
        job_costing: action.job_costing
      };
    }
    case 'EDIT_JOB_COSTING_ERROR': {
      return {
        ...state,
        // loading: false,
        // loadingEdit:false,
        error: action.error.message
      };
    }
    case 'UPDATE_JOB_COSTING_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_JOB_COSTING_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_JOB_COSTING_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_JOB_COSTING_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_JOB_COSTING_SUCCESS': {
      return {
        ...state,
        loading: false,
        job_costings: action.job_costings
      };
    }
    case "PROCESS_JOB_COSTING_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "PROCESS_JOB_COSTING_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "PROCESS_JOB_COSTING_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CANCEL_JOB_COSTING_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "CANCEL_JOB_COSTING_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "CANCEL_JOB_COSTING_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "UPLOAD_JOB_COSTING_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_JOB_COSTING_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_JOB_COSTING_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_JOB_COSTING_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_JOB_COSTING_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_JOB_COSTING_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_JOB_COSTING_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
