const initState = {
  accounts: [],
  cashBanks: [],
  cashFlowTypes: [],
  currencies: [],
  currencyRates: [],
  current_page: 1,
  customerContacts: [],
  customerAddresses: [],
  customers: [],
  departments: [],
  couriers: [],
  discountBills: [],
  discountItems: [],
  employees: [],
  error: null,
  errorField: null,
  filter: null,
  id_customer: null,
  id_sales_order: null,
  id_location: null,
  info: [], //display ordered item which exceeds available qty
  infoMessages: {},
  itemRequests: [],
  items: [],
  item_requests_current_page: 1,
  item_requests_page_size: null,
  item_requests_total: null,
  loading: false,
  loadingAttachment: false,
  loadingCancel: false,
  loadingConfirm: false,
  loadingContact: false,
  loadingClose: false,
  loadingForeign: false,
  loadingItems: false,
  loadingItemRequests: false,
  loadingSubmit: false,
  loadingUnconfirm: false,
  loadingUpdateSalesOrderDetails: false,
  locations: [],
  page_size: null,
  paymentCategories: [],
  paymentTerms: [],
  projects: [],
  SOTransactionDate: null,
  SODiscountBill: null,
  salesOrders: [],
  salesOrder: null,
  success: null,
  search: null,
  taxes: [],
  total: null,
  transactions: [],
  updateItems: true,
  update_sales_order_details: []
};

export default function salesOrdersReducer(state = initState, action) {
  switch (action.type) {
    case "GET_SALES_ORDERS_REQUEST": {
      return {
        ...state,
        //salesOrders: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_SALES_ORDERS_SUCCESS": {
      return {
        ...state,
        salesOrders: action.salesOrders,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_SALES_ORDERS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_SALES_ORDER_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_SALES_ORDER_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loadingForeign: false,
        accounts: action.accounts,
        cashBanks: action.cashBanks,
        cashFlowTypes: action.cashFlowTypes,
        currencies: action.currencies,
        customers: action.customers,
        departments: action.departments,
        employees: action.employees,
        infoMessages: action.infoMessages,
        locations: action.locations,
        paymentCategories: action.paymentCategories,
        paymentTerms: action.paymentTerms,
        projects: action.projects,
        couriers: action.couriers,
        searchFilter: action.searchFilter,
        taxes: action.taxes
      };
    }
    case "GET_SALES_ORDER_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "CREATE_SALES_ORDER_REQUEST": {
      return {
        ...state,
        id_sales_order: null,
        id_location: null,
        salesOrder: null,
        // loadingTransactions: true,
        loadingItemRequests: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null,
        item_requests_current_page: 1,
        item_requests_page_size: null,
        item_requests_total: null
      };
    }
    case "GET_FILTER_ITEMS_FOR_SO_REQUEST": {
      return {
        ...state,
        loadingItems: true,
        id_customer: action.payload.id_customer,
        id_location: action.payload.id_location,
        id_payment_category: action.payload.id_payment_category,
        search: action.payload.search
      };
    }
    case "GET_FILTER_ITEMS_FOR_SO_SUCCESS": {
      return {
        ...state,
        loadingItems: false,
        discountBills: action.discountBills,
        discountItems: action.discountItems,
        items: action.items
      };
    }
    case "GET_FILTER_ITEMS_BY_LOCATION_REQUEST": {
      return {
        ...state,
        loadingItems: true,
        search: action.payload.search,
        id_location: action.payload.id_location
      };
    }
    case "GET_FILTER_ITEMS_BY_LOCATION_SUCCESS": {
      return {
        ...state,
        loadingItems: false,
        items: action.items
      };
    }
    case "GET_FILTER_SALES_ORDERS_REQUEST": {
      return {
        ...state,
        loadingTransactions: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page
      };
    }
    case "GET_FILTER_SALES_ORDERS_SUCCESS": {
      return {
        ...state,
        loadingTransactions: false,
        transactions: action.transactions,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total
      };
    }
    case "GET_FILTER_SALES_ORDERS_ERROR": {
      return {
        ...state,
        loadingTransactions: false,
        error: action.error.message
      };
    }
    case "GET_FILTER_ITEM_REQUESTS_REQUEST": {
      return {
        ...state,
        loadingItemRequests: true,
        search: action.payload.search,
        filter: action.payload.filter,
        item_requests_current_page: action.payload.page
      };
    }
    case "GET_FILTER_ITEM_REQUESTS_SUCCESS": {
      return {
        ...state,
        loadingItemRequests: false,
        itemRequests: action.itemRequests,
        item_requests_current_page: action.current_page,
        item_requests_page_size: action.page_size,
        item_requests_total: action.total
      };
    }
    case "GET_FILTER_ITEM_REQUESTS_ERROR": {
      return {
        ...state,
        loadingItemRequests: false,
        error: action.error.message
      };
    }
    case "STORE_SALES_ORDER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_SALES_ORDER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_sales_order: action.id_sales_order,
        info: action.info
      };
    }
    case "STORE_SALES_ORDER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_SALES_ORDER_REQUEST": {
      return {
        ...state,
        loading: true,
        id_sales_order: null,
        id_location: null,
        // loadingTransactions: true,
        loadingItemRequests: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null,
        item_requests_current_page: 1,
        item_requests_page_size: null,
        item_requests_total: null
      };
    }
    case "EDIT_SALES_ORDER_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        salesOrder: action.salesOrder
      };
    }
    case "EDIT_SALES_ORDER_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_SALES_ORDER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_SALES_ORDER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_sales_order: action.id_sales_order,
        info: action.info
      };
    }
    case "UPDATE_SALES_ORDER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_SALES_ORDER_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_SALES_ORDER_SUCCESS": {
      return {
        ...state,
        loading: false,
        salesOrders: action.salesOrders
      };
    }
    case "CONFIRM_SALES_ORDER_REQUEST": {
      return {
        ...state,
        loadingConfirm: true
      };
    }
    case "CONFIRM_SALES_ORDER_SUCCESS": {
      return {
        ...state,
        loadingConfirm: false,
        loadingUnconfirm: false,
        success: action.success.message
      };
    }
    case "CONFIRM_SALES_ORDER_ERROR": {
      return {
        ...state,
        loadingConfirm: false,
        error: action.error.message
      };
    }
    case "UNCONFIRM_SALES_ORDER_REQUEST": {
      return {
        ...state,
        loadingUnconfirm: true
      };
    }
    case "UNCONFIRM_SALES_ORDER_SUCCESS": {
      return {
        ...state,
        loadingUnconfirm: false,
        success: action.success.message
      };
    }
    case "UNCONFIRM_SALES_ORDER_ERROR": {
      return {
        ...state,
        loadingUnconfirm: false,
        error: action.error.message
      };
    }
    case "CLOSE_SALES_ORDER_REQUEST": {
      return {
        ...state,
        loadingClose: true
      };
    }
    case "CLOSE_SALES_ORDER_SUCCESS": {
      return {
        ...state,
        loadingClose: false,
        success: action.success.message
      };
    }
    case "CLOSE_SALES_ORDER_ERROR": {
      return {
        ...state,
        loadingClose: false,
        error: action.error.message
      };
    }
    case "CANCEL_SALES_ORDER_REQUEST": {
      return {
        ...state,
        loadingCancel: true
      };
    }
    case "CANCEL_SALES_ORDER_SUCCESS": {
      return {
        ...state,
        loadingCancel: false,
        success: action.success.message
      };
    }
    case "CANCEL_SALES_ORDER_ERROR": {
      return {
        ...state,
        loadingCancel: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    case "GET_CUTOMER_CONTACTS_FOR_SO_REQUEST": {
      return {
        ...state,
        loadingContact: true,
        customerAddresses: [],
        customerContacts: []
      };
    }
    case "GET_CUSTOMER_CONTACTS_FOR_SO_SUCCESS": {
      return {
        ...state,
        customerAddresses: action.customer_addresses,
        customerContacts: action.customer_contacts
      };
    }
    case "UPLOAD_SALES_ORDER_DOCUMENT_REQUEST": {
      return {
        ...state,
        loadingAttachment: true
      };
    }
    case "UPLOAD_SALES_ORDER_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loadingAttachment: false,
        success: action.message
      };
    }
    case "UPLOAD_SALES_ORDER_DOCUMENT_ERROR": {
      return {
        ...state,
        loadingAttachment: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_SALES_ORDER_DOCUMENT_ERROR": {
      return {
        ...state,
        loadingAttachment: false,
        error: action.error.message
      };
    }
    case "DELETE_SALES_ORDER_DOCUMENT_REQUEST": {
      return {
        ...state,
        loadingAttachment: true
      };
    }
    case "DELETE_SALES_ORDER_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loadingAttachment: false,
        success: action.message
      };
    }
    case "DELETE_SALES_ORDER_DOCUMENT_ERROR": {
      return {
        ...state,
        loadingAttachment: false,
        error: action.error.message
      };
    }
    case "GET_UPDATE_SALES_ORDER_DETAILS_REQUEST": {
      return {
        ...state,
        loadingUpdateSalesOrderDetails: true
      };
    }
    case "GET_UPDATE_SALES_ORDER_DETAILS_SUCCESS": {
      return {
        ...state,
        loadingUpdateSalesOrderDetails: false,
        update_sales_order_details: action.payload.update_sales_order_details,
        updateItems: action.payload.updateItems,
        SOTransactionDate: action.payload.transaction_date,
        SODiscountBill: action.payload.discount_bill
      };
    }
    default:
      return state;
  }
}
