import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getSuppliers({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [suppliers, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "suppliers?search=" + search + "&page=" + page),
            call(get, "suppliers/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "suppliers?" + filter + "&page=" + page),
            call(get, "suppliers/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "suppliers?search=" + search),
          call(get, "suppliers/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "suppliers?" + filter),
          call(get, "suppliers/foreign-keys")
        ]);

    // console.log(suppliers);
    // console.log(supplier_types);
    // console.log(locations);

    // Jika sukses get data suppliers
    if (suppliers.status === "success") {
      // Jika di API menggunakan pagination
      if (suppliers.result.current_page && search) {
        yield put({
          type: "GET_SUPPLIERS_SUCCESS",
          suppliers: suppliers.result.data,
          current_page: suppliers.result.current_page,
          page_size: suppliers.result.per_page,
          total: suppliers.result.total,
          search: search
        });
      } else if (suppliers.result.current_page && filter) {
        yield put({
          type: "GET_SUPPLIERS_SUCCESS",
          suppliers: suppliers.result.data,
          current_page: suppliers.result.current_page,
          page_size: suppliers.result.per_page,
          total: suppliers.result.total,
          filter: filter
        });
      } else if (suppliers.result.current_page) {
        yield put({
          type: "GET_SUPPLIERS_SUCCESS",
          suppliers: suppliers.result.data,
          current_page: suppliers.result.current_page,
          page_size: suppliers.result.per_page,
          total: suppliers.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_SUPPLIERS_SUCCESS",
          suppliers: suppliers.result,
          page_size: null,
          total: null
        });
      }
    } else if (suppliers.status === "fail") {
      yield put({
        type: "GET_SUPPLIERS_ERROR",
        error: suppliers
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_SUPPLIERS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_SUPPLIER_FOREIGN_KEYS_SUCCESS",
        supplier_types: foreign_keys.result.supplier_types,
        locations: foreign_keys.result.locations,
        currencies: foreign_keys.result.currencies,
        accounts: foreign_keys.result.accounts,
        taxes: foreign_keys.result.taxes,
        cash_banks: foreign_keys.result.cash_banks,
        payment_categories: foreign_keys.result.payment_categories,
        payment_terms: foreign_keys.result.payment_terms,
        employees: foreign_keys.result.employees,
        countries: foreign_keys.result.countries,
        provinces: foreign_keys.result.provinces,
        cities: foreign_keys.result.cities,
        districts: foreign_keys.result.districts,
        default_id_country: foreign_keys.result.default_id_country,
        default_id_payable_account:
          foreign_keys.result.default_id_payable_account
      });
    } else {
      yield put({
        type: "GET_SUPPLIER_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_SUPPLIERS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Create data
export function* createSupplier() {
  try {
    const [foreign_keys] = yield all([call(get, "suppliers/foreign-keys")]);

    // console.log(foreign_keys);
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_SUPPLIER_FOREIGN_KEYS_SUCCESS",
        supplier_types: foreign_keys.result.supplier_types,
        locations: foreign_keys.result.locations,
        currencies: foreign_keys.result.currencies,
        accounts: foreign_keys.result.accounts,
        taxes: foreign_keys.result.taxes,
        cash_banks: foreign_keys.result.cash_banks,
        payment_categories: foreign_keys.result.payment_categories,
        payment_terms: foreign_keys.result.payment_terms,
        employees: foreign_keys.result.employees,
        countries: foreign_keys.result.countries,
        provinces: foreign_keys.result.provinces,
        cities: foreign_keys.result.cities,
        districts: foreign_keys.result.districts,
        default_id_country: foreign_keys.result.default_id_country,
        default_id_payable_account:
          foreign_keys.result.default_id_payable_account
      });
    } else {
      yield put({
        type: "GET_SUPPLIER_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    yield put({
      type: "CREATE_SUPPLIER_SUCCESS"
    });
  } catch (error) {
    yield put({
      type: "GET_SUPPLIER_FOREIGN_KEYS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Store data
export function* storeSupplier({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "suppliers", body);
    // console.log(body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_SUPPLIER_SUCCESS",
        id_supplier: resultStore.result.id_supplier,
        success: resultStore
      });
    } else if (resultStore.errors || resultStore.status === "fail") {
      yield put({
        type: "STORE_SUPPLIER_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_SUPPLIER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_SUPPLIER_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

