// Edit data Supplier
export function* editSupplier({ payload }) {
  // console.log("masuk");
  const { id_supplier } = payload;

  try {
    const [supplier, foreign_keys] = yield all([
      call(get, "suppliers/" + id_supplier),
      call(get, "suppliers/foreign-keys")
    ]);

    // console.log(supplier);
    // console.log(foreign_keys);

    // Jika sukses get data supplier type
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_SUPPLIER_FOREIGN_KEYS_SUCCESS",
        supplier_types: foreign_keys.result.supplier_types,
        locations: foreign_keys.result.locations,
        currencies: foreign_keys.result.currencies,
        accounts: foreign_keys.result.accounts,
        taxes: foreign_keys.result.taxes,
        cash_banks: foreign_keys.result.cash_banks,
        payment_categories: foreign_keys.result.payment_categories,
        payment_terms: foreign_keys.result.payment_terms,
        employees: foreign_keys.result.employees,
        countries: foreign_keys.result.countries,
        provinces: foreign_keys.result.provinces,
        cities: foreign_keys.result.cities,
        districts: foreign_keys.result.districts,
        default_id_country: foreign_keys.result.default_id_country,
        default_id_payable_account:
          foreign_keys.result.default_id_payable_account
      });
    } else {
      yield put({
        type: "GET_SUPPLIER_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    // Jika sukses
    if (supplier.status === "success") {
      yield put({
        type: "EDIT_SUPPLIER_SUCCESS",
        supplier: supplier.result
      });
    } else if (supplier.status === "fail") {
      yield put({
        type: "EDIT_SUPPLIER_ERROR",
        error: supplier
      });
    } else {
      yield put({
        type: "EDIT_SUPPLIER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_SUPPLIER_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

