// Deactivate data (mengubah is_active => 0)
export function* deactivateSupplier({ payload }) {
  const { id_supplier } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "suppliers/" + id_supplier,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DEACTIVATE_SUPPLIER_SUCCESS",
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_SUPPLIERS_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (deactivateResult.status === "fail" || deactivateResult.errors) {
      yield all([
        yield put({
          type: "DEACTIVATE_SUPPLIER_ERROR",
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_SUPPLIERS_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DEACTIVATE_SUPPLIER_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_SUPPLIERS_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DEACTIVATE_SUPPLIER_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_SUPPLIERS_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

