export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_SUPPLIERS_REQUEST", getSuppliers),
    yield takeEvery("CREATE_SUPPLIER_REQUEST", createSupplier),
    yield takeEvery("STORE_SUPPLIER_REQUEST", storeSupplier),
    yield takeEvery("STORE_SUPPLIER_CONTACT_REQUEST", storeSupplierContact),
    yield takeEvery("STORE_SUPPLIER_ADDRESS_REQUEST", storeSupplierAddress),
    yield takeEvery(
      "STORE_SUPPLIER_OPENING_BALANCE_REQUEST",
      storeSupplierOpeningBalance
    ),
    yield takeEvery("EDIT_SUPPLIER_REQUEST", editSupplier),
    yield takeEvery("UPDATE_SUPPLIER_REQUEST", updateSupplier),
    yield takeEvery("UPDATE_SUPPLIER_CONTACT_REQUEST", updateSupplierContact),
    yield takeEvery("UPDATE_SUPPLIER_ADDRESS_REQUEST", updateSupplierAddress),
    yield takeEvery(
      "UPDATE_SUPPLIER_OPENING_BALANCE_REQUEST",
      updateSupplierOpeningBalance
    ),
    yield takeEvery("SHOW_SUPPLIER_REQUEST", showSupplier),
    yield takeEvery("DEACTIVATE_SUPPLIER_REQUEST", deactivateSupplier),
    yield takeEvery(
      "DEACTIVATE_SUPPLIER_CONTACT_REQUEST",
      deactivateSupplierContact
    ),
    yield takeEvery(
      "DEACTIVATE_SUPPLIER_ADDRESS_REQUEST",
      deactivateSupplierAddress
    ),
    yield takeEvery("ACTIVATE_SUPPLIER_REQUEST", activateSupplier)
  ]);
}
