const initState = {
  id_item_transfer: null,
  itemTransfers: [],
  itemTransfer: null,
  printData: {},
  items: [],
  transactions: [],
  fromLocations: [],
  toLocations: [],
  couriers: [],
  adjustmentTypes: [],
  search: null,
  filter: null,
  loading: false,
  loadingSubmit: false,
  loadingItems: false,
  loadingForeign: false,
  loadingTransactions: false,
  current_page: 1,
  page_size: null,
  total: null,
  prevTransConfirm: null,
  prevTransReceive: null,
  success: null,
  error: null,
  errorField: null
};

export default function itemTransfersReducer(state = initState, action) {
  switch (action.type) {
    case "GET_ITEM_TRANSFERS_REQUEST": {
      return {
        ...state,
        //itemTransfers: [],
        loading: true,
        loadingForeign: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_ITEM_TRANSFERS_SUCCESS": {
      return {
        ...state,
        itemTransfers: action.itemTransfers,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
        loadingForeign: false
      };
    }
    case "GET_ITEM_TRANSFERS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "CREATE_ITEM_TRANSFER_REQUEST": {
      return {
        ...state,
        id_item_transfer: null,
        items: [],
        loadingTransactions: true,
        search: null,
        errorField: null,
        current_page: null,
        page_size: null,
        total: null
      };
    }
    case "GET_FILTER_ITEMS_IT_REQUEST": {
      return {
        ...state,
        loadingItems: true,
        search: action.search
      };
    }
    case "GET_FILTER_ITEMS_IT_SUCCESS": {
      return {
        ...state,
        loadingItems: false,
        items: action.items
      };
    }
    case "GET_FILTER_ITEMS_IT_ERROR": {
      return {
        ...state,
        loadingItems: false,
        error: action.error.message
      };
    }
    case "GET_ITEM_TRANSFER_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_ITEM_TRANSFER_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loadingForeign: false,
        fromLocations: action.fromLocations,
        toLocations: action.toLocations,
        couriers: action.couriers,
        adjustmentTypes: action.adjustmentTypes,
        prevTransConfirm: action.prevTransConfirm,
        prevTransReceive: action.prevTransReceive
      };
    }
    case "GET_ITEM_TRANSFER_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "GET_TRANSACTIONS_IT_REQUEST": {
      return {
        ...state,
        loadingTransactions: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page
      };
    }
    case "GET_TRANSACTIONS_IT_SUCCESS": {
      return {
        ...state,
        loadingTransactions: false,
        transactions: action.transactions,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total
      };
    }
    case "GET_TRANSACTIONS_IT_ERROR": {
      return {
        ...state,
        loadingTransactions: false,
        error: action.error.message
      };
    }
    case "STORE_ITEM_TRANSFER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_ITEM_TRANSFER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_item_transfer: action.id_item_transfer
      };
    }
    case "STORE_ITEM_TRANSFER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_ITEM_TRANSFER_REQUEST": {
      return {
        ...state,
        id_item_transfer: null,
        loading: true,
        loadingTransactions: true,
        search: null,
        errorField: null,
        current_page: null,
        page_size: null,
        total: null
      };
    }
    case "EDIT_ITEM_TRANSFER_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        itemTransfer: action.itemTransfer
      };
    }
    case "EDIT_ITEM_TRANSFER_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_ITEM_TRANSFER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_ITEM_TRANSFER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_item_transfer: action.id_item_transfer
      };
    }
    case "UPDATE_ITEM_TRANSFER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_ITEM_TRANSFER_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_ITEM_TRANSFER_SUCCESS": {
      return {
        ...state,
        loading: false,
        itemTransfers: action.itemTransfers
      };
    }
    case "CONFIRM_ITEM_TRANSFER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CONFIRM_ITEM_TRANSFER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CONFIRM_ITEM_TRANSFER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "RECEIVE_ITEM_TRANSFER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "RECEIVE_ITEM_TRANSFER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "RECEIVE_ITEM_TRANSFER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UNRECEIVE_ITEM_TRANSFER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "UNRECEIVE_ITEM_TRANSFER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UNRECEIVE_ITEM_TRANSFER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CANCEL_ITEM_TRANSFER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CANCEL_ITEM_TRANSFER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CANCEL_ITEM_TRANSFER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPLOAD_ITEM_TRANSFER_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_ITEM_TRANSFER_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_ITEM_TRANSFER_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_ITEM_TRANSFER_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_ITEM_TRANSFER_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_ITEM_TRANSFER_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_ITEM_TRANSFER_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "SET_PRINT_DATA_ITEM_TRANSFER": {
      return {
        ...state,
        printData: action.payload.body
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    default:
      return state;
  }
}
