const initState = {
	accountTypes: [],
	loading: true,
	error: null,
  	errorField: null
};

const accountTypesReducer = (state = initState, action) => {
	switch (action.type) {
		case "GET_ACCOUNT_TYPES_REQUEST" :
			return {
				...state,
				loading:true,
			};

		case "GET_ACCOUNT_TYPES_SUCCESS" :
			return {
				...state,
				loading:false,
				accountTypes : action.accountTypes,
			};
		case "GET_ACCOUNT_TYPES_ERROR" :
			return {
				...state,
				loading:false,
				error: action.error.message,
        		errorField: action.error.errors
			};
		case "CLEAR_SUCCESS":
			return {
				...state,
				success: null
			};
		case "CLEAR_ERROR":
			return {
				...state,
				error: null
			};
		case "CLEAR_ERROR_FIELD":
			return {
				...state,
				errorField: null
			};
		default:
			return {
				...state,
			};
	}
}

export default accountTypesReducer;