import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getTaxes({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [taxes, accounts,] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "configuration-managements/taxes?search=" + search + "&page=" + page),
            call(get, "accounts?is_active=child"), 
          ])
        : // Jika filter
          yield all([
            call(get, "configuration-managements/taxes?" + filter + "&page=" + page), 
            call(get, "accounts?is_active=child"), 
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "configuration-managements/taxes?search=" + search),  
          call(get, "accounts?is_active=child"), 
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "configuration-managements/taxes?" + filter),
          call(get, "accounts?is_active=child"), 
        ]);

    // console.log(taxes);
    // console.log(accounts);

    // Jika sukses get data taxes
    if (taxes.status === "success") {
      // Jika di API menggunakan pagination
      if (taxes.result.current_page && search) {
        yield put({
          type: 'GET_TAXES_SUCCESS',
          taxes: taxes.result.data,
          current_page: taxes.result.current_page,
          page_size: taxes.result.per_page,
          total: taxes.result.total,
          search: search
        });
      }
      else if(taxes.result.current_page && filter)
      {
        yield put({
          type: 'GET_TAXES_SUCCESS',
          taxes: taxes.result.data,
          current_page: taxes.result.current_page,
          page_size: taxes.result.per_page,
          total: taxes.result.total,
          filter: filter
        });
      }
      else if (taxes.result.current_page) {
        yield put({
          type: 'GET_TAXES_SUCCESS',
          taxes: taxes.result.data,
          current_page: taxes.result.current_page,
          page_size: taxes.result.per_page,
          total: taxes.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_TAXES_SUCCESS',
          taxes: taxes.result,
          page_size: null,
          total: null
        });
      }
    } else if (taxes.status === "fail") {
      yield put({
        type: 'GET_TAXES_ERROR',
        error: taxes
      })
    } else {
      yield put({
        type: 'GET_TAXES_ERROR',
        error: {message: "Check your internet connection"}
      })
    }

    // Jika sukses get data account 
    if (accounts.status === "success") {
      yield put({
        type: 'GET_FILTER_ACCOUNTS_SUCCESS',
        accounts: accounts.result
      });
    };
  } catch (error) {
    yield put({
      type: 'GET_TAXES_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createTax() {
  try {
    const [accounts] = yield all([
      call(get, "accounts?is_active=child"), 
    ]);
  
    // Jika sukses get data account 
    if (accounts.status === "success") {
      yield put({
        type: 'GET_FILTER_ACCOUNTS_SUCCESS',
        accounts: accounts.result
      });
    }
    else{
      yield put({
        type: 'GET_FILTER_ACCOUNTS_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_FILTER_ACCOUNTS_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeTax({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "configuration-managements/taxes", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_TAX_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_TAX_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: 'STORE_TAX_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'STORE_TAX_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editTax({ payload }) {
  const { id_tax } = payload;

  try {
    const [tax, accounts] = yield all([
      call(get, "configuration-managements/taxes/" + id_tax),
      call(get, "accounts?is_active=child"), 
    ]);
  
    // Jika sukses get data account 
    if (accounts.status === "success") {
      yield put({
        type: 'GET_FILTER_ACCOUNTS_SUCCESS',
        accounts: accounts.result
      });
    };
  
    // Jika sukses
    if (tax.status === "success") {
      yield put({
        type: 'EDIT_TAX_SUCCESS',
        tax: tax.result
      });
    }
    else{
      yield put({
        type: 'EDIT_TAX_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'EDIT_TAX_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateTax({ payload }) {
  const { id_tax, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "configuration-managements/taxes/" + id_tax,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_TAX_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_TAX_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: 'UPDATE_TAX_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'UPDATE_TAX_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showTax({ payload }) {
  const { id_tax } = payload;

  try {
    const resultShow = yield call(get, "configuration-managements/taxes/" + id_tax);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_TAX_SUCCESS',
        tax: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateTax({ payload }) {
  const { id_tax } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "configuration-managements/taxes/" + id_tax,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: 'DEACTIVATE_TAX_SUCCESS',
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_TAXES_REQUEST',
          payload: { page: false }
        })
      ])     
    }
    else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: 'DEACTIVATE_TAX_ERROR',
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_TAXES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'DEACTIVATE_TAX_ERROR',
          error: {message: "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_TAXES_REQUEST',
          payload: { page: false }
        })
      ]) 
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'DEACTIVATE_TAX_ERROR',
        error: {message: "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_TAXES_REQUEST',
        payload: { page: false }
      })
    ]) 
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activateTax({ payload }) {
  const { id_tax } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "configuration-managements/taxes/" + id_tax,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_TAX_SUCCESS',
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_TAXES_REQUEST',
          payload: { page: false }
        })
      ])     
    }
    else if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_TAX_ERROR',
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_TAXES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'ACTIVATE_TAX_ERROR',
          error: {message: "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_TAXES_REQUEST',
          payload: { page: false }
        })
      ]) 
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'ACTIVATE_TAX_ERROR',
        error: {message: "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_TAXES_REQUEST',
        payload: { page: false }
      })
    ]) 
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_TAXES_REQUEST', getTaxes),
    yield takeEvery('CREATE_TAX_REQUEST', createTax),
    yield takeEvery('STORE_TAX_REQUEST', storeTax),
    yield takeEvery('EDIT_TAX_REQUEST', editTax),
    yield takeEvery('UPDATE_TAX_REQUEST', updateTax),
    yield takeEvery('SHOW_TAX_REQUEST', showTax),
    yield takeEvery('DEACTIVATE_TAX_REQUEST', deactivateTax),
    yield takeEvery('ACTIVATE_TAX_REQUEST', activateTax)
  ]);
}
