import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getGeneralLedgers() {
  try {
    const [general_ledgers, accounts] = yield all([
      call(get, "gl-configs"),
      call(get, "accounts?is_active=child")
    ]);
  
    // console.log(general_ledgers);
  
    // Jika sukses get data general_ledgers dan account
    if (general_ledgers.status === "success" && accounts.status === "success") {
      yield put({
        type: "GET_GENERAL_LEDGERS_SUCCESS",
        general_ledgers: general_ledgers.result,
        accounts: accounts.result
      });
    } 
    else if(general_ledgers.status === "fail"){
      yield put({
        type: "GET_GENERAL_LEDGERS_ERROR",
        error: general_ledgers
      });
    }
    else {
      yield put({
        type: "GET_GENERAL_LEDGERS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_GENERAL_LEDGERS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateGeneralLedger({ payload }) {
  const { body } = payload;

  try {
    const resultUpdate = yield call(putApi, "gl-configs/update", {gl_configs : body});
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: "UPDATE_GENERAL_LEDGER_SUCCESS",
          success: resultUpdate
        }),
        yield put({
          type: "GET_GENERAL_LEDGERS_REQUEST"
        })
      ]);
    } else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_GENERAL_LEDGER_ERROR",
        error: resultUpdate
      });
    } else {
      yield put({
        type: "UPDATE_GENERAL_LEDGER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_GENERAL_LEDGER_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}
export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_GENERAL_LEDGERS_REQUEST", getGeneralLedgers),
    yield takeEvery("UPDATE_GENERAL_LEDGER_REQUEST", updateGeneralLedger)
  ]);
}
