import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getSalesOrders({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [salesOrders, foreign] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "sales/sales-orders?" + search + "&page=" + page),
            call(get, "sales/sales-orders/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "sales/sales-orders?" + filter + "&page=" + page),
            call(get, "sales/sales-orders/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "sales/sales-orders?" + search),
          call(get, "sales/sales-orders/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "sales/sales-orders?" + filter),
          call(get, "sales/sales-orders/foreign-keys")
        ]);

    // console.log(salesOrders);

    // Jika sukses get data salesOrders
    if (salesOrders.status === "success") {
      // Jika di API menggunakan pagination
      if (salesOrders.result.current_page && search) {
        yield put({
          type: "GET_SALES_ORDERS_SUCCESS",
          salesOrders: salesOrders.result.data,
          current_page: salesOrders.result.current_page,
          page_size: salesOrders.result.per_page,
          total: salesOrders.result.total,
          search: search
        });
      } else if (salesOrders.result.current_page && filter) {
        yield put({
          type: "GET_SALES_ORDERS_SUCCESS",
          salesOrders: salesOrders.result.data,
          current_page: salesOrders.result.current_page,
          page_size: salesOrders.result.per_page,
          total: salesOrders.result.total,
          filter: filter
        });
      } else if (salesOrders.result.current_page) {
        yield put({
          type: "GET_SALES_ORDERS_SUCCESS",
          salesOrders: salesOrders.result.data,
          current_page: salesOrders.result.current_page,
          page_size: salesOrders.result.per_page,
          total: salesOrders.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_SALES_ORDERS_SUCCESS",
          salesOrders: salesOrders.result,
          page_size: null,
          total: null
        });
      }
    } else if (salesOrders.status === "fail") {
      // Set message ke session storage
      yield put({
        type: "GET_SALES_ORDERS_ERROR",
        error: { message: salesOrders.result }
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_SALES_ORDERS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      yield put({
        type: "GET_SALES_ORDER_FOREIGN_KEYS_SUCCESS",
        accounts: foreign.result.accounts,
        cashBanks: foreign.result.cash_banks,
        cashFlowTypes: foreign.result.cash_flow_types,
        currencies: foreign.result.currencies,
        customers: foreign.result.customers,
        departments: foreign.result.departments,
        employees: foreign.result.employees,
        infoMessages: foreign.result.info_messages,
        locations: foreign.result.locations,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        projects: foreign.result.projects,
        couriers: foreign.result.couriers,
        searchFilter: foreign.result.search_filter,
        taxes: foreign.result.taxes
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_SALES_ORDER_FOREIGN_KEYS_ERROR",
        error: { message: foreign.result }
      });
    } else {
      yield put({
        type: "GET_SALES_ORDER_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_SALES_ORDER_FOREIGN_KEYS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Get Items data
export function* getItemsByLocation({ payload }) {
  const { id_location, search } = payload;
  let filter = "items?is_active=1&limit=1";
  filter = search
    ? filter + "&search=" + search + "&id_location=" + id_location
    : filter + "&id_location=" + id_location;

  try {
    const items = yield call(get, filter);

    if (items.status === "success") {
      yield put({
        type: "GET_FILTER_ITEMS_BY_LOCATION_SUCCESS",
        items: items.result.items
      });
      // console.log("items results", items.result.items);
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

