// Get Items data
export function* getItemsForSO({ payload }) {
  const {
    id_customer,
    id_location,
    id_payment_category,
    date,
    search
  } = payload;
  let filter = "items?is_active=1&limit=1";
  filter = search
    ? `${filter}&search=${search}&id_customer=${id_customer}&id_location=${id_location}&id_payment_category=${id_payment_category}&transaction_date=${date}`
    : `${filter}&id_customer=${id_customer}&id_location=${id_location}&id_payment_category=${id_payment_category}&transaction_date=${date}`;

  try {
    const items = yield call(get, filter);

    if (items.status === "success") {
      yield put({
        type: "GET_FILTER_ITEMS_FOR_SO_SUCCESS",
        discountBills: items.result.discount_bills,
        discountItems: items.result.discount_items,
        items: items.result.items
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeSalesOrder({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "sales/sales-orders", body);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_SALES_ORDER_SUCCESS",
        success: { message: resultStore.message },
        info: resultStore.info ? resultStore.info : [],
        id_sales_order: resultStore.result.id_sales_order
      });
    } else if (resultStore.errors) {
      yield put({
        type: "STORE_SALES_ORDER_ERROR",
        error: resultStore
      });
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_SALES_ORDER_ERROR",
        error: { message: resultStore.message }
      });
    } else {
      yield put({
        type: "STORE_SALES_ORDER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

