// Update data
export function* updateSalesOrder({ payload }) {
  const { id_sales_order, body } = payload;
  // console.log(body);

  try {
    const resultUpdate = yield call(
      putApi,
      "sales/sales-orders/" + id_sales_order,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: "UPDATE_SALES_ORDER_SUCCESS",
          success: { message: resultUpdate.message },
          info: resultUpdate.info ? resultUpdate.info : [],
          id_sales_order: id_sales_order
        }),
        yield put({
          type: "EDIT_SALES_ORDER_REQUEST",
          payload: { id_sales_order: resultUpdate.result.id_sales_order }
        })
      ]);
    } else if (resultUpdate.errors) {
      yield all([
        yield put({
          type: "UPDATE_SALES_ORDER_ERROR",
          error: resultUpdate
        }),
        yield put({
          type: "EDIT_SALES_ORDER_REQUEST",
          payload: { id_sales_order: resultUpdate.result.id_sales_order }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_SALES_ORDER_ERROR",
        error: { message: resultUpdate.message }
      });
    } else {
      yield put({
        type: "UPDATE_SALES_ORDER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_SALES_ORDER_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

