export function* confirmSalesOrder({ payload }) {
  const { id_sales_order, body } = payload;

  try {
    // POST data ke API
    const confirmResult = yield call(
      putApi,
      "sales/sales-orders/confirm-transaction/" + id_sales_order,
      body
    );

    // Jika sukses
    if (confirmResult.status === "success") {
      // Set session message
      yield all([
        put({
          type: "CONFIRM_SALES_ORDER_SUCCESS",
          success: { message: confirmResult.message }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_SALES_ORDER_REQUEST",
          payload: {
            id_sales_order: id_sales_order
          }
        })
      ]);
    } else if (confirmResult.status === "fail") {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_SALES_ORDER_ERROR",
          error: { message: confirmResult.message }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_SALES_ORDER_REQUEST",
          payload: { id_sales_order: id_sales_order }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_SALES_ORDER_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_SALES_ORDER_REQUEST",
          payload: { id_sales_order: id_sales_order }
        })
      ]);
    }
  } catch (error) {
    yield all([
      put({
        type: "CONFIRM_SALES_ORDER_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      put({
        type: "EDIT_SALES_ORDER_REQUEST",
        payload: { id_sales_order: id_sales_order }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

