export function* getCustomerContacts({ payload }) {
  const { id_customer } = payload;

  try {
    // POST ke API
    const contactResult = yield call(
      get,
      "customers/contacts/by-customer/" + id_customer
    );

    // Jika sukses
    if (contactResult.status === "success") {
      yield put({
        type: "GET_CUSTOMER_CONTACTS_FOR_SO_SUCCESS",
        customer_contacts: contactResult.result.customer_contacts,
        customer_addresses: contactResult.result.customer_addresses
      });
    }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// Upload Document
export function* uploadSalesOrderDocument({ payload }) {
  const { id_sales_order, body } = payload;
  let data = {
    ...body,
    id_sales_order
  };

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "sales/sales-orders/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: "UPLOAD_SALES_ORDER_DOCUMENT_SUCCESS",
          message: "Successfully upload sales order document",
          success: uploadResult
        }),
        yield put({
          type: "EDIT_SALES_ORDER_REQUEST",
          payload: { id_sales_order }
        })
      ]);
    } else if (uploadResult.errors) {
      yield all([
        yield put({
          type: "UPLOAD_SALES_ORDER_DOCUMENT_ERROR",
          error: { message: uploadResult.errors.document[0] }
        }),
        yield put({
          type: "EDIT_SALES_ORDER_REQUEST",
          payload: { id_sales_order }
        })
      ]);
    } else if (uploadResult.status === "fail") {
      yield all([
        yield put({
          type: "UPLOAD_SALES_ORDER_DOCUMENT_ERROR",
          error: { message: uploadResult.message }
        }),
        yield put({
          type: "EDIT_SALES_ORDER_REQUEST",
          payload: { id_sales_order }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "UPLOAD_SALES_ORDER_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "EDIT_SALES_ORDER_REQUEST",
          payload: { id_sales_order }
        })
      ]);
    }

    // }
  } catch (error) {
    yield all([
      yield put({
        type: "UPLOAD_SALES_ORDER_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "EDIT_SALES_ORDER_REQUEST",
        payload: { id_sales_order }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

// Download Document
export function* downloadSalesOrderDocument({ payload }) {
  const { id_sales_order_document } = payload;

  try {
    // POST ke API
    // console.log("masukk")
    yield call(
      get,
      "sales/sales-orders/documents/download/" + id_sales_order_document
    );
    // }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// delete document
export function* deleteSalesOrderDocument({ payload }) {
  const { id_sales_order_document, id_sales_order } = payload;
  // const body = { is_active: 0 };

  try {
    // POST data ke API
    const deleteResult = yield call(
      putApi,
      "sales/sales-orders/documents/" + id_sales_order_document
      //  body
    );

    // Jika sukses
    if (deleteResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_SALES_ORDER_DOCUMENT_SUCCESS",
          message: "Successfully delete sales order document",
          success: deleteResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_SALES_ORDER_REQUEST",
          payload: { id_sales_order: id_sales_order }
        })
      ]);
    } else if (deleteResult.status === "fail") {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_SALES_ORDER_DOCUMENT_ERROR",
          error: { message: deleteResult.message }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_SALES_ORDER_REQUEST",
          payload: { id_sales_order: id_sales_order }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_SALES_ORDER_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_SALES_ORDER_REQUEST",
          payload: { id_sales_order: id_sales_order }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DELETE_SALES_ORDER_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "EDIT_SALES_ORDER_REQUEST",
        payload: { id_sales_order: id_sales_order }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

// get update sales order details data
export function* getUpdateSalesOrderDetails({ payload }) {
  const { body } = payload;

  const {
    id_customer,
    id_location,
    id_payment_category,
    transaction_date
  } = body;

  let filter = "items?is_active=1&limit=1";
  filter = `${filter}&id_customer=${id_customer}&id_location=${id_location}&id_payment_category=${id_payment_category}&transaction_date=${transaction_date}`;

  try {
    const [items, resultUpdate] = yield all([
      yield call(get, filter),
      yield call(post, "sales/sales-orders/discount-prices", body)
    ]);
  
    if (items.status === "success") {
      yield put({
        type: "GET_FILTER_ITEMS_FOR_SO_SUCCESS",
        discountBills: items.result.discount_bills,
        discountItems: items.result.discount_items,
        items: items.result.items
      });
    }
    
    if (resultUpdate.status === "success") {
      yield put({
        type: "GET_UPDATE_SALES_ORDER_DETAILS_SUCCESS",
        payload: {
          transaction_date: resultUpdate.result.transaction_date,
          updateItems: body.updateItems,
          discount_bill: body.discount_bill,
          update_sales_order_details: resultUpdate.result.sales_order_details
        }
      });
    }
    // else if (resultUpdate.status === "fail") {
    //   yield put({
    //     type: "UPDATE_SALES_INVOICE_ERROR",
    //     error: { message: resultUpdate.message }
    //   });
    // }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_SALES_ORDERS_REQUEST", getSalesOrders),
    yield takeEvery("CREATE_SALES_ORDER_REQUEST", createSalesOrder),
    yield takeEvery("GET_FILTER_SALES_ORDERS_REQUEST", getTransactions),
    yield takeEvery("GET_FILTER_ITEM_REQUESTS_REQUEST", getItemRequests),
    yield takeEvery("GET_FILTER_ITEMS_BY_LOCATION_REQUEST", getItemsByLocation),
    yield takeEvery("GET_FILTER_ITEMS_FOR_SO_REQUEST", getItemsForSO),
    yield takeEvery("STORE_SALES_ORDER_REQUEST", storeSalesOrder),
    yield takeEvery("EDIT_SALES_ORDER_REQUEST", editSalesOrder),
    yield takeEvery("UPDATE_SALES_ORDER_REQUEST", updateSalesOrder),
    yield takeEvery("SHOW_SALES_ORDER_REQUEST", showSalesOrder),
    yield takeEvery("CONFIRM_SALES_ORDER_REQUEST", confirmSalesOrder),
    yield takeEvery("UNCONFIRM_SALES_ORDER_REQUEST", unconfirmSalesOrder),
    yield takeEvery("CLOSE_SALES_ORDER_REQUEST", closeSalesOrder),
    yield takeEvery("CANCEL_SALES_ORDER_REQUEST", cancelSalesOrder),
    yield takeEvery("GET_CUTOMER_CONTACTS_FOR_SO_REQUEST", getCustomerContacts),
    yield takeEvery(
      "UPLOAD_SALES_ORDER_DOCUMENT_REQUEST",
      uploadSalesOrderDocument
    ),
    yield takeEvery(
      "DOWNLOAD_SALES_ORDER_DOCUMENT_REQUEST",
      downloadSalesOrderDocument
    ),
    yield takeEvery(
      "DELETE_SALES_ORDER_DOCUMENT_REQUEST",
      deleteSalesOrderDocument
    ),
    yield takeEvery(
      "GET_UPDATE_SALES_ORDER_DETAILS_REQUEST",
      getUpdateSalesOrderDetails
    )
  ]);
}
