const initState = {
  users: [],
  user: null,
  search: null,
  filter: null,
  roles: [],
  loading: false,
  loadingSubmit: false,
  loadingFilterRoles: true,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function usersReducer(state = initState, action) {
  switch (action.type) {
    case "GET_USERS_REQUEST": {
      return {
        ...state,
        //users: [],
        roles:[],
        loading: true,
        loadingFilterRoles: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_USERS_SUCCESS": {
      return {
        ...state,
        users: action.users,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_ROLES_SUCCESS": {
      return {
        ...state,
        loadingFilterRoles: false,
        roles: action.roles,
      }
    }
    case "GET_ROLES_ERROR": {
      return {
        ...state,
        loadingFilterRoles: false,
        error: action.error.message,
      }
    }
    case "CREATE_USER_REQUEST": {
      return {
        ...state,
        roles : [],
        loadingFilterRoles: true,
      }
    }
    case "CREATE_USER_SUCCESS": {
      return {
        ...state,
        loadingFilterRoles: false,
        roles: action.roles,
      }
    }
    case "STORE_USER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_USER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "STORE_USER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_USER_REQUEST": {
      return {
        ...state,
        roles : [],
        loadingFilterRoles: true,
        loading: true,
        errorField: null
      };
    }
    case "EDIT_USER_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        user: action.user
      };
    }
    case "EDIT_USER_ERROR": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_USER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_USER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_USER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_USER_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_USER_SUCCESS": {
      return {
        ...state,
        loading: false,
        users: action.users
      };
    }
    case "DEACTIVATE_USER_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_USER_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_USER_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_USER_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_USER_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_USER_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
