import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getDebitMemos({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [debit_memos, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "financials/debit-memos?search=" + search + "&page=" + page),
            call(get, "financials/debit-memos/foreign-keys"),
          ])
        : // Jika filter
          yield all([
            call(get, "financials/debit-memos?" + filter + "&page=" + page),
            call(get, "financials/debit-memos/foreign-keys"),
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "financials/debit-memos?search=" + search),
          call(get, "financials/debit-memos/foreign-keys"),
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "financials/debit-memos?" + filter),
          call(get, "financials/debit-memos/foreign-keys"),
        ]);

    // console.log(debit_memos);
    // console.log(foreign_keys);

    // Jika sukses get data debit_memos
    if (debit_memos.status === "success") {
      // Jika di API menggunakan pagination
      if (debit_memos.result.current_page && search) {
        yield put({
          type: 'GET_DEBIT_MEMOS_SUCCESS',
          debit_memos: debit_memos.result.data,
          current_page: debit_memos.result.current_page,
          page_size: debit_memos.result.per_page,
          total: debit_memos.result.total,
          search: search
        });
      }
      else if(debit_memos.result.current_page && filter)
      {
        yield put({
          type: 'GET_DEBIT_MEMOS_SUCCESS',
          debit_memos: debit_memos.result.data,
          current_page: debit_memos.result.current_page,
          page_size: debit_memos.result.per_page,
          total: debit_memos.result.total,
          filter: filter
        });
      }
      else if (debit_memos.result.current_page) {
        yield put({
          type: 'GET_DEBIT_MEMOS_SUCCESS',
          debit_memos: debit_memos.result.data,
          current_page: debit_memos.result.current_page,
          page_size: debit_memos.result.per_page,
          total: debit_memos.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_DEBIT_MEMOS_SUCCESS',
          debit_memos: debit_memos.result,
          page_size: null,
          total: null
        });
      }
    } else if (debit_memos.status === "fail") {
      yield put({
        type: 'GET_DEBIT_MEMOS_ERROR', 
        error: debit_memos
      })
    } else {
      yield put({
        type: 'GET_DEBIT_MEMOS_ERROR', 
        error: {message: "Check your internet connection"}
      })
    }

    // Jika sukses get data debit_memo types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_DEBIT_MEMO_FOREIGN_KEYS_SUCCESS',
        locations: foreign_keys.result.locations,
        suppliers: foreign_keys.result.suppliers,
        cash_banks: foreign_keys.result.cash_banks,
        accounts: foreign_keys.result.accounts,
        cash_flow_types: foreign_keys.result.cash_flow_types,
        currencies: foreign_keys.result.currencies,
        base_currency: foreign_keys.result.base_currency
      });
    }
    else{
      yield put({
        type: 'GET_DEBIT_MEMO_FOREIGN_KEYS_ERROR',
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_DEBIT_MEMOS_ERROR', 
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createDebitMemo() {
  try {
    const [foreign_keys] = yield all([
      call(get, "financials/debit-memos/foreign-keys"),
    ]);
  
    // Jika sukses get data account
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_DEBIT_MEMO_FOREIGN_KEYS_SUCCESS',
        locations: foreign_keys.result.locations,
        suppliers: foreign_keys.result.suppliers,
        cash_banks: foreign_keys.result.cash_banks,
        accounts: foreign_keys.result.accounts,
        cash_flow_types: foreign_keys.result.cash_flow_types,
        currencies: foreign_keys.result.currencies,
        base_currency: foreign_keys.result.base_currency
      });
    }
    else{
      yield put({
        type: 'GET_DEBIT_MEMO_FOREIGN_KEYS_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_DEBIT_MEMO_FOREIGN_KEYS_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeDebitMemo({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "financials/debit-memos", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_DEBIT_MEMO_SUCCESS',
        id_debit_memo: resultStore.result.id_debit_memo,
        success: resultStore
      });
    }
    else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_DEBIT_MEMO_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_DEBIT_MEMO_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_DEBIT_MEMO_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editDebitMemo({ payload }) {
  const { id_debit_memo } = payload;

  try {
    const [debit_memo, foreign_keys] = yield all([
      call(get, "financials/debit-memos/" + id_debit_memo),
      call(get, "financials/debit-memos/foreign-keys"),
    ]);
  
    // Jika sukses
    if (debit_memo.status === "success") {
      yield put({
        type: 'EDIT_DEBIT_MEMO_SUCCESS',
        debit_memo: debit_memo.result
      });
    }
    else{
      yield put({
        type: "EDIT_DEBIT_MEMO_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
    
    // Jika sukses get data debit_memo types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_DEBIT_MEMO_FOREIGN_KEYS_SUCCESS',
        locations: foreign_keys.result.locations,
        suppliers: foreign_keys.result.suppliers,
        cash_banks: foreign_keys.result.cash_banks,
        accounts: foreign_keys.result.accounts,
        cash_flow_types: foreign_keys.result.cash_flow_types,
        currencies: foreign_keys.result.currencies,
        base_currency: foreign_keys.result.base_currency
      });
    }
    else{
      yield put({
        type: 'GET_DEBIT_MEMO_FOREIGN_KEYS_ERROR',
        // error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_DEBIT_MEMO_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateDebitMemo({ payload }) {
  const { id_debit_memo, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "financials/debit-memos/" + id_debit_memo,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: 'UPDATE_DEBIT_MEMO_SUCCESS',
          success: resultUpdate
        }),
        yield put({
          type: 'EDIT_DEBIT_MEMO_REQUEST',
          payload: { id_debit_memo }
        })
      ])
      
    }
    else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_DEBIT_MEMO_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_DEBIT_MEMO_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_DEBIT_MEMO_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showDebitMemo({ payload }) {
  const { id_debit_memo } = payload;

  try {
    const resultShow = yield call(get, "financials/debit-memos/" + id_debit_memo);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_DEBIT_MEMO_SUCCESS',
        debit_memo: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Decancel data (mengubah is_active => 0)
export function* processDebitMemo({ payload }) {
  const { id_debit_memo } = payload;

  try {
    // POST data ke API
    const processResult = yield call(
      putApi,
      "financials/debit-memos/process-transaction/" + id_debit_memo,
      // body
    );

    // Jika sukses
    if (processResult.status === "success") {
      yield all([
        yield put({
          type: 'PROCESS_DEBIT_MEMO_SUCCESS',
          success: processResult
        }),
        yield put({
          type: 'EDIT_DEBIT_MEMO_REQUEST',
          payload: { id_debit_memo }
        })
      ])
    }
    else if(processResult.status === "fail"){
      yield put({
        type: 'PROCESS_DEBIT_MEMO_ERROR',
        error: processResult
      })
    }
    else{
      yield put({
        type: 'PROCESS_DEBIT_MEMO_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'PROCESS_DEBIT_MEMO_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error);
  }
}

