import React, { Component } from "react";
import { connect } from "react-redux";
import Popover from "../../components/uielements/popover";
import IntlMessages from "../../components/utility/intlMessages";
// import userpic from "../../image/user1.png";
import Button from "../../components/uielements/button";
import ModalChangeServer from "./modalChangeServer";
import TopbarDropdownWrapper from "./topbarDropdown.style";
import authAction from "../../redux/auth/actions";
import { APP_VERSION } from "../../helpers/helper";
import { Divider, Badge, Tooltip, Row, Col } from "antd";
import { baseUrl } from "../../helpers/helper";
import actions from '../../redux/auth/actions';
import config from "../LanguageSwitcher/config"

const { logout } = authAction;
const { switchLanguage } = actions;

const langs = config.options

class TopbarUser extends Component {
  constructor(props) {
    super(props);
    this.handleVisibleChange = this.handleVisibleChange.bind(this);
    this.hide = this.hide.bind(this);
    this.state = {
      visible: false,
      visibleModal: false
    };
  }

  hide() {
    this.setState({ visible: false });
  }

  handleVisibleChange() {
    this.setState({ visible: !this.state.visible });
  }

  handleModalChangeServer = () => {
    this.setState({ visibleModal: !this.state.visibleModal });
    this.hide();
  };

  stringToHslColor(username) {
    var hash = 0;
    // param
    const str = username;
    const s = 50;
    const l = 50;
    for (var i = 0; i < str.length; i++) {
      hash = str.charCodeAt(i) + ((hash << 5) - hash);
    }

    var h = hash % 300;
    return "hsl(" + h + ", " + s + "%, " + l + "%)";
  }

  render() {
    const { user, history } = this.props;
    const { roles } = user ? user : [];
    const server = () => {
      const serverName = baseUrl();
      let server = null;
      if (serverName.length > 20) {
        server = serverName.substr(0, 20) + "...";
      } else {
        server = serverName;
      }

      return server;
    };
    const content = (
      <TopbarDropdownWrapper className="isoUserDropdown">
        <span className="isoDropdownUser">{user ? user.username : null}</span>
        <span className="isoDropdownRole">
          {user
            ? roles.map((role, index) => {
              if (index < roles.length - 1) {
                return role.role_name + " | ";
              } else {
                return role.role_name;
              }
            })
            : null}
        </span>
        <Divider style={{ margin: "10px 0" }} />
        <span className="isoDropdownRole" style={{ flexDirection: "column" }}>
          <Row style={{ marginBottom: "7px" }}>
            <Col span={24}>
              <span>{langs.map(lang => {
                if (this.props.auth.selectedLanguage && (lang.languageId === this.props.auth.selectedLanguage)) {
                  return lang.text
                }
              }) || "English"}</span>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              {langs.map(option => {
                const { languageId } = option;
                const customDisabled =
                  languageId === this.props.auth.selectedLanguage
                    ? true
                    : false;

                return (
                  <Button
                    type="default"
                    size="small"
                    disabled={customDisabled}
                    style={{ marginRight: "5px" }}
                    key={languageId}
                    onClick={() => {
                      this.props.switchLanguage(languageId);
                    }}
                  >
                    {languageId}
                  </Button>
                );
              })}
            </Col>
          </Row>
        </span>
        <Divider style={{ margin: "10px 0" }} />
        <span className="isoDropdownRole" style={{ paddingBottom: "2px" }}>
          <Row type="flex">
            <Col span={8} style={{ display: "flex" }}>
              <span style={{ margin: "auto 0" }}>Server</span>
            </Col>
            <Col span={16}>
              <Tooltip title={baseUrl()} placement="bottom">
                <Badge
                  count={server()}
                  style={{ backgroundColor: "#52c41a" }}
                />
              </Tooltip>
            </Col>
          </Row>
        </span>
        <span
          className="isoDropdownRole"
          style={{ marginTop: "5px", paddingBottom: "5px" }}
        >
          <Button
            size="small"
            style={{ width: "100%" }}
            onClick={this.handleModalChangeServer}
          >
            <IntlMessages id="changeUrl" />
          </Button>
        </span>
        <Divider style={{ margin: "10px 0" }} />
        <span className="isoDropdownRole" style={{ paddingBottom: "2px" }}>
          <><IntlMessages id="appVersion" /> {' : '} <strong>&nbsp;{APP_VERSION}</strong></>
        </span>
        {/* <Divider style={{ margin: "10px 0" }} /> */}
        <span className="isoDropdownRole" style={{ paddingBottom: "2px" }}>
          {
            user && user.db_version
              ?
              <>
                <IntlMessages id="dbVersion" /> {' : '} <strong>&nbsp;{user.db_version}</strong>
              </>
              : null
          }
        </span>

        <Divider style={{ margin: "10px 0" }} />
        <a className="isoDropdownLink" onClick={this.props.logout} href="#">
          <IntlMessages id="topbar.logout" />
        </a>
      </TopbarDropdownWrapper>
    );

    return (
      <>
        <ModalChangeServer
          handleModal={this.handleModalChangeServer}
          visible={this.state.visibleModal}
          history={history}
        />
        <Popover
          content={content}
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
          arrowPointAtCenter={true}
          placement="bottomLeft"
        >
          <div
            className="isoImgWrapper"
            style={
              user
                ? {
                  backgroundColor: this.stringToHslColor(user.username)
                }
                : { backgroundColor: "inherit" }
            }
          >
            {user ? (
              <span style={{ fontWeight: "bold", color: "white" }}>
                {user ? user.username.charAt(0) : ""}
              </span>
            ) : // <img alt="user" src={userpic} />
              null}

            <span className="userActivity online" />
          </div>
        </Popover>
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    // auth: state.auth,
    user: state.auth.user
  };
};
export default connect(
  mapStateToProps,
  { logout, switchLanguage }
)(TopbarUser);
