
const initState = {
  currency_rates: [],
  currency_rate: null,
  currencies: [],
  search: null,
  filter: null,
  loading: false,
  loadingEdit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function currencyRateReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_CURRENCY_RATES_REQUEST': {
      return {
        ...state,
        //currency_rates: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_CURRENCY_RATES_SUCCESS': {
      return {
        ...state,
        currency_rates: action.currency_rates,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_CURRENCY_RATES_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        //errorField: action.error.errors
      };
    }
    case 'CREATE_CURRENCY_RATE_REQUEST': {
      return {
        ...state,
        // currency_rate: null,
        errorField: null,
      };
    }
    case "GET_CURRENCY_RATE_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        currencies: action.currencies,
      }
    }
    case "GET_CURRENCY_RATE_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        //errorField: action.error.errors,
        error: action.error.message
      }
    }
    case 'STORE_CURRENCY_RATE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_CURRENCY_RATE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_CURRENCY_RATE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_CURRENCY_RATE_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null,
      };
    }
    case 'EDIT_CURRENCY_RATE_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        currency_rate: action.currency_rate,
      };
    }
    case 'EDIT_CURRENCY_RATE_ERROR': {
      return {
        ...state,
        //loading: false,
        error: action.error.message,
        //errorField: action.error.errors
      };
    }
    case 'UPDATE_CURRENCY_RATE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_CURRENCY_RATE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_CURRENCY_RATE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_CURRENCY_RATE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_CURRENCY_RATE_SUCCESS': {
      return {
        ...state,
        loading: false,
        currency_rates: action.currency_rates
      };
    }
    case "DEACTIVATE_CURRENCY_RATE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_CURRENCY_RATE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_CURRENCY_RATE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_CURRENCY_RATE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_CURRENCY_RATE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_CURRENCY_RATE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
