const initState = {
  cash_flow_daily_reports: [],
  opening_balance: null,
  locations: [],
  cash_banks: [],
  cash_flow_groups: [],
  filter: null,
  filter_select: [],
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function cashFlowDailyReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_CASH_FLOW_DAILY_REPORTS_REQUEST": {
      return {
        ...state,
        //cash_flow_daily_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_CASH_FLOW_DAILY_REPORTS_SUCCESS": {
      return {
        ...state,
        cash_flow_daily_reports: action.cash_flow_daily_reports,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_CASH_FLOW_DAILY_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_CASH_FLOW_REPORT_REQUEST": {
      return {
        ...state,
        cash_flow_report: null,
        errorField: null
      };
    }

    case "GET_CASH_FLOW_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        cash_banks: action.cash_banks,
        cash_flow_groups: action.cash_flow_groups
      };
    }
    case "GET_CASH_FLOW_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
