import { all, takeEvery, put, call, fork } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPettyCashTypes({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [petty_cash_types, accounts] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "accounts/petty-cash-types?search=" + search + "&page=" + page),
            call(get, "accounts?is_active=child"),
          ])
        : // Jika filter
          yield all([
            call(get, "accounts/petty-cash-types?" + filter + "&page=" + page),
            call(get, "accounts?is_active=child"),
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "accounts/petty-cash-types?search=" + search),
          call(get, "accounts?is_active=child"),
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "accounts/petty-cash-types?" + filter),
          call(get, "accounts?is_active=child"),
        ]);

    // console.log(petty_cash_types);
    // console.log(accounts);

    // Jika sukses get data petty_cash_types
    if (petty_cash_types.status === "success") {
      // Jika di API menggunakan pagination
      if (petty_cash_types.result.current_page && search) {
        yield put({
          type: 'GET_PETTY_CASH_TYPES_SUCCESS',
          petty_cash_types: petty_cash_types.result.data,
          current_page: petty_cash_types.result.current_page,
          page_size: petty_cash_types.result.per_page,
          total: petty_cash_types.result.total,
          search: search
        });
      }
      else if(petty_cash_types.result.current_page && filter)
      {
        yield put({
          type: 'GET_PETTY_CASH_TYPES_SUCCESS',
          petty_cash_types: petty_cash_types.result.data,
          current_page: petty_cash_types.result.current_page,
          page_size: petty_cash_types.result.per_page,
          total: petty_cash_types.result.total,
          filter: filter
        });
      }
      else if (petty_cash_types.result.current_page) {
        yield put({
          type: 'GET_PETTY_CASH_TYPES_SUCCESS',
          petty_cash_types: petty_cash_types.result.data,
          current_page: petty_cash_types.result.current_page,
          page_size: petty_cash_types.result.per_page,
          total: petty_cash_types.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_PETTY_CASH_TYPES_SUCCESS',
          petty_cash_types: petty_cash_types.result,
          page_size: null,
          total: null
        });
      }
    } else if (petty_cash_types.status === "fail") {
      yield put({
        type: 'GET_PETTY_CASH_TYPES_ERROR',
        error: petty_cash_types
      });
    } else {
      yield put({
        type: 'GET_PETTY_CASH_TYPES_ERROR',
        error: {message: "Check your internet connection"}
      });
    }

    // Jika sukses get data petty_cash_type types
    if (accounts.status === "success") {
      yield put({
        type: 'GET_ACCOUNTS_SUCCESS',
        accounts: accounts.result
      });
    }
    else{
      yield put({
        type: 'GET_ACCOUNTS_ERROR',
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_PETTY_CASH_TYPES_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createPettyCashType() {
  try {
    const [accounts] = yield all([
      call(get, "accounts?is_active=child"),
    ]);
  
    // Jika sukses get data cash_flow_group
    if (accounts.status === "success") {
      yield put({
        type: 'GET_ACCOUNTS_SUCCESS',
        accounts: accounts.result
      });
    }
    else{
      yield put({
        type: 'GET_ACCOUNTS_ERROR',
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_ACCOUNTS_ERROR',
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storePettyCashType({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "accounts/petty-cash-types", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_PETTY_CASH_TYPE_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_PETTY_CASH_TYPE_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_PETTY_CASH_TYPE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }  
  } catch (error) {
    yield put({
      type: "STORE_PETTY_CASH_TYPE_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editPettyCashType({ payload }) {
  const { id_petty_cash_type } = payload;

  try {
    const [petty_cash_type, accounts] = yield all([
      call(get, "accounts/petty-cash-types/" + id_petty_cash_type),
      call(get, "accounts?is_active=child"),
    ]);
  
    // Jika sukses
    if (petty_cash_type.status === "success") {
      yield put({
        type: 'EDIT_PETTY_CASH_TYPE_SUCCESS',
        petty_cash_type: petty_cash_type.result
      });
    }
    else{
      yield put({
        type: 'EDIT_PETTY_CASH_TYPES_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  
    // Jika sukses get data petty_cash_type types
    if (accounts.status === "success") {
      yield put({
        type: 'GET_ACCOUNTS_SUCCESS',
        accounts: accounts.result
      });
    }
    else{
      yield put({
        type: 'GET_ACCOUNTS_ERROR',
      });
    }
  } catch (error) {
    yield put({
      type: 'EDIT_PETTY_CASH_TYPES_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error);
  }
}

// Update data
export function* updatePettyCashType({ payload }) {
  const { id_petty_cash_type, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "accounts/petty-cash-types/" + id_petty_cash_type,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_PETTY_CASH_TYPE_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_PETTY_CASH_TYPE_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: 'UPDATE_PETTY_CASH_TYPE_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'UPDATE_PETTY_CASH_TYPE_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showPettyCashType({ payload }) {
  const { id_petty_cash_type } = payload;

  try {
    const resultShow = yield call(get, "accounts/petty-cash-types/" + id_petty_cash_type);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_PETTY_CASH_TYPE_SUCCESS',
        petty_cash_type: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivatePettyCashType({ payload }) {
  const { id_petty_cash_type } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "accounts/petty-cash-types/" + id_petty_cash_type,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: "DEACTIVATE_PETTY_CASH_TYPE_SUCCESS",
          success: deactivateResult
        }),
        yield put({
          type: 'GET_PETTY_CASH_TYPES_REQUEST',
          payload: { page: false }
        })
      ])  
    }
    else if (deactivateResult.status === "fail" || deactivateResult.errors) {
      yield all([
        yield put({
          type: "DEACTIVATE_PETTY_CASH_TYPE_ERROR",
          error: deactivateResult
        }),
        yield put({
          type: 'GET_PETTY_CASH_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: "DEACTIVATE_PETTY_CASH_TYPE_ERROR",
          error: {message: "Check your internet connection"}
        }),
        yield put({
          type: 'GET_PETTY_CASH_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DEACTIVATE_PETTY_CASH_TYPE_ERROR",
        error: {message: "Check your internet connection"}
      }),
      yield put({
        type: 'GET_PETTY_CASH_TYPES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error)
  }
}

// Activate data (mengubah is_active => 1)
