import actions from "./actions";

const initState = {
  sites: [],
  site: null,
  loading: false,
  loadingSubmit: false,
  current_page: null,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function siteReducer(state = initState, action) {
  switch (action.type) {
    case actions.GET_SITES_REQUEST: {
      return {
        ...state,
        loading: true,
        current_page: action.payload.page
      };
    }
    case actions.GET_SITES_SUCCESS: {
      return {
        ...state,
        loading: false,
        sites: action.sites,
        page_size: action.page_size,
        total: action.total
      };
    }
    case "GET_SITES_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message,
      };
    }
    case actions.STORE_SITE_REQUEST: {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case actions.STORE_SITE_SUCCESS: {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case actions.STORE_SITE_ERROR: {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case actions.EDIT_SITE_REQUEST: {
      return {
        ...state,
        loading: true,
        site: null
      };
    }
    case actions.EDIT_SITE_SUCCESS: {
      return {
        ...state,
        loading: false,
        site: action.site
      };
    }
    case actions.EDIT_SITE_ERROR: {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case actions.UPDATE_SITE_REQUEST: {
      return {
        ...state,
        errorField: null,
        loadingSubmit: true
      };
    }
    case actions.UPDATE_SITE_SUCCESS: {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case actions.UPDATE_SITE_ERROR: {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case actions.DEACTIVATE_SITE_REQUEST: {
      return {
        ...state,
        loading: true
      };
    }
    case actions.DEACTIVATE_SITE_SUCCESS: {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case actions.DEACTIVATE_SITE_ERROR: {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case actions.ACTIVATE_SITE_REQUEST: {
      return {
        ...state,
        loading: true
      };
    }
    case actions.ACTIVATE_SITE_SUCCESS: {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case actions.ACTIVATE_SITE_ERROR: {
      return {
        ...state,
        loading: false,
        success: action.error.message
      };
    }
    case actions.CLEAR_SUCCESS:
      return {
        ...state,
        success: null
      };
    case actions.CLEAR_ERROR:
      return {
        ...state,
        error: null
      };
    case actions.CLEAR_ERROR_FIELD:
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
