const initState = {
  retained_earning_reports: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  loadingReport: false,
  success: null,
  error: null,
  errorField: null
};

export default function retainedEarningReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_RETAINED_EARNING_REPORTS_REQUEST": {
      return {
        ...state,
        retained_earning_reports: [],
        loadingReport: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_RETAINED_EARNING_REPORTS_SUCCESS": {
      return {
        ...state,
        retained_earning_reports: action.retained_earning_reports,
        // current_page: action.current_page,
        // page_size: action.page_size,
        // total: action.total,
        filter: action.filter,
        search: action.search,
        loadingReport: false
      };
    }
    case "GET_RETAINED_EARNING_REPORTS_ERROR": {
      return {
        ...state,
        loadingReport: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_RETAINED_EARNING_REPORT_REQUEST": {
      return {
        ...state,
        errorField: null
      };
    }

    // case "GET_RETAINED_EARNING_REPORT_FOREIGN_KEYS_SUCCESS": {
    //   return {
    //     ...state,
    //     loading: false,
    //     account_types: action.account_types,
    //     locations: action.locations,
    //     projects: action.projects
    //   };
    // }
    // case "GET_RETAINED_EARNING_REPORT_FOREIGN_KEYS_ERROR": {
    //   return {
    //     ...state,
    //     loading: false,
    //     error: action.error.message
    //   };
    // }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
