import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPurchaseOrderReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/purchase-orders?";
  if(search){
    url += "search=" + search  
  }
  if(filter){
    search
      ? url += "&" + filter
      : url += filter  
  }
  if(page){
    search || filter
      ? url += "&page=" + page
      : url += "page=" + page 
  }

  try {
    if(filter){
      const [purchase_order_reports, foreign_keys] = 
      yield all([
        call(get, url),
        call(get, "reports/purchase-orders/foreign-keys"),
      ])
  
      // Jika sukses get data purchase_order_reports
      if (purchase_order_reports.status === "success") {
        yield put({
          type: 'GET_PURCHASE_ORDER_REPORTS_SUCCESS',
          purchase_order_reports: purchase_order_reports.result.details,
          total_amount: purchase_order_reports.result.summaries.total_amount,
          total_discount: purchase_order_reports.result.summaries.total_discount,
          total_qty: purchase_order_reports.result.summaries.total_qty,
          total_tax: purchase_order_reports.result.summaries.total_tax,
          total_item: purchase_order_reports.result.summaries.total_item,
          total: purchase_order_reports.result.summaries.total,
          filter: filter,
          search: search
        });
      } else if (purchase_order_reports.status === "fail") {
        yield put({
          type: 'GET_PURCHASE_ORDER_REPORTS_ERROR', 
          error: purchase_order_reports
        })
      } else {
        yield put({
          type: 'GET_PURCHASE_ORDER_REPORTS_ERROR', 
          error: {message: "Check your internet connection"}
        })
      }
  
      // Jika sukses get data purchase_order_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: 'GET_PURCHASE_ORDER_REPORT_FOREIGN_KEYS_SUCCESS',
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers,
          currencies: foreign_keys.result.currencies,
          taxes: foreign_keys.result.taxes,
          cash_banks: foreign_keys.result.cash_banks,
          accounts: foreign_keys.result.accounts,
          cash_flow_types: foreign_keys.result.cash_flow_types,
          payment_categories: foreign_keys.result.payment_categories,
          payment_terms: foreign_keys.result.payment_terms,
          departments: foreign_keys.result.departments,
          projects: foreign_keys.result.projects,
          base_currency: foreign_keys.result.base_currency,
          messages: foreign_keys.result.messages,
        });
      }
      else{
        yield put({
          type: 'GET_PURCHASE_ORDER_REPORT_FOREIGN_KEYS_ERROR',
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = 
      yield all([
        call(get, "reports/purchase-orders/foreign-keys"),
      ])
  
      yield put({
        type: 'GET_PURCHASE_ORDER_REPORTS_SUCCESS',
        purchase_order_reports: [],
        total_amount: null,
        total_discount: null,
        total_qty: null,
        total_tax: null,
        total_item: null,
        total: null,
      });
      
      // Jika sukses get data purchase_order_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: 'GET_PURCHASE_ORDER_REPORT_FOREIGN_KEYS_SUCCESS',
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers,
          currencies: foreign_keys.result.currencies,
          taxes: foreign_keys.result.taxes,
          cash_banks: foreign_keys.result.cash_banks,
          accounts: foreign_keys.result.accounts,
          cash_flow_types: foreign_keys.result.cash_flow_types,
          payment_categories: foreign_keys.result.payment_categories,
          payment_terms: foreign_keys.result.payment_terms,
          departments: foreign_keys.result.departments,
          projects: foreign_keys.result.projects,
          base_currency: foreign_keys.result.base_currency,
          messages: foreign_keys.result.messages,
        });
      }
      else if (foreign_keys.status === "fail") {
        yield put({
          type: 'GET_PURCHASE_ORDER_REPORT_FOREIGN_KEYS_ERROR',
          error:foreign_keys
        });
      }
      else{
        yield put({
          type: 'GET_PURCHASE_ORDER_REPORT_FOREIGN_KEYS_ERROR',
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: 'GET_PURCHASE_ORDER_REPORTS_ERROR', 
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}



export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_PURCHASE_ORDER_REPORTS_REQUEST', getPurchaseOrderReports),
  ]);
}
