export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_ITEM_CATEGORIES_REQUEST", getItemCategories),
    yield takeEvery("STORE_ITEM_CATEGORY_REQUEST", storeItemCategory),
    yield takeLatest("SHOW_ITEM_CATEGORY_REQUEST", showItemCategory),
    yield takeEvery("UPDATE_ITEM_CATEGORY_REQUEST", updateItemCategory),
    yield takeEvery("ACTIVATE_ITEM_CATEGORY_REQUEST", activateItemCategory),
    yield takeEvery("DEACTIVATE_ITEM_CATEGORY_REQUEST", deactivateItemCategory)
  ]);
}
