import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getVatInReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/taxes/pajak-masukan?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    if(filter){
      const [vat_in_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/taxes/pajak-masukan/foreign-keys")
      ]);
    
    
      // Jika sukses get data vat_in_reports
      if (vat_in_reports.status === "success") {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_VAT_IN_REPORTS_SUCCESS",
          vat_in_reports: vat_in_reports.result,
          filter_response : vat_in_reports.result.filter,
          filter
        });
      }
      else if (vat_in_reports.status === "fail") {
        yield put({
          type: "GET_VAT_IN_REPORTS_ERROR",
          error: { message: vat_in_reports.message }
        });
      }
      else {
        yield put({
          type: "GET_VAT_IN_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data vat_in_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_VAT_IN_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
        });
      }
      else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_VAT_IN_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      }
      else {
        yield put({
          type: "GET_VAT_IN_REPORT_FOREIGN_KEYS_ERROR",
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/taxes/pajak-masukan/foreign-keys")
      ]);
  
      yield put({
        type: "GET_VAT_IN_REPORTS_SUCCESS",
        vat_in_reports: {
          purchase_invoices: [],
          purchase_returns : []
        },
        filter_response : null
      });
    
      // Jika sukses get data vat_in_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_VAT_IN_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
        });
      }
      else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_VAT_IN_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      }
      else {
        yield put({
          type: "GET_VAT_IN_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_VAT_IN_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_VAT_IN_REPORTS_REQUEST",
      getVatInReports
    )
  ]);
}
